import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ReleaseMilestonesState } from 'src/app/release-management/state/release-milestones.state';
import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { MilestoneType} from 'src/app/release-management/models/milestoneType.model';
import { ReleasePlan } from 'src/app/release-management/models/release.model';
import { MileStone } from 'src/app/release-management/models/release.model';
import { SaveMilestone } from 'src/app/release-management/actions/release-milestones.action';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'matilda-add-milestone',
  templateUrl: './add-milestone.component.html' 
})
export class AddMilestoneComponent implements OnInit {
  public milestoneData : MileStone;
  public addMilestoneForm: FormGroup;
  @Input('milestoneInfo') milestoneInfo: MileStone;
  @Input('releaseInfo') releaseInfo: ReleasePlan;
  @Output() refreshChart = new EventEmitter<string>();
  @Select(ReleaseMilestonesState.milestoneTypes) milestoneTypes$: Observable<MilestoneType[]>;
  // @Select(ReleaseMilestonesState.newMilestone) newmilestone: Observable<any>;
  constructor(private _fb: FormBuilder,public activeModal: NgbActiveModal,private _store: Store) { }

  ngOnInit() {  
    this.initMileStone();
    if(this.milestoneInfo){
      console.log(this.milestoneInfo);
      this.assignMilestonedata();
    }
  }
  
  initMileStone() {
    
    this.addMilestoneForm= this._fb.group({
      
      milestoneTypeCode: [, Validators.required],
      startDate: [, Validators.nullValidator],
      endDate: [, Validators.nullValidator],
      description: ['', Validators.nullValidator],
      percentComplete: ['', Validators.nullValidator],
      // releaseID:['',Validators.required],
      // percentComplete:['',Validators.required]
      
    });
  }

  assignMilestonedata(){
   
     console.log(this.milestoneInfo);
      this.addMilestoneForm.setValue({
        
        milestoneTypeCode:this.milestoneInfo.milestoneTypeCode,
        startDate: new Date( this.milestoneInfo.startDate),
        endDate: new Date(this.milestoneInfo.endDate),
        description: this.milestoneInfo.description,
        percentComplete: this.milestoneInfo.percentComplete 
         

        
      })
    
    
  }
  onSubmit(dataform: MileStone){
    console.log(this.releaseInfo);
    let pipe = new DatePipe('en-US');
    let _milestone  = new MileStone();
    
    _milestone.description = dataform.description;
    _milestone.endDate = dataform.endDate; 
    _milestone.startDate =  dataform.startDate;
    _milestone.milestoneTypeCode = dataform.milestoneTypeCode;    
    _milestone.percentComplete = dataform.percentComplete; 
    if(this.releaseInfo!=null){
    _milestone.releaseId=this.releaseInfo.releaseId;}
    if(this.milestoneInfo!=null){
          _milestone.milestoneId=this.milestoneInfo.milestoneId;//0.20 means 20%
          _milestone.releaseId=this.milestoneInfo.releaseId;
    }
     this._store.dispatch(new  SaveMilestone(_milestone));
     this.refreshChart.emit("Hello");
  }
   
}
