import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { State, process } from '@progress/kendo-data-query';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';

@Component({
  selector: 'matilda-host-package',
  templateUrl: './host-package.component.html',
  styleUrls: ['./host-package.component.scss']
})
export class HostPackageComponent implements OnInit {
  //state to get package list
  packageinfo$: Observable<any>;
  //pagination
  public pageSize = 10;
  public package:any;
  public skip = 0;
  public gridState: State = {
    sort: [],
    skip: 0,
    take: 10,
  };
  public allData(): ExcelExportData {
    const result: ExcelExportData = {
      data: process(this.package, { filter: this.gridState.filter }).data,
    };
    return result;
  }
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
}
public onPageChange(state: any): void {
    this.pageSize = state.take;
}

  constructor( private _store: Store,private _http: HttpClient) {
    this.allData = this.allData.bind(this);
   }
  ngOnInit() {
    this.packageinfo$= this._store.select(state=>state.discoveryResultDetails.hostPackageData);
    this.packageinfo$.subscribe(element=>{
      this.package= element
          });
  }

}
