import { Component, OnInit, Input } from '@angular/core';
import { Task } from 'src/app/release-management/models/release.model';
import { Select } from '@ngxs/store';
import { WorkflowAvailableServicesState } from 'src/app/release-management/state/workflow-available-services.state';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'matilda-view-release-plugin-modal',
  templateUrl: './view-release-plugin-modal.component.html',
  styleUrls: ['./view-release-plugin-modal.component.scss']
})
export class ViewReleasePluginModalComponent implements OnInit {
  @Input('task') task: Task;
  @Input('stageId') stageId: string;
  @Input('previousConfig') previousConfig: any;
  @Select(WorkflowAvailableServicesState.pluginFields) pluginFields$: Observable<any[]>;
  @Select(WorkflowAvailableServicesState.pluginLoadMessage) pluginLoadMessage$: Observable<string>;
  constructor() { }

  ngOnInit() {
  }

}
