import { JsonProperty } from "json-typescript-mapper";

export class Savings {
    @JsonProperty('application_name')
    application: string = '';
    @JsonProperty('resource_id')
    resourceID: string = '';
    @JsonProperty('resource_name')
    resourceName: string = '';
    @JsonProperty('environment')
    environment: string = null;
    @JsonProperty('project_name')
    project: string = '';
    @JsonProperty('cost_spent')
    costSpent: string = '';
    @JsonProperty('projected_savings_monthly')
    projectedSavingsMonthly: number = null;
    @JsonProperty('projected_savings_yearly')
    projectedSavingsYearly: number = null;
    @JsonProperty('recommendations')
    recommendations: string = '';
    @JsonProperty('private_ip_address')
    privateIPAddress: number = 0;
    @JsonProperty('instance_state')
    instanceState: string = '';
    @JsonProperty('instance_type')
    instanceType: string = '';
    @JsonProperty('availability_zone')
    availabilityZone: string = '';
    @JsonProperty('no_vcpu')
    numberOfvCPUs : string = '';
}