import { Project, Application } from "./project.model";
import { Platform, Operatingsystem } from "./platform.model";
import { ReleaseType } from "./releaseType.model";
import { VNFNode, VNFSite } from "./VNFNodeType.model";

export class ExistingReleasePlan {
    releaseType: ReleaseType = null;
    name: string;
    version: string;
    duration: string;
    description: string;
    releaseDate: string;
    project: Project;
    application: Application;
    vnfNodeType: VNFNode;
    vnfSite: VNFSite;
    platform: Platform;
    operatingsystem: Operatingsystem;

    constructor(plan: any){
        this.releaseType = plan.releaseType;
        this.name = plan.name;
        this.version = plan.version;
        this.duration = plan.duration;
        this.description = plan.description;
        this.releaseDate = plan.releaseDate;
        this.project = plan.projectId;
        this.application = plan.applicationId;
        this.vnfNodeType = plan.vnfNodeType;
        this.vnfSite = plan.vnfSite;
        this.platform = plan.platformId;
        this.operatingsystem = plan.operatingsystemId;
    }
}