import { MileStone } from "../models/release.model";
import { State, Action, StateContext, Selector } from "@ngxs/store";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { ReleaseMgmtAppSettings } from "../release-management.settings";

import { tap } from "rxjs/operators/tap";
import { UpdateMilestoneList, GETMilestoneTypes, GETMilestones, AddMilestoneType, ClearMilestones,SaveMilestone } from "../actions/release-milestones.action";
import { deserialize, serialize } from "json-typescript-mapper";
import { MilestoneType } from "../models/milestoneType.model";
import * as _ from 'lodash';
import { NotificationsService } from "angular2-notifications";
import { shareReplay } from "rxjs/operators";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";

export class MilestonesModel {
    milestones: MileStone[];
    milestoneTypes: MilestoneType[];
    newMilestone:any
}

@State<MilestonesModel>({
    name: 'milestones',
    defaults: {
        milestones: [],
        milestoneTypes: [],
        newMilestone:null,
    }
})
export class ReleaseMilestonesState {
    public httpOptions = {
        headers: new HttpHeaders(
            { 'Content-Type': 'application/json' }
        )
    };

    constructor(private _http: HttpClient, private _releaseMgmtAppSettings: ReleaseMgmtAppSettings,
        private _notificationsService: NotificationsService, private _modalService: NgbModal,) { }
    @Selector()
    static milestoneTypes(state: MilestonesModel) {
        return state.milestoneTypes;
    }
    @Selector()
    static milestones(state: MilestonesModel) {
        return state.milestones;
    }

    @Action(UpdateMilestoneList)
    updateMilestoneList(context: StateContext<MilestonesModel>, { payload }: UpdateMilestoneList) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('GET_MILESTONES');
        let releaseId = payload.releaseId;
        let milestones = payload.milestones;
        let serializedPayload = milestones.map(_milestone => serialize(_milestone));

        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        return this._http.post(_.template(url)({releaseId}), serializedPayload[0], this.httpOptions)
        .subscribe(
            (_milestoneResponse: any) => {
                if (_milestoneResponse) {
                    state.milestones = milestones;
                    this._notificationsService.remove();
                    this._notificationsService.success('', 'Milestones have been added successfully');
                    context.patchState(state);
                }
            },
            (error) => {
                this._notificationsService.remove();
                if (error.status === 0) {
                    this._notificationsService.error('Error Occurred', 'Server is not responding');
                } else if (error.status === 400) {
                    this._notificationsService.error('Something went wrong', error.error['message']);
                } else {
                    this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                }
            }
        );
    }

    @Action(GETMilestoneTypes)
    getMilestoneTypes(context: StateContext<MilestonesModel>, { payload }: GETMilestoneTypes) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('GET_MILESTONE_TYPES');
        let releaseTypeCode = payload;
        return this._http.get(_.template(url)({})).pipe(
            tap((data: any[]) => {
                if (data.length > 0) {
                    let mappedMilestoneTypes = data.map(_milestoneType => deserialize(MilestoneType, _milestoneType));
                    state.milestoneTypes = mappedMilestoneTypes;
                    context.patchState(state);
                }
            }));
    }

    @Action(GETMilestones)
    getMilestones(context: StateContext<MilestonesModel>, { payload }: GETMilestones) {
        var state = context.getState();        
        let url = this._releaseMgmtAppSettings.getURL('GET_MILESTONES');
        let releaseId = payload;
        return this._http.get(_.template(url)({releaseId})).pipe(
            tap((data: any[]) => {
                if (data) {
                    let mappedMilestones = data.map(_milestone => deserialize(MileStone, _milestone));
                    state.milestones = mappedMilestones;
                    context.patchState(state);
                }
            }));
    }

    @Action(AddMilestoneType)
    addMilestoneType(context: StateContext<MilestonesModel>, { payload }: AddMilestoneType) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('GET_MILESTONE_TYPES');
        let releaseId = payload.releaseId;
        let releaseTypeCode = payload.releaseTypeCode;
        let newMilestoneType = serialize(payload.milestoneType);
        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        return this._http.put(_.template(url)({releaseTypeCode}),newMilestoneType, this.httpOptions).pipe(
            tap((data) => {
                if (data) {
                    this._notificationsService.remove();
                    this._notificationsService.success('', 'MilestoneType has been added successfully');
                   context.dispatch(new GETMilestoneTypes(releaseTypeCode));
                }
            },
            (error) => {
                this._notificationsService.remove();
                if (error.status === 0) {
                    this._notificationsService.error('Error Occurred', 'Server is not responding');
                } else if (error.status === 400) {
                    this._notificationsService.error('Something went wrong', error.error['message']);
                } else {
                    this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                }
            }
            ),
            shareReplay()
            );
    }

    @Action(ClearMilestones)
    clearMilestones(context: StateContext<MilestonesModel>, { payload }: ClearMilestones) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('DEL_MILESTONES');
        var releaseId=payload.releaseId;
        var milestoneId=payload.milestoneId;
        return this._http.delete(_.template(url)({releaseId,milestoneId})).pipe(
            tap((data) => {

                this._notificationsService.success('', 'Milestone  has been deleted successfully');
                context.dispatch(new GETMilestones(payload.releaseId));
            },

        ));
        state.milestones = [];
        context.patchState(state);
    }

    @Action(SaveMilestone)
    saveMilestone(context: StateContext<MilestonesModel>, { payload }: SaveMilestone) {
        var state = context.getState();
        
        let releaseId = payload.releaseId;
        let milestoneId = payload.milestoneId;
        let addMilestone = serialize(payload);
        console.log(payload);
        if(milestoneId == 0){
            let url = this._releaseMgmtAppSettings.getURL('GET_MILESTONES');
        return this._http.post(_.template(url)({releaseId}),addMilestone, this.httpOptions).pipe(
            tap((data) => {
                if (data) {
                    console.log(data);
                  //  state.newMilestone = { "isCreated": true };
                   // let addedMilestone = deserialize(MileStone, data);
                   // state.milestones.push(addedMilestone);
                    this._notificationsService.success('', 'Milestone saved successfully');
                    this._modalService.dismissAll();
                    context.dispatch(new GETMilestones(payload.releaseId));
                }
            },
            ));
        }
        if(milestoneId != 0)
        {
            let url = this._releaseMgmtAppSettings.getURL('SAVE_MILESTONES');
            return this._http.put(_.template(url)({releaseId,milestoneId}),addMilestone, this.httpOptions).pipe(
                tap((data) => {
                    if (data) {
                        console.log(data);
                      //  state.newMilestone = { "isCreated": true };
                       // let addedMilestone = deserialize(MileStone, data);
                       // state.milestones.push(addedMilestone);
                        this._notificationsService.success('', 'Milestone updated successfully');
                        this._modalService.dismissAll();
                        context.dispatch(new GETMilestones(payload.releaseId));
                    }
                },
                ));

        }
        }


}