import { CloneReleaseComponent } from './../components/clone-release/clone-release.component';
import { State, StateContext, Selector, Action } from '@ngxs/store';
import { Release } from '../models/release.model';
import { DynamicWorkflowState } from './dynamic-workflow.state';
import { ViewDynamicWorkflowState } from './view-dynamic-workflow.state';
import {ReleaseMgmtAppSettings} from '../release-management.settings';
import * as _ from 'lodash';
import { deserialize } from 'json-typescript-mapper';
import { GetAppReleases, GetInfraReleases, CloneRelease, OpenCloneReleaseConfiguration, GetVNFReleases } from '../actions/release.action';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NotificationsService } from 'angular2-notifications';


export class ReleaseStateModel {
  applicationReleases: Release[];
  infrastructureReleases: Release[];
  vnfReleases: Release[];
  applicationMetaData: any[];
  infrastructureMetaData: any[];
  vnfMetaData: any[];
}

@State<ReleaseStateModel>({
  name: 'releases',
  defaults: {
    applicationReleases: [],
    infrastructureReleases: [],
    vnfReleases: [],
    applicationMetaData: [],
    infrastructureMetaData: [],
    vnfMetaData: []
  },
  children: [
    DynamicWorkflowState,
    ViewDynamicWorkflowState
  ]
})

export class ReleaseState {
  public metaData: any[] = [];
  public httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
  };
  constructor(private _http: HttpClient,
    private _notificationsService: NotificationsService,
    private _modalService: NgbModal,
    private _releaseMgmtAppSettings:ReleaseMgmtAppSettings) { }

  
  calculate(val) {
    if (_.size(val) < 5) {
      return 10
    }
    else if (_.size(val) > 5 && _.size(val) < 12) {
      return 40
    }
    else return 20
  }
  @Selector()
  static applicationReleases(state: ReleaseStateModel) {
    return state.applicationReleases;
  }

  @Selector()
  static infrastructureReleases(state: ReleaseStateModel) {
    return state.infrastructureReleases;
  }

  @Selector()
  static vnfReleases(state: ReleaseStateModel) {
    return state.vnfReleases;
  }

  @Selector()
  static applicationMetaData(state: ReleaseStateModel) {
    return state.applicationMetaData;
  }
  @Selector()
  static infrastructureMetaData(state: ReleaseStateModel) {
    return state.infrastructureMetaData;
  }
  @Selector()
  static vnfMetaData(state: ReleaseStateModel) { 
    return state.vnfMetaData;
  }

  
  @Action(OpenCloneReleaseConfiguration)
  openCloneReleaseConfiguration(context: StateContext<ReleaseStateModel>, {payload}: OpenCloneReleaseConfiguration) {
    const state = context.getState();
    const cloneReleaseRef = this._modalService.open(CloneReleaseComponent, { centered: true, backdrop: 'static', keyboard: false });
    cloneReleaseRef.componentInstance.sourceRelease = payload;
  }

  @Action(CloneRelease)
  cloneRelease(context: StateContext<ReleaseStateModel>, {payload}: CloneRelease) {
    const state = context.getState();
    let sourceRelease: Release = payload.sourceRelease;
    let releaseId = sourceRelease.releaseId;
    let sourceReleaseTypeCode = payload.releaseTypeCode;
    let url = this._releaseMgmtAppSettings.getURL('CLONE_RELEASE');
    this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
     return this._http.post(_.template(url)({releaseId}), payload.targetRelease, this.httpOptions)
     .subscribe(
         (cloneReleaseReponse:any) => {
             if (cloneReleaseReponse) {
               if(sourceReleaseTypeCode === 2 || sourceReleaseTypeCode === 3 ){
                 context.dispatch(new GetInfraReleases());
               }else{
                context.dispatch(new GetAppReleases());
               }
               this._notificationsService.remove();
               this._notificationsService.success('Release cloned successfully!', '');
               this._modalService.dismissAll();
             }

         },
         (error) => {
          this._notificationsService.remove();
          if (error.status === 0) {
            this._notificationsService.error('Error Occurred', 'Server is not responding');
        } else if (error.status === 400) {
            this._notificationsService.error('Something went wrong', error.error['message']);
        } else {
            this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
        }
      }
     );
  }

  @Action(GetAppReleases)
  getAppReleases(context: StateContext<ReleaseStateModel>, payload: GetAppReleases) {
    const state = context.getState();
    let url = this._releaseMgmtAppSettings.getURL('GET_RELEASE');
    let releaseTypeCode:number = 1;
    return this._http.get(_.template(url)({releaseTypeCode}))
      .subscribe((data: any[]) => {
        if (data) {
          state.applicationMetaData = [{
            "field": "name",
            "title": "Release Name",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "project",
            "title": "Project",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "application",
            "title": "Application",
            "locked": false,
            "filterable": true,
            "width": 10
          },
          {
            "field": "version",
            "title": "Version",
            "locked": false,
            "filterable": true,
            "width": 10
          },
          {
            "field": "status",
            "title": "Status",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "duration",
            "title": "Duration",
            "locked": false,
            "filterable": false,
            "width": 11
          },
          {
            "field": "releaseDate",
            "title": "Release Date",
            "locked": false,
            "type": "date",
            "format": "{0:MM/dd/yyyy}",
            "filterable": true,
            "width": 14
          },
          {
            "field": "button",
            "title": "Actions",
            "width": 15
          }
          ];
          state.applicationReleases = [];
          data.forEach((_release) => {
            let date = new Date(_release.release_dt);
            let year = date.getFullYear();
            let month = date.getMonth();
            let dt = date.getDate();
            _release.release_dt = new Date(year,month,dt);
            const _mappedRelease = deserialize(Release, _release);
            state.applicationReleases.push(_mappedRelease);
          });
          context.patchState(state);
        }
      });

  }

  @Action(GetInfraReleases)
  getInfraReleases(context: StateContext<ReleaseStateModel>, payload: GetInfraReleases) {
    const state = context.getState();
    let releaseTypeCode:number = 2;
    let url = this._releaseMgmtAppSettings.getURL('GET_RELEASE');
    return this._http.get(_.template(url)({releaseTypeCode}))
      .subscribe((data: any[]) => {
        if (data) {
          state.infrastructureMetaData = [{
            "field": "name",
            "title": "Release Name",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "platform",
            "title": "Platform",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "operatingsystem",
            "title": "Operating System",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "version",
            "title": "Version",
            "locked": false,
            "filterable": true,
            "width": 10
          },
          {
            "field": "status",
            "title": "Status",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "duration",
            "title": "Duration",
            "locked": false,
            "filterable": false,
            "width": 11
          },
          {
            "field": "releaseDate",
            "title": "Release Date",
            "locked": false,
            "type": "date",
            "format": "{0:MM/dd/yyyy}",
            //"format": "{0:d}",
            "filterable": true,
            "width": 14
          },
          {
            "field": "button",
            "title": "Actions",
            "width": 15
          }
          ];
          state.infrastructureReleases = [];

          data.forEach((_release) => {
            let date = new Date(_release.release_dt);
            let year = date.getFullYear();
            let month = date.getMonth();
            let dt = date.getDate();
            _release.release_dt = new Date(year,month,dt);
            const _mappedRelease = deserialize(Release, _release);
            state.infrastructureReleases.push(_mappedRelease);
          });
          context.patchState(state);
        }

      });

  }

  
  @Action(GetVNFReleases)
  getVNFRelease(context: StateContext<ReleaseStateModel>, payload: GetVNFReleases) {
    const state = context.getState();
    let releaseTypeCode:number = 3;
    let url = this._releaseMgmtAppSettings.getURL('GET_RELEASE');
    return this._http.get(_.template(url)({releaseTypeCode}))
      .subscribe((data: any[]) => {
        if (data) {
          state.vnfMetaData = [{
            "field": "name",
            "title": "Release Name",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "vnfNodeType",
            "title": "VNF Node Type",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "vnfSite",
            "title": "VNF Site",
            "locked": false,
            "filterable": true,
            "width": 11
          },
          {
            "field": "version",
            "title": "Version",
            "locked": false,
            "filterable": true,
            "width": 10
          },
          // {
          //   "field": "status",
          //   "title": "Status",
          //   "locked": false,
          //   "filterable": true,
          //   "width": 11
          // },
          // {
          //   "field": "duration",
          //   "title": "Duration",
          //   "locked": false,
          //   "filterable": false,
          //   "width": 11
          // },
          {
            "field": "releaseDate",
            "title": "Release Date",
            "locked": false,
            "type": "date",
            "format": "{0:MM/dd/yyyy}",
            "filterable": true,
            "width": 14
          },
          
          {
            "field": "button",
            "title": "Actions",
            "width": 15
          }
          ];
          state.vnfReleases = [];

          data.forEach((_release) => {
            let date = new Date(_release.release_dt);
            let year = date.getFullYear();
            let month = date.getMonth();
            let dt = date.getDate();
            _release.release_dt = new Date(year,month,dt);
            const _mappedRelease = deserialize(Release, _release);
            state.vnfReleases.push(_mappedRelease);
          });
          context.patchState(state);
        }

      });

    }

}
