import { Component, OnInit } from '@angular/core';
import * as echarts from 'echarts';
import { ListOfServicesService } from '../../services/list-of-services.service';

@Component({
  selector: 'matilda-releasechart3',
  templateUrl: './releasechart3.component.html',
  styleUrls: ['./releasechart3.component.scss']
})
export class Releasechart3Component implements OnInit {

  public sdata:any=[];
  public sdata1:any[]=[];
  public sdata2:any[]=[];
  public sdata3:any[]=[];
  public keys1=[];
  public values1=[];
  public values2=[];
  public values3=[];
  public arr1=[];
  public arr2=[];
  public arr3=[];

  constructor(private servermetrics:ListOfServicesService){}

  options: any;
  ngOnInit() {
    this.servermetrics.getServerMetrics().subscribe((x) => {
    
      this. sdata= x;
      
  
      this.sdata1=this.sdata.YTD_Normal_Changes_for_ERMEMMWs.YTD_Normal_Changes;
      this.sdata2=this.sdata.YTD_Normal_Changes_for_ERMEMMWs.ERME_YTD_Normal_Changes;
      this.sdata3=this.sdata.YTD_Normal_Changes_for_ERMEMMWs.Goal_Trend_Line;
     
      for(let i=0; i<this.sdata1.length;i++){
        this.keys1.push(Object.keys(this.sdata1[i]));
        this.values1.push(Object.values(this.sdata1[i]));
      }
      for(let j=0; j<this.sdata2.length;j++){
        this.values2.push(Object.values(this.sdata2[j]));
      }
      
      for(let k=0; k<this.sdata3.length;k++){
        this.values3.push(Object.values(this.sdata3[k]));
      }
      
for(let i1=0; i1<this.values1.length; i1++){
  this.arr1[i1] = this.values1[i1][0];
 }
 for(let j1=0; j1<this.values2.length; j1++){
  this.arr2[j1] = this.values2[j1][0];
 }
 for(let k1=0; k1<this.values3.length; k1++){
  this.arr3[k1] = this.values3[k1][0];
 }


             

      this.options = 
       {
            tooltip: {},
        calculable: true,
      xAxis: [
          {
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                rotate: 45
              },
              silent: false,
              splitLine: {
                show: false
              },
             
              type: 'category',
              
              data: this.keys1
          }
      ],
      yAxis: {
          
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          
        },
      series: [
        
          {
              name: '# YTD Normal Changes',
              type: 'bar',
              barGap: 0,
              label: 'labelOption',
              data: this.arr1,
              itemStyle: {
                      normal: {
                        color: new echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            { offset: 0, color: '#34bfa3' },
                            { offset: 0.5, color: '#34bfa3' },
                            { offset: 1, color: '#34bfa3' }
                          ]
                        )
                      }
                    },
          },
          {
              name: 'ERM&E YTD Normal Changes',
              type: 'bar',
              label: 'labelOption',
              data:this.arr2,
              itemStyle: {
                      normal: {
                        color: new echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            { offset: 0, color: '#ffb822' },
                            { offset: 0.5, color: '#ffb822' },
             
                            { offset: 1, color: '#ffb822' }
                          ]
                        )
                      }
                    },
          },
          
          {
            name: 'Goal Trend line (%)',
            type: 'line',
            label: 'labelOption',
            data: this.arr3,
            itemStyle: {
                    normal: {
                      color: new echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          { offset: 0, color: '#000000' },
                          { offset: 0.5, color: '#000000' },
           
                          { offset: 1, color: '#000000' }
                        ]
                      )
                    }
                  },
        }
          
      ],
        
        animationEasing: 'elasticOut',
        animationDelayUpdate: function (idx) {
          return idx * 8;
        }
      }
    
  
  

    });
  }
  onChartEvent(event: any, type: string) {
  }
}

