export class SeriesValue {
  key: any;
  value: any;
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
}

export class AreaValue {
  value: valueSpendingList[];
  savings: valuesavingList[]
  constructor(value,savings) {
    this.value = value;
    this.savings = savings;
  }
}

export class valueSpendingList
{
  key: any;
  value: any[] = [];
  constructor(key) {
    this.key = key;
  }
}
export class valuesavingList
{
  key: any;
  value: any[] = [];
  constructor(key) {
    this.key = key;
  }
}

export class CategoryValue {
  key: any;

  constructor(key) {
    this.key = key;

  }
}
export class ChartData {
  title: string;
  pietype: string;
  isStack: boolean;
  category: CategoryValue[];
  seriesData: SeriesValue[];
  stackData: StackValue;
  constructor(title, pietype, isStack, category, seriesData, stackData) {
    this.title = title;
    this.pietype = pietype;
    this.isStack = isStack;
    this.category = category;
    this.seriesData = seriesData;
    this.stackData = stackData;
  }
}

export class StackValue {
  key: any[];
  value: KeyValuePair[];
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
}
export class KeyValuePair {
  key: any;
  value: any[] = [];
  constructor(key) {
    this.key = key;
  }
}


export class SeriesIdValue {
  key: any;
  value: any;
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
}

export class DropdownData {
  seriesData: SeriesIdValue[];
  constructor( seriesData) {
    this.seriesData = seriesData;
  }
}
