import { LoadWorkflowTemplate, GetWorkflowTemplates } from './../../../actions/workflow-schedule.action';
import { InitializeServices } from './../../../actions/workflow-available-services.action';
import {
    DeleteStage, LoadTask, AddStage, DeleteTask,
    SavePreConditions, AddWorkflowProperties, LaunchWorkflow,
    OpenTaskConfiguration,
    OpenSaveTemplateConfiguration
} from './../../../actions/workflow.action';
import { WorkflowAvailableServices } from './../../../models/workflow-available-services.model';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DeleteconfirmComponent } from '../../../../shared/deleteconfirm/deleteconfirm.component';
import { PreConditionsComponent } from '../pre-conditions/pre-conditions.component';
import { PostConditionsComponent } from '../post-conditions/post-conditions.component';
import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs/Observable';;
import { Stage, DynamicWorkflow, WorkFlowProperties, ReleasePlan, ReleaseEnvironment } from '../../../models/release.model';
import { DynamicWorkflowState } from '../../../state/dynamic-workflow.state';
import { WorkflowAvailableServicesState } from '../../../state/workflow-available-services.state';
import { CloneTaskComponent } from '../clone-task/clone-task.component';
import { ReleasePlanningState } from '../../../state/release-planning.state';
import { Navigate } from '@ngxs/router-plugin';
import { SelectedEnvironmentState } from 'src/app/release-management/state/selected-environment.state';

@Component({
    selector: 'app-create-dynamic-workflow',
    templateUrl: './create-dynamic-workflow.component.html',
    styleUrls: ['./create-dynamic-workflow.component.scss']
})
export class CreateDynamicWorkflowComponent implements OnInit {
    @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
    @Select(DynamicWorkflowState.workFlowProperties) workFlowProperties$: Observable<any>;
    @Select(WorkflowAvailableServicesState.getWorkflowAvailableServicesStore)
    componentList$: Observable<WorkflowAvailableServices>;
    @Select(DynamicWorkflowState.validLaunch) validLaunch$: Observable<any>;
    @Select(ReleasePlanningState.releasePlan) releasePlan$: Observable<ReleasePlan[]>;
    @Select(SelectedEnvironmentState.selectedEnvironment) selectedEnvironment$: Observable<ReleaseEnvironment[]>;
    public releasePlan: ReleasePlan;

    public count = 0;
    public validLaunch: boolean = false;
    public operation: string;
    public isCollapsed: boolean = false;
    public showStages: boolean = false;
    activeParent: boolean = false;
    public enableLaunchButton: boolean = false;
    public isLoading: boolean = true;  // Loader
    public stages: Stage[] = [];
    public workflowProperties: any = {};
    public selectedEnvironment: any = {};
    public configureMode: boolean = false;

    constructor(
        private modalService: NgbModal,
        private _store: Store,
        private _activatedroute: ActivatedRoute,

    ) {
        this.operation = this._activatedroute.snapshot.url.join('/');
        this._store.dispatch(new GetWorkflowTemplates({}));
    }

    ngOnInit() {
        this._store.dispatch(new InitializeServices());

        this.selectedEnvironment$.subscribe((_selectedEnvironment) => {
            if (_selectedEnvironment.length > 0) {
                this.selectedEnvironment = _selectedEnvironment[0];
                if (this.selectedEnvironment.workflows.length > 0
                    && this.selectedEnvironment.workflows[0].workflowId) {
                    this.configureMode = true;
                    this.isCollapsed = true;
                    this.showStages = true;
                    this.enableLaunchButton = true;
                }
            }
            this.isLoading = false;
        });

        this.releasePlan$.subscribe(
            (_releasePlan: any) => {
                _releasePlan.forEach(releasePlan => {
                    this.releasePlan = releasePlan;
                })
            });

        this.workFlowProperties$.subscribe((data) => {
            this.workflowProperties = data;
        });
        this.validLaunch$.subscribe(_workflowlaunchValidity => {
            this.validLaunch = _workflowlaunchValidity;
            if (this.validLaunch) {
                this.enableLaunchButton = true;
                this.isCollapsed = true;
                this.showStages = true;
            }
        });

        this.stages$.subscribe((_stages) => {
            this.stages = _stages;
        });
    }

    onListAdd(newListName) {
        if (newListName === '') {
            return;
        }
        let workflowItem = new Stage();
        workflowItem.name = newListName;
        workflowItem.id = newListName + this.count;
        this._store.dispatch(new AddStage(workflowItem));
        ++this.count;
    }

    deleteStage(stage) {
        const deleteStageModalRef = this.modalService.open(DeleteconfirmComponent, { centered: true, backdrop: 'static', keyboard: false });
        deleteStageModalRef.componentInstance.confirmResponse.subscribe((confirmResponse: string) => {
            if (confirmResponse.toLowerCase() === 'yes') {
                this._store.dispatch(new DeleteStage(stage));
            }
        });
    }

    selectedItem(serviceName: string, action: any, stageId: string) {
        this._store.dispatch(new LoadTask({ serviceName, action, stageId }));
        // this.verifyLaunch();
    }

    deleteTask(task, stageId) {
        const deleteTaskModalRef = this.modalService.open(DeleteconfirmComponent, { centered: true, backdrop: 'static', keyboard: false });
        deleteTaskModalRef.componentInstance.confirmResponse.subscribe((confirmResponse: string) => {
            if (confirmResponse.toLowerCase() === 'yes') {
                this._store.dispatch(new DeleteTask({ task, stageId }));
            }
        });
    }

    open(task, stageId) {
        this._store.dispatch(new OpenTaskConfiguration({ task, stageId }));
    }

    taskpreconditions(task, stageId) {
        const taskconditionsRef = this.modalService.open(PreConditionsComponent, { windowClass: 'modal-extra-large', centered: true, backdrop: 'static', keyboard: false });
        taskconditionsRef.componentInstance.task = task;

        //Subscribing to Saved Configuration
        taskconditionsRef.componentInstance.preConditionsConfiguration.subscribe(
            (preConditions) => {
                this._store.dispatch(new SavePreConditions({ task, stageId, preConditions }));
                //this.verifyLaunch();
            });
    }

    cloneTask(task: any) {
        const clone_taskRef = this.modalService.open(CloneTaskComponent, { size: 'sm', centered: true, backdrop: 'static', keyboard: false });
        clone_taskRef.componentInstance.task = task;
    }

    taskpostconditions(task: any, stageId: string) {
        const task_postconditionsRef = this.modalService.open(PostConditionsComponent, { size: 'lg', centered: true, backdrop: 'static', keyboard: false });

    }
    saveSchedule(workflowSchedule) {
        this.enableLaunchButton = true;

        if (workflowSchedule.templateVersion) {
            let templateVersionId = workflowSchedule.templateVersion.templateId;
            // Start Loader
            if (templateVersionId) {
                this.isLoading = true;
                this._store.dispatch(new LoadWorkflowTemplate(templateVersionId))
                    .subscribe(data => {
                        this.isLoading = false;
                        this.isCollapsed = true;
                        this.showStages = true;
                    });
            }
        }

        let name = workflowSchedule.name;
        let frequency = workflowSchedule.frequency.frequencyId;
        //Map the selectedEnvironment Object here to Workflow Properties
        let workflowProperties = new WorkFlowProperties();
        workflowProperties.name = name;
        workflowProperties.frequencyId = workflowSchedule.frequency.frequencyId;
        workflowProperties.frequencyType = workflowSchedule.frequency.frequencyType;
        workflowProperties.template = workflowSchedule.templateName;
        workflowProperties.releaseId = this.releasePlan.releaseId;
        workflowProperties.environmentId = this.selectedEnvironment.environmentId;
        workflowProperties.environmentName = this.selectedEnvironment.environmentName || this.workflowProperties.environmentName;
        workflowProperties.release = this.workflowProperties.release || '';
        workflowProperties.scheduleDate = this.workflowProperties.scheduleDate || null;
        workflowProperties.workflowId = this.workflowProperties.workflowId || 0;
        this._store.dispatch(new AddWorkflowProperties(workflowProperties));
        this.isCollapsed = true;
        this.showStages = true;
    }

    openProperties() {
        this.isCollapsed = !this.isCollapsed;
    }

    onSorting(isParent: boolean) {
        this.activeParent = isParent;
    }
    saveAsTemplate() {
        let workflow = new DynamicWorkflow();
        workflow.createDate = this.workflowProperties.createDate || '';
        workflow.description = this.workflowProperties.description;
        workflow.environmentId = parseInt(this.workflowProperties.environmentId);
        workflow.environmentName = this.workflowProperties.environmentName;
        workflow.frequencyId = this.workflowProperties.frequencyId;
        workflow.frequencyType = this.workflowProperties.frequencyType;
        workflow.name = this.workflowProperties.name;
        workflow.overallProgress = this.workflowProperties.overallProgress;
        workflow.overallStatus = this.workflowProperties.overallStatus;
        workflow.release = this.workflowProperties.release || '';
        workflow.releaseId = this.workflowProperties.releaseId;
        workflow.scheduleDate = this.workflowProperties.scheduleDate;
        workflow.workflowId = this.workflowProperties.workflowId || 0;
        workflow.stages = this.stages;
        this._store.dispatch(new OpenSaveTemplateConfiguration(workflow));
        // const saveTemplateRef = this.modalService.open(SaveWorkflowTemplateComponent, { size: 'lg', centered: true });
        // saveTemplateRef.componentInstance.workflow = workflow;
    }

    launchWorkFlow() {
        let workflow = new DynamicWorkflow();
        workflow.createDate = this.workflowProperties.createDate || '';
        workflow.description = this.workflowProperties.description;
        workflow.environmentId = parseInt(this.workflowProperties.environmentId);
        workflow.environmentName = this.workflowProperties.environmentName;
        workflow.frequencyId = this.workflowProperties.frequencyId;
        workflow.frequencyType = this.workflowProperties.frequencyType;
        workflow.name = this.workflowProperties.name;
        workflow.overallProgress = this.workflowProperties.overallProgress;
        workflow.overallStatus = this.workflowProperties.overallStatus;
        workflow.release = this.workflowProperties.release || '';
        workflow.releaseId = this.workflowProperties.releaseId;
        workflow.scheduleDate = this.workflowProperties.scheduleDate;
        workflow.workflowId = this.workflowProperties.workflowId || 0;
        workflow.stages = this.stages;
        this._store.dispatch(new LaunchWorkflow(workflow));
    }

    ngOnDestroy() {
        // this.commondata.clear();
        // this.commondata.viewTemplateClear();
    }

    loadReleaseList() {
        this._store.dispatch(new Navigate(['/release-orchestration']));
    }
    loadReleaseView(){
        this._store.dispatch(new Navigate(['/release-orchestration/viewrelease']));
    }
    loadReleaseDashboard(){
        this._store.dispatch(new Navigate(['/release-orchestration/dashboard']));
    }
}
