import { State, Store, Selector, Action, StateContext } from "@ngxs/store";
import { HttpClient } from "@angular/common/http";
import { NotificationsService } from "angular2-notifications";
import * as _ from 'lodash';
import { deserialize } from "json-typescript-mapper";
import { DiscoverySettings } from "../discovery-settings";
import { Navigate } from "@ngxs/router-plugin";
import { HostPackageModel, HostStorageModel, HostServicesModel } from "../models/discovery-result.model";
import { FetchDiscoveryHostResultList, FetchDiscoveryHostRetryList, CreateDiscoveryHostRetryLists, FetchDiscoveryHostSummaryPageList, FetchDiscoveryHostTopologyPageList, FetchDiscoveryHostUtilization } from "../actions/discovery-result.actions";


export class DiscoverResultModel {

  discoveryHostRetryList: any;
  discoveryHostTopologyPageList: any;
  discoveryHostSummaryPageList: any;

  discoveryUtilization: any;
  hostPackageData: any;
  hostServiceData: any;
  hostStorageData: any;
  hostDetailsData: any;
  hostCapacityData: any;
  hostAvailableData: any;
  hostCpuCount: any;
  hostCpuCores: any;
  hostCpuName: any;
  hostMemoryData: any;
  hostMemoryTotal: any;
  hostMemoryUsed: any;
  hostMemoryBalance: any;
  hostDiskData: any;
  hostDiskTotalSpace: any;
  hostDiskUsedSpace: any;
  hostDiskBalance: any;


  hostNetworkData: any;
  hostUsersGroups: any;
  hostPortData: any;
  hostConectedServicesData: any;
  hostName: any;
  hostOperating: any;
  hostBios: any;
  hostvCpuCount: any;
  hostVersion:any;
  hostRelVersion:any;
  
  hostTotalListeningports: any;
  hostTotalInterfaces: any;
  hostTotalServices: any;
  hostMemory: any;
  hostInstanceType: any;
  hostSystemDate: any;
  hostServiceProvider: any;
}
@State<DiscoverResultModel>({
  name: 'discoveryResultDetails',
  defaults: {

    discoveryUtilization: null,
    discoveryHostRetryList: null,
    discoveryHostTopologyPageList: null,
    discoveryHostSummaryPageList: null,
    hostPackageData: null,
    hostServiceData: null,
    hostVersion:null,
  hostRelVersion:null,
    hostName: null,
    hostBios: null,
    hostStorageData: null,
    hostDetailsData: null,
    hostCapacityData: null,
    hostAvailableData: null,
    hostCpuCount: null,
    hostCpuCores: null,
    hostCpuName: null,
    hostMemoryData: null,
    hostMemoryTotal: null,
    hostMemoryUsed: null,
    hostMemoryBalance: null,
    hostDiskData: null,
    hostDiskTotalSpace: null,
    hostDiskUsedSpace: null,
    hostDiskBalance: null,


    hostNetworkData: null,
    hostUsersGroups: null,

    hostPortData: null,
    hostConectedServicesData: null,

    hostOperating: null,
    hostvCpuCount: null,
   
    hostTotalListeningports: null,
    hostTotalInterfaces: null,
    hostTotalServices: null,
    hostMemory: null,
    hostInstanceType: null,
    hostSystemDate: null,
    hostServiceProvider: null
  }
})
export class DiscoveryResultState {
  constructor(private _store: Store,
    private _http: HttpClient,
    private _notificationsService: NotificationsService,
    private _discoverySettings: DiscoverySettings) {
  }
  @Selector()
  static discoveryHostRetryList(state: DiscoverResultModel) {
    return state.discoveryHostRetryList;
  }
  @Selector()
  static discoveryHostSummaryPageList(state: DiscoverResultModel) {
    return state.discoveryHostSummaryPageList;
  }
  @Selector()
  static discoveryHostTopologyPageList(state: DiscoverResultModel) {
    return state.discoveryHostTopologyPageList;
  }

  @Action(FetchDiscoveryHostResultList)
  fetchDiscoveryHostResultList(context: StateContext<DiscoverResultModel>, { payload }: any) {
    var state = context.getState();
    let host_ID = payload.discoveryIdHost;
    let url = this._discoverySettings.getNetworkURL('HOST_DETAILS_URL');

    this._notificationsService.info('', 'Getting Host Details');
    return this._http.get(_.template(url)({ host_ID })).subscribe(
      (data: any[]) => {
        //Packages table
        let Packageslist: any[] = data["Packages"];
        let mappedList = Packageslist.map(_Packageslist => deserialize(HostPackageModel, _Packageslist));
        state.hostPackageData = mappedList;

        //Services Table
        let Serviceslist: any[] = data["Services"];
        let hostservicelistdata = [];
        Serviceslist.forEach(_service => {
          let hostService = new HostServicesModel();
          hostService.Service_Name = _service.ServiceName;
          hostService.Service_type = _service["ServiceType"];
          hostService.Service_Status = _service.Status;
          hostService.additional_details = _service.AdditionalDetails;
          hostservicelistdata.push(hostService);
        });
        state.hostServiceData = hostservicelistdata;
        context.patchState(state);

        //Storage Table
        // let Storagelist: any[] = data["Storage_Information"];
        state.hostStorageData = data["Storage_Information"] ? data["Storage_Information"] : [];
        // let StoragemappedList = Storagelist.map(_Storagelist => deserialize(HostStorageModel, _Storagelist));
        // state.hostStorageData = StoragemappedList;



        state.hostDetailsData = data["Cpu_Information"] ? data["Cpu_Information"]["Utilization(%)"] : null;
        state.hostCapacityData = data["Cpu_Information"] ? data["Cpu_Information"]["Capacity(%)"] : null;
        state.hostAvailableData = data["Cpu_Information"] ? data["Cpu_Information"]["Available(%)"] : null;
        state.hostCpuCount = data["Cpu_Information"] ? data["Cpu_Information"].CPUCount : null;
        state.hostCpuCores = data["Cpu_Information"] ? data["Cpu_Information"].CoresCount : null;
        state.hostCpuName = data["Cpu_Information"] ? data["Cpu_Information"].DeviceName : null;
        //Memory Information
        state.hostMemoryData = data["Memory_Information"] ? data["Memory_Information"]["Used(%)"] : null;
        state.hostMemoryTotal = data["Memory_Information"] ? data["Memory_Information"]["Capacity"] : null;
        state.hostMemoryUsed = data["Memory_Information"] ? data["Memory_Information"]["Used(GB)"] : null;
        state.hostMemoryBalance = data["Memory_Information"] ? data["Memory_Information"]["Available"] : null;
        //Disk Information
        state.hostDiskData = data["Disk_usage"] ? data["Disk_usage"]["Used(%)"] : null;
        state.hostDiskTotalSpace = data["Disk_usage"] ? data["Disk_usage"]["Capacity"] : null;
        state.hostDiskUsedSpace = data["Disk_usage"] ? data["Disk_usage"]["Used(GB)"] : null;
        state.hostDiskBalance = data["Disk_usage"] ? data["Disk_usage"]["Available"] : null;

        //Network/Interface
        state.hostNetworkData = data["Interfaces"] ? data["Interfaces"] : [];
        // Users and Groups 
        state.hostUsersGroups = data["Usergroups"] ? data["Usergroups"] : [];
        //Port
        state.hostPortData = data["Ports"] ? data["Ports"] : [];
        state.hostConectedServicesData = data["Connected_Services"] ? data["Connected_Services"] : [];
        // alert(state.hostConectedServicesData)
        //Host Detailes
        state.hostName = data["Host_details"] ? data["Host_details"]["Hostname"] : null;
        state.hostBios = data["Host_details"] ? data["Host_details"]["BIOS_Configuration"] : null;
        state.hostOperating = data["Host_details"] ? data["Host_details"]["OperatingSystem"] : null;
        state.hostvCpuCount = data["Host_details"] ? data["Host_details"]["CoresCount"] : null;
        state.hostVersion = data["Host_details"] ? data["Host_details"]["Version"] : null;
        state.hostRelVersion = data["Host_details"] ? data["Host_details"]["ReleaseVersion"] : null;
       
        state.hostTotalListeningports = data["Host_details"] ? data["Host_details"].TotalListeningPorts : null;
        state.hostTotalInterfaces = data["Host_details"] ? data["Host_details"].TotalInterfaces : null;
        state.hostTotalServices = data["Host_details"] ? data["Host_details"].TotalServices : null;
        state.hostMemory = data["Host_details"] ? data["Host_details"]["MemSize"] : null;
        state.hostInstanceType = data["Host_details"] ? data["Host_details"]["InstanceType"] : null;
        state.hostSystemDate = data["Host_details"] ? data["Host_details"].system_date : null;
        state.hostServiceProvider = data["Host_details"] ? data["Host_details"]["ServiceProvider"] : null;
        this._notificationsService.remove();
        //context.dispatch(new Navigate(['/discovery/discovery-details']));
        context.setState(state);
        context.dispatch(new Navigate(['/discovery/hostdetails']));

      });

  }

  @Action(FetchDiscoveryHostRetryList)
  fetchDiscoveryHostRetryList(context: StateContext<DiscoverResultModel>, { payload }: any) {

    var state = context.getState();
    var serviceDiscoveryID = payload.discoveryId;
    let url = this._discoverySettings.getServiceURL('SERVICE_RETRY_LIST_URL');
    return this._http.get(_.template(url)({ serviceDiscoveryID }))
      // return this._http.get('../../../assets/mockdata/Discovery/hostserviceretrylist.json')
      .subscribe((data: any[]) => {
        if (data) {
          state.discoveryHostRetryList = data;

          context.setState(state);
        }
      }
      );

  }
  @Action(CreateDiscoveryHostRetryLists)
  createDiscoveryHostRetryLists(context: StateContext<DiscoverResultModel>, { payload }: any) {
    var state = context.getState();
    var serviceDiscoveryID = localStorage.getItem("serviceDiscoveryId");
    let url = this._discoverySettings.getServiceURL('RETRY_PATCH_URL');
    return this._http.patch(_.template(url)({ serviceDiscoveryID }), payload)
      .subscribe(
        (data: any) => {
          if (data.statusCode == '200') {
            this._notificationsService.success(data.response, 'Updated', {
              timeOut: 3000,
              showProgressBar: true,
              // pauseOnHover: true,
              clickToClose: true
            });

          }
          context.setState(state);

        }
      );

  }



  @Action(FetchDiscoveryHostUtilization)
  fetchDiscoveryHostUtilizatio(context: StateContext<DiscoverResultModel>, action: FetchDiscoveryHostUtilization) {
    var state = context.getState();

    // return this._http.get(this._discoverySettings.getNetworkURL('NETWORK_PORT_URL'))
    this._http.get('../../../assets/mockdata/Discovery/hostUtilization.json').subscribe(
      (data) => {

        if (data) {
          state.discoveryUtilization = data;
          // state.discoveryMemoryUtilization = data["Memory"];
          // state.discoveryCpuUtilization = data["Cpu"];
          // state.discoveryStorageUtilization = data["Storage"];
          context.patchState(state);
        }
      }
    );


  }


  @Action(FetchDiscoveryHostSummaryPageList)
  fetchDiscoveryHostSummaryPageList(context: StateContext<DiscoverResultModel>, { payload }: any) {

    var state = context.getState();
    var serviceDiscoveryID = payload.discoveryId;
    if (serviceDiscoveryID == '999999') {
      return this._http.get('../../../assets/mockdata/Discovery/hostsummarylist.json')
        .subscribe((data: any[]) => {
          if (data) {
            // this._notificationsService.success(data);
            state.discoveryHostSummaryPageList = data;
            //context.dispatch(new Navigate(['/discovery/servicedetails']));
            context.patchState(state);
            // this.router.navigate(['/discovery/servicedetails']);
            // context.setState(state);
          }
        }, (error) => {
          this._notificationsService.remove();
          if (error.status === 0) {
            this._notificationsService.error('Error Occurred', 'Server is not responding');
          } else if (error.status === 400) {
            this._notificationsService.error('Something went wrong', error.error['message']);
          } else {
            state.discoveryHostSummaryPageList = error;
            context.patchState(state);
            // this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
          }
        }
        );
    }
    else {
      // return this._http.get('../../../assets/mockdata/Discovery/hostsummarylist.json')
      // return this._http.get(this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL'))
      let url = this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL');
      return this._http.get(_.template(url)({ serviceDiscoveryID }))
        .subscribe((data: any[]) => {
          if (data) {
            // this._notificationsService.success(data);
            state.discoveryHostSummaryPageList = data;
            //context.dispatch(new Navigate(['/discovery/servicedetails']));
            context.patchState(state);
            // this.router.navigate(['/discovery/servicedetails']);
            // context.setState(state);
          }
        }, (error) => {
          this._notificationsService.remove();
          if (error.status === 0) {
            this._notificationsService.error('Error Occurred', 'Server is not responding');
          } else if (error.status === 400) {
            this._notificationsService.error('Something went wrong', error.error['message']);
          } else {
            state.discoveryHostSummaryPageList = error;
            context.patchState(state);
            // this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
          }
        }
        );
    }

  }
  @Action(FetchDiscoveryHostTopologyPageList)
  fetchDiscoveryHostTopologyPageList(context: StateContext<DiscoverResultModel>, { payload }: any) {

    var state = context.getState();
    var serviceDiscoveryID = payload.discoveryId;
    if (serviceDiscoveryID == '999999') {
      return this._http.get('../../../assets/mockdata/Discovery/hosttopology.json')
        .subscribe((data: any[]) => {
          if (data) {
            state.discoveryHostTopologyPageList = data;
            context.setState(state);
            // context.patchState(state);
          }
        }, (error) => {
          this._notificationsService.remove();
          if (error.status === 0) {
            this._notificationsService.error('Error Occurred', 'Server is not responding');
          } else if (error.status === 400) {
            this._notificationsService.error('Something went wronga', error.error['errorMessag']);
          } else {
            state.discoveryHostTopologyPageList = error;
            context.setState(state);
            // this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
          }
        }
        );
    }
    // return this._http.get('../../../assets/mockdata/Discovery/discoveryos.json')
    //    return this._http.get(this._discoverySettings.getNetworkURL('NETWORK_OS_URL'))


    else {
      // return this._http.get('../../../assets/mockdata/Discovery/hostsummarylist.json')
      // return this._http.get(this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL'))
      let url = this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_TOPOLOGY_URL');
      return this._http.get(_.template(url)({ serviceDiscoveryID }))
        .subscribe((data: any[]) => {
          if (data) {
            // this._notificationsService.success(data);
            state.discoveryHostTopologyPageList = data;
            //context.dispatch(new Navigate(['/discovery/servicedetails']));
            context.patchState(state);
            // this.router.navigate(['/discovery/servicedetails']);
            // context.setState(state);
          }
        }, (error) => {
          this._notificationsService.remove();
          if (error.status === 0) {
            this._notificationsService.error('Error Occurred', 'Server is not responding');
          } else if (error.status === 400) {
            this._notificationsService.error('Something went wrong', error.error['message']);
          } else {
            state.discoveryHostTopologyPageList = error;
            context.patchState(state);
            // this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
          }
        }
        );
    }

  }
}