import { Component, OnInit, Output, EventEmitter, ViewChild, Input, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Store, Select } from '@ngxs/store';
import { ParametersListByJobName } from 'src/app/release-management/actions/workflow-available-services.action';
import { WorkflowAvailableServicesState } from 'src/app/release-management/state/workflow-available-services.state';
import { Observable } from 'rxjs/Observable';
import { parameterList } from 'src/app/release-management/models/parameterList.model';

@Component({
  selector: 'matilda-plugin-params-by-jobname',
  templateUrl: './plugin-params-by-jobname.component.html',
  styleUrls: ['./plugin-params-by-jobname.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PluginParamsByJobnameComponent implements OnInit {
  formActive = false;
  showLoadMessage = false;
  form: FormGroup;

  @ViewChild('jobNameInput') jobNameInputField;
  @Input('openFormMode') openFormMode: boolean;
  @Output('closeBuildForm') closeBuildForm = new EventEmitter<boolean>();
  @Output('parameterList') parameterList = new EventEmitter<any>();
  @Select(WorkflowAvailableServicesState.paramaterList) paramaterList$: Observable<any>;

  constructor(private formBuilder: FormBuilder,
    private _store: Store) { }

  ngOnInit() {
    this.formActive = this.openFormMode;
    this.openForm();
  }

  openForm() {
   
      this.form = this.formBuilder.group({
        jobName: ['',
          [
            Validators.required,
            //Validators.maxLength(30),
            //this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should start with an alphabet' }),
            //this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_ ]+$'), { 'remainingpattern': 'should not contain special characters except _' }),
          ]
        ],
        userName: ['', Validators.required],
        token: ['', Validators.required],
        url: ['', [Validators.required, Validators.pattern(/^(http?|ftp?|https):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/)]]
      });
      this.formActive = true;
      this.focusNameField();

  }

  closeForm() {
    this.formActive = false;
    this.closeBuildForm.emit(false);
  }

  get jobName(){
    return this.form.get('jobName');
  }
  get userName(){
    return this.form.get('userName');
  }
  get token(){
    return this.form.get('token');
  }

  focusNameField() {
    setTimeout(() => {
      this.jobNameInputField.nativeElement.focus();
    });
  }

  onFormSubmit() {
    if (this.form.valid) {
      this.showLoadMessage = true;
      let searchParameterList = new parameterList();
      searchParameterList.jobName = this.form.getRawValue().jobName;
      searchParameterList.userName = this.form.getRawValue().userName;
      searchParameterList.token = this.form.getRawValue().token;
      searchParameterList.url = this.form.getRawValue().url;
      this._store.dispatch(new ParametersListByJobName(searchParameterList)).subscribe( value => {
        this.paramaterList$.subscribe((_parameterList)=> {
              this.parameterList.emit(_parameterList[0]);
              this.formActive = false;
              this.showLoadMessage = false;
              this.closeBuildForm.emit(false);
        });
      });
    
    }else{
      //this.validateAllFormFields(this.form);
    }
  }


}
