    var nowdate = new Date();
    this.filterModel.StartDate = new Date(
      nowdate.getFullYear(),
      nowdate.getMonth(),
      1
    );
    this.filterModel.EndDate = new Date(
      nowdate.getFullYear(),
      nowdate.getMonth() + 1,
      0
    );

    this.metrics.getDataApplicationFilter().subscribe(data => {
      this.dataApplicationFilter = data.response;
    });

    this.metrics.getDataEnvironmentFilter().subscribe(data => {
      this.dataEnvironmentFilter = data.response;
    });

    this.metrics.getDataProjectFilter().subscribe(data => {
      this.dataProjectFilter = data.response;
    });

