import { JsonProperty } from "json-typescript-mapper";
export class VNFSite {
    @JsonProperty('vnf_node_id')
    vnfNodeId: string = '';
    @JsonProperty('name')
    name: string = '';
    @JsonProperty('vnf_site_id')
    id: string = '';
    
}

export class VNFNode {
    @JsonProperty('vnf_node_id')
    id: string = '';
    @JsonProperty('name')
    name: string = '';
    @JsonProperty({clazz: VNFSite,name:'vnf_sites'})
    vnfSites: VNFSite[] = [];
}

