import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AsideLeftDisplayDisabledComponent } from '../theme/pages/aside-left-display-disabled/aside-left-display-disabled.component';
import { CostDashboardComponent } from './components/cost-dashboard/cost-dashboard.component';
import { CostRiManagementComponent } from './components/cost-ri-management/cost-ri-management.component';
import { CostForecastComponent } from './components/cost-forecast/cost-forecast.component';
import { CostAnalyticsComponent } from './components/cost-analytics/cost-analytics.component';
import { CostConfigureComponent } from './components/cost-configure/cost-configure.component';
import { CostCloudComponent } from './components/cost-cloud/cost-cloud.component';
import { CostBudgetComponent } from './components/cost-budget/cost-budget.component';
import { CostAlertComponent } from './components/cost-alert/cost-alert.component';
import { CostSavingComponent } from './components/cost-saving/cost-saving.component';

const routes: Routes = [
  {
    path:'',
    component: AsideLeftDisplayDisabledComponent,
    'children': [
      {
        "path": "cloud",
        "component": CostCloudComponent
      },
      {
        "path": "dashboard",
        "component": CostDashboardComponent
      },
      {
        "path": "ri",
        "component": CostRiManagementComponent
      },
      {
        "path": "analytics",
        "component": CostAnalyticsComponent,
        //"canActivate": ['AuthenticationGuard'],
         //runGuardsAndResolvers : 'always',
    
      },
      {
        "path": "saving",
        "component": CostSavingComponent
      },
      {
        "path": "budget",
        "component": CostBudgetComponent
      },
      {
        "path": "configure",
        "component": CostAlertComponent
        //"component": CostConfigureComponent
      }
    ]
  }
  ];
  
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CostManagementRoutingModule { }
