import { Component, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngxs/store';
import { State, process } from '@progress/kendo-data-query';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';
import * as _ from 'lodash';
import { TooltipDirective } from '@progress/kendo-angular-tooltip';

@Component({
  selector: 'matilda-host-network',
  templateUrl: './host-network.component.html',
  styleUrls: []
})
export class HostNetworkComponent implements OnInit {
  //from state call
  public Interface$: Observable<any>;
  public interfaceData: any;
  public network: any;
  @Output() emitInterface: EventEmitter<any> = new EventEmitter();
  @Output() emitData: EventEmitter<any> = new EventEmitter();;



  //ToolTip
@ViewChild(TooltipDirective) public tooltipDir: TooltipDirective;
public showTooltip(e: MouseEvent): void {
  const element = e.target as HTMLElement;
  if ((element.nodeName === 'TD' || element.nodeName === 'TH')
    && element.offsetWidth < element.scrollWidth) {
    this.tooltipDir.toggle(element);
  } else {
    this.tooltipDir.hide();
  }
}

  //status color
  getStatusBadge(Status: string) {
    if (Status) {
      // alert(Status)
      if (Status === 'Up') {
        return 'la-arrow-circle-o-up m-badge--success';
      } else if (Status === 'Down') {
        return 'la-arrow-circle-o-down m-badge--danger';
      }

    }
  }

  //pagination
  public pageSize = 10;
  public metaData: any[] = [];
  public skip = 0;
  public gridState: State = {
    sort: [],
    skip: 0,
    take: 10,
  };
  public allData(): ExcelExportData {
    const result: ExcelExportData = {
      data: process(this.network, { filter: this.gridState.filter }).data,
    };
    return result;
  }
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
  }

  public onPageChange(state: any): void {
    this.pageSize = state.take;
  }

  constructor(private _store: Store, private _http: HttpClient) {
    this.allData = this.allData.bind(this);
  }
  ngOnInit() {
    //getting network list
    this.Interface$ = this._store.select(state => state.discoveryResultDetails.hostNetworkData);
    this.Interface$.subscribe((element: any[]) => {
      this.network = element;
      // console.log(this.network);
      _.each(this.network, (key: any, index) => {
        let value = _.keys(key)
        _.each(value, (keys) => {
          if (this.metaData.length == 0) {
            index = {
              field: _.trim(keys),
              title: _.upperFirst(keys),
              width: 40,
              locked: false,
              filterable: true
            }
            this.metaData.push(index)
            this.metaData = _.uniqBy(this.metaData, "field")
          }
          else if (this.metaData[index] != keys) {
            index = {
              field: _.trim(keys),
              title: _.upperFirst(keys),
              width: 40,
              locked: false,
              filterable: true

            }
            this.metaData.push(index)
            this.metaData = _.uniqBy(this.metaData, "field")
          }
        });
      });
    });
  }


  getInterfaceName(InterfaceName: any) {
    this.interfaceData = { "interfaceName": InterfaceName };
    this.emitInterface.emit(this.interfaceData);
    this.emitData.emit(4);

  }
}
