import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';

@Component({
  selector: 'matilda-secondline',
  templateUrl: './secondline.component.html',
  styleUrls: ['./secondline.component.scss']
})
export class SecondlineComponent implements OnInit {

  @Input() public networkservers;
  public view: Observable<GridDataResult>;
  public skip = 0;

  constructor() { }
  
  public ngOnInit(): void {
    this.view = this.networkservers["applications"];
}

public pageChange({ skip, take }: PageChangeEvent): void {
    this.skip = skip;
}

}
