import { WorkflowAvailableServices } from "../models/workflow-available-services.model";
import { State, Action, StateContext, Selector } from "@ngxs/store";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import * as _ from 'lodash';
import { deserialize, serialize } from "json-typescript-mapper";
import { ReleaseMgmtAppSettings } from "../release-management.settings";
import { InitializeServices, PluginConfiguration, ClearPluginConfiguration, ParametersListByJobName, GETPluginOutputParameters } from '../actions/workflow-available-services.action';
import { Frequency } from "../models/frequency.model";
import { tap } from "rxjs/operators/tap";
import { shareReplay } from "rxjs/operators";
import { PluginOutputFields } from "../models/plugin-output-fields.model";


export class WorkflowAvailableServicesStateModel {
    workflowAvailableServices: WorkflowAvailableServices[];
    pluginFields: any[];
    frequencyTypes: Frequency[];
    pluginLoadMessage: string;
    paramaterList: any[];
    pluginOutputParameters: any[]
}

@State<WorkflowAvailableServicesStateModel>({
    name: 'workflowAvailableServices'
})

export class WorkflowAvailableServicesState {
    public httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json'
        })
      };

    constructor(private _http: HttpClient, private _releaseMgmtAppSettings: ReleaseMgmtAppSettings) { }
    @Selector()
    static getWorkflowAvailableServicesStore(state: WorkflowAvailableServicesStateModel) {
        return state.workflowAvailableServices;
    }
    @Selector()
    static frequencyTypes(state: WorkflowAvailableServicesStateModel) {
        return state.frequencyTypes;
    }
    @Selector()
    static pluginFields(state: WorkflowAvailableServicesStateModel) {
        return state.pluginFields;
    }
    @Selector()
    static pluginLoadMessage(state: WorkflowAvailableServicesStateModel) {
        return state.pluginLoadMessage;
    }
    @Selector()
    static paramaterList(state: WorkflowAvailableServicesStateModel) {
        return state.paramaterList;
    }
    @Selector()
    static pluginOutputParameters(state: WorkflowAvailableServicesStateModel) {
        return state.pluginOutputParameters;
    }
    @Action(InitializeServices)
    InitializeServices(context: StateContext<WorkflowAvailableServicesStateModel>) {
        const state = context.getState();
        //let url = '../../../../src/assets/mockdata/createenv.json'
        let pluginURL = this._releaseMgmtAppSettings.getURL('GET_AVAILABLE_PLUGINS');
        this._http.get(pluginURL)
            .subscribe((data: any[]) => {
                if (data) {
                    let workflowAvailableServices = data.map(_availableService => deserialize(WorkflowAvailableServices, _availableService));
                    state.workflowAvailableServices = workflowAvailableServices;
                    context.patchState(state);
                }
            });

        let frequencyURL = this._releaseMgmtAppSettings.getURL('GET_FREQUENCY_TYPES');
        this._http.get(frequencyURL)
            .subscribe((data: any[]) => {
                if (data) {
                    let mappedfrequencyTypes = data.map(_frequencyType => deserialize(Frequency, _frequencyType));
                    state.frequencyTypes = mappedfrequencyTypes;
                    context.patchState(state);
                }
            });
    }

    @Action(PluginConfiguration)
    pluginConfiguration(context: StateContext<WorkflowAvailableServicesStateModel>, { payload }: PluginConfiguration) {
        var state = context.getState();
        state.pluginLoadMessage = 'Please Wait.. While we are waiting for the data';
        let url = this._releaseMgmtAppSettings.getURL('GET_PLUGIN_FIELDS');
        let serviceId: string = payload.serviceId;
        let actionId: string = payload.actionId;
        return this._http.get(_.template(url)({ serviceId, actionId }))//{ serviceId, actionId }
            .subscribe(
                (data: any[]) => {
                    if (data.length > 0) {
                        state.pluginFields = data;
                        context.patchState(state);
                    }
                },
                (error) => {
                    state.pluginLoadMessage = "Error occurred. We couldn't load the data from the server";
                    context.patchState(state);
                }
            );
    }
    @Action(ClearPluginConfiguration)
    clearPluginConfiguration(context: StateContext<WorkflowAvailableServicesStateModel>) {
        const state = context.getState();
        state.pluginFields = [];
        context.patchState(state);
    }

    @Action(ParametersListByJobName)
    parametersListByJobName(context: StateContext<WorkflowAvailableServicesStateModel>, { payload }: ParametersListByJobName) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('GET_PARAMETER_LIST');;
        let searchPayload = serialize(payload);
        return this._http.post(url ,searchPayload, this.httpOptions).pipe(
            tap(value => {
                state.paramaterList = [];
                state.paramaterList.push(value);
                context.patchState(state);
            }),
            shareReplay()
          );
       
    }

    @Action(GETPluginOutputParameters)
    getPluginOutputParameters(context: StateContext<WorkflowAvailableServicesStateModel>, { payload }: GETPluginOutputParameters) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('PLUGIN_OUTPUT_FIELDS');
        let serviceId = payload.serviceId;
        let actionId = payload.actionId;
        return this._http.get(_.template(url)({ serviceId, actionId })).pipe(
            tap((data:any[]) => {
             let mappedPluginOutputFields = data.map(_outparameter => deserialize(PluginOutputFields,_outparameter));
                state.pluginOutputParameters = [];
                mappedPluginOutputFields.forEach((_pluginOutputFields: PluginOutputFields)=> {
                    state.pluginOutputParameters.push(_pluginOutputFields.outputParameter);
                });
                context.patchState(state);
            }),
            shareReplay()
          );
       
    }


}