import { Component, OnInit } from '@angular/core';
import { ListOfServicesService } from '../../services/list-of-services.service';

@Component({
  selector: 'matilda-releasechart4',
  templateUrl: './releasechart4.component.html',
  styleUrls: ['./releasechart4.component.scss']
})
export class Releasechart4Component implements OnInit {

  public series:any;
  public categories:string[];
  public sdata:any=[];
  public sdata4:any[]=[];
  public keys=[];
  public values=[];
  constructor(private servermetrics:ListOfServicesService){}
  


ngOnInit(): void {
  this.servermetrics.getServerMetrics().subscribe((x) => {
    
    this. sdata= x;
    

    this.sdata4=this.sdata.Change_Induced_Incidents.Incidents;
    for(let i=0; i<this.sdata4.length;i++){
    this.keys.push(Object.keys(this.sdata4[i]));
    this.values.push(Object.values(this.sdata4[i]));
    }
    // console.log(this.sdata4);
    console.log(this.keys);
    console.log(this.values);
    this.series= [{
      type: 'column',
      data: this.values,
      stack: true,
     
      color: '#1674b9'
  },  ];
  this.categories = this.keys;

    
   });
  




  }
}