import { State, Store, Selector, Action, StateContext } from "@ngxs/store";
import { HttpClient } from "@angular/common/http";
import { NotificationsService } from "angular2-notifications";
import * as _ from 'lodash';
import { deserialize } from "json-typescript-mapper";
import { DiscoverySettings } from "../discovery-settings";
import { Navigate } from "@ngxs/router-plugin";
import { FetchDiscoveryServiceFailedReason, FetchDiscoveryServiceSummaryPageList, FetchDiscoveryServiceTopologyPageList } from "../actions/discovery-serviceresult.actions";

export class DiscoverServiceResultModel {
    discoveryServiceFailedReason:any;
    discoveryServiceSummaryPageList:any;
    discoveryServiceTopologyPageList:any;
}

@State<DiscoverServiceResultModel>({
    name: 'discoveryServiceResultDetails',
    defaults: {
        discoveryServiceFailedReason:null,
        discoveryServiceSummaryPageList:null,
        discoveryServiceTopologyPageList:null
    }
})

export class DiscoveryServiceResultState {
    constructor(private _store: Store,
        private _http: HttpClient,
        private _notificationsService: NotificationsService,
        private _discoverySettings: DiscoverySettings) {
    }
    @Selector()
    static discoveryServiceFailedReason(state: DiscoverServiceResultModel) {
      return state.discoveryServiceFailedReason;
    }

    @Selector()
    static discoveryServiceSummaryPageList(state: DiscoverServiceResultModel) {
      return state.discoveryServiceSummaryPageList;
    }

    @Selector()
    static discoveryServiceTopologyPageList(state: DiscoverServiceResultModel) {
      return state.discoveryServiceTopologyPageList;
    }

    @Action(FetchDiscoveryServiceFailedReason)
    fetchDiscoveryServiceFailedReason(context: StateContext<DiscoverServiceResultModel>,{ payload }: any) {
      var state=context.getState();
      var serviceDiscoveryID = payload.discoveryId;
      let url=this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL'); 
      return this._http.get(_.template(url)({serviceDiscoveryID}))
      .subscribe((data: any[]) => {
          if (data) {
            state.discoveryServiceFailedReason = data;
           context.setState(state);
           
          }
        }
        );
       
    }
  

    @Action(FetchDiscoveryServiceSummaryPageList )
    fetchDiscoveryServiceSummaryPageList (context: StateContext<DiscoverServiceResultModel>, { payload }: any) {
       
        var state=context.getState();
        var serviceDiscoveryID = payload.discoveryId;
        // return this._http.get('../../../assets/mockdata/Discovery/hostsummarylist.json')
        // return this._http.get(this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL'))
        let url=this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL'); 
        return this._http.get(_.template(url)({serviceDiscoveryID}))
        .subscribe((data: any[]) => {
            if (data) {
              state.discoveryServiceSummaryPageList = data;
              //context.dispatch(new Navigate(['/discovery/servicedetails']));
              context.setState(state);
              // this.router.navigate(['/discovery/servicedetails']);
              // context.setState(state);
            }
          }
          );

    }
    @Action(FetchDiscoveryServiceTopologyPageList )
    fetchDiscoveryHostServicePageList (context: StateContext<DiscoverServiceResultModel>, { payload }: any) {
       
        var state=context.getState();
        var serviceDiscoveryID = payload.discoveryId;
          let url=this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_TOPOLOGY_URL'); 
        return this._http.get(_.template(url)({serviceDiscoveryID}))
        .subscribe((data: any[]) => {
            if (data) {
              state.discoveryServiceTopologyPageList= data;
              context.setState(state);
              // context.patchState(state);
            }
          }
          );

    }
}