import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';
import { Store, Select } from '@ngxs/store';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SaveWorkflowAsTemplate } from 'src/app/release-management/actions/workflow.action';
import { serialize } from 'json-typescript-mapper';
import { DynamicWorkflow } from 'src/app/release-management/models/release.model';
import { AvailableWorkflowTemplatesState } from 'src/app/release-management/state/workflow-schedule.state';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'matilda-save-workflow-template',
  templateUrl: './save-workflow-template.component.html',
  styleUrls: ['./save-workflow-template.component.scss']
})
export class SaveWorkflowTemplateComponent implements OnInit {
  public saveTemplateForm: FormGroup;
  saveMessage: boolean = false;
  public decimals: number = 0;
  public format: string = 'n';
  public newTemplateTypeSelected: boolean = true;
  @Input('workflow') workflow: DynamicWorkflow;
  @Select(AvailableWorkflowTemplatesState.availableWorkflowTemplates) templateOptions$: Observable<any[]>;
  
  constructor(private _fb: FormBuilder,
    public activeModal: NgbActiveModal,
    private _store: Store
    ) { }

  ngOnInit() {
    this.saveTemplateForm = this._fb.group({
      newTemplateName: ['', [
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(30),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_-]+$'), { 'remainingpattern': 'should not contain special characters' }),
      ]],
      existingTemplateName: ['', Validators.nullValidator]
    });
  }

  onSubmit(form: any){
    if(this.saveTemplateForm.valid){
      this.saveMessage = true;
      setTimeout(() => {
        this.saveMessage = false;
      }, 3000);
      
      let workflow = serialize(this.workflow);
      let templateName = this.newTemplateTypeSelected ? form.newTemplateName : form.existingTemplateName;
      let template = Object.assign({}, {
        template_name: templateName,
        //template_version: form.templateVersion,
        template_json: workflow
      });
     this._store.dispatch(new SaveWorkflowAsTemplate(template));
    }
  }
  templateTypeSelected(selectedType:string){
    if(selectedType == 'newTemplate'){
      this.newTemplateTypeSelected = true;
      this.newTemplateName.setValidators([Validators.required]);
      this.existingTemplateName.setValidators([Validators.nullValidator]);
      this.newTemplateName.updateValueAndValidity();
      this.existingTemplateName.updateValueAndValidity();
    }else{
      this.newTemplateTypeSelected = false;
      this.newTemplateName.setValidators([Validators.nullValidator]);
      this.existingTemplateName.setValidators([Validators.required]);
      this.newTemplateName.updateValueAndValidity();
      this.existingTemplateName.updateValueAndValidity();
    }
  }

  get newTemplateName(){
    return this.saveTemplateForm.get('newTemplateName');
  }

  get existingTemplateName(){
    return this.saveTemplateForm.get('existingTemplateName');
  }
  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }
}
