import { Component, OnInit } from '@angular/core';
import { Store, Actions } from '@ngxs/store';
import { Router } from '@angular/router';

@Component({
  selector: 'matilda-discovery-device',
  templateUrl: './discovery-device.component.html',
  styleUrls: ['./discovery-device.component.scss']
})
export class DiscoveryDeviceComponent implements OnInit {
  public discoverydevices$:any;
  public devicepiedata:any
  public options:any;
  public devicedetails:any;
 

  constructor(private _store: Store, private router: Router, private actions$: Actions) { }

  ngOnInit() {
    this.discoverydevices$ = this._store.select(state => state.discoveryDashboardDetails.discoverydeviceslist );
    this.discoverydevices$   .subscribe((element) => {
     
      
    //   var output = Object.entries(element).map(([name, value]) => ({name,value}));
    //   output = output.filter(function( obj ) {
    //      return obj.name!= null;
    //  });
    
      this.devicepiedata= element;
     
     

      this.options= {
        // title: {
        //   text:'Devices', 
        //   x:'center'
        //    },
        tooltip: {
        trigger:'item',
        // formatter:"{b} : {c} ({d}%)"
        formatter:"{b} : {c} ({d}%)"
         },
        legend: {
        type:'scroll',
        bottom:120,
        
          
        },
        
        series: [
         {
          // color:['#34bfa3','#ffb822', '#f4516c', '#36a3f7','#87bdd8', '#91c7ae','#749f83', '#ca8622'],
          color:['#1873b9','#62bdea','#607B8B', '#61b0bc', '#afd7dd','#ddeef0', '#edf6f7','#cccccc', '#ca8622'],
        label: {
        normal: {
        show:true,
        position:'inside',
        
        
        formatter:'{c}',
        avoidLabelOverlap:true,
        // align: 'center',
        fontSize:6,
         }, 
        
        },
        
        type:'pie',
        radius: '58%',
        center: ['50%','33%', '100%','50%'],
        data:this.devicepiedata, 
        itemStyle: {
        emphasis: {
        shadowBlur:10,
        shadowOffsetX:0,
        // shadowColor: 'rgba(0, 0, 0, 0.5)'
         }
         }
         }
         ]
         };
      
                     });
  }
  onChartEvent(event) {
    this.devicedetails=event.data.data;
   if(this.devicedetails){
     this.options= {
      //  title: {
      //    text:'device ', 
      //    x:'center'
      //     },
       tooltip: {
       trigger:'item',
       // formatter:"{b} : {c} ({d}%)"
       formatter:"{b} : {c} ({d}%)"
        },
       legend: {
       type:'scroll',
       bottom:120,
         
       },
       
       series: [
        {
         // color:['#34bfa3','#ffb822', '#f4516c', '#36a3f7','#87bdd8', '#91c7ae','#749f83', '#ca8622'],
         color:['#1873b9','#62bdea','#607B8B', '#61b0bc', '#afd7dd','#ddeef0', '#edf6f7','#cccccc', '#ca8622'],
       label: {
       normal: {
       show:true,
       position:'inside',
       
       
       formatter:'{c}',
       avoidLabelOverlap:true,
       // align: 'center',
       fontSize:8,
        }, 
       
       },
       
       type:'pie',
       radius: '58%',
       center: ['50%','33%', '100%','50%'],
       data:this.devicedetails, 
       itemStyle: {
       emphasis: {
       shadowBlur:10,
       shadowOffsetX:0,
       // shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
        }
        }
        ]
        };
   }
   else{
     this.options= {
      //  title: {
      //    text:'Devices ', 
      //    x:'center'
      //     },
       tooltip: {
       trigger:'item',
       // formatter:"{b} : {c} ({d}%)"
       formatter:"{b} : {c} ({d}%)"
        },
       legend: {
       type:'scroll',
       bottom:120,
         
       },
       
       series: [
        {
         // color:['#34bfa3','#ffb822', '#f4516c', '#36a3f7','#87bdd8', '#91c7ae','#749f83', '#ca8622'],
         color:['#1873b9','#62bdea','#607B8B', '#61b0bc', '#afd7dd','#ddeef0', '#edf6f7','#cccccc', '#ca8622'],
       label: {
       normal: {
       show:true,
       position:'inside',
       
       
       formatter:'{c}',
       avoidLabelOverlap:true,
       // align: 'center',
       fontSize:8,
        }, 
       
       },
       
       type:'pie',
       radius: '58%',
       center: ['50%','33%', '100%','50%'],
       data:this.devicepiedata, 
       itemStyle: {
       emphasis: {
       shadowBlur:10,
       shadowOffsetX:0,
       // shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
        }
        }
        ]
        };
   }
   
     }

}
