import { CloneEnvironmentComponent } from './../components/clone-environment/clone-environment.component';
import { CloneEnvironment, RehydrateEnvironment } from './../actions/release-planning.action';
import { State, Action, StateContext, Selector } from '@ngxs/store';
import { ReleasePlan, AdditionalReleasePlan, ReleaseEnvironment, WorkFlowProperties } from '../models/release.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {
    ReleasePlanUpdated, GetArtifactTable,
    SaveReleasePlan, SaveReleasePlanAndNext,
    AddEnvironment, OpenEnvironmentConfiguration,
    CreateWorkflow, StartRelease,
    StartEnvironmentRelease, GetEnvironments, OpenCloneEnvironmentConfiguration,
} from '../actions/release-planning.action';
import { ViewReleases, GetReleaseItem, ResetReleasePlan, UpdateEnvironment, UpdateEnvironmentOnRelease } from '../actions/release.action';
import settings from '../../settings';
import * as _ from 'lodash';
import { NotificationsService } from 'angular2-notifications';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AddEnvironmentComponent } from '../components/add-environment/add-environment.component';
import { serialize, deserialize } from 'json-typescript-mapper';
import { ReleaseMgmtAppSettings } from '../release-management.settings';
import { LoadWorkflow, ResetCreateWorkflow, AddWorkflowProperties, VerifyWorkflowLaunch } from '../actions/workflow.action';
import { GetImpactedSystems, ResetImpactedSystems } from '../actions/infra-impactedsystems.action';
import { Navigate } from '@ngxs/router-plugin';
import { ClearWorkflowSchedule } from '../actions/workflow-schedule.action';
import { GETMilestones } from '../actions/release-milestones.action';

export class ReleasePlanningModel {
    releasePlan: ReleasePlan[];
}
@State<ReleasePlanningModel>({
    name: 'releasePlan',
    defaults: {
        releasePlan: [],
    }
})

export class ReleasePlanningState {
    // { 'Accept': 'application/json', 'Content-Type': 'application/json', 'DataType': 'application/json' }
    public httpOptions = {
        headers: new HttpHeaders(
            { 'Content-Type':  'application/json' }
        )
    };

    constructor(
        private _notificationsService: NotificationsService,
        private modalService: NgbModal,
        private _http: HttpClient,
        private _releaseMgmtAppSettings: ReleaseMgmtAppSettings) {

       // this.httpOptions.headers.append('', '')
        // this.httpOptions.headers.append('Accept', 'application/json');
        // this.httpOptions.headers.append('Access-Control-Allow-Methods', 'POST, GET, OPTIONS, DELETE, PUT');
        // this.httpOptions.headers.append('Access-Control-Allow-Origin', '*');
        // this.httpOptions.headers.append('Access-Control-Allow-Headers', "X-Requested-With, Content-Type, Origin, Authorization, Accept, Client-Security-Token, Accept-Encoding");
    }

    @Selector()
    static releasePlan(state: ReleasePlanningModel) {
        return state.releasePlan;
    }
    @Selector()
    static additionalReleasePlans(state: ReleasePlanningModel) {
        return state.releasePlan[0].additionalReleasePlans;
    }
    @Selector()
    static environmentList(state: ReleasePlanningModel) {
        return state.releasePlan[0].environmentList;
    }


    @Action(ReleasePlanUpdated)
    releasePlanUpdated(context: StateContext<ReleasePlanningModel>, { payload }: ReleasePlanUpdated) {
        var state = context.getState();
        if (state.releasePlan.length === 0) {
            state.releasePlan.push(payload);
        } else {
            state.releasePlan[0] = Object.assign({}, state.releasePlan[0], payload);
        }
        context.patchState(state);
       
    }

    @Action(ResetReleasePlan)
    resetReleasePlan(context: StateContext<ReleasePlanningModel>, { payload }: ResetReleasePlan) {
        var state = context.getState();
        state.releasePlan = [];
        context.patchState(state);
    }

    @Action(AddEnvironment)
    addEnvironment(context: StateContext<ReleasePlanningModel>, { payload }: AddEnvironment) {
        var state = context.getState();
        //state.releasePlan[0].environmentList.push(payload);
        let releaseId = state.releasePlan[0].releaseId;
        let url = this._releaseMgmtAppSettings.getURL('CREATE_ENV');
        context.patchState(state);
        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        this._http.post(_.template(url)({ releaseId }), serialize(payload), this.httpOptions)
            .subscribe(
                (saveEnvironemntResponse) => {
                    if (saveEnvironemntResponse) {
                        let mappedEnvionmentResponse = deserialize(ReleaseEnvironment, saveEnvironemntResponse);
                        let _releaseEnvironment = new ReleaseEnvironment();
                        _releaseEnvironment.createDate = mappedEnvionmentResponse.createDate;
                        _releaseEnvironment.dependentEnvironment = mappedEnvionmentResponse.dependentEnvironment;
                        _releaseEnvironment.EndDate = mappedEnvionmentResponse.EndDate;
                        _releaseEnvironment.environmentId = mappedEnvionmentResponse.environmentId;
                        _releaseEnvironment.environmentName = mappedEnvionmentResponse.environmentName;
                        _releaseEnvironment.environmentType = mappedEnvionmentResponse.environmentType;
                        _releaseEnvironment.environmentTypeCode = mappedEnvionmentResponse.environmentTypeCode;
                        _releaseEnvironment.releaseEnviornmentId = mappedEnvionmentResponse.releaseEnviornmentId;
                        _releaseEnvironment.releaseId = mappedEnvionmentResponse.releaseId;
                        _releaseEnvironment.stages = mappedEnvionmentResponse.stages;
                        _releaseEnvironment.status = mappedEnvionmentResponse.status;
                        _releaseEnvironment.statusCode = mappedEnvionmentResponse.statusCode;
                        _releaseEnvironment.workflows = [];
                        state.releasePlan[0].environmentList.push(_releaseEnvironment);
                        context.patchState(state);
                        this._notificationsService.remove();
                        this._notificationsService.success('Environment added!', '');
                        this.modalService.dismissAll();
                    }
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Duplicate Environment', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );

    }

    @Action(OpenEnvironmentConfiguration)
    openEnvironmentConfiguration(context: StateContext<ReleasePlanningModel>, { payload }: OpenEnvironmentConfiguration) {
        const addEnvironmentModalRef = this.modalService.open(AddEnvironmentComponent, { size: 'lg', centered: true, backdrop: 'static', keyboard: false });
    }

    @Action(OpenCloneEnvironmentConfiguration)
    openCloneEnvironmentConfiguration(context: StateContext<ReleasePlanningModel>, { payload }: OpenCloneEnvironmentConfiguration) {
        const cloneEnvironmentModalRef = this.modalService.open(CloneEnvironmentComponent, { size: 'lg', centered: true, backdrop: 'static', keyboard: false });
        cloneEnvironmentModalRef.componentInstance.sourceEnvironment = payload;
    }
    @Action(CloneEnvironment)
    cloneEnvironment(context: StateContext<ReleasePlanningModel>, { payload }: CloneEnvironment) {
        const state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('CLONE_ENVIRONMENT');
        let releaseId = payload.sourceEnvironment.releaseId;
        let sourceEnvironmentId = payload.sourceEnvironment.environmentId;
        let targetEnvironmentName = payload.targetEnvironmentName;
        let targetEnvironmentTypeCode = payload.targetEnvironmentTypeCode;
        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        return this._http.post(_.template(url)({ releaseId, sourceEnvironmentId, targetEnvironmentName, targetEnvironmentTypeCode }), '', this.httpOptions)
            .subscribe(
                (cloneEnvironmentResponse: any) => {
                    if (cloneEnvironmentResponse) {
                        let _mappedcloneEnvironment = deserialize(ReleaseEnvironment, cloneEnvironmentResponse);
                        this._notificationsService.remove();
                        this.modalService.dismissAll();
                        this._notificationsService.success('Environment cloned successfully', '');
                        state.releasePlan[0].environmentList.push(_mappedcloneEnvironment);
                        context.patchState(state);
                    }
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Duplicate Environment', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }

    @Action(CreateWorkflow)
    createWorkflow(context: StateContext<ReleasePlanningModel>, { payload }: CreateWorkflow) {
        var state = context.getState();

        context.dispatch(new ResetCreateWorkflow({}));

        let _selectedWorkflow: any[] = payload['workflows'];
        if (_selectedWorkflow.length > 0) {
            let workflowId = _selectedWorkflow[_selectedWorkflow.length - 1].workflowId;
            let releaseId = payload['releaseId'];
            let environmentId = payload['environmentId'];
            context.dispatch(new LoadWorkflow({
                releaseId: releaseId,
                environmentId: environmentId,
                workflowId: workflowId
            }));
        } else {
            context.dispatch(new ClearWorkflowSchedule());
            let workflowProperties = new WorkFlowProperties();
            workflowProperties.release = state.releasePlan[0].name;
            workflowProperties.environmentName = payload.environmentName;
            context.dispatch(new AddWorkflowProperties(workflowProperties));
        }
        context.dispatch(new Navigate(['/release-orchestration/create']));
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(GetArtifactTable)
    getArtifactTable(context: StateContext<ReleasePlanningModel>, { payload }: GetArtifactTable) {
        var state = context.getState();
        let key = payload.selectedArtifactType;
        let project = state.releasePlan[0].projectId;
        let application = state.releasePlan[0].applicationId;
        //Below URL will be used in future when integrated with Real Services
        let url = 'http://192.168.10.133:6002/api/release/artifacts/<%=project%>/<%=application%>/<%=key%>';
        this._http.get(_.template(settings.RELEASE_CONFIG)({ project, application, key }))
            .subscribe(
                (data: any) => {
                    let additionalreleaseplan = new AdditionalReleasePlan();
                    additionalreleaseplan.data = data.data;
                    additionalreleaseplan.title = data.title;
                    additionalreleaseplan.metaData = data.metaData;
                    const i = state.releasePlan[0].additionalReleasePlans.findIndex(existingPlan => existingPlan.title === additionalreleaseplan.title);
                    if (i > -1) state.releasePlan[0].additionalReleasePlans[i] = additionalreleaseplan;
                    else state.releasePlan[0].additionalReleasePlans.push(additionalreleaseplan);
                    context.patchState(state);
                }
            );
    }

    @Action(SaveReleasePlan)
    saveReleasePlan(context: StateContext<ReleasePlanningModel>, { payload }: SaveReleasePlan) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('CREATE_RELEASE');
        let updatedReleaseDateFormat = new Date(Date.parse(payload.releaseDate)).toISOString();
        let saveRelease = serialize(payload);
        let postcallPayload = Object.assign({}, saveRelease, {
            release_dt: updatedReleaseDateFormat,
            additionalReleasePlans: []
        });

        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        this._http.post(url, postcallPayload, this.httpOptions)
            .subscribe(
                (saveReleaseResponse) => {
                    if (saveReleaseResponse) {
                        state.releasePlan[0] = deserialize(ReleasePlan, saveReleaseResponse)
                        this._notificationsService.remove();
                        this._notificationsService.success('Release Plan is Saved!', '');
                        context.dispatch(new Navigate(['/release-orchestration/list']))
                    }
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }

    @Action(SaveReleasePlanAndNext)
    saveReleasePlanAndNext(context: StateContext<ReleasePlanningModel>, { payload }: SaveReleasePlanAndNext) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('CREATE_RELEASE');
        let updatedReleaseDateFormat = new Date(Date.parse(payload.releaseDate)).toISOString();
        let saveRelease = serialize(payload);
        let postcallPayload = Object.assign({}, saveRelease, {
            release_dt: updatedReleaseDateFormat,
            additionalReleasePlans: []
        });

        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        this._http.post(url, postcallPayload, this.httpOptions)
            .subscribe(
                (saveReleaseResponse) => {
                    if (saveReleaseResponse) {
                        var response = deserialize(ReleasePlan, saveReleaseResponse);
                        state.releasePlan[0].releaseId = response.releaseId;
                        context.patchState(state);
                        if (state.releasePlan[0].releaseTypeCode === 2 || state.releasePlan[0].releaseTypeCode === 3) {
                            context.dispatch(new GetImpactedSystems(
                                {
                                    "releaseTypeCode": state.releasePlan[0].releaseTypeCode,
                                    "releaseId": response.releaseId
                                }
                            ));
                            //context.dispatch(new GetEnvironments(response.releaseId));
                            this._notificationsService.remove();
                            this._notificationsService.success('Release Plan is Saved!', '', { timeOut: 500 });
                            context.dispatch(new Navigate(['/release-orchestration/viewrelease']));
                        } else {
                            context.dispatch(new ResetImpactedSystems({}));
                            this._notificationsService.remove();
                            this._notificationsService.success('Release Plan is Saved!', '', { timeOut: 500 });
                            context.dispatch(new Navigate(['/release-orchestration/viewrelease']));
                        }

                    }
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }


    @Action(GetReleaseItem)
    getReleaseItem(context: StateContext<ReleasePlanningModel>, { payload }: GetReleaseItem) {
        var state = context.getState();
        this._notificationsService.remove();
        let url = this._releaseMgmtAppSettings.getURL('GET_RELEASE_ITEM');
        this._notificationsService.info('', 'Fetching release details...', { timeOut: 0 });
        let releaseId = payload.releaseId;
        let releaseTypeCode: number = payload.releaseTypeCode;
        this.fetchRelease(releaseId, releaseTypeCode).then(response => {
            state.releasePlan[0] = deserialize(ReleasePlan, response);
            context.patchState(state);
            this._notificationsService.remove();
            if (releaseTypeCode === 2 || releaseTypeCode === 3) {
                context.dispatch(new GetImpactedSystems(
                    {
                        "releaseTypeCode": releaseTypeCode,
                        "releaseId": releaseId
                    }
                ));
            } else {
                context.dispatch(new ResetImpactedSystems({}));
            }
            context.dispatch(new GETMilestones(releaseId));
            context.dispatch(new Navigate(['/release-orchestration/viewrelease']));
        });

    }

    @Action(ViewReleases)
    viewReleases(context: StateContext<ReleasePlanningModel>, { releaseId }: ViewReleases) {
        var state = context.getState();
        setTimeout(() => {
            context.dispatch(new Navigate(['/release-orchestration/viewrelease']));
           // this.router.navigate(['/release-orchestration/viewrelease']);
        }, 1000);
    }

    @Action(UpdateEnvironment)
    updateEnvironment(context: StateContext<ReleasePlanningModel>, { payload }: UpdateEnvironment) {
        var state = context.getState();
        state.releasePlan[0].environmentList.forEach((_environment) => {
            let oldenvId = _environment.environmentId; //existing EnvironmentId
            let newenvId = payload.environmentId; //Updated EnvironmentId
            // let newenvId = payload.environmentId;
            if (oldenvId == newenvId) {
                //_environment = payload;
                _environment.status = payload.environmentStatus;
                _environment.statusCode = payload.environmentStatusCode;
                if (_environment.workflows.length === 0) {
                    _environment.workflows.push(payload.updatedWorkflow);
                } else {
                    const i = _environment.workflows.findIndex(_workflow => _workflow.workflowId === payload.workflowId);
                    if (i > -1) _environment.workflows[i] = payload.updatedWorkflow;
                    else _environment.workflows.push(payload.updatedWorkflow);
                }
            }
        })
        context.patchState(state);
    }

    @Action(UpdateEnvironmentOnRelease)
    updateEnvironmentOnRelease(context: StateContext<ReleasePlanningModel>, { payload }: UpdateEnvironmentOnRelease) {
        var state = context.getState();
        let environmentIndex = state.releasePlan[0].environmentList.findIndex(_environment => _environment.environmentId === payload.environmentId);
        if (environmentIndex > -1) state.releasePlan[0].environmentList[environmentIndex] = payload;
        else;
        context.patchState(state);
    }

    @Action(StartRelease)
    startRelease(context: StateContext<ReleasePlanningModel>, { payload }: StartRelease) {
        const state = context.getState();
        let releaseId = state.releasePlan[0].releaseId || '';
        let url = this._releaseMgmtAppSettings.getURL('START_RELEASE');
        this._http.post(_.template(url)({ releaseId }), '', this.httpOptions)
            .subscribe(
                (releaseResponse) => {
                    this._notificationsService.remove();
                    this._notificationsService.success('Release has been initiated');
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }

    @Action(StartEnvironmentRelease)
    StartEnvironmentRelease(context: StateContext<ReleasePlanningModel>, { payload }: StartEnvironmentRelease) {
        const state = context.getState();
        let releaseId = payload.releaseId || '';
        let environmentId = payload.environmentId || '';
        let workflowId = payload.workflows[0].workflowId;

        let url = this._releaseMgmtAppSettings.getURL('START_ENVIRONMENT_RELEASE');
        this._notificationsService.info('', 'Please wait.. while we are waiting for the Re-hydration to trigger', { timeOut: 0 });
        this._http.post(_.template(url)({ releaseId, environmentId, workflowId }), '', this.httpOptions)
            .subscribe(
                (releaseResponse) => {
                    let _environmentReleaseResponse = deserialize(ReleaseEnvironment, releaseResponse);
                    state.releasePlan[0].environmentList.forEach(_environment => {
                        if (_environment.environmentId === environmentId) {
                            //Logic for Updating Environment
                            context.dispatch(new UpdateEnvironmentOnRelease(_environmentReleaseResponse));
                        }
                    });
                    this._notificationsService.remove();
                    this._notificationsService.success('Re-hydration has been initiated');
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }

    @Action(GetEnvironments)
    getEnvironments(context: StateContext<ReleasePlanningModel>, { payload }: GetEnvironments) {
        var state = context.getState();
        let releaseId = payload;
        //Below URL will be used in future when integrated with Real Services
        let url = this._releaseMgmtAppSettings.getURL('CREATE_ENV');
        return this._http.get(_.template(url)({ releaseId }))
            .subscribe((enviornments: any[]) => {
                if (enviornments.length > 0) {
                    let _environments = enviornments.map(_infraEnvironment => deserialize(ReleaseEnvironment, _infraEnvironment));
                    state.releasePlan[0].environmentList = _environments;
                    context.patchState(state);
                    // this.ref.tick();
                }
            });
    }

    fetchRelease(releaseId, releaseTypeCode) {
        let url = this._releaseMgmtAppSettings.getURL('GET_RELEASE_ITEM');
        return new Promise((resolve, reject) => {
            this._http.get(_.template(url)({ releaseId, releaseTypeCode }))
                .subscribe(data => {
                    //DO SOMETHING, THEN ----
                    resolve(data);
                },
                    (err) => {
                        this._notificationsService.remove();
                        this._notificationsService.error("Something went wrong. We couldn't load the release from the server");
                    }
                );
        });
    }

    @Action(RehydrateEnvironment)
    rehydrateEnvironment(context: StateContext<ReleasePlanningModel>, { payload }: RehydrateEnvironment) {
        var state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('CLONE_ENVIRONMENT');
        let releaseId = payload.releaseId;
        let sourceEnvironmentId = payload.environmentId;
        let targetEnvironmentName = payload.environmentName + '_rehydrate';
        let targetEnvironmentTypeCode = payload.environmentTypeCode;
        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        return this._http.post(_.template(url)({ releaseId, sourceEnvironmentId, targetEnvironmentName, targetEnvironmentTypeCode }), '', this.httpOptions)
            .subscribe(
                (cloneEnvironmentResponse: any) => {
                    if (cloneEnvironmentResponse) {
                        this._notificationsService.remove();
                        // this.modalService.dismissAll();
                        this._notificationsService.success('Environment cloned successfully', '');
                        let _mappedcloneEnvironment = deserialize(ReleaseEnvironment, cloneEnvironmentResponse);
                        state.releasePlan[0].environmentList.push(_mappedcloneEnvironment);
                        context.patchState(state);
                        this._notificationsService.remove();
                        context.dispatch(new StartEnvironmentRelease(_mappedcloneEnvironment));
                    }
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Duplicate Environment', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }
}
