import { NgModule , CUSTOM_ELEMENTS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';

import { CostManagementRoutingModule } from './cost-management-routing.module';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { Routes, RouterModule } from '@angular/router';
import { LayoutModule } from '../theme/layouts/layout.module';
import { AppMaterialModule } from '../app-material/app-material.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormTemplateComponent } from '../shared/dynamic_forms/form-template/form-template.component';
import { NgbModule ,NgbDatepickerModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from '../shared/shared.module';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { NgxEchartsModule } from 'ngx-echarts';
import { WindowModule } from '@progress/kendo-angular-dialog';
import { ChartsModule } from '@progress/kendo-angular-charts';
import { MetricsService } from './services/metrics.service';
import { GridModule, PDFModule, ExcelModule} from '@progress/kendo-angular-grid';
import { IntlModule } from '@progress/kendo-angular-intl';
import { DateInputsModule } from '@progress/kendo-angular-dateinputs';
import { LabelModule } from '@progress/kendo-angular-label';
import 'hammerjs';
import { NgMultiSelectDropDownModule } from "ng-multiselect-dropdown";
import { MultiselectDropdownModule } from 'angular-2-dropdown-multiselect';
import { HttpClientModule, HttpClient ,HttpClientJsonpModule} from '@angular/common/http';
import { CostRiManagementComponent } from './components/cost-ri-management/cost-ri-management.component';
import { CostAnalyticsComponent } from './components/cost-analytics/cost-analytics.component';
import { CostDashboardComponent } from './components/cost-dashboard/cost-dashboard.component';
import { CostConfigureComponent } from './components/cost-configure/cost-configure.component';
import { CostForecastComponent } from './components/cost-forecast/cost-forecast.component';
import { CostSavingComponent } from "./components/cost-saving/cost-saving.component";
import { CostFilterComponent } from './shared/components/cost-filter/cost-filter.component';
import { DialogsModule } from "@progress/kendo-angular-dialog";
import { EditService } from './services/edit.service';
import { CostManagementSettings } from './cost-management-settings';
import { CostEliminateComponent } from './components/cost-eliminate/cost-eliminate.component';
import { GaugesModule } from '@progress/kendo-angular-gauges';
import { CostCloudComponent } from './components/cost-cloud/cost-cloud.component';
import { PDFExportModule } from '@progress/kendo-angular-pdf-export';
import { CostBudgetComponent } from './components/cost-budget/cost-budget.component';
import { TooltipModule } from '@progress/kendo-angular-tooltip';
import { ScrollViewModule } from '@progress/kendo-angular-scrollview';
import { CostAlertComponent } from './components/cost-alert/cost-alert.component';

@NgModule({
  imports: [
    CommonModule,
    LayoutModule,
    AppMaterialModule,
    NgbModule,
    ReactiveFormsModule,
    FormsModule,
    NgSelectModule,
    RouterModule,
    NgMultiSelectDropDownModule,
    InputsModule,
    MultiselectDropdownModule,
    NgxEchartsModule,
    GaugesModule,
    PDFExportModule,
    CostManagementRoutingModule,
    SharedModule,
    WindowModule,
    ChartsModule,
    DropDownsModule,
    NgbDatepickerModule,
    GridModule,
    IntlModule,
    DateInputsModule,
    LabelModule,
    HttpClientModule,
    GridModule,
    DialogsModule,
    HttpClientJsonpModule,
    PDFModule,
    ExcelModule,
    TooltipModule,
    ScrollViewModule
  ],
  declarations: [
    CostForecastComponent,
    CostRiManagementComponent,
    CostSavingComponent,
    CostAnalyticsComponent,
    CostFilterComponent ,
    CostDashboardComponent,
    CostConfigureComponent ,
    CostFilterComponent,
    CostEliminateComponent,
    CostCloudComponent,
    CostBudgetComponent,
    CostAlertComponent
  ],

  exports: [
    AppMaterialModule,
    FormTemplateComponent,
    GridModule,
    DropDownsModule
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  providers: [MetricsService,
    {
      deps: [HttpClient],
      provide: EditService,
      useFactory: (jsonp: HttpClient) => () => new EditService(jsonp)
    },CostManagementSettings]
})
export class CostManagementModule {}
