import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AsideLeftDisplayDisabledComponent } from '../theme/pages/aside-left-display-disabled/aside-left-display-disabled.component';
import { MigrationDashboardComponent } from './components/migration-dashboard/migration-dashboard.component';
import { MigrationListComponent } from './components/migration-list/migration-list.component';
import { MigrationStepperComponent } from './components/new-migration/migration-stepper/migration-stepper.component';

const routes: Routes = [
  {
    path:'',
    'component': AsideLeftDisplayDisabledComponent,
    'children': [
      {
        path: 'dashboard',
        component: MigrationDashboardComponent
      },
      {
        path: 'list',
        component: MigrationListComponent
      },
      {
        path: 'newmigration',
        component: MigrationStepperComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MigrationRoutingModule { }
