import { GetWorkflowTemplates, PlanWorkflowSchedule, ClearWorkflowSchedule } from './../actions/workflow-schedule.action';
import { AvailableWorkflowTemplateVersions, TemplateVersion } from './../models/available-workflow-templates.model';
import { State, Selector, Action, StateContext } from "@ngxs/store";
import * as _ from 'lodash';
import { HttpClient } from "@angular/common/http";
import { deserialize } from "json-typescript-mapper";
import {ReleaseMgmtAppSettings} from "../release-management.settings";
import { WorkflowSchedule } from '../models/workflow-schedule.model';
import { Frequency } from '../models/frequency.model';

export class AvailableWorkflowTemplatesModel {
    availableWorkflowTemplates: string[];
    availableWorkflowTemplateVersions: AvailableWorkflowTemplateVersions[];
    workflowSchedule: WorkflowSchedule[];
}

@State<AvailableWorkflowTemplatesModel>({
    name: 'availableWorkflowTemplates',
    defaults: {
        availableWorkflowTemplates: [],
        availableWorkflowTemplateVersions: [],
        workflowSchedule: []
    }
})
export class AvailableWorkflowTemplatesState {
    constructor(private http: HttpClient,private _releaseMgmtAppSettings:ReleaseMgmtAppSettings) {}
    @Selector()
    static availableWorkflowTemplates(state: AvailableWorkflowTemplatesModel) {
        return state.availableWorkflowTemplates;
    }
    @Selector()
    static availableWorkflowTemplateVersions(state: AvailableWorkflowTemplatesModel) {
        return state.availableWorkflowTemplateVersions;
    }
    
    @Selector()
    static workflowSchedule(state: AvailableWorkflowTemplatesModel) {
        return state.workflowSchedule;
    }

    @Action(GetWorkflowTemplates)
    getWorkflowTemplates(context: StateContext<AvailableWorkflowTemplatesModel>, { payload }: GetWorkflowTemplates) {
        const state = context.getState();
        let url = this._releaseMgmtAppSettings.getURL('GET_WORKFLOW_TEMPLATES')
        return this.http.get(url)
            .subscribe((data: any[]) => {
                if (data.length > 0) {
                    let _mappedAvailableWorkflowTemplateVersions = data.map(_templateVersions => deserialize(AvailableWorkflowTemplateVersions, _templateVersions));
                    let _mappedAvailableWorkflowTemplates:any[] = [];
                    _mappedAvailableWorkflowTemplateVersions.forEach((_template)=> {
                        _mappedAvailableWorkflowTemplates.push(_template.templateName);
                    })
                    state.availableWorkflowTemplates = _mappedAvailableWorkflowTemplates;
                    state.availableWorkflowTemplateVersions = _mappedAvailableWorkflowTemplateVersions;
                    context.patchState(state);
                }
            });
    }

    @Action(PlanWorkflowSchedule)
    planWorkflowSchedule(context: StateContext<AvailableWorkflowTemplatesModel>, { payload }: PlanWorkflowSchedule) {
       var state = context.getState();
       state.workflowSchedule = [];
       state.workflowSchedule.push(payload);
       context.patchState(state);
    }

    @Action(ClearWorkflowSchedule)
    clearWorkflowSchedule(context: StateContext<AvailableWorkflowTemplatesModel>) {
       var state = context.getState();
       let templateVersion= new TemplateVersion();
       let frequency = new Frequency();
       let emptyWorkflowSchedule = new WorkflowSchedule('','',templateVersion,frequency);
       state.workflowSchedule = [];
       state.workflowSchedule.push(emptyWorkflowSchedule);
       context.patchState(state);
    }
}