import { JsonProperty } from "json-typescript-mapper";
export class Application {
    status: string = '';
    @JsonProperty('create_dt')
    createDate: string = '';
    @JsonProperty('project_id')
    projectId: string = '';
    name: string = '';
    owner: string = '';
    @JsonProperty('app_id')
    id: string = '';
    
}

export class Project {
    status: string = '';
    @JsonProperty('project_id')
    id: string = '';
    @JsonProperty({clazz: Application})
    applications: Application[] = [];
    @JsonProperty('name')
    name: string = '';
    @JsonProperty('create_dt')
    createDate: string = '';
    owner: string = '';
}

