import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Store, Actions } from '@ngxs/store';
import { Router } from '@angular/router';

@Component({
  selector: 'matilda-discovery-osversions',
  templateUrl: './discovery-osversions.component.html',
  styleUrls: ['./discovery-osversions.component.scss']
})
export class DiscoveryOsversionsComponent implements OnInit {
  public discoveryoperatingsystemVersions$ :any;
  public discoveryoperatingsystemVersions:any;
    
  colors:Array<string> = ['#1873b9','#62bdea','#607B8B', '#61b0bc', '#afd7dd','#ddeef0', '#edf6f7','#cccccc', '#ca8622'];
  bars: Array<any> = [];

  constructor(private _store: Store, private router: Router, private actions$: Actions) { }

  ngOnInit() {
    this.discoveryoperatingsystemVersions$ = this._store.select(state => state.discoveryDashboardDetails.discoveryosversionslist);

    this.discoveryoperatingsystemVersions$.subscribe((element) => {
      this.discoveryoperatingsystemVersions=element
      console.log(this.discoveryoperatingsystemVersions)
                           });
                           this.calculateBarWidth();
  }
    calculateBarWidth() {
      //console.log(this.discoveryoperatingsystemVersions)

    this.discoveryoperatingsystemVersions.forEach(barItem =>{
      //console.log(this.discoveryoperatingsystemVersions)

        let total:number = 0;
        barItem.data.forEach(barDataItem=>{
            total =  total + barDataItem.Value;
        });
        barItem.data.forEach((barDataItem, index) =>{
            barDataItem["percentage"] = (barDataItem.Value/total)*100;
            barDataItem["color"] = this.colors[index];
        });
    });
    this.bars = this.discoveryoperatingsystemVersions;
    // console.log(this.data);
  }

}
