import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges, OnChanges, OnDestroy }
from '@angular/core';
import { process, State }
from '@progress/kendo-data-query';
import { PageChangeEvent, GridDataResult, DataStateChangeEvent, SelectAllCheckboxState, RowArgs }
from '@progress/kendo-angular-grid';
import * as _ from 'lodash';
// declare var loadedgrid: any;
@Component({
  selector: 'matilda-grid, [matilda-grid]',
  templateUrl: './dynamic-kendo-grid.component.html',
  styleUrls: ['./dynamic-kendo-grid.component.scss']
})
export class DynamicKendoGridComponent implements OnInit, OnChanges, OnDestroy {
  public static idCount: number = 0;
  @Input() public metaData: any[];// Defines the headers of the grid with settigs(filter/sorting)
  @Input() public data: any[];// Get the data array to implement pagination and sorting
  @Input() public pageSize = 10; // Declares the page size by default 10
  @Input() public id: string;// Defines the id of the component
  @Input() public pageSizes = true;// Defines the page size to be enable of disable
  @Input() public previousNext = true;  // Page next and button enabling
  @Input() public buttonCount = 3; // Page button count
  @Input() public skip = 0;
  @Input() public title: string = 'Untitled';
  @Input() public info = true; // Paging information in footer
  @Input() public mySelection: any[] = [];
  @Input() public selectName: string; // Gets the by default name for multiple selection purpose
  @Input() public height: number = 500; // Default height of the grid table
  @Input() public sorting: boolean = true; // Sorting enabling and disabling
  @Input() public filtering: boolean = true; // Filtering enabling and disabling
  @Input() public clone: boolean = false; // For emitting button action
  @Input() public view: boolean = false; // For emitting button action
  @Input() public checkbox: boolean = false; // For enabling checkbox for the table
  public checkedRows: any[] = [];
  public selectAllState: SelectAllCheckboxState = 'unchecked'; // Checkbox default condtions
  public loading = false;
  public type: 'numeric' | 'input' = 'numeric';
  @Output() public idChange: EventEmitter<string> = new EventEmitter<string>();
  @Output('action') action = new EventEmitter(); // Button action emitter to throw event in the component
  @Output('cloneaction') cloneaction = new EventEmitter();
  @Output('selectionChange') selectionChange = new EventEmitter();
  public state: State = {
    skip: 0,
    take: 5,
    filter: {
      logic: 'and',
      filters: [],
    },
    sort: [{
      field: '',
      dir: 'asc'
    }]
  }; // Default state configuration
  @Input() public gridData: GridDataResult;
  // public sort: SortDescriptor[] = [{
  //   field: '',
  //   dir: 'asc'
  // }];
  constructor() {
    if (!this.id) {
      this.id = 'matilda-grid-' + DynamicKendoGridComponent.idCount;
      this.idChange.emit(this.id);
      DynamicKendoGridComponent.idCount++;
    }
  }
  public mySelectionKey(context: RowArgs): string {
    return context.dataItem;
  }
  public onSelectedKeysChange(e) {
    const len = e.length;
    if (len === 0) {
      this.selectAllState = 'unchecked';
    } else if (len > 0 && len < this.data.length) {
      this.selectAllState = 'indeterminate';
    } else {
      this.selectAllState = 'checked';
    }
    this.selectionChange.emit({ title: this.title, data: e, metaData: this.metaData });
  }
  public onSelectAllChange(checkedState: SelectAllCheckboxState) {
    if (checkedState === 'checked' && this.mySelection != []) {
      this.mySelection = this.data.map((item) => item);
      this.selectAllState = 'checked';
    } else {
      this.mySelection = [];
      this.selectAllState = 'unchecked';
    }
  }
  /* On Page change will take next 10 rows and and update the grid data */
  public pageChange({ skip, take }: PageChangeEvent): void {
    // this.skip = skip;
    // this.pageSize = take;
    //this.loadData();
  }
  private loadData(): void {
    this.gridData = {
      data: this.data.slice(this.skip, this.skip + this.pageSize),
      total: this.data.length
    };
  }
  public dataStateChange(state: DataStateChangeEvent): void {
    this.state = state;
    this.gridData = process(this.data, this.state);
  }

  public actionEmitter(value) {
    this.action.emit(value);
  }
  public cloneEmitter(value) {
    this.cloneaction.emit(value);
  }
  public ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    this.pageSize = 5;
    this.buttonCount = 3;
    // this.sort = this.sort;
    this.state = this.state;
    this.data = this.data;
    this.gridData = process(this.data, this.state);
  }
  public ngOnChanges(changes: SimpleChanges) {
    if (changes.data &&
      changes.data.currentValue !== changes.data.previousValue &&
      !changes.data.isFirstChange()) {
      this.pageSize = 5;
      this.buttonCount = 3;
      //this.sort = this.sort;
      this.state = this.state;
      this.data = this.data;
      this.gridData = process(this.data, this.state);
    }
  }
  public ngOnDestroy() {
    delete this.id
  }

  getStatusBadge(statusCode: number) {
    // if (status) {
    //   if (status.toLowerCase() === 'active' || status.toLowerCase() === 'complete') {
    //     return 'm-badge--success';
    //   } else if (status.toLowerCase() === 'failed') {
    //     return 'm-badge--danger';
    //   }
    //   else if (status.toLowerCase() === 'in progress' || status.toLowerCase() === 'pending') {
    //     return 'm-badge--warning';
    //   } else {
    //     return 'm-badge--brand';
    //   }
    // } else {
    //   return 'm-badge--warning';
    // }
    if (statusCode) {
      if (statusCode === 4) {
        return 'badge-success custom-badge-success';
      } else if (statusCode === 5) {
        return 'badge-danger';
      }
      else if (statusCode === 3) {
        return 'custom-badge-Inprogress';
      } else if (statusCode === 2) {
        return 'custom-badge-configured';
      } else if (statusCode === 1) {
        return 'custom-badge-defined';
      } else {
        return 'badge-warning';
      }
    }
  }
}
