  public onChangePie(event: any): void {
    if (event === 'application') {
      this.selected = true;
      this.Secondselected = false;
      this.disabled = true;
    } else if (event === 'service') {
      this.selected = false;
      this.Secondselected = true;
      this.disabled = true;
      this.metrics.getDashboardPieDropDownData().subscribe(data => {
        var seriesData: SeriesValue[] = [];
        var category: CategoryValue[] = [];
        this.piedata = data.response.pie_data;

        this.piedatadefaultvalues = data.input_json;
        this.piedata.forEach(item => {
          seriesData.push(new SeriesValue(item.category, item.value));
        });

        this.piedata.forEach(item => {
          category.push(new CategoryValue(item.category));
        });
        // var category: any[] = [];
        this.pieServicemodel = new ChartData(
          data.response.Title,
          data.response.pietype,
          'false',
          category,
          seriesData,
          null
        );
      });
    }
  }

