  // Apply the filter to the selected values
  applyFilter(event: any) {
    var dimensionKeys: KeyOrderPair[] = [];
    var tagKeys: KeyOrderPair[] = [];
    var order = 1;

    if (this.filterModel.GroupBy.length !== 0) {
      this.filterModel.GroupBy.forEach(item => {
        this.groupbyOptions.forEach(option => {
          if (option.itemName === item.itemName) {
            if (option.category === 'Dimension') {
              dimensionKeys.push(new KeyOrderPair(item.itemName, order));
              order++;
            } else if (option.category === 'Tag') {
              tagKeys.push(new KeyOrderPair(item.itemName, order));
              order++;
            }
          }
        });
      });
    }
    var dimensionFilterResult: FilterResult[] = [];
    var tagFilterResult: FilterResult[] = [];

    this.advancedFilter.GroupValues.forEach(item => {
      if (item.Title === 'Dimension') {
        item.Filter.forEach(option => {
          if (option.SelectedValues) {
            dimensionFilterResult.push(
              new FilterResult(
                option.Title,
                option.SelectedValues,
                option.Exclude
              )
            );
          }
        });
      } else if (item.Title === 'Tag') {
        item.Filter.forEach(option => {
          if (option.SelectedValues) {
            tagFilterResult.push(
              new FilterResult(
                option.Title,
                option.SelectedValues,
                option.Exclude
              )
            );
          }
        });
      }
    });
    let finalFilter = {
      TimePeriod: {
        Start: this.datePipe.transform(
          this.filterModel.StartDate,
          'yyyy-MM-dd'
        ),
        End: this.datePipe.transform(this.filterModel.EndDate, 'yyyy-MM-dd')
      },
      Granularity: this.filterModel.Granularity,

      GroupBy: {
        Dimensions: dimensionKeys,
        Tags: tagKeys
      },
      Metrics: this.filterModel.Metrics,
      Filter: {
        Dimensions: dimensionFilterResult,
        Tags: tagFilterResult
      }
    };
    if (finalFilter != null) {
      this._notifications.info('Calculating...', '');
    }

    this.metrics.applyFilters(finalFilter).subscribe(data => {
      this.responsedata = data;
      if (data != null) {
        this._notifications.success('Done', '');
      }
    });
    let params = JSON.stringify(finalFilter);
  }

