  // Fire Event
  public onSeriesClick(e): void {
    this.startup = e.category;

    // Bar Data
    this.metrics.getDashboardBarData(this.startup).subscribe(data => {
      var seriesData: SeriesValue[] = [];


      var category: any[] = [];
      var value: KeyValuePair[] = [];
      this.bardata = data.response.metrics;
      this.bardata.forEach(item => {
        category.push(item.category);

        if (value.length === 0) {
          item.values.forEach(keyPair => {
            value.push(new KeyValuePair(keyPair.key));
          });
        }
        item.values.forEach(keyPair => {
          value.forEach(valueData => {
            if (valueData.key === keyPair.key) {
              valueData.value.push(keyPair.value);
            }
          });
        });
      });
      var stackData = new StackValue(category, value);
      // title, type, isStack, name, seriesData, stackData
      this.model = new ChartData(
        data.response.Title,
        data.response.type,
        'true',
        '',
        seriesData,
        stackData
      );
    });

    this.mastertab = true;
    this.secondarytab = false;
    this.selected = false;
    this.Nextselected = true;
    this.disabled = false;

    // Service Piechart
    this.metrics.getDashboardPieDualData().subscribe(data => {
      var seriesData: SeriesValue[] = [];
      var category: CategoryValue[] = [];
      this.piedata = data.response.pie_data;

      this.piedatadefaultvalues = data.input_json;
      this.piedata.forEach(item => {
        seriesData.push(new SeriesValue(item.category, item.value));
      });

      this.piedata.forEach(item => {
        category.push(new CategoryValue(item.category));
      });
      // var category: any[] = [];
      this.piemodel = new ChartData(
        data.response.Title,
        data.response.pietype,
        'false',
        category,
        seriesData,
        null
      );
    });
    // access e.dataItem.url of the bound item; use window.location or router from there
  }

