  drawChart(result) {
    this.openedAddFilter = false;
    var seriesData: SeriesValue[] = [];
    var category: CategoryValue[] = [];
    this.piedata = result.response.pie_data;
    this.piedata.forEach(item => {
      seriesData.push(new SeriesValue(item.category, item.value));
    });

    this.piedata.forEach(item => {
      category.push(new CategoryValue(item.category));
    });
    // var category: any[] = [];
    this.piemodel = new ChartData(
      result.response.Title,
      result.response.pietype,
      'false',
      category,
      seriesData,
      null
    );

    var categoryBar: any[] = [];
    var value: KeyValuePair[] = [];
    this.bardata = result.response.bar_stack_data;
    this.bardata.forEach(item => {
      categoryBar.push(item.categoryBar);

      if (value.length === 0) {
        item.values.forEach(keyPair => {
          value.push(new KeyValuePair(keyPair.key));
        });
      }
      item.values.forEach(keyPair => {
        value.forEach(valueData => {
          if (valueData.key === keyPair.key) {
            valueData.value.push(keyPair.value);
          }
        });
      });
    });
    var stackData = new StackValue(categoryBar, value);
    // title, type, isStack, name, seriesData, stackData
    this.model = new ChartData(
      result.Title,
      result.type,
      'true',
      '',
      seriesData,
      stackData
    );
  }


