import { HttpClient } from '@angular/common/http';
import { DynamicWorkflow } from "../models/release.model";
import { State, Selector, Action, StateContext } from "@ngxs/store";
import { ViewWorkflow, ViewTaskResults, ViewTaskConfiguration, PollWorkflow } from "../actions/workflow.action";
import * as _ from 'lodash';
import { TaskResultsComponent } from '../components/dynamic-workflow/task-results/task-results.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {ReleaseMgmtAppSettings} from '../release-management.settings';
import { NotificationsService } from 'angular2-notifications';
import { deserialize } from 'json-typescript-mapper';
import { Navigate } from '@ngxs/router-plugin';
import { ClearPluginConfiguration, PluginConfiguration } from '../actions/workflow-available-services.action';
import { ViewReleasePluginModalComponent } from '../shared/components/view-release-plugin-modal/view-release-plugin-modal.component';
import { tap } from 'rxjs/operators/tap';
import { shareReplay } from 'rxjs/operators';

export class ViewWorkflowModel {
    viewWorkflow: DynamicWorkflow[];

}
@State<ViewWorkflowModel>({
    name: 'viewWorkflow',
    defaults: {
        viewWorkflow: [],
    },
})
export class ViewDynamicWorkflowState {
    constructor(private _http: HttpClient,
        private _notificationsService: NotificationsService,
        private modalService: NgbModal,
        private _releaseMgmtAppSettings:ReleaseMgmtAppSettings) { }

    @Selector()
    static viewWorkflow(state: ViewWorkflowModel) {
        return state.viewWorkflow;
    }

    ngxsOnInit(context: StateContext<ViewWorkflowModel>) {
      var state = context.getState();
    //   let workflowId = '';
    //     this._http.get('../../../assets/mockdata/release-management/VIEW_WORKFLOW.json')
    //         .subscribe((workflow: DynamicWorkflow) => {
    //             if (workflow) {
    //                 let test = deserialize(DynamicWorkflow,workflow);
    //                 state.viewWorkflow.push(test);
    //                 context.patchState(state);
    //             }
    //         });
    }

    @Action(ViewWorkflow)
    viewWorkflow(context: StateContext<ViewWorkflowModel>, { payload }: ViewWorkflow) {
        const state = context.getState();
        let workflowId = payload.workflowId;
        let releaseId = payload.releaseId;
        let envId = payload.environmentId;
        let url = this._releaseMgmtAppSettings.getURL('GET_WORKFLOW');

        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        return this._http.get(_.template(url)({releaseId,envId,workflowId})).pipe(
            tap(workflow => {
                if (workflow) {
                    state.viewWorkflow = [];
                    let mappedWorkflow = deserialize(DynamicWorkflow,workflow);
                    state.viewWorkflow.push(mappedWorkflow);
                    context.patchState(state);

                    this._notificationsService.remove();
                    context.dispatch(new Navigate(['/release-orchestration/view']));
                }
            },
            (error) => {
                this._notificationsService.remove();
                if (error.status === 0) {
                    this._notificationsService.error('Error Occurred', 'Server is not responding');
                } else if (error.status === 400) {
                    this._notificationsService.error('Something went wrong', error.error['message']);
                } else {
                    this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                }
            }
            ));
    }

    @Action(ViewTaskResults)
    ViewTaskResults(context: StateContext<ViewWorkflowModel>, { payload }: ViewTaskResults) {
        var state = context.getState();
        const modalRef = this.modalService.open(TaskResultsComponent, { size: 'lg', centered: true });

        this._http.get(_.template()({}))
            .subscribe(
                (data) => {
                    if (data) {
                        modalRef.componentInstance.name = 'World';
                        modalRef.componentInstance.taskResults = 'World';
                    }
                },
                (error) => {
                    //Some Logic on Error
                }
            );
    }

    @Action(ViewTaskConfiguration)
    viewTaskConfiguration(context: StateContext<ViewWorkflowModel>, { payload }: ViewTaskConfiguration) {
        var state = context.getState();
        context.dispatch(new ClearPluginConfiguration());

        let previousConfig:any = {};
        state.viewWorkflow[0].stages.forEach(stage => {
            if (stage.id === payload.stageId) {
                stage.tasks.forEach(_task => {
                    if (_task.id === payload.task.id) {
                        previousConfig = _task.configuration;
                    }
                })
            }
        });


        let serviceId: number = payload.task.serviceId;
        let actionId: number = payload.task.actionId;
        context.dispatch(new PluginConfiguration({ serviceId, actionId }));
        const modalRef = this.modalService.open(ViewReleasePluginModalComponent, { size: 'lg', centered: true , backdrop: 'static', keyboard: false  });
        modalRef.componentInstance.previousConfig = previousConfig;
        modalRef.componentInstance.task = payload.task;
        modalRef.componentInstance.stageId = payload.stageId;

    }
   

    @Action(PollWorkflow)
    pollWorkflow(context: StateContext<ViewWorkflowModel>, { payload }: PollWorkflow) {
        const state = context.getState();
        let workflowId = payload.workflowId;
        let releaseId = payload.releaseId;
        let envId = payload.environmentId;
        let url = this._releaseMgmtAppSettings.getURL('GET_WORKFLOW');

        return this._http.get(_.template(url)({releaseId,envId,workflowId})).pipe(
            tap(workflow => {
                state.viewWorkflow = [];
                let mappedWorkflow = deserialize(DynamicWorkflow,workflow);
                state.viewWorkflow.push(mappedWorkflow);
                context.patchState(state);

            }),
            shareReplay()
            );
        }

}
