
import {throwError as observableThrowError,  Observable ,  BehaviorSubject } from 'rxjs';
import { Injectable } from "@angular/core";
import { Http, Headers, Response } from '@angular/http';

import AppSettings from '../../settings';

@Injectable()
export class AppGraphServiceService {
  private dataStore: any;
  private _mapDataset = new BehaviorSubject<any>(null);// any => class/interface Dataset
  private _map2Dataset = new BehaviorSubject<any>(null);// any => class/interface Dataset
  private _map3Dataset = new BehaviorSubject<any>(null);// any => class/interface Dataset
  private _topologyDataset = new BehaviorSubject<any>(null);// any => class/interface Dataset
  private _topologyTreeDataset = new BehaviorSubject<any>(null);// any => class/interface Dataset

  get mapDataset(): Observable<object[]> {
    return this._mapDataset.asObservable();
  }
  get map2Dataset(): Observable<object[]> {
    return this._map2Dataset.asObservable();
  }
  get map3Dataset(): Observable<object[]> {
    return this._map3Dataset.asObservable();
  }
  get topologyDataset(): Observable<object[]> {
    return this._topologyDataset.asObservable();
  }
  get topologyTreeDataset(): Observable<object[]> {
    return this._topologyTreeDataset.asObservable();
  }
  headers: Headers;

  constructor(public http: Http) {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    this.dataStore = {
      mapDataset: null,
      map2Dataset: null,
      map3Dataset: null,
      topologyDataset: null,
      topologyTreeDataset: null
    };
  }

  public getServiceData(): void {
    this.http.get(AppSettings.MAP_DATA)
      .map(this.extractJSONResponse)
      .catch(this.handleError)
      .subscribe((data) => {
        this.dataStore.mapDataset = data || {};
        this._mapDataset.next(this.dataStore.mapDataset);
      });

    this.http.get(AppSettings.MAP2_DATA)
      .map(this.extractJSONResponse)
      .catch(this.handleError)
      .subscribe((data) => {
        this.dataStore.map2Dataset = data || {};
        this._map2Dataset.next(this.dataStore.map2Dataset);
      });

    this.http.get(AppSettings.MAP3_DATA)
      .map(this.extractJSONResponse)
      .catch(this.handleError)
      .subscribe((data) => {
        this.dataStore.map3Dataset = data || {};
        this._map3Dataset.next(this.dataStore.map3Dataset);
      });

    this.http.get(AppSettings.WEBLOGIC_DATA)
      .map(this.extractJSONResponse)
      .catch(this.handleError)
      .subscribe((data) => {
        this.dataStore.topologyDataset = data || {};
        this._topologyDataset.next(this.dataStore.topologyDataset);
      });

    this.http.get(AppSettings.TOPOLOGY_TREE_DATA)
      .map(this.extractJSONResponse)
      .catch(this.handleError)
      .subscribe((data) => {
        this.dataStore.topologyTreeDataset = data || {};
        this._topologyTreeDataset.next(this.dataStore.topologyTreeDataset);
      });
  }

  private extractJSONResponse(res: Response): any {
    let body = res.json();
    console.log('Toplogy ' + body);
    return body;
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return observableThrowError(errMsg);
  }
}
