      public editClick({ dataItem, rowIndex, columnIndex }: any): void {
        this.cellClickHandler({
            'dataItem': dataItem,
            'rowIndex': rowIndex,
            'columIndex': columnIndex,
            'sender': this.grid
        });
      }
      
      public cellClickHandler({ dataItem, rowIndex, columIndex, sender }): void {
        if (this.formGroup && !this.formGroup.valid) {
            return;
        }
      
        this.formGroup = this.createFormGroup(dataItem);
        this.editedRowIndex = rowIndex;
      
        sender.editRow(rowIndex, this.formGroup);
      }
      
      public cancelHandler(): void {
        this.closeEditor();
      }

      private closeEditor(): void {
        this.grid.closeRow(this.editedRowIndex);
      
        this.isNew = false;
        this.editedRowIndex = undefined;
        this.formGroup = undefined;
      }
      
      private saveCurrent(): void {
        if (this.isInEditingMode && this.formGroup) {
            this.save(this.formGroup.value, this.isNew);
            this.closeEditor();    
        }
      }
      
    public save(product: any, isNew: boolean): void {
        if (isNew) {
            product.DiscoveryID = this.ipList.length++;
            this.ipList.splice(0, 0, product);
        } else {
            Object.assign(this.ipList.find(p => p.DiscoveryID === product.DiscoveryID), product);
        }
    }

