            this.newNetworkDiscoveryForm.addControl('username', new FormControl('', Validators.required));
            this.newNetworkDiscoveryForm.addControl('password', new FormControl('', Validators.required));
            this.newNetworkDiscoveryForm.addControl('domain', new FormControl('', Validators.required));
            this.newNetworkDiscoveryForm.addControl('community', new FormControl('', Validators.required));
