    public keyPress(event: any) {
                const pattern = /[0-9\.\ ]/;
              let inputChar = String.fromCharCode(event.charCode);
        if (event.keyCode != 8 && !pattern.test(inputChar)) {
            event.preventDefault();
        }
    }

    
    
//On submiting the data from form
    public onNetworkSubmit(event: Event, dataform: any): void {

        this.submitted = true;
        if (this.newNetworkDiscoveryForm.invalid) {
            return;
        }
        else {
            event.stopPropagation();
            this.saveCurrent();
            let postData = {};
            let parameters = {};
            // this.ipList =this.ipList.forEach(function(v){ delete v.DiscoveryID });
            delete dataform["DiscoveryID"];
            parameters['IP_List'] = this.ipList;
            delete dataform["networkAddress"];
            parameters['Excution_mode'] = this.setDate ? 'Scheduled' : 'Immediate';
            if(this.setDate ){
            let datetime= new DatePipe('en-US').transform(dataform.controls.date.value,'MM-dd-yyyy')+' '+new DatePipe('en-US').transform(dataform.controls.time.value,'HH:mm:ss')
            parameters['Scheduled_Time'] = datetime;
                delete dataform["time"];
                delete dataform["date"];
            }
                parameters['Login_Mode'] = this.login ? 'Credentials' : 'Pemkey';
                parameters['Utilization'] = this.setUtili ? 'Yes' : 'No';
                if(this.setUtili ){
                parameters['Interval'] = dataform.controls.Interval.value;
                parameters['Period'] = dataform.controls.Period.value;
                }
                parameters['Login_Mode'] = this.login ? 'Credentials' : 'Pemkey';
            if (this.login) {
                parameters['Username'] = dataform.controls.username.value;
                parameters['Password'] = dataform.controls.password.value;
                parameters['Domain'] = dataform.controls.domain.value;
                parameters['Community'] = dataform.controls.community.value;
            }
            else {
                parameters['Pemkey'] = dataform.controls.pemfile.value;
            }
            ['username', 'password', 'domain','community', 'pemfile'].forEach(o => delete dataform[o]);
            parameters['Name'] = dataform.controls.name.value;
            delete dataform["name"];
            parameters['Created_By'] = this.created_by;
            delete dataform["this.created_by"];
            parameters["Account_ID"] = this.account_id;
            delete dataform['this.account_id'];
            //Taking the data which only true and posting that data to service
             this.newNetworkDiscoveryForm.controls.portConfigs.value.filter(config => config.isChecked);
            let filteredPortConfig = dataform.controls.portConfigs.value.filter(config => config.isChecked);
            let formattedPortConfig: Array<any> = [];
            filteredPortConfig.forEach((config) => {
                let newConfig: any = {};
                newConfig[config.name] = config.port;
                formattedPortConfig.push(newConfig);
            });
            parameters['User_port_input'] = formattedPortConfig;
            delete dataform["portConfigs"];

            //post data
            postData['request'] = parameters;
            console.log(postData['request'] );
            
            this._store.dispatch(new CreatingNetworkDisocvery(postData));
            this._activeModal.close();
            this.redirectdiscoveryList();
        }
    }
    //When Check mark changed from true to false on networkport table
    checkChanged(event,i){
        this.newNetworkDiscoveryForm.controls.portConfigs.value[i].isChecked=event.target.checked;
    }

    

   
    public regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
        return (control: AbstractControl): {
            [key: string]: any
        } => {
            if (!control.value) {
                return null;
            }
            const valid = regex.test(control.value);
            return valid ? null : error;
        };
    }
    
