export class Filter {
  Filter: Filte;
}

export interface Filte {
  And: And[];
}

export interface And {
  Or?: Or[];
  Not?: Not;
}

export interface Not {
  Dimensions: Dimensions;
}

export interface Or {
  Dimensions?: Dimensions;
  Tags?: Dimensions;
}

export interface Dimensions {
  Key: string;
  Values: string[];
}

export class KeyOrderPair {
  Key: any;
  Order: number;
  constructor(key, order) {
    this.Key = key;
    this.Order = order;
  }
}
export class FilterResult {
  Key: string;
  Values: any[];
  Exclude: boolean;
  constructor(key, values, exclude) {
    this.Key = key;
    this.Values = values;
    this.Exclude = exclude;
  }
}
export class AnalyticsResult {
  Key: string;
  Values: any[];
  
  constructor(key, values) {
    this.Key = key;
    this.Values = values;
    
  }
}
export class AnalyticsResult1 {
  
  Key: string;
  
  constructor(Key  ) {
    
    this.Key = Key;
    
  }
}


export class AnalyticsResult2 {
  
  Values: any[];
  
  constructor( values ) {
    
    this.Values = values;
    
  }
}
export class AdvancedFilter {
  GroupValues: GroupData[] = [];
  Application: any;
  Service: any;
  Project: any;
  Environment: any;
  //Top level
}
export class GroupData {
  Title: string; //Dimension
  Filter: GroupFilter[];
  constructor(title, filter) {
    this.Title = title;
    this.Filter = filter;
  }
}
export class GroupFilter {
  Title: string; //Az
  DropDownValues: any[]; // VAlues
  Exclude: boolean;
  SelectedValues: any[];
  constructor(title, values) {
    this.Title = title;
    this.DropDownValues = values;
  }
}

