import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { DropdownListService } from '../../../services/dropdown-list.service';
import { FormBuilder, FormGroup, Validators,  ValidationErrors, ValidatorFn, AbstractControl , FormsModule, ReactiveFormsModule,  FormControl} from '@angular/forms';

import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';

import { GetCloudProviders, GetAccounts, GetRegionslist, GetProjects, GetEnvironments } from '../../../actions/migration.action';
import { MigrationState } from '../../../state/migration.state';

@Component({
  selector: 'matilda-migration-strategy',
  templateUrl: './migration-strategy.component.html',
  styles: []
})
export class MigrationStrategyComponent implements OnInit {


  public isDisabledProducts: boolean = true;
  public isDisabledOrders: boolean = true;
  public isDisabledProjects: boolean = true;
  public isDisabledEnvironments: boolean = true;
  public container: boolean = true;
  public _targetConfigFrom: FormGroup;
  @Output('cloudProviderChange') cloudProviderChange = new EventEmitter();
  public selectedLink: string = 'fulldeployment';
  public defaultCloud: { cloudproviderName: string, cloudId: number } = { cloudproviderName: "Select Cloud", cloudId: null };
  public defaultaccount: { accountName: string, accountId: number } = { accountName: "Select Account", accountId: null };
  public defaultRegion: { regionName: string, regionId: number } = { regionName: "Select Region", regionId: null };
  public defaultProject: { projectName: string, projectId: number } = { projectName: "Select Project", projectId: null };
  public defaultEnvironment: { environmentName: string, environmentId: number } = { environmentName: "Select Environment", environmentId: null };


  public cloudProvider: Array<{ cloudproviderName: string, cloudId: number }>;
  public accounts: Array<{ accountName: string, accountId: number, cloudId: number }>;
  public regionslist: Array<{ regionName: string, regionId: number, accountId: number, }>;
  public Projects: Array<{ projectName: string, projectId: number, regionId: number }>
  public Environments: Array<{ environmentName: string, environmentId: number, projectId: number }>

  public dataResultProducts: Array<{ accountName: string, accountId: number, cloudId: number }>;
  public dataResultOrders: Array<{ regionName: string, regionId: number, accountId: number, }>;
  public dataResultProjects: Array<{ projectName: string, projectId: number, regionId: number, }>;
  public dataResultEnvironments: Array<{ environmentName: string, environmentId: number, projectId: number }>;

  public selectedCategory: { cloudproviderName: string, cloudId: number };
  public selectedProject: { accountName: string, accountId: number };
  public selectedProduct: { accountName: string, accountId: number };
  public selectedOrder: { regionName: string, regionId: number };
  public selectedEnvironment: { EnvironmentName: string, EnvironmentId: number };
  handleCategoryChange(value) {
    this.selectedCategory = value;
    this.cloudProviderChange.emit(value.cloudproviderName);
    this.selectedProduct = undefined;
    this.selectedOrder = undefined;
    this.selectedProject = undefined;
    this.selectedEnvironment = undefined;

    if (value.cloudId == this.defaultCloud.cloudId) {
      this.isDisabledProducts = true;
      this.dataResultProducts = [];
    } else {
      this.isDisabledProducts = false;
      this.dataResultProducts = this.accounts.filter((s) => s.cloudId === value.cloudId)
    }

    this.isDisabledOrders = true;
    this.dataResultOrders = [];
  }

  @Select(MigrationState.cloudProviders) cloudProviders$: Observable<any[]>;
  @Select(MigrationState.accounts) accounts$: Observable<any[]>;
  @Select(MigrationState.regionslist) regionslist$: Observable<any[]>;
  @Select(MigrationState.projects) projects$: Observable<any[]>;
  @Select(MigrationState.environments) environments$: Observable<any[]>;

  constructor(private dropdownService: DropdownListService, private _fb: FormBuilder, private _store: Store) { }


  changed(e) {
    if (e.target.checked) {
      this.container = false;
    }
    else {
      this.container = true;
    }
  }
  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.    

    this._store.dispatch(new GetCloudProviders());
    this.cloudProviders$.subscribe(data => {
        if(data){
          this.cloudProvider = data;             
        }            
    });

    this._store.dispatch(new GetAccounts());
    this.accounts$.subscribe(data => {
        if(data){
          this.accounts = data;                         
        }            
    });

    this._store.dispatch(new GetRegionslist());
    this.regionslist$.subscribe(data => {
        if(data){
          this.regionslist = data;                          
        }            
    });

    this._store.dispatch(new GetProjects());
    this.projects$.subscribe(data => {
        if(data){
          this.Projects = data;                         
        }            
    });

    this._store.dispatch(new GetEnvironments());
    this.environments$.subscribe(data => {
        if(data){
          this.Environments = data;                          
        }            
    });

  }
  handleOrderChange(value) {
    this.selectedOrder = value;
    this.selectedProject = undefined;
    this.selectedEnvironment = undefined;

    if (value.regionId == this.defaultRegion.regionId) {
      this.isDisabledProjects = true;
      this.dataResultProjects = [];
    }
    else {
      this.isDisabledProjects = false;
      this.dataResultProjects = this.Projects.filter((s) => s.regionId === value.regionId)
    }
  }
  handleProductChange(value) {
    this.selectedProduct = value;
    this.selectedOrder = undefined;
    this.selectedProject = undefined;
    this.selectedEnvironment = undefined;
    if (value.accountId == this.defaultaccount.accountId) {
      this.isDisabledOrders = true;
      this.dataResultOrders = [];
    } else {
      this.isDisabledOrders = false;
      this.dataResultOrders = this.regionslist.filter((s) => s.accountId === value.accountId)
    }
  }
  handleProjectChange(value) {
    this.selectedProject = value;
    this.selectedEnvironment = undefined;

    if (value.projectId == this.defaultProject.projectId) {
      this.isDisabledEnvironments = true;
      this.dataResultEnvironments = [];
    }
    else {
      this.isDisabledEnvironments = false;
      this.dataResultEnvironments = this.Environments.filter((s) => s.projectId === value.projectId);
    }
  }
  handleEnvironmentChange(value,form :FormGroup) {
    this.selectedEnvironment = value;
    console.log(form)
  }
  public initForm() {
    this._targetConfigFrom = this._fb.group({
      cloudprovider:['', Validators.nullValidator],
      account:['', Validators.nullValidator],
      region:['', Validators.nullValidator],
      project:['', Validators.nullValidator],
      environment:['', Validators.nullValidator],

    });
  }
  get cloudprovider() {
    return this._targetConfigFrom.get('cloudprovider');
  }
  get account() {
    return this._targetConfigFrom.get('account');
  }
  get region() {
    return this._targetConfigFrom.get('region');
  }
  get project() {
    return this._targetConfigFrom.get('project');
  }
  get environment(){
    return this._targetConfigFrom.get('environment');
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }
  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }
  public onServiceSubmit(event: Event, dataform: any): void {
    let postData = {};
    event.stopPropagation();
    if (this._targetConfigFrom.valid) {
   
      postData['request'] = dataform;
      // this.postRequest.postData(postData)
      //     .subscribe((data) => {
      //         console.log(data)
      //     });
    } else {
      this.validateAllFormFields(this._targetConfigFrom);
    }
  }
}