import {map} from 'rxjs/operators';
import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl, ValidationErrors, AbstractControl, ValidatorFn,  FormArray } from '@angular/forms';
import { NotificationsService } from '../../../../../../node_modules/angular2-notifications';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';
// import { CreateNewNetworkDiscovery } from '../../../actions/discovery.action';
import { DiscoveryState } from '../../../state/discovery.state';
import { NewNetworkDiscoveryService } from '../../../services/new-network-discovery.service';
import { CreatingNetworkDisocvery } from 'src/app/discovery/actions/discovery-creating.actions';
import { DiscoveryCreatingState } from 'src/app/discovery/state/discovery-creating.state';
@Component({
  selector: 'matilda-host-discovery',
  templateUrl: './host-discovery.component.html'
})
export class HostDiscoveryComponent implements OnInit {
  public newNetworkDiscoveryForm: FormGroup;
  submitted = false;
  public portObj = {
      snmp: '162',
      ssh: '22',
      telnet: '23'
  }
  public range = true;
  marked = true;
  ismoredetails :boolean = true;
  public type = true;
  public credential= false;
  public index: any[] = [];
  public serviceError:boolean = false;
  public applicationsCountMsg: boolean = false;

  @Select(DiscoveryCreatingState.discoveryCreatingNewNetwork) newNetworkDiscovery$: Observable<any[]>;

  constructor(private _router: Router,
      private _newNetworkDiscoveryService: NewNetworkDiscoveryService,
      private _fb: FormBuilder,
      private _notificationsService: NotificationsService,
      public _activeModal: NgbActiveModal,
      private _store: Store) {}

  ngOnInit() {
      for (let k = 1; k <= 24; k++) {
          this.index.push(k);
      }

      this.initForm();
       this.removeCidr();
      this.removeRange();
      this.removeSsh();
      this.removeSnmp();
      this.removeSubnetMask();
      this.removeTelnet();
      this.newNetworkDiscoveryForm.removeControl('app');


  }

  public setradio(e: any) {
      if (e === 'range') {
          this.type = true;
          this.addRange();
          this.removeCidr();
      } else {
          this.type = false;
          this.addCidr();
          this.removeRange();
      }
  }

  public setCredential(o:any ){
    if(o =='SSH'){
      this.credential=true;
       }
       if(o!='SSH'){
        this.credential=false;
       }


  }
  toggleVisibility(e){
      console.log('ok222');
    this.ismoredetails = e.target.checked;
    if(!this.ismoredetails){
    this.addRange();
    this.addSnmp();
    this.addSsh();
    this.addSubnetMask();
    this.addTelnet();
    this.removeDomain();
    this.newNetworkDiscoveryForm.removeControl('username');
    this.newNetworkDiscoveryForm.removeControl('password');
    this.newNetworkDiscoveryForm.removeControl('os');
    this.newNetworkDiscoveryForm.removeControl('iplist');

    }
    if(this.ismoredetails){
        console.log('nono');
      this.removeCidr();
      this.removeRange();
      this.removeSsh();
      this.removeSnmp();
      this.removeSubnetMask();
      this.removeTelnet();
      this.addDomain();

      this.newNetworkDiscoveryForm.addControl('username', new FormControl('', Validators.required));
      this.newNetworkDiscoveryForm.addControl('os', new FormControl('', Validators.required));
      this.newNetworkDiscoveryForm.addControl('password', new FormControl('', Validators.required));
      this.newNetworkDiscoveryForm.addControl('iplist', new FormControl('', Validators.required));
    }
  }
  public addRange() {
      this.newNetworkDiscoveryForm.addControl('netrange', new FormControl('', Validators.required));
      this.newNetworkDiscoveryForm.addControl('netrangeport', new FormControl('', Validators.required));
  }
  public removeRange() {
      this.newNetworkDiscoveryForm.removeControl('netrange');
      this.newNetworkDiscoveryForm.removeControl('netrangeport');
  }
  public addCidr() {
      this.newNetworkDiscoveryForm.addControl('netcidr', new FormControl('', Validators.required));
      this.newNetworkDiscoveryForm.addControl('netcidrport', new FormControl('', Validators.required));
  }
  public removeCidr() {
      this.newNetworkDiscoveryForm.removeControl('netcidr');
      this.newNetworkDiscoveryForm.removeControl('netcidrport');
  }
  public addSubnetMask() {
      this.newNetworkDiscoveryForm.addControl('subnetmask', new FormControl('', Validators.required));

  }
  public removeSubnetMask(){

    this.newNetworkDiscoveryForm.removeControl('subnetmask');

  }

  public addSsh() {
    this.newNetworkDiscoveryForm.addControl('ssh', new FormControl('', Validators.required));

}
  public removeSsh(){

    this.newNetworkDiscoveryForm.removeControl('ssh');

  }
  public addSnmp() {
    this.newNetworkDiscoveryForm.addControl('snmp', new FormControl('', Validators.required));

}
  public removeSnmp(){

    this.newNetworkDiscoveryForm.removeControl('snmp');

  }

  public addTelnet() {
    this.newNetworkDiscoveryForm.addControl('telnet', new FormControl('', Validators.required));

}
  public removeTelnet(){

    this.newNetworkDiscoveryForm.removeControl('telnet');

  }
public addDomain(){
  this.newNetworkDiscoveryForm.addControl('domains', new FormControl('', Validators.required));

}
  public removeDomain(){
this.newNetworkDiscoveryForm.removeControl('domains');

  }

  public redirectdiscoveryList() {
      this._notificationsService.info('Discovery is Initiated Scheduled...');
      setTimeout(() => {
          this._router.navigate(['/discovery/list']);
      }, 2000);
  }
  public redirectdiscoveryList2() {
    this._notificationsService.error('Invalid IP/Username/Password');
    setTimeout(() => {
        this._router.navigate(['/discovery/list']);
    }, 2000);
}
  public initForm() {
      this.newNetworkDiscoveryForm = this._fb.group({
          name: ['', Validators.required, ValidationNameNotTaken.createValidator(this._newNetworkDiscoveryService)
          ], // async Validator passed as 3rd parameter
          netrange: ['', [Validators.required, Validators.minLength(11), Validators.maxLength(14), this.regexValidator(new RegExp('^((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$'), { 'remainingpattern': 'IP Address format should be 192.168.0.0' })],],
          netrangeport: ['', [Validators.required, Validators.minLength(2), Validators.maxLength(3)]],
          subnetmask: ['', Validators.required],
          os:['',Validators.required],
          netcidr: ['', Validators.required],
          netcidrport: ['', Validators.required],
          snmp: ['',[ Validators.required,
              Validators.min(0),
              Validators.max(65535)]],
          ssh: ['', [ Validators.required,
              Validators.min(0),
              Validators.max(65535)]],
          telnet: ['', [ Validators.required,
              Validators.min(0),
              Validators.max(65535)]],
              username: ['', Validators.required],
              password: ['', Validators.required],
              iplist:['',Validators.required],
              //sshkey:['',Validators.required],
              ismoredetails:['true',],
              domains:[''],
          app: this._fb.array([this.initApps()])
      });

  }

  public initApps() {
      return this._fb.group({
          Key: ['', Validators.required],
          Value: ['', Validators.required]
      });
  }

  public getApps(form) {
      return form.controls.app.controls;
  }
  get discNetwork() {
      return this.newNetworkDiscoveryForm.controls;
      }
  public addApplication(event: Event) {
      const arrayControl = <FormArray>this.newNetworkDiscoveryForm.controls['app'];
      if(arrayControl.controls.length < 10){
          arrayControl.push(this.initApps());
      }

      if(arrayControl.controls.length == 10){
          this.applicationsCountMsg = true;
      }
  }
  public delInput(index: number): void {
      const arrayControl = <FormArray>this.newNetworkDiscoveryForm.controls['app'];
      arrayControl.removeAt(index);

      if(arrayControl.controls.length < 10){
          this.applicationsCountMsg = false;
      }
  }

  public keyPress(event: any) {
      const pattern = /[0-9\.\ ]/;
      // const pattern = /^(([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])$/;

      let inputChar = String.fromCharCode(event.charCode);
      if (event.keyCode != 8 && !pattern.test(inputChar)) {
          event.preventDefault();
      }
  }

  public onNetworkSubmit(event: Event, dataform: any): void {

      this.submitted = true;
  if (this.newNetworkDiscoveryForm.invalid) {
      const invalid = [];
    const controls = this.newNetworkDiscoveryForm.controls;
    for (const name in controls) {
        if (controls[name].invalid) {
            invalid.push(name);
        }
    }
    console.log(invalid);

    return;
}
     else{ event.stopPropagation();
      var postData = {};

      if(!this.ismoredetails){

      dataform['type'] = this.type ? 'range' : 'cidr';
      if (this.type) {
          dataform['ip_range'] = dataform.netrange;
          dataform['range'] = dataform.netrangeport;
      } else {
          dataform['ip_range'] = dataform.netcidr;
          dataform['range'] = dataform.netcidrport;
      }
      ['netrange', 'netrangeport', 'netcidr', 'netcidrport'].forEach(e => delete dataform[e]);

      dataform['port_config'] = {
          "snmp": dataform.snmp,
          "ssh": dataform.ssh,
          "telnet": dataform.telnet
      }


      delete dataform["snmp"];
      delete dataform["ssh"];
      delete dataform["telnet"];

      for (let i = 0; i < dataform.app.length; i++) {
          dataform['port_config'][dataform.app[i]['Key']] = dataform.app[i]['Value'];
      }

      delete dataform["app"];

    }
    if(this.ismoredetails){
         /* console.log('okokokokk');
var x=JSON.stringify(dataform.iplist);
var xy= JSON.parse("[" + x.split(",") + "]");
console.log(dataform.iplist.split(","));  */

      if(!this.credential){
        dataform['hostusername'] = dataform.username;
        dataform['hostpassword'] = dataform.password;
        dataform['mode'] = 'Server';
      }
      dataform['ismorediscovery'] = dataform.ismoredetails;
      delete dataform["ismoredetails"];

      delete dataform["username"];
      delete dataform["password"];

      dataform['host'] = dataform.iplist.split(",");
      delete dataform["iplist"];

      dataform['operatingsystem']=dataform.os;
      delete dataform["os"];

      dataform['domain']=dataform.domains;
      delete dataform["domains"];
      delete dataform["credential"];
      delete dataform["app"];

    }
      postData['request'] = dataform;
      console.log(postData);
      
      this._store.dispatch(new CreatingNetworkDisocvery(postData));
    //   this._store.dispatch(new CreateNewNetworkDiscovery(postData));
      this.newNetworkDiscovery$.subscribe(res => {
          if(res){
              if(res["isCreated"] == true){
                  this._activeModal.close();
              } else {
                //this._activeModal.close();
               // this.redirectdiscoveryList2();
                  this.serviceError = true;
              }
          }
      });
  }
}

  public isNameUnique(control: AbstractControl) {
      return this._newNetworkDiscoveryService.checkNameUnique(control.value).pipe(map(res => {
          return res ? null : {
              nameToken: true
          };
      }));
  }
  public regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
      return (control: AbstractControl): {
          [key: string]: any
      } => {
          if (!control.value) {
              return null;
          }
          const valid = regex.test(control.value);
          return valid ? null : error;
      };
  }
  public validateAllFormFields(formGroup: FormGroup) {
      Object.keys(formGroup.controls).forEach(field => {
          const control = formGroup.get(field);
          if (control instanceof FormControl) {
              control.markAsTouched({
                  onlySelf: true
              });
          } else if (control instanceof FormGroup) {
              this.validateAllFormFields(control);
          }
      });
  }

}

export class ValidationNameNotTaken {
  static createValidator(namecheckservice: NewNetworkDiscoveryService) {
      return (control: AbstractControl) => {
          return namecheckservice.checkNameUnique(control.value).pipe(map(res => {
              return res ? null : {
                  nameTaken: true
              };
          }));
      };
  }
}
