
import {throwError as observableThrowError,  BehaviorSubject } from 'rxjs';

import {catchError, map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import * as _ from 'lodash';
import { Observable } from 'rxjs/internal/Observable';
import settings from '../../settings';
@Injectable()
export class ServiceInformationService {
  headers: Headers;
  private dataStore: any;
  private _workflowData = new BehaviorSubject<any>(null);
  get mapworkflow(): Observable<any> {
    return this._workflowData.asObservable();
  }
  constructor(private http: Http) {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    this.dataStore = {
      wrkflwData: null
    };
  }
  public getInformation(service: any) {
    return this.http.get(_.template(settings.SERVICE_INFO_URL)({ service })).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getServices1Service() {
    return this.http.get(_.template(settings.SERVICES_URL1)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getServices2Service() {
    return this.http.get(_.template(settings.SERVICES_URL2)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getServices3Service() {
    return this.http.get(_.template(settings.SERVICES_URL3)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getProcess1Service() {
    return this.http.get(_.template(settings.PROCESS_URL1)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getProcess2Service() {
    return this.http.get(_.template(settings.PROCESS_URL2)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getProcess3Service() {
    return this.http.get(_.template(settings.PROCESS_URL3)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getUsers1Service() {
    return this.http.get(_.template(settings.USERS_URL1)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getUsers2Service() {
    return this.http.get(_.template(settings.USERS_URL2)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getUsers3Service() {
    return this.http.get(_.template(settings.USERS_URL3)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getServersService() {
    return this.http.get(_.template(settings.SERVERS_URL)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getApplicationsService() {
    return this.http.get(_.template(settings.APPLICATIONS_URL)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getDatasourcesService() {
    return this.http.get(_.template(settings.DATASOURCES_URL)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  public getClustersService() {
    return this.http.get(_.template(settings.CLUSTERS_URL)({})).pipe(
      map(this.extractJSONResponse),
      catchError(this.handleError),)
  }
  // public getDiscoveryServiceService() {
  //   return this.http.get(_.template(settings.DISCOVERY_SERVICE_URL)({}))
  //     .map(this.extractJSONResponse)
  //     .catch(this.handleError)
  // }
  private extractJSONResponse(res: Response) {
    const body = res.json();
    return body;
  }
  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return observableThrowError(errMsg);
  }
}
