import { Injectable } from '@angular/core';
import { ConfigService } from '../app-config.service';

@Injectable({
    providedIn: 'root'
  })
export  class CostManagementSettings  {
    private _baseUrl: string;
    constructor(private _configService: ConfigService) {

    }
    public getURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').BASE_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }

    public getSecondaryURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').SECONDARY_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }

    public getThirdURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').THIRD_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }

    public getFourthURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').FOURTH_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }

    public getFifthURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').FIFTH_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }
    public getSixthURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').SIXTH_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }

    public getRelativeURL(operation: any) {
       // this._baseUrl=this._configService.get("cost_services").SECONDARY_URL;
        return this._configService.get('cost_services')[operation] ;
    }

    public getLocalURL(operation: any) {
        this._baseUrl = this._configService.get('cost_services').LOCAL_URL;
        return this._baseUrl + this._configService.get('cost_services')[operation] ;
    }
}
