  handleCategoryChange(value) {
    this.budgetFilter.BudgetName = value;

    if (this.budgetFilter.BudgetName == "AWS_Budget_Quarterly") {
      this.cost = this.BudgetListpromo;
      this.cost.forEach(item => {
        var BudgetLimitQuatAmount: any[] = [];
        var BudgetQuaterlyAmount: any[] = [];

        BudgetLimitQuatAmount.push(item.BudgetLimit);
        this.BudgetCostsQuartAmount = BudgetLimitQuatAmount;

        BudgetQuaterlyAmount.push(item.CalculatedSpend);
        this.BudgetQuatAmt = item.CalculatedSpend;
        this.ActualSpend = this.BudgetQuatAmt.ActualSpend.Amount;
        // this.ActualSpend = this.BudgetQuatAmt.ActualSpend.Amount;
      });
      this.BudgetCostsQuartAmount.forEach(item => {
        var Amount: any[] = [];
        this.wholeQuaterlyCost = item.Amount;
      });

      this.remainingBudget = this.wholeQuaterlyCost - this.ActualSpend;
    }


    else if (this.budgetFilter.BudgetName == "AWS_Budget_Annually") {
      this.cost = this.BudgetListAnnual;
      this.cost.forEach(item => {
        var BudgetLimitAmount: any[] = [];
        var BudgetAnnualAmount: any[] = [];

        BudgetLimitAmount.push(item.BudgetLimit);
        this.BudgetCostsAmount = BudgetLimitAmount;

        BudgetAnnualAmount.push(item.CalculatedSpend);
        this.BudgetAnnualAmt = item.CalculatedSpend;
        this.ActualSpend = this.BudgetAnnualAmt.ActualSpend.Amount;
      });
      this.BudgetCostsAmount.forEach(item => {
        var Amount: any[] = [];
        this.wholeQuaterlyCost = item.Amount;
      });

      this.remainingBudget = this.wholeQuaterlyCost - this.ActualSpend;
    }

    else if (this.budgetFilter.BudgetName == "AWS_Budget_Quarterly_2018") {
      this.cost = this.BudgetListQuart;
      this.cost.forEach(item => {
        var BudgetLimitQtAmount: any[] = [];
        var BudgetQtAmount: any[] = [];

        BudgetLimitQtAmount.push(item.BudgetLimit);
        this.BudgetCostsQtAmount = BudgetLimitQtAmount;

        BudgetQtAmount.push(item.CalculatedSpend);
        this.BudgetQtAmt = item.CalculatedSpend;
        this.ActualSpend = this.BudgetQtAmt.ActualSpend.Amount;
      });
      this.BudgetCostsQtAmount.forEach(item => {
        var Amount: any[] = [];
        this.wholeQuaterlyCost = item.Amount;
      });

      this.remainingBudget = this.wholeQuaterlyCost - this.ActualSpend;
    }
  }

