  //Creating a New Alert
  public onClick(event: any): void {
    if (this.configureFilter) {
    }

    var alertlist: Alert[] = [];

    if (this.configureFilter.Notification.length != 0)
      alertlist.push(
        new Alert(
          this.configureFilter.Notification,
          this.configureFilter.Comparison,
          this.Thresholddata,
          this.configureFilter.Thresholdtype,
          this.Email_Add
        )
      );

    if (this.configureFilter.Comparison.length != 0)
      alertlist.push(
        new Alert(
          this.configureFilter.Notification,
          this.configureFilter.Comparison,
          this.Thresholddata,
          this.configureFilter.Thresholdtype,
          this.Email_Add
        )
      );

    if (this.Thresholddata != 0)
      alertlist.push(
        new Alert(
          this.configureFilter.Notification,
          this.configureFilter.Comparison,
          this.Thresholddata,
          this.configureFilter.Thresholdtype,
          this.Email_Add
        )
      );

    if (this.Email_Add.length != 0)
      alertlist.push(
        new Alert(
          this.configureFilter.Notification,
          this.configureFilter.Comparison,
          this.Thresholddata,
          this.configureFilter.Thresholdtype,
          this.Email_Add
        )
      );

    if ((this.Thresholddata != this.Old_Threshold) || (this.configureFilter.Thresholdtype != this.Old_Threshold_Type)
      || (this.configureFilter.Notification = this.Old_Notificaton) || (this.configureFilter.Comparison != this.Old_Comparison)) {
      let thresholdalert = {
        account_id: "046692759124",
        budget_name: this.budgetFilter.BudgetName,
        old_notification: {
          NotificationType: this.Old_Notificaton,
          ComparisonOperator: this.Old_Comparison,
          Threshold: Number(this.Old_Threshold),
          ThresholdType: this.Old_Threshold_Type,
        },
        new_notification: {
          NotificationType: this.configureFilter.Notification,
          ComparisonOperator: this.configureFilter.Comparison,
          Threshold: Number(this.Thresholddata),
          ThresholdType: this.configureFilter.Thresholdtype,
        }
      };

      this.metrics.applyThresholdAlter(thresholdalert).subscribe(data => {
        console.log("in metric service call");

        if (data != null) {
          this._notifications.success("Done", "");
        }
      });
    }

    else if (this.Email_Add != this.Updated_Email) {
      let emailalert = {
        account_id: "046692759124",
        budget_name: this.budgetFilter.BudgetName,
        notification: {
          NotificationType: this.configureFilter.Notification,
          ComparisonOperator: this.configureFilter.Comparison,
          Threshold: Number(this.Thresholddata)
        },
        old_subscriber:
        {
          SubscriptionType: "EMAIL",
          Address: this.Updated_Email
        }
        ,
        new_subscriber:
        {
          SubscriptionType: "EMAIL",
          Address: this.Email_Add
        }

      };

      this.metrics.applyEmailalertAlter(emailalert).subscribe(data => {
        console.log("in metric service call");

        if (data != null) {
          this._notifications.success("Done", "");
        }
      });
    }

    else if (this.Email_Add == "null") {
      let emailalert = {
        account_id: "046692759124",
        budget_name: this.budgetFilter.BudgetName,
        notification: {
          NotificationType: this.configureFilter.Notification,
          ComparisonOperator: this.configureFilter.Comparison,
          Threshold: Number(this.Thresholddata)
        },
        subscriber: {
          SubscriptionType: "EMAIL",
          Address: this.Email_Add
        }
      };

      this.metrics.applyDeleteAlter(emailalert).subscribe(data => {
        console.log("in metric service call");

        if (data != null) {
          this._notifications.success("Done", "");
        }
      });
    }


    let totalalert = {
      account_id: this.Account_ID,
      budget_name: this.budgetFilter.BudgetName,
      notification: {
        NotificationType: this.configureFilter.Notification,
        ComparisonOperator: this.configureFilter.Comparison,
        Threshold: Number(this.Thresholddata)
      },
      subscribers: [
        {
          SubscriptionType: "EMAIL",
          Address: this.Email
        }
      ]
    };

    this.metrics.applyAlter(totalalert).subscribe(data => {
      console.log("in metric service call");

      if (data != null) {
        this._notifications.success("Done", "");
      }
    });

    this.closebudgetWindow();


  }

