  //Delete current notification
  public removeHandler({ dataItem }) {
    this.editService.remove(dataItem);

    this.editDataItem = dataItem;
    this.budgetFilter.BudgetName = this.editDataItem.Budget_Name;
    this.configureFilter.Notification = this.editDataItem.NotificationType;
    this.configureFilter.Comparison = this.editDataItem.ComparisonOperator;
    this.Thresholddata = this.editDataItem.Threshold;

    // this.Old_delete_Email = this.editDataItem.Subscription_Type;
    this.Email = this.Old_delete_Email.toString();


    if (this.editDataItem.ThresholdType == "")
      this.configureFilter.Thresholdtype = 'PERCENTAGE';
    else
      this.configureFilter.Thresholdtype = 'ABSOLUTE_VALUE';

    let deletealert = {
      account_id: "046692759124",
      budget_name: this.budgetFilter.BudgetName,
      notification: {
        NotificationType: this.configureFilter.Notification,
        ComparisonOperator: this.configureFilter.Comparison,
        Threshold: Number(this.Thresholddata),
        ThresholdType: this.configureFilter.Thresholdtype
      }
    };

    this.metrics.applyDeleteAlter(deletealert).subscribe(data => {
      console.log("in metric service call");

      if (data != null) {
        this._notifications.success("Done", "");
      }
    });
  }

  public cancelHandler() {
    this.editDataItem = undefined;
  }

  public saveHandler(notifications: Notifications) {
    this.editService.save(notifications, this.isNew);

    this.editDataItem = undefined;
  }

