import { JsonProperty } from "json-typescript-mapper";
export class Action{
   @JsonProperty('action_id')
   actionId: string = '';
   name: string = '';
   description : string = '';
   @JsonProperty('service_id')
   serviceId: string = '';
}

export class Service{
  @JsonProperty('service_id')
  serviceId: string = '';
  name: string = '';
  category: string = '';
  comments: string = '';
  @JsonProperty({clazz: Action})
  actions: Action[] = [];
  
}

export class WorkflowAvailableServices {
  name: string = '';
  @JsonProperty({clazz: Service})
  services: Service[] = []
}
