export class SerivceModal {
    public name: string;
    public host: string;
    public hostusername: string;
    public hostpassword: string;
    constructor(service: any){
        this.name = service.name || '';
        this.host = service.host || '192.168.0.0';
        this.hostusername = service.hostusername || '';
        this.hostpassword = service.hostpassword || '';
    }
}