import { Component, OnInit } from '@angular/core';
import { Store, Actions } from '@ngxs/store';
import { Router } from '@angular/router';
import * as echarts from 'echarts';

@Component({
  selector: 'matilda-discovery-chart',
  templateUrl: './discovery-chart.component.html',
  styleUrls: ['./discovery-chart.component.scss']
})
export class DiscoveryChartComponent implements OnInit {
  public stacklist$:any;
  public stackdata:any;
  public options:any=[];
  public Network_discovery: any[] = [];
  public Service_discovery: any[] = [];
  // public MMWStatisticsDeployedSuccess: any[] = [];
  // public MMWStatisticsDeployedIssues: any[] = [];
  // public MMWStatisticsCancelled: any[] = [];
  public xAxisValues: any[] = [];
  public networkValues = [];
  public serviceValues = [];
  public successValues = [];
  public issueValues = [];
  public cancelledValues = [];
 
 
  
    
  
  constructor(private _store: Store, private router: Router, private actions$: Actions) { }

  ngOnInit() {
    
    
    this.stacklist$ = this._store.select(state => state.discoverylist.discoverystacklist);
    this.stacklist$.subscribe((x) => {
      this.stackdata= x;
      

      if(this.stackdata){
        this.renderChart(this.stackdata);
      }
    });
  }
    renderChart(stackdata:any){
      this.Network_discovery = [];
    this.Service_discovery = [];
    // this.MMWStatisticsDeployedSuccess = [];
    // this.MMWStatisticsDeployedIssues = [];
    // this.MMWStatisticsCancelled = [];
    this.xAxisValues = [];
    this.networkValues = [];
    this.serviceValues = [];
    // this.successValues = [];
    // this.issueValues = [];
    // this.cancelledValues = [];
      this.Network_discovery.push(stackdata.discovery_metrics.Network_discovery);
      this.Service_discovery.push(stackdata.discovery_metrics.Service_discovery);
      // this.MMWStatisticsDeployedSuccess.push(stackdata.discovery_metrics.MMW_Statistics_Deployed_Success);
      // this.MMWStatisticsDeployedIssues.push(stackdata.discovery_metrics.MMW_Statistics_Deployed_Issues);
      // this.MMWStatisticsCancelled.push(stackdata.discovery_metrics.MMW_Statistics_Cancelled);
  
      for (let i = 0; i < this.Network_discovery.length; i++) {
        this.xAxisValues.push(Object.keys(this.Network_discovery[i]));
        this.networkValues.push(Object.values(this.Network_discovery[i]));
      }
      for (let j = 0; j < this.Service_discovery.length; j++) {
        this.serviceValues .push(Object.values(this.Service_discovery[j]));
      }
      // for (let k = 0; k < this.MMWStatisticsDeployedSuccess.length; k++) {
      //   this.successValues.push(Object.values(this.MMWStatisticsDeployedSuccess[k]));
      // }
      // for (let l = 0; l < this.MMWStatisticsDeployedIssues.length; l++) {
      //   this.issueValues.push(Object.values(this.MMWStatisticsDeployedIssues[l]));
     // }
      // for (let m = 0; m < this.MMWStatisticsCancelled.length; m++) {
      //   this.cancelledValues.push(Object.values(this.MMWStatisticsCancelled[m]));
      // }
     
      this.options = {
        tooltip: {},
        legend: {
          
        },
        calculable: true,
        xAxis: [
          {
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            
            axisLabel: {
              rotate: 45
            },
            silent: false,
            splitLine: {
              show: false
            },

            type: 'category',

            data: this.xAxisValues[0]
          }
        ],
        yAxis: {

          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },

        },
        series: [

          {
            name: 'Network Discovery',
            type: 'bar',
            barGap: 0,
            label: 'labelOption',
            data: this.networkValues[0],
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0, 0, 0, 1,
                  [
                    { offset: 0, color: '#34bfa3' },
                    { offset: 0.5, color: '#34bfa3' },
                    { offset: 1, color: '#34bfa3' }
                  ]
                )
              }
            },
          },
          {
            name: 'Service Discovery',
            type: 'bar',
            label: 'labelOption',
            data: this.serviceValues[0],
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0, 0, 0, 1,
                  [
                    { offset: 0, color: '#ffb822' },
                    { offset: 0.5, color: '#ffb822' },

                    { offset: 1, color: '#ffb822' }
                  ]
                )
              }
            },
          }
          // {
          //   name: 'Successful Changes',
          //   type: 'bar',
          //   label: 'labelOption',
          //   data: this.successValues[0],
          //   itemStyle: {
          //     normal: {
          //       color: new echarts.graphic.LinearGradient(
          //         0, 0, 0, 1,
          //         [
          //           { offset: 0, color: '#f4516c' },
          //           { offset: 0.5, color: '#f4516c' },
          //           { offset: 1, color: '#f4516c' }
          //         ]
          //       )
          //     }
          //   },
          // },
          // {
          //   name: 'Changes with Issues',
          //   type: 'bar',
          //   label: 'labelOption',
          //   data: this.issueValues[0],
          //   itemStyle: {
          //     normal: {
          //       color: new echarts.graphic.LinearGradient(
          //         0, 0, 0, 1,
          //         [
          //           { offset: 0, color: '#36a3f7' },
          //           { offset: 0.5, color: '#36a3f7' },
          //           { offset: 1, color: '#36a3f7' }
          //         ]
          //       )
          //     }
          //   },
          // },
          // {
          //   name: 'Changes Rolled Back',
          //   type: 'bar',
          //   label: 'labelOption',
          //   data: this.cancelledValues[0],
          //   itemStyle: {
          //     normal: {
          //       color: new echarts.graphic.LinearGradient(
          //         0, 0, 0, 1,
          //         [
          //           { offset: 0, color: '#87bdd8' },
          //           { offset: 0.5, color: '#87bdd8' },
          //           { offset: 1, color: '#87bdd8' }
          //         ]
          //       )
          //     }
          //   },
          // }
        ],

        animationEasing: 'elasticOut',
        animationDelayUpdate: function (idx) {
          return idx * 8;
        }
      }
    }
  }
                  

