  private extractJSONResponse(res: Response) {
    const body = res.json();
    return body;
  }

  public extractChartdate(res: Response): any {
    const body = res.json();
    const groups: any[] = [];
    const costvalue: any = {};
    const values: any[] = [];
    _.each(body.response.bar_stack_data, (item: any, index) => {
      groups.push(item.category);
      _.each(item.values, (val: any) => {
        if (!costvalue[val.key]) {
          costvalue[val.key] = [];
        }
        costvalue[val.key].push(val.value);
      });
    });
    _.each(_.keys(costvalue), (key: any, ind) => {
      ind = {
        name: key,
        data: _.values(costvalue[key])
      };

      values.push(ind);
    });

    console.log('inside service', values);
    return { values, categories: groups };
  }

  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    return observableThrowError(errMsg);
  }
