import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { State, process } from '@progress/kendo-data-query';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';
import * as _ from 'lodash';
import { TooltipDirective } from '@progress/kendo-angular-tooltip';
@Component({
  selector: 'matilda-host-users',
  templateUrl: './host-users.component.html',
  styleUrls: ['./host-users.component.scss']
})
export class HostUsersComponent implements OnInit {
  //pagination
  public pageSize = 10;
  public package:any;
  public skip = 0;
  public gridState: State = {
    sort: [],
    skip: 0,
    take: 10,
  };
  //tooltip
  @ViewChild(TooltipDirective) public tooltipDir: TooltipDirective;
  public showTooltip(e: MouseEvent): void {
    const element = e.target as HTMLElement;
    if ((element.nodeName === 'TD' || element.nodeName === 'TH')
      && element.offsetWidth < element.scrollWidth) {
      this.tooltipDir.toggle(element);
    } else {
      this.tooltipDir.hide();
    }
  }
  public userinfo$:any;
  public usersinfo:any;
  public metaDataUsers: any[] = [];
  public allData(): ExcelExportData {
    const result: ExcelExportData = {
      data: process(this.usersinfo, { filter: this.gridState.filter }).data,
    };
    return result;
  }
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
}
public onPageChange(state: any): void {
    this.pageSize = state.take;
}

  constructor( private _store: Store,private _http: HttpClient) {
    this.allData = this.allData.bind(this);
   }

  ngOnInit() {
    this.userinfo$= this._store.select(state=>state.discoveryResultDetails.hostUsersGroups);
    this.userinfo$.subscribe(element=>{
      this.usersinfo= element
          });


          this.userinfo$.subscribe((element: any[]) => {
            this.usersinfo = element;
            _.each(this.usersinfo, (key: any, index) => {
              let value = _.keys(key)
              _.each(value, (keys) => {
                if (this.metaDataUsers.length == 0) {
                  index = {
                    field: _.trim(keys),
                    title: _.upperFirst(keys),
                    width: 40,
                    locked: false,
                    filterable: true
                  }
                  this.metaDataUsers.push(index)
                  this.metaDataUsers = _.uniqBy(this.metaDataUsers, "field")
                }
                else if (this.metaDataUsers[index] != keys) {
                  index = {
                    field: _.trim(keys),
                    title: _.upperFirst(keys),
                    width: 40,
                    locked: false,
                    filterable: true
      
                  }
                  this.metaDataUsers.push(index)
                  this.metaDataUsers = _.uniqBy(this.metaDataUsers, "field")
                }
              });
            });
          });


  }

}
