import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';

@Component({
  selector: 'matilda-thirdline1',
  templateUrl: './thirdline1.component.html',
  styleUrls: ['./thirdline1.component.scss']
})
export class Thirdline1Component implements OnInit {
  @Input() public networkservers1;
  public view: Observable<GridDataResult>;
  public skip = 0;

  constructor() { }
  
  public ngOnInit(): void {
    this.view = this.networkservers1["jmresources"];
}

public pageChange({ skip, take }: PageChangeEvent): void {
    this.skip = skip;
}

}
