import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ServiceClientService {

  private baseURL: string;
  constructor(private _httpclient: HttpClient){

  }

  get(url: string){
      return this._httpclient
        .get(url);
    }
}
