import { Component, OnInit, Input } from '@angular/core';
import { FormBase } from 'src/app/shared/dynamic_forms/form-base';
import { Task } from 'src/app/release-management/models/release.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'matilda-view-release-modal-dialog',
  templateUrl: './view-release-modal-dialog.component.html',
  styleUrls: ['./view-release-modal-dialog.component.scss']
})
export class ViewReleaseModalDialogComponent implements OnInit {
 
  @Input('dataSource') dataSource: FormBase<any>[] = [];
  @Input('task') task: Task;
  @Input('stageId') stageId: string;
  @Input('previousConfig') previousConfig: any;

  form: FormGroup;
  constructor(public activeModal: NgbActiveModal) { }

  ngOnInit() {
    if (this.dataSource.length > 0) {
      this.form = this.toFormGroup(this.dataSource);
    } else {
      this.form = this.toFormGroup();
    }
    
    this.form.disable();

    if (this.previousConfig) {
      for (let key of Object.keys(this.previousConfig)) {
        if(typeof this.previousConfig[key] === 'string' || this.previousConfig[key] instanceof String){
          this.form.controls[key].setValue(this.previousConfig[key]);
        }else{
          this.form.controls[key].setValue(JSON.stringify(this.previousConfig[key]));
        }
        
      }
    }
   
  }

  toFormGroup(data?: FormBase<any>[]) {
    let group: any = {};

    data.forEach(field => {
      group[field.key] = field.required ? new FormControl(field.value || '', Validators.required)
        : new FormControl(field.value || '');

    });
    return new FormGroup(group);
  }

}
