import { State, Store, Selector, Action, StateContext } from "@ngxs/store";
import { HttpClient } from "@angular/common/http";
import { NotificationsService } from "angular2-notifications";
import * as _ from 'lodash';
import { deserialize } from "json-typescript-mapper";
import { DiscoverySettings } from "../discovery-settings";
import { Navigate } from "@ngxs/router-plugin";
import { FetchDiscoveryNetworkPortList, CreatingNetworkDisocvery, FetchDiscoveryNetWorkList, CreatingServiceDisocvery, FetchDiscoveryServiceList, FetchDiscoveryServiceSummary, FetchDiscoveryDataCenterList, FetchDiscoveryServiceFailedReason } from "../actions/discovery-creating.actions";
import { DatePipe } from "@angular/common";

export class DiscoverCreatingModel {
    discoveryNetworkPortlist:any[];
    discoveryDataCenterList:any;
    discoveryCreatingNewNetwork:any;
    discoveryCreatingNewService:any;
    discoveryNetworkList:any[];
    discoveryServiceList:any[];
    discoveryNetworkListSummary:any[];
    discoveryServiceListSummary:any[];
    discoveryServiceListFailedReason:any[];
    
}
@State<DiscoverCreatingModel>({
    name: 'discoveryCreatingDetails',
    defaults: {
        discoveryNetworkPortlist:null,
        discoveryDataCenterList:null,
        discoveryCreatingNewNetwork:null,
        discoveryCreatingNewService:null,
        discoveryNetworkList:null,
        discoveryServiceList:null,
        discoveryNetworkListSummary:null,
        discoveryServiceListFailedReason:null,
        discoveryServiceListSummary:null

    }
})

export class DiscoveryCreatingState {
    // static discoveryCreatingNewNetwork(discoveryCreatingNewNetwork: any) {
    //     throw new Error("Method not implemented.");
    // }
    constructor(private _store: Store, 
        private _http: HttpClient, 
        private _notificationsService: NotificationsService, 
        private _discoverySettings: DiscoverySettings) {
    }
  
   
    @Selector()
    static discoveryNetworkPortlist(state: DiscoverCreatingModel) {
      return state.discoveryNetworkPortlist;
    }
    @Selector()
    static discoveryDataCenterList(state: DiscoverCreatingModel) {
      return state.discoveryDataCenterList;
    }
    @Selector()
    static discoveryCreatingNewNetwork(state: DiscoverCreatingModel) {
      return state.discoveryCreatingNewNetwork;
    }
    @Selector()
    static discoveryCreatingNewService(state: DiscoverCreatingModel) {
      return state.discoveryCreatingNewService;
    }
    @Selector()
    static discoveryNetworkList(state: DiscoverCreatingModel) {
      return state.discoveryNetworkList;
    }
    @Selector()
    static discoveryServiceList(state: DiscoverCreatingModel) {
      return state.discoveryServiceList;
    }
    @Selector()
    static discoveryServiceListSummary(state: DiscoverCreatingModel) {
      return state.discoveryServiceListSummary;
    }
    @Selector()
    static discoveryServiceListFailedReason(state: DiscoverCreatingModel) {
      return state.discoveryServiceListFailedReason;
    }
   
   
    @Action(FetchDiscoveryNetworkPortList)
    fetchDiscoveryNetworkPortList(context: StateContext<DiscoverCreatingModel>,action:FetchDiscoveryNetworkPortList) {
        var state = context.getState();
        //this._http.get('../../../assets/mockdata/Discovery/Networkportlist.json')
        return this._http.get(this._discoverySettings.getNetworkURL('NETWORK_PORT_URL')).subscribe(
          (data: any[]) => {
            if (data.length > 0) {
              state.discoveryNetworkPortlist = data;
              context.patchState(state);
            }
          }
        );
   
     
  }

  @Action(FetchDiscoveryDataCenterList)
  fetchDiscoveryDataCenterList(context: StateContext<DiscoverCreatingModel>,action:FetchDiscoveryDataCenterList) {
        var state = context.getState();
       
        // return this._http.get(this._discoverySettings.getNetworkURL('NETWORK_PORT_URL'))
        this._http.get('../../../assets/mockdata/Discovery/NewNetwork.json').subscribe(
          (data) => {
            
            if (data) {
              state.discoveryDataCenterList = data;
              context.patchState(state);
            }
          }
        );
   
     
  }

  @Action(CreatingNetworkDisocvery)
  creatingNetworkDisocvery(context: StateContext<DiscoverCreatingModel>,{ payload }: any) {
    var state = context.getState();
    var acc = payload.request.Account_ID;
    
    console.log( JSON.stringify(payload))
      return this._http.post(this._discoverySettings.getNetworkURL('DISCOVERY_NEW_NETWORK_URL'),payload).subscribe(
        (data: any) => {
          
          if(data){
            this._notificationsService.remove();
            this._notificationsService.info('','Discovery is Initiated ...');
            state.discoveryCreatingNewNetwork = { "isCreated": true };
            context.patchState(state);
            context.dispatch(new FetchDiscoveryNetWorkList({}));
          }
          
        },
        (error)=>{
          this._notificationsService.remove();
            this._notificationsService.error('','Something went wrong');
            state.discoveryCreatingNewNetwork = { "isCreated": false };
        }
      );
   
     
  }
  @Action(CreatingServiceDisocvery)
  creatingServiceDisocvery(context: StateContext<DiscoverCreatingModel>,{ payload }: any) {
    var state = context.getState();
    return this._http.post(this._discoverySettings.getServiceURL('NEW_SERVICE_URL'),payload)
       // return this._http.post('http://192.168.20.15:8080/matilda/servicediscovery', payload)
      .subscribe(
        (data: any) => {
          if(data){
            this._notificationsService.remove();
            this._notificationsService.info('','Discovery is Initiated ...');
            state.discoveryCreatingNewService = { "isCreated": true };
            context.patchState(state);
            context.dispatch(new FetchDiscoveryServiceList({}));
            context.dispatch(new FetchDiscoveryServiceSummary());
         
          }
          
        },
        (error)=>{
          this._notificationsService.remove();
            this._notificationsService.error('','Something went wrong');
            state.discoveryCreatingNewService = { "isCreated": false };
        }
        
      );
   
     
  }

  @Action(FetchDiscoveryNetWorkList)
  fetcDiscoveryhNetworkList(context: StateContext<DiscoverCreatingModel>, { payload }: any) {
    const state = context.getState();
      let acc_id = payload.acc;
      return this._http.get(this._discoverySettings.getNetworkURL('NETWORK_LIST_URL')).subscribe((data: any[]) => {
        if (data) {
           state.discoveryNetworkList = [];
          state. discoveryNetworkListSummary =data["summary"];
       let pipe = new DatePipe('en-US');
          data["response"].forEach((networkListItem) => {
           
            state.discoveryNetworkList.push(networkListItem);
                  });
          context.setState(state);
        }

      });
  }
  @Action(FetchDiscoveryServiceList)
  fetcDiscoveryServiceList(context: StateContext<DiscoverCreatingModel>, { payload }: any) {
    const state = context.getState();
    return this._http.get(this._discoverySettings.getServiceURL('SERVICE_LIST_URL'))
   
      .subscribe((data: any[]) => {
        if (data) {
          state.discoveryServiceList = [];

          data.forEach((serviceListItem) => {
            state.discoveryServiceList.push(serviceListItem);
          });
          context.setState(state);
        }

      });
  }
  @Action(FetchDiscoveryServiceSummary)
  fetchDiscoveryServiceSummary(context: StateContext<DiscoverCreatingModel>, { payload }: any) {
    const state = context.getState();
    return this._http.get(this._discoverySettings.getServiceURL('SERVICE_SUMMARY_LIST_URL'))
   
      .subscribe((data: any[]) => {
        if (data) {
          state.discoveryServiceListSummary=data ;
           context.setState(state);
        }

      });
  }
    


  //Failed Reason
 
  @Action(FetchDiscoveryServiceFailedReason)
  fetchDiscoveryServiceFailedReason(context: StateContext<DiscoverCreatingModel>, { payload }: any) {
    var state=context.getState();
    var serviceDiscoveryID = payload.discoveryId;
    let url=this._discoverySettings.getServiceURL('SERVICE_DISCOVERY_SUMMARY_URL'); 
    return this._http.get(_.template(url)({serviceDiscoveryID}))
    .subscribe((data: any[]) => {
        if (data) {
          state.discoveryServiceListFailedReason= data;
         context.setState(state);
         
        }
      }
      );
  }
  

  





  
}