  public type: string;
  public list: any[] = [];
  public _load: boolean= true;
  public cloud: string;
  public hostip: string;
  // public migrationData: EventEmitter<MigrationModal> = new EventEmitter();
  constructor(private _fb: FormBuilder,
    private _notificationsService: NotificationsService,
    private router: Router,
    
    // private postRequest: PostService,
    private activatedRoute: ActivatedRoute) { }

    sdStatus:string = "false";

  receiveSDStatus($event) {
    this.sdStatus = $event;
  }

  ngOnInit() {
    
    this.type = this.activatedRoute.snapshot.queryParams['type'];
    this.migrationFormgroup = this._fb.group({
    })
  }
  formInitialized( name:string, form: FormGroup){
    this.migrationFormgroup.setControl(name,form);
  }
  serviceList(event){
    // event['data'].forEach(element => {
    //   this.list.push(element['IPAddress']);
    // });
    let len = event.data.length;
    const servicelist: any[]=[];
    for(let i = 0; i<len; i++){
      servicelist.push(event.data[i].IPAddress)
    }
    this.list = servicelist
  }
  step(i: number){
    console.log(i)
  }
  redirectMigration(){
    this._notificationsService.info('Migration Scheduled...');
    setTimeout(() => {
      this.router.navigate(['/migration/migration-list']);
    }, 2000);
  }
 
  cloudprovider(event){

    this.cloud = event;
  }
  loader1(){
    setTimeout(()=>{
        this._load = false;
        // this.router.navigate(['/migration/new-migration'],{ queryParams: { host: this.hostip } });
    },3000)
    this._load= true
    
  }
  loader2(){
    this._notificationsService.info('Step to start Migration...');
    setTimeout(()=>{
        this._load = false;
        // this.router.navigate(['/migration/new-migration'],{ queryParams: { host: this.hostip } });
    },3000)
    this._load= true
  }
