import { Component, OnInit } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { GetReleaseItem, GetAppReleases, GetInfraReleases, OpenCloneReleaseConfiguration, GetVNFReleases, ResetReleasePlan } 
from '../../actions/release.action';
import { Release } from '../../models/release.model';
import { ReleaseState } from '../../state/release.state';
import { Navigate } from '@ngxs/router-plugin';
import { Observable } from 'rxjs/Observable';
import { ClearExistingReleasePlan } from '../../actions/existing-release-plan.action';

@Component({
  selector: 'matilda-release-list',
  templateUrl: './release-list.component.html',
  styleUrls: ['./release-list.component.scss']
})
export class ReleaseListComponent implements OnInit {
  public releaseList$: Observable<Release>;
  public metadata$: Observable<any>;
  public pageSize;
  public clone = true;
  public view = true;
  itemsPerPage: number;
  totalItems: any;
  page: any;
  p: number = 1;
  previousPage: any;
  public dataSource: any;
  
  @Select(ReleaseState.applicationReleases) applicationReleases$: Observable<any[]>;
  @Select(ReleaseState.infrastructureReleases) infrastructureReleases$: Observable<any[]>;
  @Select(ReleaseState.vnfReleases) vnfReleases$: Observable<any[]>;
  @Select(ReleaseState.applicationMetaData) applicationMetaData$: Observable<any[]>;
  @Select(ReleaseState.infrastructureMetaData) infrastructureMetaData$: Observable<any[]>;
  @Select(ReleaseState.vnfMetaData) vnfMetaData$: Observable<any[]>;
  constructor(private _store: Store) { }

  ngOnInit() {
    this._store.dispatch((new GetAppReleases()));
    this._store.dispatch((new GetInfraReleases()));
    this._store.dispatch(new GetVNFReleases());
  }
  ngAfterViewInit() {
    this.pageSize = 5;
  }

  newRelease() {
    this._store.dispatch(new ResetReleasePlan({}));
    this._store.dispatch(new ClearExistingReleasePlan());
    this._store.dispatch(new Navigate(['/release-orchestration/newrelease']));
  }

  clonerelease(selectedRelease: Release) {
    this._store.dispatch(new OpenCloneReleaseConfiguration(selectedRelease));
  }
  viewrelease(event: Release) {
    this._store.dispatch(new GetReleaseItem({ "releaseId": event.releaseId, "releaseTypeCode": event.releaseTypeCode }));
  }
  loadReleaseModule() {
    this._store.dispatch(new Navigate(['/release-orchestration']));
  }
}
