
import { Injectable } from '@angular/core';
import { ConfigService } from '../app-config.service';
@Injectable({
    providedIn: 'root'
  })
export  class ReleaseMgmtAppSettings  {
    private _baseUrl:string;  
    constructor(private _configService: ConfigService) {   
       
    }
    public getURL(operation: any) {
        this._baseUrl=this._configService.get("release_services").BASE_URL;    
        return this._baseUrl + this._configService.get("release_services")[operation] ;
    }
}