import { Component, OnInit } from '@angular/core';
import { Select, Store } from '@ngxs/store';
import { ReleasePlanningState } from 'src/app/release-management/state/release-planning.state';
import { Observable } from 'rxjs';
import { ReleasePlan, ReleaseEnvironment } from 'src/app/release-management/models/release.model';
import { InfraEnvironmentsState } from 'src/app/release-management/state/infra-impactedsystems.state';
import { InfraEnvironments } from 'src/app/release-management/models/impactedSystems.model';
import { EnvironmentDataShareService } from 'src/app/release-management/services/environment-data-share.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'matilda-infra-release-view',
  templateUrl: './infra-release-view.component.html',
  styleUrls: ['./infra-release-view.component.scss']
})
export class InfraReleaseViewComponent implements OnInit {
  @Select(ReleasePlanningState.releasePlan) releaseView$: Observable<ReleasePlan[]>;
  @Select(InfraEnvironmentsState.infraEnvironments)
  infraEnvironments$: Observable<InfraEnvironments[]>;
  public releaseView: ReleasePlan;
  public operation: string;

  defaultEnviornments: Array<string> = ['Development'];
  public availableenvironmentList: ReleaseEnvironment[] = [];
  constructor(private _environmentDataShareService: EnvironmentDataShareService,
    private _activatedRoute: ActivatedRoute,
    private _store: Store) {
    this.releaseView = new ReleasePlan();
    let environment = new ReleaseEnvironment();
    environment.environmentName = 'Development';
    //environment.environment_name = this.defaultEnviornments[i];
    this.availableenvironmentList.push(environment);

  }

  ngOnInit() {
    this.operation = this._activatedRoute.snapshot.url.join("/");
    this.releaseView$.subscribe(
      (_releaseView: any) => {
        _releaseView.forEach(releaseView => {
          this.releaseView = releaseView;
        })

      });
  }
  addMe() {
    this.defaultEnviornments.push('Test');

    let environment = new ReleaseEnvironment();
    environment.environmentName = 'Test';
    //environment.environment_name = this.defaultEnviornments[i];
    this.availableenvironmentList.push(environment);
  }

  getFlexDirection() {
    if (this.availableenvironmentList.length % 3 === 1 && this.availableenvironmentList.length != 1) {
      return true;
    } else {
      return false;
    }

  }

  increaseWidth() {
    if (this.availableenvironmentList.length % 3 === 1) {
      return true;
    }else{
      return false;
    }
   
  }

}
