import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { NetworkDiscoveryList } from '../../../models/discovery.model';
import { Observable } from 'rxjs';
import { Store } from '@ngxs/store';
import { Router } from '@angular/router';
import { SortDescriptor, orderBy } from '@progress/kendo-data-query'
import { TooltipDirective } from '@progress/kendo-angular-tooltip';
import { FetchDiscoveryDetailList } from 'src/app/discovery/actions/discovery-list.actions';
import { FetchDiscoveryNetWorkList } from 'src/app/discovery/actions/discovery-creating.actions';
@Component({
  selector: 'matilda-network-discovery',
  templateUrl: './network-discovery.component.html'
})
export class NetworkDiscoveryComponent implements OnInit {
  public networkList$: Observable<NetworkDiscoveryList>;
  public networkList: any;

  //latest created  network discovery  with task id
  public sort: SortDescriptor[] = [{
    field: 'TASK_ID',
    dir: 'desc'
  }];
  //ToolTip
  @ViewChild(TooltipDirective) public tooltipDir: TooltipDirective;
  public showTooltip(e: MouseEvent): void {
    const element = e.target as HTMLElement;
    if ((element.nodeName === 'TD' || element.nodeName === 'TH')
      && element.offsetWidth < element.scrollWidth) {
      this.tooltipDir.toggle(element);
    } else {
      this.tooltipDir.hide();
    }
  }
  //pagination
  public pageSize = 10;
  public skip = 0;
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
  }
  public onPageChange(state: any): void {
    this.pageSize = state.take;
  }
  //status color
  getStatusBadge(Status: string) {
    if (Status) {
      if (Status.toLowerCase() === 'active' || Status.toLowerCase() === 'completed') {
        return 'm-badge--success';
      } else if (Status.toLowerCase() === 'failed') {
        return 'm-badge--danger';
      }
      else if (Status.toLowerCase() === 'in_progress' || Status.toLowerCase() === 'submitted') {
        return 'm-badge--warning';
      } else {
        return 'm-badge--brand';
      }
    }
  }
  constructor(private router: Router, private _store: Store, private cd: ChangeDetectorRef) { }
  ngOnInit() {
    this._store.dispatch(new FetchDiscoveryNetWorkList({}));
    this.networkList$ = this._store.select(state => state.discoveryCreatingDetails.discoveryNetworkList);
    this.networkList$.subscribe((x) => {
      if (x != null && x != undefined) {
        this.networkList = x
      }
      // this.errormessage = x.errorMessage
    });
  }

  //redirecting to dicovery details ip 
  getResults(ID: any) {
    this._store.dispatch(new FetchDiscoveryDetailList({ "discoveryID": ID }));

  }

}
