import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { NotificationsService } from 'angular2-notifications';
import * as _ from 'lodash';
import { TargetConfigurationService } from '../../../services/targetconfig.service';

import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';

import { GetTargetConfiguration, GetSourceConfiguration } from '../../../actions/migration.action';
import { MigrationState } from '../../../state/migration.state';

@Component({
  selector: 'matilda-target-configuration',
  templateUrl: './target-configuration.component.html',
  styles: []
})
export class TargetConfigurationComponent implements OnInit {
  public infra = false;
  public keys= Object.keys;
  public currentClusterIndex = 0;
  public currentComponenetName = '';
  public currentServiceIndex = 0;
  public componentList: any[] = [];
  public count = 0;
  public groupList: any[] = [];
  public uniquegroupList: any[] = [];
  public isMessage: boolean;
  public message = '';
  public isComponentActive: boolean;
  public isvalidLaunch: boolean;
  public serviceList: any;
  public service: any;
  closeResult: string;
  public operation: string;
  public isCollapsed = true;
  public clustermessage: boolean = false;
  public _config = false;
  public disable = false;
  public account = true;
  public region = true;
  public project = true;
  public environment = true;
  public _kubernetesSwitch = false;
  data: any;
  sourcedata:any;
  cloudprovider: string;
  targetdata: any;
  @Input() cloud : string;

  @Select(MigrationState.targetConfigurations) targetConfigurations$: Observable<any[]>;
  @Select(MigrationState.sourceConfigurations) sourceConfigurations$: Observable<any[]>;
  
  constructor(
    private formBuilder: FormBuilder,
    private modalService: NgbModal,
    private activatedRoute: ActivatedRoute,
    private _notifications: NotificationsService,
    private router: Router,
    private titleService: Title,
    private targetService:TargetConfigurationService,
    private _store: Store
  ) {
  }
  onStageNameChanged(stageName, item) {
    //In Future any things to be done when stageName Changed use this method
}
  ngOnInit() {
    console.log("this.cloud-->", this.cloud)

    this._store.dispatch(new GetTargetConfiguration(this.cloud));
    this.targetConfigurations$.subscribe(data => {
        if(data){
          this.targetdata = data;    
          console.log(this.targetdata)        
        }            
    });

    this._store.dispatch(new GetSourceConfiguration(this.cloud));
    this.sourceConfigurations$.subscribe(data => {
        if(data){
          this.sourcedata = data;                         
        }            
    });
    
 
  }
  loadCloudmodule() {
    this.router.navigate(['/cloud-management']);
  }

  changed(e){
    if(e.target.checked){
      this._kubernetesSwitch = true;
    }
    else{
      this._kubernetesSwitch = false;
    }
  }
 
}