import { CloneEnvironment } from './../../actions/release-planning.action';
import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Store, Select } from '@ngxs/store';
import { ReleaseEnvironment } from '../../models/release.model';
import { ReleasePlanningDropdownsState } from '../../state/release-planning-dropdowns.state';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'matilda-clone-environment',
  templateUrl: './clone-environment.component.html',
  styleUrls: ['./clone-environment.component.scss']
})
export class CloneEnvironmentComponent implements OnInit {
  @Select(ReleasePlanningDropdownsState.availableEnvironmentTypes) availableEnvironmentTypes$: Observable<any[]>;
  @Input('sourceEnvironment') sourceEnvironment: ReleaseEnvironment;
  public cloneEnvironmentForm: FormGroup;
  public saveMessage: boolean;
 
  constructor(private _fb: FormBuilder,
    public activeModal: NgbActiveModal,
    private _store: Store) { }

  ngOnInit() {
    this.cloneEnvironmentForm = this._fb.group({
      environmentName: ['', [
        Validators.required,
        //Validators.minLength(5),
        Validators.maxLength(30),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_-]+$'), { 'remainingpattern': 'should not contain special characters' }),
      ]],
      environmentTypeCode: ['', Validators.required]
    });
  }

  onSubmit(form: any) {
    if (this.cloneEnvironmentForm.valid) {

      this._store.dispatch(new CloneEnvironment({
        "targetEnvironmentName": form.environmentName, 
        "targetEnvironmentTypeCode":form.environmentTypeCode,
        "sourceEnvironment": this.sourceEnvironment
      }));

    }
  }

  get environmentName() {
    return this.cloneEnvironmentForm.get('environmentName');
  }

  get environmentTypeCode() {
    return this.cloneEnvironmentForm.get('environmentTypeCode');
  }

  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }
}
