import { ReleasePlanningState } from './../../state/release-planning.state';
import { FormGroup, FormBuilder, Validators, FormControl, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';
import { OnInit, Component } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import { ReleasePlan } from '../../models/release.model';
import { ReleasePlanningDropdownsState } from '../../state/release-planning-dropdowns.state';
import { ReleasePlanUpdated, GetArtifactTable, SaveReleasePlan, SaveReleasePlanAndNext } from '../../actions/release-planning.action';
import { InitializeDropDowns} from '../../actions/release-planning-dropdowns.action';
import { addYears } from '@progress/kendo-date-math';
import { Navigate } from '@ngxs/router-plugin';
import { ExistingReleasePlanState } from '../../state/existing-release-plan.state';
import { ExistingReleasePlan } from '../../models/existing-release-plan.model';
import { UpdateExistingReleasePlan } from '../../actions/existing-release-plan.action';


@Component({
  selector: 'app-release-planning',
  templateUrl: './release-planning.component.html',
  styleUrls: ['./release-planning.component.scss'],
})
export class ReleasePlanningComponent implements OnInit {
  public releasePlan: ReleasePlan;
  public selectedRows: Map<string, { any }> = new Map();
  // public selectedRows: any[] = [];
  public additionalReleasePlans: string[] = [];
  public filteredReleasePlans: any;
  public scheduled;
  public checkbox = true;
  public expand = [];
  public selectedItems = [];
  public disabledLink = true;
  public today = new Date();
  public min: Date;
  public max: Date;
  public isDisabledApplications: boolean = true;
  public isDisabledOperatingsystem: boolean = true;
  // Form Related dropdowns and its settings
  projectOptions = [];
  applicationOptions = [];
  releaseVersionOptions = [];
  environmentOptions = [];
  releaseTypeOptions = [];
  platformOptions = [];
  operatingsystemOptions = [];
  VNFNodeTypeOptions = [];
  VNFSiteOptions = [];
  existingReleaseNames = [];
  availableArtifacts = [];
  vnfUpgradeTypeOptions: Array<string> = ['ISO_Upgrade','Patch_Upgrade'];
  singleDropdownSettings: any = {};
  customSingleDropdownSettings: any = {};
  closeDropdownSelection = false;
  disabled = false;

  //Dropdown Selected Values
  selectedProject: any[] = [];
  selectedApplication: any[] = [];
  selectedReleaseVersion: any[] = [];
  selectedEnvironment: any[] = [];
  public features: any[] = [];
  public selectedFeature: any;
  public releasePlanInfoForm: FormGroup;
  public releaseTypeForm: FormGroup;
  public releaseTypeURLparam: string;
  public isvalidSave: boolean = false;
  public isCollapsed: boolean = false;
  public isDisabledVNFSite: boolean = true;
  private ExtraValidators: boolean;
  public milestoneValidity: Observable<string>;
  public previousReleasePlan: ExistingReleasePlan;

  @Select(ReleasePlanningDropdownsState.projectOptions) projectOptions$: Observable<any[]>;
  //@Select(ReleasePlanningDropdownsState.applicationOptions) applicationOptions$: Observable<any[]>;
  @Select(ReleasePlanningDropdownsState.releaseVersionOptions) releaseVersionOptions$: Observable<any[]>;
  //@Select(ReleasePlanningDropdownsState.e) environmentOptions$: Observable<any[]>;
  @Select(ReleasePlanningDropdownsState.releaseTypeOptions) releaseTypeOptions$: Observable<any[]>;
  @Select(ReleasePlanningDropdownsState.platformOptions) platformOptions$: Observable<any[]>;
  @Select(ReleasePlanningDropdownsState.VNFNodeTypeOptions) VNFNodeTypeOptions$: Observable<any[]>;
  @Select(ReleasePlanningDropdownsState.existingReleaseNames) existingReleaseNames$: Observable<any[]>;
  @Select(ReleasePlanningDropdownsState.availableArtifacts) availableArtifacts$: Observable<any[]>;
  @Select(ReleasePlanningState.additionalReleasePlans) additionalReleasePlans$: Observable<any[]>;
  @Select(ExistingReleasePlanState.existingReleasePlan) existingReleasePlan$: Observable<any[]>;
  

  constructor(
    private _fb: FormBuilder,
    private _store: Store) {
    this.releasePlan = new ReleasePlan();
      this.min = new Date(this.today.getFullYear(),this.today.getMonth(), this.today.getDate());
      this.max = addYears(this.min,2);
  }


  ngOnInit() {
    this._store.dispatch(new InitializeDropDowns());
    //Populating All Dropdowns
    this.loadAllPlanningProperties();
    
    //Existing ReleasePlans
    this.existingReleasePlan$.subscribe((_existingReleasePlan)=>{
      if(_existingReleasePlan.length > 0){
        this.previousReleasePlan = _existingReleasePlan[0];
      }
    });

    this.loadDropdownSettings();
    this.releaseTypeForm = this._fb.group({
      releaseType: [, Validators.required]
    });
    this.releaseTypeForm.get('releaseType').setValue({
      "releaseTypeCode": 1,
      "releaseType": "Application"
    });
    this.releasePlan.releaseTypeCode = this.releaseTypeForm.get('releaseType').value['releaseTypeCode'];
    this.releasePlan.releaseType = this.releaseTypeForm.get('releaseType').value['releaseType'];

    this.releaseTypeForm.get('releaseType').valueChanges.subscribe(selectedreleasetype => {
      this.releasePlan.releaseTypeCode = selectedreleasetype.releaseTypeCode;
      this.releasePlan.releaseType = selectedreleasetype.releaseType;
      if (selectedreleasetype.releaseTypeCode === 1) {
        this.projectId.setValidators([Validators.required]);
        this.applicationId.setValidators([Validators.required]);
        this.platformId.setValidators([Validators.nullValidator]);
        this.platformId.setValue(null);
        this.operatingsystemId.setValue(null);
        this.operatingsystemId.setValidators([Validators.nullValidator]);
        this.vnfNodeType.setValue(null);
        this.vnfSite.setValue(null);
        this.vnfUpgradeType.setValue(null);
        this.vnfNodeType.setValidators([Validators.nullValidator]);
        this.vnfSite.setValidators([Validators.nullValidator]);
        this.projectId.updateValueAndValidity();
        this.applicationId.updateValueAndValidity();
        this.platformId.updateValueAndValidity();
        this.operatingsystemId.updateValueAndValidity();
        this.vnfNodeType.updateValueAndValidity();
        this.vnfSite.updateValueAndValidity();
        this.vnfUpgradeType.updateValueAndValidity();
      }
      else if (selectedreleasetype.releaseTypeCode === 2) {
        this.projectId.setValidators([Validators.nullValidator]);
        this.projectId.setValue(null);
        this.applicationId.setValue(null);
        this.applicationId.setValidators([Validators.nullValidator]);
        this.vnfNodeType.setValue(null);
        this.vnfSite.setValue(null);
        this.vnfUpgradeType.setValue(null);
        this.vnfNodeType.setValidators([Validators.nullValidator]);
        this.vnfSite.setValidators([Validators.nullValidator]);
        this.platformId.setValidators([Validators.required]);
        this.operatingsystemId.setValidators([Validators.required]);
        this.projectId.updateValueAndValidity();
        this.applicationId.updateValueAndValidity();
        this.platformId.updateValueAndValidity();
        this.operatingsystemId.updateValueAndValidity();
        this.vnfNodeType.updateValueAndValidity();
        this.vnfSite.updateValueAndValidity();
        this.vnfUpgradeType.updateValueAndValidity();
      }else if (selectedreleasetype.releaseTypeCode === 3) {
        this.projectId.setValidators([Validators.nullValidator]);
        this.projectId.setValue(null);
        this.applicationId.setValue(null);
        this.applicationId.setValidators([Validators.nullValidator]);
        this.platformId.setValidators([Validators.nullValidator]);
        this.operatingsystemId.setValidators([Validators.nullValidator]);
        this.vnfNodeType.setValidators([Validators.required]);
        this.vnfSite.setValidators([Validators.required]);
        this.vnfUpgradeType.setValidators([Validators.required]);
        this.vnfNodeType.updateValueAndValidity();
        this.vnfSite.updateValueAndValidity();
        this.vnfUpgradeType.updateValueAndValidity();
        this.projectId.updateValueAndValidity();
        this.applicationId.updateValueAndValidity();
        this.platformId.updateValueAndValidity();
        this.operatingsystemId.updateValueAndValidity();

      }
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
    });
    this.initForm();

   
  }
 

  initForm() {
    this.releasePlanInfoForm = this._fb.group({
      name: ['', [
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(30),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_-]+$'), { 'remainingpattern': 'should not contain special characters' }),
        this.isReleaseNameUnique.bind(this) // async Validator passed as 3rd parameter 
      ]],
      description: ['', Validators.nullValidator],
      releaseDate: ['', Validators.required],
      projectId: [, Validators.nullValidator],
      applicationId: [, Validators.nullValidator],
      version: [, Validators.required],
      platformId: [, Validators.nullValidator],
      vnfNodeType: [, Validators.nullValidator],
      vnfSite: [, Validators.nullValidator],
      operatingsystemId: [, Validators.nullValidator],
      //patchLocation: ['', Validators.nullValidator],
      vnfUpgradeType: ['', Validators.nullValidator]
    });

    if(this.previousReleasePlan){
      this.bindReleasePlan();
    }
  }
  public valueChange(event: any): void {
    if (this.features.length === 0 && event.length !== 0) {
      this.features.push(event);
      this.selectedFeature = [event];
      this.getArtifactTable(this.selectedFeature[0]);
    }
    else {
      if (event.length == 0) {
        this.releasePlan.selectedAdditionalReleasePlans = [];
        this.releasePlan.additionalReleasePlans = [];
        this._store.dispatch(new ReleasePlanUpdated(this.releasePlan))
      }
      this.features = event;
      let index = event.length - 1;
      this.getArtifactTable(this.features[index]);
      for (let i = 0; i < this.releasePlan.additionalReleasePlans.length; i++) {
        if (!this.features.includes(this.releasePlan.additionalReleasePlans[i].title)) {
          this.releasePlan.selectedAdditionalReleasePlans.splice(i, 1);
          this.releasePlan.additionalReleasePlans.splice(i, 1);
          this._store.dispatch(new ReleasePlanUpdated(this.releasePlan))
        }
      }
    }
  }
  getArtifactTable(selectedArtifactType) {
    if (this.features.includes(selectedArtifactType)) {

      this._store.dispatch(new GetArtifactTable({
        "selectedArtifactType": selectedArtifactType.replace(/ /g, ""),
        "releasePlan": this.releasePlan
      }));
      // this.selectedItems.push(selectedArtifactType);
      this.expand.push({
        isCollapsed: false
      })
      return
    }


  }

  recordSelected(event) {
    // if (event.action === 'add') {
    if (this.selectedRows.has(event.title)) {
      this.selectedRows.set(event.title, event);
    } else {
      this.selectedRows.set(event.title, event);
    }
    for (let entry of Array.from(this.selectedRows.keys())) {
      const i = this.releasePlan.selectedAdditionalReleasePlans.findIndex(existingPlan => existingPlan.title === entry);
      if (i > -1) this.releasePlan.selectedAdditionalReleasePlans[i] = this.selectedRows.get(entry);
      else this.releasePlan.selectedAdditionalReleasePlans.push(this.selectedRows.get(entry));

    }
    this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
  }
  openProperties() {
    this.isCollapsed = !this.isCollapsed;
  }

  saveReleasePlan(form: any) {
    if (this.releasePlanInfoForm.valid) {
      this.releasePlan.applicationId = this.releasePlan.releaseTypeCode == 1 ? form.applicationId.id : '';
      this.releasePlan.description = form.description;
      this.releasePlan.projectId = this.releasePlan.releaseTypeCode == 1 ? form.projectId.id : '';
      this.releasePlan.name = form.name;
      this.releasePlan.releaseDate = form.releaseDate;
      this.releasePlan.version = form.version;
      //this.releasePlan.patchLocation = this.releasePlan.releaseTypeCode == 2 ? form.patchlocation : '';
      if(this.releasePlan.releaseTypeCode === 1){
        this.releasePlan.projectId = form.projectId.id;
        this.releasePlan.projectName = form.projectId.name;
        this.releasePlan.applicationId = form.applicationId.id;
        this.releasePlan.applicationName = form.applicationId.name;
      }else if(this.releasePlan.releaseTypeCode === 2){
        this.releasePlan.platformId = form.platformId.id;
        this.releasePlan.platformName = form.platformId.name;
        this.releasePlan.operatingsystemId = form.operatingsystemId.id;
        this.releasePlan.operatingsystemName = form.operatingsystemId.name;
      }else if(this.releasePlan.releaseTypeCode === 3){
        this.releasePlan.vnfNodeTypeId = form.vnfNodeType.id;
        this.releasePlan.vnfSiteId = form.vnfSite.id;
        this.releasePlan.vnfNodeType = form.vnfNodeType.name;
        this.releasePlan.vnfSite = form.vnfSite.name;
      }

      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
      this._store.dispatch(new SaveReleasePlan(this.releasePlan));
      this._store.dispatch(new UpdateExistingReleasePlan(this.releasePlan));

    } else {
      this.validateAllFormFields(this.releasePlanInfoForm);
    }
    this.validatebutton();

  }

  savereleaseAndnextStep(form: any) {
    if (this.releasePlanInfoForm.valid) {
      this.releasePlan.description = form.description;
      this.releasePlan.name = form.name;
      this.releasePlan.releaseDate = form.releaseDate;
      this.releasePlan.version = form.version;
      //this.releasePlan.patchLocation = this.releasePlan.releaseTypeCode == 2 ? form.patchlocation : '';
      if(this.releasePlan.releaseTypeCode === 1){
        this.releasePlan.projectId = form.projectId.id;
        this.releasePlan.projectName = form.projectId.name;
        this.releasePlan.applicationId = form.applicationId.id;
        this.releasePlan.applicationName = form.applicationId.name;

      }else if(this.releasePlan.releaseTypeCode === 2){
        this.releasePlan.platformId = form.platformId.id;
        this.releasePlan.platformName = form.platformId.name;
        this.releasePlan.operatingsystemId = form.operatingsystemId.id;
        this.releasePlan.operatingsystemName = form.operatingsystemId.name;
      }else if(this.releasePlan.releaseTypeCode === 3){
        this.releasePlan.vnfNodeTypeId = form.vnfNodeType.id;
        this.releasePlan.vnfSiteId = form.vnfSite.id;
        this.releasePlan.vnfNodeType = form.vnfNodeType.name;
        this.releasePlan.vnfSite = form.vnfSite.name;
      }
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
      this._store.dispatch(new SaveReleasePlanAndNext(this.releasePlan));
      this._store.dispatch(new UpdateExistingReleasePlan(this.releasePlan));
    } else {
      this.validateAllFormFields(this.releasePlanInfoForm);
    }
  }
  cancelReleasePlan() {
    //this.router.navigate(['/release-orchestration']);
    this._store.dispatch(new Navigate(['/release-orchestration']));
  }
  validatebutton() {
    if (!this.projectId.invalid && !this.applicationId.invalid) {
      this.disabledLink = false;
    }
    else {
      this.disabledLink = true;
    }
  }

  get projectId() {
    return this.releasePlanInfoForm.get('projectId');
  }
  get name() {
    return this.releasePlanInfoForm.get('name');
  }
  get description() {
    return this.releasePlanInfoForm.get('description');
  }
  get applicationId() {
    return this.releasePlanInfoForm.get('applicationId');
  }
  get releaseDate() {
    return this.releasePlanInfoForm.get('releaseDate');
  }
  get platformId() {
    return this.releasePlanInfoForm.get('platformId');
  }
  get operatingsystemId() {
    return this.releasePlanInfoForm.get('operatingsystemId');
  }
  get version() {
    return this.releasePlanInfoForm.get('version');
  }
  get releaseType() {
    return this.releaseTypeForm.get('releaseType');
  }
  get vnfNodeType(){
    return this.releasePlanInfoForm.get('vnfNodeType');
  }
  get vnfSite(){
    return this.releasePlanInfoForm.get('vnfSite');
  }
  get vnfUpgradeType(){
    return this.releasePlanInfoForm.get('vnfUpgradeType');
  }
  isReleaseNameUnique(control: FormControl) {
    let keyUnique;
    for (let previousKey of this.existingReleaseNames) {
      if (previousKey.toLowerCase() === control.value.toLowerCase()) {
        keyUnique = { 'keyInUse': true }
        break;
      } else {
        keyUnique = null;
      }
    }
    return keyUnique;
  }

  loadAllPlanningProperties() {

    this.releaseTypeOptions$.subscribe(_releaseTypeOptions => {
        this.releaseTypeOptions = _releaseTypeOptions;      
    });

    this.projectOptions$.subscribe(_projectOptions => {
      this.projectOptions = _projectOptions;
    });
    this.platformOptions$.subscribe(_platformOptions => {
      this.platformOptions = _platformOptions;
    });
    this.VNFNodeTypeOptions$.subscribe(_VNFNodeTypeOptions => {
      this.VNFNodeTypeOptions = _VNFNodeTypeOptions;
    });
    this.releaseVersionOptions$.subscribe(_releaseVersionOptions => {
      this.releaseVersionOptions = _releaseVersionOptions;
    });

    this.availableArtifacts$.subscribe(_availableArtifacts => {
      this.availableArtifacts = _availableArtifacts;
    });

    this.existingReleaseNames$.subscribe(_existingReleaseNames => {
      this.existingReleaseNames = _existingReleaseNames;
    });
  }

  loadDropdownSettings() {
    this.singleDropdownSettings = {
      singleSelection: true,
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      allowSearchFilter: false,
      closeDropDownOnSelection: this.closeDropdownSelection
    };
    this.customSingleDropdownSettings = {
      singleSelection: true,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      allowSearchFilter: false,
      closeDropDownOnSelection: this.closeDropdownSelection
    };
  }

  onProjectSelect(event: any) {
    if (event != null || event !== '') {
      this.isDisabledApplications = false
      this.applicationId.setValue(null);
      this.releasePlan.platformId = 0;
      this.releasePlan.operatingsystemId = 0;
      this.releasePlan.projectId = event.id || event[0];
      this.applicationOptions = event.applications;
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
    }
    else {
      this.isDisabledApplications = true;
    }
  }

  onPlatformSelect(event: any) {
    if (event !== null || event == '') {
      this.isDisabledOperatingsystem = false
      this.operatingsystemId.setValue(null);
      this.releasePlan.projectId = '';
      this.releasePlan.applicationId = '';
      this.releasePlan.platformId = event.id;
      this.releasePlan.operatingsystemId = event.id || event[0];
      this.operatingsystemOptions = event.operatingsystems;
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
    }
    else {
      this.isDisabledOperatingsystem = true;
    }

  }
  onVNFNodeTypeSelect(event: any){
    if (event !== null || event == '') {
      this.isDisabledVNFSite = false
      this.vnfSite.setValue(null);
      this.releasePlan.projectId = '';
      this.releasePlan.applicationId = '';
      this.releasePlan.platformId = 0;
      this.releasePlan.operatingsystemId = 0;
      this.VNFSiteOptions = event.vnfSites;
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
    }
    else {
      this.isDisabledVNFSite = true;
    }
  }
  onApplicationSelect(event: any) {
    if (event !== null || event == '') {
      this.releasePlan.applicationId = event.id;
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
    }
  }
  onOperatingSystemSelect(event: any) {
    if (event !== null || event == '') {
      this.releasePlan.operatingsystemId = event.id;
      this._store.dispatch(new ReleasePlanUpdated(this.releasePlan));
    }
  }
  
  bindReleasePlan(){
    this.releaseType.setValue(this.previousReleasePlan.releaseType);
    
    this.name.setValue(this.previousReleasePlan.name);
    this.projectId.setValue(this.previousReleasePlan.project);
    this.applicationId.setValue(this.previousReleasePlan.application);
    this.platformId.setValue(this.previousReleasePlan.platform);
    this.operatingsystemId.setValue(this.previousReleasePlan.operatingsystem);
    this.vnfNodeType.setValue(this.previousReleasePlan.vnfNodeType);
    this.vnfSite.setValue(this.previousReleasePlan.vnfSite);
    this.version.setValue(this.previousReleasePlan.version);
    this.releaseDate.setValue(new Date(this.previousReleasePlan.releaseDate));
    this.description.setValue(this.previousReleasePlan.description);
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }
  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }
  loadReleaseList() {
    this._store.dispatch(new Navigate(['/release-orchestration']));
  }
 
  loadReleaseDashboard() {
    this._store.dispatch(new Navigate(['/release-orchestration/dashboard']));
  }
}
