import { Component, OnInit, ViewChild } from '@angular/core';
// import { GetHostDetails } from 'src/app/discovery/actions/discovery.action';
import { Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { State, process } from '@progress/kendo-data-query';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';
import * as _ from 'lodash';
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';
import { TooltipDirective } from '@progress/kendo-angular-tooltip';
@Component({
  selector: 'matilda-host-storage',
  templateUrl: './host-storage.component.html',
  styleUrls: [ ]
})
export class HostStorageComponent implements OnInit {
public storage_info$:Observable<any>;
public storage:any;
public gridState: State = {
  sort: [],
  skip: 0,
  take: 10,
};
public allData(): ExcelExportData {
  const result: ExcelExportData = {
    data: process(this.storage, { filter: this.gridState.filter }).data,
  };
  return result;
}
//tooltip
@ViewChild(TooltipDirective) public tooltipDir: TooltipDirective;
public showTooltip(e: MouseEvent): void {
  const element = e.target as HTMLElement;
  if ((element.nodeName === 'TD' || element.nodeName === 'TH')
    && element.offsetWidth < element.scrollWidth) {
    this.tooltipDir.toggle(element);
  } else {
    this.tooltipDir.hide();
  }
}
//pagination
public pageSize = 10;
public skip = 0;
public metaData: any[] = [];
public sliderChange(pageIndex: number): void {
  this.skip = (pageIndex - 1) * this.pageSize;
}
public onPageChange(state: any): void {
  this.pageSize = state.take;
}
  constructor(private _store: Store,private _http: HttpClient) {
    this.allData = this.allData.bind(this);
   }
  ngOnInit() {
   //Getting data to table
   
    this.storage_info$= this._store.select(state=>state.discoveryResultDetails.hostStorageData);
    // this.storage_info$.subscribe(element=>{
      
    //   this.storage= element.filter(x=>{
    //        return x.Used_Percent!=null;
    //      });
    //     });
        




        this.storage_info$.subscribe((element: any[]) => {
          this.storage = element.filter(x=>{
            return x.Used_Percent!=null;
          });
          // console.log(this.storage);
               _.each(this.storage, (key: any, index) => {
            let value = _.keys(key)
            _.each(value, (keys) => {
              if (this.metaData.length == 0) {
                index = {
                  field: _.trim(keys),
                  title: _.upperFirst(keys),
                  width: 40,
                  locked: false,
                  filterable: true
                }
                this.metaData.push(index)
                this.metaData =_.uniqBy(this.metaData,"field")
              }
              else if (this.metaData[index] != keys &&keys != "Total_Space"&&keys != "Used_Space"&&keys != "Used_Percent") {
                index = {
                  field: _.trim(keys),
                  title: _.upperFirst(keys),
                  width: 40,
                  locked: false,
                  filterable: true
    
                }
                
                this.metaData.push(index)
                

                // let discovered = {
                //   field: "discovered",
                //   title: "Discovered",
                //   width: 100,
                  
                // }
                // this.metaData.push(discovered)
                this.metaData = _.uniqBy(this.metaData,"field")
              }
              
            });
          });
        });   
     }
  }
