import { Component, OnInit, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Store } from '@ngxs/store';
import { State, process } from '@progress/kendo-data-query';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';
import * as _ from 'lodash';
@Component({
  selector: 'matilda-host-port',
  templateUrl: './host-port.component.html',
  styleUrls: []
})
export class HostPortComponent implements OnInit {
  portinfo$: Observable<any>;
  @Input() interfaceData: any;
  public port: any;
  public conectedServicesinfo$: any;
  public conectedServicesinfo: any;
  public metaData: any[] = []
  public metaDataService: any[] = [];
  //pagination
  public pageSize = 10;
  public skip = 0;
  public gridState: State = {
    sort: [],
    skip: 0,
    take: 10,
  };
  public allData(): ExcelExportData {
    const result: ExcelExportData = {
      data: process(this.port, { filter: this.filter }).data,
    };
    return result;
  }
  public allData1(): ExcelExportData {
    const result: ExcelExportData = {
      data: process(this.conectedServicesinfo, { filter: this.gridState.filter }).data,
    };
    return result;
  }
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
  }
  public onPageChange(state: any): void {
    this.pageSize = state.take;
  }
  public filter: any;
  public port1: any;
  //status color
  getStatusBadge(Status: string) {
    if (Status) {
      // alert(Status)
      if (Status === 'OPEN') {
        return 'la-arrow-circle-o-up m-badge--success';
      } else if (Status === 'ACTIVE') {
        return 'm-badge--success';
      }

    }
  }
  constructor(private _store: Store, private _http: HttpClient) {
    this.allData = this.allData.bind(this);
    this.allData1 = this.allData1.bind(this);
  }
  ngOnInit() {
    //getting the data to tabel for kendo grid
    if (this.interfaceData != undefined) {
      console.log(this.interfaceData)
      this.filter = {
        logic: 'and',
        filters: [{ field: 'Interface', operator: 'contains', value: this.interfaceData.interfaceName },

        ]
      }
    }
    //   this.portinfo$= this._store.select(state=>{
    //     return state.discoverylist.hostPortData.filter(x=>{
    //       return x.Interface === this.interfaceData.interfaceName;
    //     });
    //   });
    // }else{
    this.portinfo$ = this._store.select(state => state.discoveryResultDetails.hostPortData);
    // }

    this.portinfo$.subscribe((element: any[]) => {
      this.port = element;
      _.each(this.port, (key: any, index) => {
        let value = _.keys(key)
        _.each(value, (keys) => {
          if (this.metaData.length == 0) {
            index = {
              field: _.trim(keys),
              title: _.upperFirst(keys),
              width: 40,
              locked: false,
              filterable: true
            }
            this.metaData.push(index)
            this.metaData = _.uniqBy(this.metaData, "field")
          }
          else if (this.metaData[index] != keys) {
            index = {
              field: _.trim(keys),
              title: _.upperFirst(keys),
              width: 40,
              locked: false,
              filterable: true

            }
            this.metaData.push(index)
            this.metaData = _.uniqBy(this.metaData, "field")
          }
        });
      });
    });
    //Getting Data To connected Services Data
    this.conectedServicesinfo$ = this._store.select(state => state.discoveryResultDetails.hostConectedServicesData);
    this.conectedServicesinfo$.subscribe((element: any[]) => {
      this.conectedServicesinfo = element;
      _.each(this.conectedServicesinfo, (key: any, index) => {
        let value = _.keys(key)
        _.each(value, (keys) => {
          if (this.metaDataService.length == 0) {
            index = {
              field: _.trim(keys),
              title: _.upperFirst(keys),
              width: 40,
              locked: false,
              filterable: true
            }
            this.metaDataService.push(index)
            this.metaDataService = _.uniqBy(this.metaDataService, "field")
          }
          else if (this.metaDataService[index] != keys) {
            index = {
              field: _.trim(keys),
              title: _.upperFirst(keys),
              width: 40,
              locked: false,
              filterable: true

            }
            this.metaDataService.push(index)
            this.metaDataService = _.uniqBy(this.metaDataService, "field")
          }
        });
      });
    });

  }

}
