import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Task } from 'src/app/release-management/models/release.model';

@Component({
  selector: 'app-task-results',
  templateUrl: './task-results.component.html',
  styleUrls: ['./task-results.component.scss']
})
export class TaskResultsComponent implements OnInit {
  @Input('task') task: Task;
  //@Input('taskResults') taskResults: any;
  constructor(public activeModal: NgbActiveModal) { }

  ngOnInit() {
  }

  getStatusBadge(statusCode: number) {
    if (statusCode) {
      if (statusCode === 4) {
        return 'badge-success custom-badge-success';
      } else if (statusCode === 5) {
        return 'badge-danger';
      }
      else if (statusCode === 3) {
        return 'custom-badge-Inprogress';
      } else if (statusCode === 2) {
        return 'custom-badge-configured';
      } else if (statusCode === 1) {
        return 'custom-badge-defined';
      } else {
        return 'badge-warning';
      }
    }
  }
  
  innerExists(value:any){
    if(Object.keys(value).length > 0){
      return true;
    }else{
      return false;
    }
  }
}
