import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'matilda-release-pie-chart',
  templateUrl: './release-pie-chart.component.html',
  styleUrls: ['./release-pie-chart.component.scss']
})
export class ReleasePieChartComponent implements OnInit {
  options: any;
  constructor() { }

  ngOnInit() {
    this.options = {
      title: {
        text: '',
        subtext: 'Project Deployment Statastics',
        x: 'center'
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)'
      },
      legend: {
        x: 'center',
        y: 'bottom',
        data: ['Completed', 'Delayed', 'In Progress']
      },
      color: ['#8BC34A', '#ffb822', '#36a3f7', '#d48265', '#91c7ae', '#749f83', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'],
      calculable: true, 
      series: [
        {
          name: '',
          type: 'pie',
          radius: '55%',
          center: ['40%', '50%'],
          data: [
            { value: 2, name: 'Completed' },
            { value: 2, name: 'Delayed' },
            { value: 5, name: 'In Progress' }
          ]
        }
      ]
    };
  }


}
