import { State, StateContext, Selector, Action } from "@ngxs/store";
import { HttpClient } from "@angular/common/http";
import { Project } from "../models/project.model";
import { deserialize } from "json-typescript-mapper";
import { InitializeDropDowns, GETEnvironmenTypes } from '../actions/release-planning-dropdowns.action';
import { Platform } from "../models/platform.model";
import { ReleaseMgmtAppSettings } from "../release-management.settings";
import { ConfigService } from '../../app-config.service';
import { VNFNode } from "../models/VNFNodeType.model";
import { ReleaseType } from "../models/releaseType.model";
import { EnvironmentType } from "../models/environmentType.model";

export class ReleasePlanningDropdownsModel {
    releaseTypeOptions: any[];
    existingReleaseNames: any[];
    projectOptions: Project[];
    platformOptions: any[];
    VNFNodeTypes: any[];
    releaseVersionOptions: any[];
    availableArtifacts: any[];
    availableEnvironmentTypes: any[]
}

@State<ReleasePlanningDropdownsModel>({
    name: 'releasePlanDropdowns',
    defaults: {
        releaseTypeOptions: [],
        existingReleaseNames: [],
        projectOptions: [],
        platformOptions: [],
        VNFNodeTypes: [],
        releaseVersionOptions: [],
        availableArtifacts: [],
        availableEnvironmentTypes: []
    }
})

export class ReleasePlanningDropdownsState {
    constructor(private _http: HttpClient, private _releaseMgmtAppSettings: ReleaseMgmtAppSettings, private _ConfigService: ConfigService) {
    }

    @Selector()
    static releaseTypeOptions(state: ReleasePlanningDropdownsModel) {
        return state.releaseTypeOptions;
    }
    @Selector()
    static existingReleaseNames(state: ReleasePlanningDropdownsModel) {
        return state.existingReleaseNames;
    }
    @Selector()
    static projectOptions(state: ReleasePlanningDropdownsModel) {
        return state.projectOptions;
    }
    @Selector()
    static platformOptions(state: ReleasePlanningDropdownsModel) {
        return state.platformOptions;
    }
    @Selector()
    static releaseVersionOptions(state: ReleasePlanningDropdownsModel) {
        return state.releaseVersionOptions;
    }
    @Selector()
    static availableArtifacts(state: ReleasePlanningDropdownsModel) {
        return state.availableArtifacts;
    }
    @Selector()
    static VNFNodeTypeOptions(state: ReleasePlanningDropdownsModel) {
        return state.VNFNodeTypes;
    }
    @Selector()
    static availableEnvironmentTypes(state: ReleasePlanningDropdownsModel) {
        return state.availableEnvironmentTypes;
    }
    @Action(InitializeDropDowns)
    InitializeDropDowns(context: StateContext<ReleasePlanningDropdownsModel>) {
        const state = context.getState();

        this._http.get(this._releaseMgmtAppSettings.getURL('GET_RELEASE_TYPES'))
            .subscribe(
                (data: any[]) => {
                    state.releaseTypeOptions = data.map(_releaseType => deserialize(ReleaseType, _releaseType));
                    context.patchState(state);
                }
        );

        this._http.get(this._releaseMgmtAppSettings.getURL('GET_RELEASE_NAMES'))
            .subscribe(
                (data: any) => {
                    state.existingReleaseNames = data.release_names;
                    context.patchState(state);
                }
            );


        this._http.get(this._releaseMgmtAppSettings.getURL('GET_PROJECTS'))
            .subscribe(
                (data: any[]) => {
                    if (data) {
                        let mappedProjects = data.map(_project => deserialize(Project, _project));
                        state.projectOptions = mappedProjects;
                        context.patchState(state);
                    }
                }
            );
        //platformOptions Dropdown service
        this._http.get(this._releaseMgmtAppSettings.getURL('GET_PLATFORMS'))
            //this._http.get('../../../assets/mockdata/releasemanagement/release-planning/platformOptions.json')
            .subscribe(
                (data: any[]) => {
                    if (data) {
                        let mappedPlatforms = data.map(_platform => deserialize(Platform, _platform));
                        state.platformOptions = mappedPlatforms;
                        context.patchState(state);
                    }
                }
            );

        //releaseVersionOptions Dropdown service
        this._http.get('../../../assets/mockdata/release-management/RELEASE_VERSIONS.json')
            .subscribe(
                (data: any[]) => {
                    state.releaseVersionOptions = data;
                    context.patchState(state);
                }
            );

        //VNF Node Types & Sites 
        this._http.get(this._releaseMgmtAppSettings.getURL('GET_VNF_NODE_TYPES'))
            //this._http.get('../../../assets/mockdata/releasemanagement/release-planning/platformOptions.json')
            .subscribe(
                (data: any[]) => {
                    if (data) {
                        let mappedVNFNodeTypes = data.map(_vnfType => deserialize(VNFNode, _vnfType));
                        state.VNFNodeTypes = mappedVNFNodeTypes;
                        context.patchState(state);
                    }
                }
            );

         //VNF Upgrade Type Options 
         this._http.get('../../../assets/mockdata/release-management/VNF_UPGRADE_TYPES.json')
         //this._http.get('../../../assets/mockdata/releasemanagement/release-planning/platformOptions.json')
         .subscribe(
             (data: any[]) => {
                 if (data) {
                     let mappedVNFNodeTypes = data.map(_vnfType => deserialize(VNFNode, _vnfType));
                     state.VNFNodeTypes = mappedVNFNodeTypes;
                     context.patchState(state);
                 }
             }
         );
        //availableArtifacts Dropdown service
        this._http.get('../../../assets/mockdata/release-management/AVAILABLE_ARTIFACTS.json')
            .subscribe(
                (data: any) => {
                    state.availableArtifacts = data.artifact_types;
                    context.patchState(state);
                }
            );
    }

    @Action(GETEnvironmenTypes)
    getEnvironmenTypes(context: StateContext<ReleasePlanningDropdownsModel>) {
        const state = context.getState();
        this._http.get(this._releaseMgmtAppSettings.getURL('GET_ENVIRONMENT_TYPES'))
         //this._http.get('../../../assets/mockdata/releasemanagement/release-planning/platformOptions.json')
         .subscribe(
             (data: any[]) => {
                 if (data) {
                     let mappedEnvironmentTypes = data.map(_environmentType => deserialize(EnvironmentType, _environmentType));
                     state.availableEnvironmentTypes = mappedEnvironmentTypes;
                     context.patchState(state);
                 }
             }
         );
    }

}
