  //            state.discoverydeviceslist = data["Total_Devices"];
             
  //                         context.setState(state);
  //          }
  //        }
  //        );
   
     
  // }
  

    @Action(FetchDiscoveryServerList)
    fetchDiscoveryServerList(context: StateContext<DiscoverDashboardModel>,action:FetchDiscoveryDeviceList) {
        var state=context.getState();
            //return this._http.get('../../../assets/mockdata/Discovery/Server.json')
            return this._http.get(this._discoverySettings.getNetworkURL('NETWORK_SERVERS_OS_DEVICES_URL'))
              .subscribe((data: any[]) => {
           if (data) {
             state.discoveryserverslist = data["Application_Services"];
             state.discoveryoslist = data["Operating_Systems"];
             state.discoveryserviceslist = data["Services"];
             state.discoverydeviceslist = data["Devices"];
             
                          context.setState(state);
           }
         }
         );
   
     
  }
  

  @Action(FetchDiscoveryOsVersionList)
  fetchDiscoveryOsVersionList(context: StateContext<DiscoverDashboardModel>,action:FetchDiscoveryDeviceList) {
    var state=context.getState();
              return this._http.get('../../../assets/mockdata/Discovery/discoveryosversions.json')
                .subscribe((data: any[]) => {
             if (data) {
               state.discoveryosversionslist = data['OperatingSystemVersions'];
              
               state.discoverydeviceinformationlist=data['DeviceInformation'];
               state.cpu= data["UtilizationInformation"] ;
              
              // console.log(JSON.stringify(state.cpu[0]))
               
                            context.setState(state);
             }
           }
           );
   
}


