import { AvailableWorkflowTemplateVersions, TemplateVersion } from './../../../models/available-workflow-templates.model';
import { Component, OnInit, Output, Input, EventEmitter, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms'
import { Select, Store } from '@ngxs/store';
import { AvailableWorkflowTemplatesState } from '../../../state/workflow-schedule.state';
import { WorkFlowProperties } from '../../../models/release.model';
import { Frequency } from 'src/app/release-management/models/frequency.model';
import { WorkflowAvailableServicesState } from 'src/app/release-management/state/workflow-available-services.state';
import { Observable } from 'rxjs/Observable';
import { PlanWorkflowSchedule } from 'src/app/release-management/actions/workflow-schedule.action';
import { WorkflowSchedule } from 'src/app/release-management/models/workflow-schedule.model';

@Component({
    selector: 'schedule-form',
    templateUrl: './schedule-form.component.html',
    encapsulation: ViewEncapsulation.None,
    styles: []
})
export class ScheduleComponent implements OnInit {
    public scheduleForm: FormGroup;
    public isDisabledtemplateVersion: boolean = true;
    templateVersionOptions: AvailableWorkflowTemplateVersions[] = [];
    versionOptions: any[] = [];
    public defaultItem: string = "Select Template";
    @Input('data') data: WorkFlowProperties;
    @Select(AvailableWorkflowTemplatesState.availableWorkflowTemplates) templateOptions$: Observable<any[]>;
    @Select(AvailableWorkflowTemplatesState.availableWorkflowTemplateVersions)
    templateVersionOptions$: Observable<AvailableWorkflowTemplateVersions[]>;
    @Select(WorkflowAvailableServicesState.frequencyTypes) frequencyTypeOptions$: Observable<Frequency[]>;
    @Select(AvailableWorkflowTemplatesState.workflowSchedule) workflowSchedule$: Observable<WorkflowSchedule[]>;
    workflowSchedule: WorkflowSchedule;
    @Output('saveSchedule') saveSchedule: EventEmitter<any> = new EventEmitter();

    constructor(private _fb: FormBuilder,
        private _store: Store) { }

    ngOnInit() {
       
        this.templateVersionOptions$.subscribe((_templateVersions) => {
            _templateVersions.forEach((_templateVersion) => {
                this.templateVersionOptions.push(_templateVersion);
            });
        });

        this.workflowSchedule$.subscribe((_workflowSchedule) => {
            if(_workflowSchedule.length > 0){
                this.workflowSchedule = _workflowSchedule[0];
            }else{
                let templateVersion= new TemplateVersion();
                let frequency = new Frequency();
                this.workflowSchedule = new WorkflowSchedule('','',templateVersion,frequency);
            }
            
        });
         this.initForm();
    }

    initForm() {
        this.scheduleForm = this._fb.group({
            name: [this.workflowSchedule.name, [
                Validators.required,
                Validators.minLength(5),
                Validators.maxLength(30),
                this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
                this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_-]+$'), { 'remainingpattern': 'should not contain special characters' }),
            ]],
            templateName: [this.workflowSchedule.templateName],
            templateVersion: [this.workflowSchedule.templateVersion, Validators.nullValidator],
            frequency: [this.workflowSchedule.frequency, Validators.required],
        });

        this.scheduleForm.get('templateName').valueChanges.subscribe(selectedTemplate => {
            this.versionOptions = [];
            if (selectedTemplate !== null && selectedTemplate !== '' && selectedTemplate.toLowerCase() !== 'select template') {
                this.templateVersionOptions.forEach((_template) => {
                    if (_template.templateName === selectedTemplate) {
                        this.versionOptions = _template.templateVersions;
                        this.isDisabledtemplateVersion = false;
                    }
                });
                this.templateVersion.setValidators([Validators.required]);
            } else {
                this.templateVersion.setValidators([Validators.nullValidator]);
                this.isDisabledtemplateVersion = true;
            }
            this.templateVersion.setValue(null);
            this.templateVersion.updateValueAndValidity();
        });
    }

    templateSelected(event: any) {
       
    }
    onSubmit(form: any) {
        if (this.scheduleForm.valid) {
            this.saveSchedule.emit(form);
            this._store.dispatch(new PlanWorkflowSchedule(form));
        } else {
            // this.validateAllFormFields(this._scheduleForm);
        }
    }
    get name() {
        return this.scheduleForm.get('name');
    }
    get templateName() {
        return this.scheduleForm.get('templateName');
    }

    get templateVersion() {
        return this.scheduleForm.get('templateVersion');
    }
    get frequency() {
        return this.scheduleForm.get('frequency');
    }

    regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } => {
            if (!control.value) {
                return null;
            }
            const valid = regex.test(control.value);
            return valid ? null : error;
        };
    }
}
