import { JsonProperty } from "json-typescript-mapper";
import { Type } from "@angular/compiler";
export class HostPackageModel {
    @JsonProperty('PackageName')
    Package_Name: string = '';
    @JsonProperty('PackageVersion')
    PackageVersion: string = '';
}
export class HostServicesModel{
    @JsonProperty('ServiceName')
    Service_Name: string = '';
    @JsonProperty('Servicetype')
    Service_type: string = '';
    @JsonProperty('Status')
    Service_Status: string = '';
    @JsonProperty('AdditionalDetails')
    additional_details: undefined ;
   
}
export class HostStorageModel{
    @JsonProperty('Available_Space')
    Available_Space: string = '';
    @JsonProperty('Disk')
    Disk: string = '';
    @JsonProperty('File_System')
    File_System: string = '';
    @JsonProperty('Group')
    Group: string = '';
    @JsonProperty('Logical_Volume')
    Logical_Volume: string = '';
    @JsonProperty('Type')
   Type: string = '';
    @JsonProperty('Mounted')
    Mounted: string = '';
    @JsonProperty('Owner')
    Owner: string = '';
    @JsonProperty('Partition')
    Partition: string = '';
    @JsonProperty('Permissions')
    Permissions: string = '';
    @JsonProperty('Capacity')
    Total_Space: string = '';
    @JsonProperty('Used_Percent')
    Used_Percent: string = '';
    @JsonProperty('Used(GB)')
    Used_Space: string = '';
    
   
}