import { Component, OnInit, Input } from '@angular/core';
import { Task, Stage } from '../../../models/release.model';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Select, Store } from '@ngxs/store';
import { DynamicWorkflowState } from '../../../state/dynamic-workflow.state';
import { Observable } from 'rxjs';
import { CloneTask } from '../../../actions/workflow.action';

@Component({
  selector: 'matilda-clone-task',
  templateUrl: './clone-task.component.html',
  styleUrls: ['./clone-task.component.scss']
})
export class CloneTaskComponent implements OnInit {
  @Input('task') task: Task;
  @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
 
  constructor(public activeModal: NgbActiveModal,
    private _store: Store) { }

  ngOnInit() {
  }

  onStageSelect(selectedStageId: string) {
    let cloneTask: Task = Object.assign({}, this.task,{
      taskName: '',
      id: this.task.id + Math.floor((Math.random() * Math.pow(10, 3)))
    });
     let newTask = new Task();
     newTask.id = cloneTask.id;
     newTask.taskName = cloneTask.taskName;
     newTask.actionId =  cloneTask.actionId;
     newTask.actionName =  cloneTask.actionName;
     newTask.actualEndDate =  cloneTask.actualEndDate;
     newTask.actualStartDate =  cloneTask.actualStartDate;
     newTask.configuration =  cloneTask.configuration;
     newTask.duration =  cloneTask.duration;
     newTask.ignoreFailure =  cloneTask.ignoreFailure;
     newTask.output =  cloneTask.output;
     newTask.owner =  cloneTask.owner;
     newTask.plannedEndDate =  cloneTask.plannedEndDate;
     newTask.postConditions =  cloneTask.postConditions;
     newTask.preConditions =  cloneTask.preConditions;
     newTask.serviceId =  cloneTask.serviceId;
     newTask.serviceName =  cloneTask.serviceName;
     newTask.stageId =  cloneTask.stageId;
     newTask.status =  cloneTask.status;
     newTask.taskGroupId =  cloneTask.taskGroupId;
     newTask.taskId =  cloneTask.taskId;
    this._store.dispatch(new CloneTask({ selectedStageId, newTask}));
    this.activeModal.close();
  }
  
  cancelClone() {
    this.activeModal.close();
  }

}
