import { Component, OnInit, OnChanges, ViewChild, ElementRef, Input, ViewEncapsulation, SimpleChanges, AfterViewInit } from '@angular/core';
import * as d3 from 'd3';
// npm install --save glyphicons
import icons from 'glyphicons';

@Component({
  selector: 'matilda-topology-tree',
  templateUrl: './topology-tree.component.html',
  styleUrls: ['./topology-tree.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TopologyTreeComponent implements AfterViewInit {

  @ViewChild('table') private chartContainer: ElementRef;
  @ViewChild('tree') private chartContainer1: ElementRef;
   @Input() private data: any;
  private chartId: any;
  private element: any;
  private tooltip: any;
  private nodes: any;
  private margin = { top: 20, right: 120, bottom: 20, left: 120 };
  private width = 960 - this.margin.right - this.margin.left;
  private height = 800 - this.margin.top - this.margin.bottom;
 // private values = this.dataInfo2
  private i = 0;
  private j = 0;
  private duration = 350;
  private root;
  private root1;
  private barHeight = 20;
  //  private barWidth = (this.width - this.margin.left - this.margin.right) * 0.8;
  private barWidth = 150;
  private svg: any;
  private treemap = d3.tree().size([this.height, this.width]);
  private svgtree: any;
  
  constructor(private elementRef: ElementRef) {
  }


  ngAfterViewInit() {

  
 
    let native = this.elementRef.nativeElement;
    this.chartId = native.getAttribute('data-id') || 'tree-id-' + Date.now();
    this.element = this.chartContainer.nativeElement;
    this.svgtree = d3.select(".test").append('svg')
      .attr('id','topology-tree')
      .attr("width", this.width-250) // + margin.left + margin.right)
      .append("g")
      .attr("transform", "translate(" + (this.margin.left -50) + "," + this.margin.top + ")");
      this.root = d3.hierarchy(this.data);
    // d3.json("../../assets/flare.json").then(data => {
    //   this.root = d3.hierarchy(data);
      this.root.x0 = 0;
      this.root.y0 = 0;
      this.root.children.forEach(this.collapse);
      this.updateTree(this.root, "table");
       
    
    this.svg = d3.select('.d3-topologytree-chart').append('svg')
      .attr('id', 'topologytree-svg')
      
      .attr('width', "2000")
      //.attr('height', this.height)
      .attr('height', "800")
      .append("g")
      .attr("transform", "translate("
        + this.margin.left + "," + this.margin.top + ")")
    this.root = d3.hierarchy(this.data, function (d) { return d.children; });
    this.root.x0 = this.height / 2;
    this.root.y0 = 0;
    this.root.children.forEach(this.collapse);
    this.updateTree(this.root, "tree");

    // d3.select(self.frameElement).style("height", "800px");
  }
  private collapse = (d: any) => {
    if (d.children && d.depth > 2) {
      d._children = d.children;
      d._children.forEach(this.collapse);
      d.children = null;
    } else if (d.children) {
      d.children.forEach(this.collapse);
    }
  }
  private diagonals = () => {
    return d3.linkHorizontal()
      .x((d) => { return d.y; })
      .y((d) => { return d.x; });
  }

  private click = (d) => {
    if (d.children) {
      d._children = d.children;
      d.children = null;
    }
    else {
      d.children = d._children;
      d._children = null;
    }
    this.updateTree(d, "tree");
    this.updateTree(d, "table");
  }


  //recursively collapse children
  // private collapse = (d: any) => {
  //   if (d.children) {
  //     d._children = d.children;
  //     d.children = null;
  //     d._children.forEach((item) => this.collapse(item));
  //   }
  // }
  diagonal(s, d) {
    let path = `M ${s.y} ${s.x}
            C ${(s.y + d.y) / 2} ${s.x},
              ${(s.y + d.y) / 2} ${d.x},
              ${d.y} ${d.x}`
    return path
  }
  updateTree(source: any, value: any) {
    this.nodes = this.root.descendants();
    var treeData = this.treemap(this.root);
    // Compute the new tree layout.
    let nodes = treeData.descendants();
    let links = treeData.descendants().slice(1);
    if (value == "table") {
      // Assigns the x and y position for the nodes

      this.height = Math.max(500, this.nodes.length * this.barHeight + this.margin.top + this.margin.bottom);

      // d3.select("svg").transition()
      //   .duration(this.duration)
      //   .attr("height", this.height);

      d3.select(self.frameElement).transition()
        .duration(this.duration)
        .style("height", this.height + "px");
      let index = -1;
      this.root.eachBefore((d) => {
        d.x = ++index * this.barHeight;
        d.y = d.depth * 20;
      });
      var node = this.svgtree.selectAll("g.node")
        .data(this.nodes, (d:any)=> { return d.id || (d.id = ++this.i); });

      var nodeEnter = node.enter()
        .append("g")
        .attr("class", "node")
        .attr("transform", (d) => { return "translate(" + source.y0 + "," + source.x0 + ")"; })
        .style("opacity", 0);

      nodeEnter.append("rect")
        .attr("y", -this.barHeight / 2)
        .attr("height", this.barHeight)
        .attr("width", this.barWidth)
        .style("fill", this.color)
        .on("click", (d) => {
          this.click(d);
        })

        nodeEnter.append("text")
        .attr("dy", 3.5)
        .attr("dx", 5.5)
        .text((d) => {let icon= d._children ? icons.plus : d.children ? icons.hole : "" ;
        return icon + " " + d.data.name });

      nodeEnter.transition()
        .duration(this.duration)
        .attr("transform", (d) => { return "translate(" + d.y + "," + d.x + ")"; })
        .style("opacity", 1);

      node.transition()
        .duration(this.duration)
        .attr("transform", (d) => { return "translate(" + d.y + "," + d.x + ")"; })
        .style("opacity", 1)
        .select("text")
.text((d) => { let icon = d.children ? icons.hole
: d._children ? icons.plus : "" ;
return icon +" "+ d.data.name; })
      // Transition exiting nodes to the parent's new position.
      node.exit().transition()
        .duration(this.duration)
        .attr("transform", (d) => { return "translate(" + source.y + "," + source.x + ")"; })
        .style("opacity", 0)
        .remove();

      var link = this.svgtree.selectAll(".link")
        .data(this.root, (d) => { return d.target.id; });
      link.enter().insert("path", "g")
        .attr("class", "link")
        .attr("d", (d) => {
          return this.diagonals
        })
        .transition()
        .duration(this.duration)
      // Transition links to their new position.
      link.transition()
        .duration(this.duration)
        .attr("d", this.diagonals);
      // Transition exiting nodes to the parent's new position.
      link.exit().transition()
        .duration(this.duration)
        .attr("d", (d) => {
          let o = { x: source.x, y: source.y };
          return this.diagonals;
        })
        .remove();


      this.root.each((d) => {
        d.x0 = d.x;
        d.y0 = d.y;
      });
    }
    else {
      // Normalize for fixed-depth.
      this.nodes.forEach(function (d) { d.y = d.depth * 180; });
      // ****************** Nodes section ***************************
      // Update the nodes..
      let node = this.svg.selectAll("g.node")
        .data(this.nodes, (d: any) =>{ return d.id || (d.id = ++this.i); });
      // Enter any new nodes at the parent's previous position.
      let nodeEnter = node.enter()
        .append("g")
        .attr("class", "node")
        .attr("transform", function (d) { return "translate(" + source.y + "," + source.x + ")"; })
        // .attr("transform",function (d) { return  "translate(" + d.x + "," + d.y + ")";})
        .on("click", (d) => {
          this.click(d);
        })
      // var nodeRect = nodeEnter.append("rect")
      //   .attr('class', 'node')
      //   .attr('rx', 4)
      //   .attr('ry', 4)
      //   // .attr("y", (d) => { return d.children || d._children ? -7 : 7; })
      //   .attr("y", -14)
      //   .attr("height", 25)
      //   .style("fill", function (d) {
      //     return d._children ? "lightsteelblue" : "dodgerblue";
      //   });
      nodeEnter.append("circle")
      .attr("r", 1e-6)
      .style("fill", function(d) { return d._children ? "lightsteelblue" : "dodgerblue"; });
      // nodeEnter.append("image")
      //   .attr("xlink:href", function (d) { return "https://thumb.ibb.co/hHja4x/box.png"; })
      //   .attr("x", "-12px")
      //   .attr("y", "-12px")
      //   .attr("width", "24px")
      //   .attr("height", "24px");
    
      nodeEnter.append("text")
        .attr("x", function (d) { return d.children || d._children ? -14 : 14; })
        .attr("dy", ".35em")
        .attr("text-anchor", (d) => { return d.children || d._children ? "end" : "start"; })
        .text(function (d) { return d.data.name; })
        .style("fill", "black");


      // nodeRect.attr("width", function (d) {
      //   return this.nextSibling.nextSibling.getComputedTextLength() + 32
      // })
      //   .attr("x", function (d, i) {
      //     return d._children || d.depth === 0 ? -(this.nextSibling.nextSibling.getComputedTextLength() + 18) : -14
      //   });


      // nodeText.attr("x", function (d, i) {
      //   return d._children || d.depth === 0 ? -(this.nextSibling.nextSibling.getComputedTextLength() + 18) : -(this.nextSibling.nextSibling.getComputedTextLength() + 18)
      // })
      // Add labels for the nodes


      // Transition nodes to their new position.
      var nodeUpdate = nodeEnter.merge(node);
      nodeUpdate.transition()
        .duration(this.duration)
        //vertical
        // .attr("transform", function (d) { return "translate(" + d.x + "," + d.y + ")"; });
        //Horizontal
        .attr("transform", (d) => { return "translate(" + d.y + "," + d.x + ")"; });

        nodeUpdate.select("circle")
	  .attr("r", 6)
	  .style("fill", function(d) { return d._children ? "lightsteelblue" : "#fff"; });

      // nodeUpdate.select("rect.node")
      //   .attr('rx', 4)
      //   .attr('ry', 4)
      //   .attr("x", function (d, i) {
      //     return d._children || d.depth === 0 ? -(this.nextSibling.nextSibling.getComputedTextLength() + 18) : -14
      //   })
        // .attr("x",function (d) { return d.children || d._children ? -13 : 13; })
        // .attr("y", function (d) { return d.children || d._children ? -14 : -14; })
        // .attr("height", 25)
        // .style("fill", function (d) {
        //   return d._children ? "lightsteelblue" : "dodgerblue";
        // })
      //   .attr("width", function (d) {
      //     console.log(this.nextSibling.nextSibling.getComputedTextLength())
      //     return this.nextSibling.nextSibling.getComputedTextLength() + 32;
      //   })
      //   .attr("x", function (d, i) {
      //     return d._children || d.depth === 0 ? -(this.nextSibling.nextSibling.getComputedTextLength() + 18) : -14
      //   })
      //   .attr('cursor', 'pointer');
      //   .style("fill-opacity", 1);

      nodeUpdate.select("text")
      .style("fill-opacity", 1);
      // nodeUpdate.select("text")
      //   .attr("text-anchor", function (d) { return d._children || d.depth == 0 ? "end" : "start"; })
      //   .attr("x", function (d) { return d._children || d.depth == 0 ? -14 : 14; })

      // Transition exiting nodes to the parent's new position.

      var nodeExit = node.exit().transition()
      .duration(this.duration)
      .attr("transform", function(d) { return "translate(" + source.y + "," + source.x + ")"; })
      .remove();
  
    nodeExit.select("circle")
      .attr("r", 1e-6);
  
    nodeExit.select("text")
      .style("fill-opacity", 1e-6);


      // var nodeExit = node
      //   .exit()
      //   .transition()
      //   .duration(this.duration)
      //   .attr("transform", function (d) { return "translate(" + source.y + "," + source.x + ")"; })
      //   .remove();

      // nodeExit.select("text")
      //   .style("fill-opacity", 1e-6);
      // ****************** links section ***************************
      // Update the links
      var link = this.svg.selectAll("path.link")
        .data(links, function (d: any) { return d.id; });

      // Enter any new links at the parent's previous position.
      var linkEnter = link.enter().insert("path", "g")
        .attr("class", "link")
        .attr("d", (d) => {
          var o = { x: source.x, y: source.y };
          return this.diagonal(o, o);
        });
      var linkUpdate = linkEnter.merge(link);
      // Transition links to their new position.
      linkUpdate.transition()
        .duration(this.duration)
        .attr("d", function (d) {
          //Horizontal structure
          var path = `M ${d.y} ${d.x}
      C ${(d.y + d.parent.y) / 2} ${d.x},
        ${(d.y + d.parent.y) / 2} ${d.parent.x},
        ${d.parent.y} ${d.parent.x}`;
          // vertical structure
          // var path = `M ${d.x} ${d.y}
          //  C ${d.x} ${(d.y + d.parent.y) / 2}
          // ${d.parent.x} ${(d.y + d.parent.y) / 2}
          // ${d.parent.x} ${d.parent.y}`;
          return path
        })

      // Transition exiting nodes to the parent's new position.
      link
        .exit()
        .transition()
        .duration(this.duration)
        .attr("d", (d) => {
          var o = { x: source.x, y: source.y };
          return this.diagonal(o, o);
        })
        .remove();

      // Stash the old positions for transition.
      nodes.forEach(function (d: any) {
        d.x = d.x;
        d.y = d.y;
      });
    }
  }



  color(d) {
    return d._children ? "#fff" : d.children ? "#fff" : "#fff";
  }

}

