import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Store } from '@ngxs/store';
import { StartEnvironmentRelease } from '../../actions/release-planning.action';

@Component({
  selector: 'matilda-re-hydrate',
  templateUrl: './re-hydrate.component.html',
  styleUrls: ['./re-hydrate.component.scss']
})
export class ReHydrateComponent implements OnInit {
  public reHydrateEnvironmentForm: FormGroup;
  saveMessage: boolean;
  @Input('selectedEnvironment') selectedEnvironment: any;
  constructor(private _fb: FormBuilder,
    private _store: Store) { }

  ngOnInit() {
    this.initForm();
    console.log(this.selectedEnvironment);
  }

  initForm(){
    this.reHydrateEnvironmentForm = this._fb.group({
      environmentName: [this.selectedEnvironment.environmentName || ''],
      amiId: ['ami-06b3cbe9a4'],
    });
  }

  onSubmit(form: any){
    if(this.reHydrateEnvironmentForm.valid){
      // let newEnvironment = new ReleaseEnvironment();
      // newEnvironment.environmentName = form.environmentName;
      // newEnvironment.environmentType = form.environmentType;
      // newEnvironment.dependentEnvironment = form.dependentEnvironment || '';
      // newEnvironment.releaseId = 0;
      
      this.saveMessage = true;
      setTimeout(() => {
        this.saveMessage = false;
      }, 3000);
      this._store.dispatch(new StartEnvironmentRelease(this.selectedEnvironment)); 
    }
  }

  // get environmentName(){
  //   return this.reHydrateEnvironmentForm.get('environmentName');
  // }

  // get environmentType(){
  //   return this.reHydrateEnvironmentForm.get('amiId');
  // }


}
