import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Store, Actions } from '@ngxs/store';
import { Router } from '@angular/router';
@Component({
  selector: 'matilda-discovery-services',
  templateUrl: './discovery-services.component.html',
  styleUrls: ['./discovery-services.component.scss']
})
export class DiscoveryServicesComponent implements OnInit,AfterViewInit {
  public discoveryservice$ :any;
  public servicepiedata:any;
  public options:any;

  constructor(private _store: Store, private router: Router, private actions$: Actions) { }
ngOnInit(){}
   ngAfterViewInit() {
    this.discoveryservice$ = this._store.select(state => state.discoveryDashboardDetails.discoveryserviceslist);
    this.discoveryservice$   .subscribe((element) => {
       // this.operatingsystemdata= x;
      //Assigining/changing the JSON in key/value format
      var output = Object.entries(element).map(([name, value]) => ({name,value}));
      output = output.filter(function( obj ) {
         return obj.value != 0;
     });
    
      this.servicepiedata= output;
     

      this.options= {
        title: {
          text:'Services ', 
          x:'center'
           },
        tooltip: {
        trigger:'item',
        // formatter:"{b} : {c} ({d}%)"
        formatter:"{b} : {c} ({d}%)"
         },
        legend: {
        type:'scroll',
        bottom:120,
        
          
        },
        
        series: [
         {
          // color:['#34bfa3','#ffb822', '#f4516c', '#36a3f7','#87bdd8', '#91c7ae','#749f83', '#ca8622'],
          color:['#1873b9','#62bdea','#607B8B', '#61b0bc', '#afd7dd','#ddeef0', '#edf6f7','#cccccc', '#ca8622'],
        label: {
        normal: {
        show:true,
        position:'inside',
        
        
        formatter:'{c}',
        avoidLabelOverlap:true,
        // align: 'center',
        fontSize:6,
         }, 
        
        },
        
        type:'pie',
        radius: '58%',
        center: ['50%','33%', '100%','50%'],
        data:this.servicepiedata, 
        itemStyle: {
        emphasis: {
        shadowBlur:10,
        shadowOffsetX:0,
        // shadowColor: 'rgba(0, 0, 0, 0.5)'
         }
         }
         }
         ]
         };
      
                     });
  }

}
