  //sending host id to service call and host ip to summary page for displaying ip on top
  getResults(HOST_ID: any, Host_IP: any) {
    localStorage.setItem("networkDiscoveryIdHost", HOST_ID);
    localStorage.setItem("networkDiscoveryHostIP", Host_IP);
    this._store.dispatch(new FetchDiscoveryHostResultList({ "discoveryIdHost": HOST_ID }));
   this._store.dispatch(new FetchDiscoveryHostUtilization());


  }

  //status color
  getStatusBadge(Status: string) {
    if (Status) {
      if (Status.toLowerCase() === 'active' || Status.toLowerCase() === 'complete' || Status.toLowerCase() === 'success') {
        return 'm-badge--success';
        // return 'comp-color';
      } else if (Status.toLowerCase() === 'failed') {
        return 'm-badge--danger';
      }
      else if (Status.toLowerCase() === 'submitted') {
        return 'm-badge--primary';
      }
      else if (Status.toLowerCase() === 'in_progress' ) {
        return 'm-badge--warning';
      } else {
        return 'm-badge--brand';
      }
    }
  }

  //redirecting to dash board
  loadDiscoveryModule() {
    this.router.navigate(['/discovery/dashboard']);
  }
  //redirecting to network discovery list
  loadDiscoverylist() {
    this.router.navigate(['/discovery/list']);
  }
