import { JsonProperty } from "json-typescript-mapper";

export class ImpactedApplications {
    @JsonProperty('name')
    name: string = '';
    @JsonProperty('status')
    status: string = '';
}

export class ImpactedSystems {
    @JsonProperty('status')
    status: string = '';
    @JsonProperty('os')
    operatingsystem: string = '';
    @JsonProperty('platform')
    platform: string = '';
    @JsonProperty('ip')
    ip: string = '';
    @JsonProperty({clazz: ImpactedApplications,name:'applications'})
    impactedApplications: ImpactedApplications[] = [];
}

export class InfraEnvironments {
    @JsonProperty('name')
    environmentName: string = '';
    @JsonProperty('type')
    environmentType: string = '';
    @JsonProperty('env_id')
    environmentId: number = 0;
    @JsonProperty('create_dt')
    createDate: string = '';
    @JsonProperty('start_dt')
    startDate: string = '';
    @JsonProperty('status')
    status: string = '';
    @JsonProperty('release_id')
    releaseId: number = 0;
    @JsonProperty({clazz: ImpactedSystems,name:'impactedSystems'})
    impactedSystems: ImpactedSystems[] = [];
    
}
