import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';

@Component({
  selector: 'matilda-thirdline2',
  templateUrl: './thirdline2.component.html',
  styleUrls: ['./thirdline2.component.scss']
})
export class Thirdline2Component implements OnInit {

  @Input() public networkservers2;
  public view: Observable<GridDataResult>;
  public skip = 0;

  constructor() { }
  
  public ngOnInit(): void {
    this.view = this.networkservers2["datasources"];
}

public pageChange({ skip, take }: PageChangeEvent): void {
    this.skip = skip;
}

}
