import { Component, OnInit } from '@angular/core';
// import { GetDiscoveryServicepieList } from 'src/app/discovery/actions/discovery.action';
import { Store, Select, Actions, ofActionSuccessful } from '@ngxs/store';
import { Router } from '@angular/router';

@Component({
  selector: 'matilda-discovery-statistics',
  templateUrl: './discovery-statistics.component.html',
  styleUrls: ['./discovery-statistics.component.scss']
})
export class DiscoveryStatisticsComponent implements OnInit {
  public discoveryservice$:any;
  public servicepiedata:any;
  public options:any=[];
  public servicetable:any;

  constructor(private _store: Store, private router: Router, private actions$: Actions) { }

  ngOnInit() {
    // this._store.dispatch(new GetDiscoveryServicepieList());
    this.discoveryservice$ = this._store.select(state => state.discoverylist.discoveryservicepielist);
    this.discoveryservice$   .subscribe((x) => {
      this.servicepiedata= x;
     

      this.options= {
        title: {
          text:'Service Overview', 
          x:'center'
           },
        tooltip: {
        trigger:'item',
        // formatter:"{b} : {c} ({d}%)"
        formatter:"{b} : {c} ({d}%)"
         },
        legend: {
        type:'scroll',
        bottom:40,
          
        },
        
        series: [
         {
          color:['#34bfa3','#ffb822', '#f4516c', '#36a3f7','#87bdd8', '#91c7ae','#749f83', '#ca8622'],
        label: {
        normal: {
        show:true,
        position:'inside',
        
        
        formatter:'{c}',
        avoidLabelOverlap:true,
        // align: 'center',
        fontSize:8,
         }, 
        
        },
        
        type:'pie',
        radius: '60%',
        center: ['50%', '40%'],
        data:this.servicepiedata, 
        itemStyle: {
        emphasis: {
        shadowBlur:10,
        shadowOffsetX:0,
        // shadowColor: 'rgba(0, 0, 0, 0.5)'
         }
         }
         }
         ]
         };
      
                     });
 

  }
  public onChartEvent($event){
    $event.name;
     this.servicepiedata.forEach(item => {
     if(item.name== $event.name){
            this.servicetable= item.details
         }
   });
   }

}
