import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EnvironmentDataShareService {
  private selectedEnviornmentSource = new BehaviorSubject<any>(null);
  current_selectedEnviornmentSource = this.selectedEnviornmentSource.asObservable();

  private selectedWorkflowSource = new BehaviorSubject<any>(null);
  current_selectedWorkflowSource = this.selectedWorkflowSource.asObservable();

  constructor() { }

  changeSelectedEnvironment(selectedenvironment: any) {
    this.selectedEnviornmentSource.next(selectedenvironment);
  }

  changeSelectedWorkflow(selectedWorkflow: any) {
    this.selectedWorkflowSource.next(selectedWorkflow);
  }
}
