import { Injectable } from '@angular/core';
import { ConfigService } from '../app-config.service';
@Injectable({
    providedIn: 'root'
  })
export  class DiscoverySettings  {
    private _baseUrl:string;  
    constructor(private _configService: ConfigService) {   
       
    }
    public getNetworkURL(operation: any) {   
        this._baseUrl=this._configService.get("discovery_services").BASE_NETWORK_URL;  
        return this._baseUrl + this._configService.get("discovery_services")[operation] ;
    }

    public getServiceURL(operation: any) {
        this._baseUrl = this._configService.get('discovery_services').BASE_SERVICE_URL;
        return this._baseUrl + this._configService.get('discovery_services')[operation] ;
    }
}