import { JsonProperty } from "json-typescript-mapper";

export class DiscoveryInfoList {
    @JsonProperty('HostId')
    HOST_ID: number=0 ;
    @JsonProperty('HostIp')
    Host_IP: string = '';
    @JsonProperty('Status')
    Status: string = '';
    @JsonProperty('DeviceType')
    DeviceType: string = '';
    @JsonProperty('OperatingSystem')
    Operating_System: string = '';
    @JsonProperty('MemSize')
    Memory: string = '';
    @JsonProperty('CoresCount')
    Vcpu_Count: string = '';
    @JsonProperty('InstanceType')
    Instance_Type: string = '';
    @JsonProperty('ServiceProvider')
    Service_Provider: string = '';
    @JsonProperty('Reason')
    Reason: string = '';
}

export class DiscoverySummaryDetails{
    @JsonProperty('InstanceType')
    Instance_Type: string = '';
    @JsonProperty('ServiceProvider')
    Service_Provider: string = '';
    @JsonProperty('Reason')
    Reason: string = '';
}