import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBase } from '../../../../shared/dynamic_forms/form-base';
import { Store, Select } from '@ngxs/store';
import { SaveConfiguration } from '../../../actions/workflow.action';
import { Task, Stage } from '../../../models/release.model';
import * as CryptoJS from 'crypto-js';
import { ConfigService } from 'src/app/app-config.service';
import { DynamicWorkflowState } from 'src/app/release-management/state/dynamic-workflow.state';
import { Observable } from 'rxjs/Observable';
@Component({
  selector: 'app-release-modal-dialog',
  templateUrl: './release-modal-dialog.component.html',
  styleUrls: ['./release-modal-dialog.component.scss']
})
export class ReleaseModalDialogComponent implements OnInit {

  @Input('dataSource') dataSource: FormBase<any>[] = [];
  @Input('task') task: Task;
  @Input('stageId') stageId: string;
  @Input('previousConfig') previousConfig: any;
  @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
  stages: Stage[] = [];

  key = CryptoJS.enc.Utf8.parse('inputKey65781239');
  iv = CryptoJS.enc.Utf8.parse('inputIV657812396');

  form: FormGroup;
  saveMessage: boolean = false;
  constructor(public activeModal: NgbActiveModal,
    private _store: Store, private _configService: ConfigService) {
    this.key = this._configService.get('aesEncryption').key;
    this.iv = this._configService.get('aesEncryption').iv;
  }

  ngOnInit() {
    this.stages$.subscribe((_stages) => {
      this.stages = _stages;
    });

    if (this.dataSource.length > 0) {
      this.form = this.toFormGroup(this.dataSource);
    } else {
      this.form = this.toFormGroup();
    }

    if (this.previousConfig) {
      // var decrypted = CryptoJS.AES.decrypt(this.previousConfig, this.key, {
      //   keySize: 128 / 8,
      //   iv: this.iv,
      //   mode: CryptoJS.mode.CBC,
      //   padding: CryptoJS.pad.Pkcs7
      // });
      // let decryptedConfiguration = JSON.parse(decrypted.toString(CryptoJS.enc.Utf8));
      for (let key of Object.keys(this.previousConfig)) {
        if (typeof this.previousConfig[key] === 'string' || this.previousConfig[key] instanceof String) {
          this.form.controls[key].setValue(this.previousConfig[key]);
        } else {
          this.form.controls[key].setValue(JSON.stringify(this.previousConfig[key]));
        }

      }
    }
  }

  toFormGroup(data?: FormBase<any>[]) {
    let group: any = {};

    data.forEach(field => {
      group[field.key] = field.required ? new FormControl(field.value || '', Validators.required)
        : new FormControl(field.value || '');

    });
    return new FormGroup(group);
  }

  onSubmit(form) {
    let task = this.task;
    let stageId = this.stageId;
    let configuration = form;
    this.saveMessage = true;
    setTimeout(() => {
      this.saveMessage = false;
      this.activeModal.close();
    }, 500);

    // var encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(JSON.stringify(form)), this.key,
    //   {
    //     keySize: 128 / 8,
    //     iv: this.iv,
    //     mode: CryptoJS.mode.CBC,
    //     padding: CryptoJS.pad.Pkcs7
    //   });
    // let configuration = encrypted.toString();
    this._store.dispatch(new SaveConfiguration({ task, stageId, configuration }));
  }
  onselectFromLookup(selectedData: any) {
    // let formattedString = '{{' + selectedData.stage + ':'
    //   + selectedData.task + ':' + selectedData.variable + '}}';

    let taskList: any[] = [];
    let previousTaskList = this.form.get(selectedData.dataItem.key).value;

    let hasxcomPull = this.isJson(previousTaskList);
    if (hasxcomPull) {
      taskList = JSON.parse(previousTaskList).xcom_pull;
    }else{
      taskList = [];
    }
    
    let currentTask = Object.assign({}, {
      "task_name": selectedData.task,
      "key": selectedData.variable
    });

    taskList.push(currentTask);

    let formattedTaskList = Object.assign({}, {
      "xcom_pull": taskList
    });

    this.form.controls[selectedData.dataItem.key].setValue(JSON.stringify(formattedTaskList));
  }

  onUpdateParameterList(_parameterList: any) {
    this.form.controls['params'].setValue(JSON.stringify(_parameterList, undefined, Object.keys(_parameterList).length));
  }

  isJson(str) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}

}
