import { Component, OnInit } from '@angular/core';
import { Store, Actions } from '@ngxs/store';
import { Router } from '@angular/router';
@Component({
  selector: 'matilda-discovery-progressdeviceinformation',
  templateUrl: './discovery-progressdeviceinformation.component.html',
  styleUrls: ['./discovery-progressdeviceinformation.component.scss']
})
export class DiscoveryProgressdeviceinformationComponent implements OnInit {

  public discoverydevicelist$ :any;
  public discoverydevicelist:any;
    
  colors:Array<string> = ['#1873b9','#62bdea','#607B8B', '#61b0bc', '#afd7dd','#ddeef0', '#edf6f7','#cccccc', '#ca8622'];
  bars: Array<any> = [];

  constructor(private _store: Store, private router: Router, private actions$: Actions) { }


  ngOnInit() {
    this.discoverydevicelist$ = this._store.select(state => state.discoveryDashboardDetails.discoverydeviceinformationlist);

    this.discoverydevicelist$.subscribe((element) => {
      this.discoverydevicelist=element
      console.log(this.discoverydevicelist)
                           });
                           this.calculateBarWidth();
  }
  calculateBarWidth() {

    this.discoverydevicelist.forEach(barItem =>{
        let total:number = 0;
        barItem.data.forEach(barDataItem=>{
            total =  total + barDataItem.Value;
        });
        barItem.data.forEach((barDataItem, index) =>{
            barDataItem["percentage"] = (barDataItem.Value/total)*100;
            barDataItem["color"] = this.colors[index];
        });
    });
    this.bars = this.discoverydevicelist;
    // console.log(this.data);
  }

}
