import { Component, OnInit, Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResizeEvent } from 'angular-resizable-element';
import { FlatTreeControl, CdkTree } from '@angular/cdk/tree';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { BehaviorSubject, Observable, of } from 'rxjs';
import * as Moment from 'moment';
import { extendMoment } from 'moment-range';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
const moment = extendMoment(Moment);

import { Step } from '../../../../models/step'
import { StepFlatNode } from '../../../../models/stepFlatNode';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { Store, Select } from '@ngxs/store';
import {ChartDatabase} from './chart-database'
import { AddMilestoneComponent } from '../add-milestone.component';
import { ReleaseMilestonesState } from 'src/app/release-management/state/release-milestones.state';
import { MileStone, ReleasePlan } from 'src/app/release-management/models/release.model';
import { ReleasePlanningState } from 'src/app/release-management/state/release-planning.state';
import { Tree } from '@angular/router/src/utils/tree';
import { ClearMilestones } from 'src/app/release-management/actions/release-milestones.action';
import { DeleteconfirmComponent } from 'src/app/shared/deleteconfirm/deleteconfirm.component';

/**
 * @title Tree with nested nodes
 */
@Component({
  selector: 'app-gantt-chart',
  templateUrl: './gantt-chart.component.html',
  styleUrls: ['./gantt-chart.component.scss'],
  providers: [ChartDatabase]
})
export class GanttChartComponent  {
  @Select(ReleaseMilestonesState.milestones) milestones$: Observable<MileStone[]>;
  @Select(ReleasePlanningState.releasePlan) releaseView$: Observable<ReleasePlan[]>;
  public previousMilestones: MileStone[] = [];
  public releaseView: ReleasePlan;
  public releaseMilestonesForm: FormGroup;
  moment = moment;
  dates: string[] = []; // all days in chart
  today = moment().format('YYYY-MM-DD');
  timePeriod=3;
  /** Map from flat node to nested node. This helps us finding the nested node to be modified */
  flatNodeMap: Map<StepFlatNode, Step> = new Map<StepFlatNode, Step>();

  /** Map from nested node to flattened node. This helps us to keep the same object for selection */
  nestedNodeMap: Map<Step, StepFlatNode> = new Map<Step, StepFlatNode>();

  treeControl: FlatTreeControl<StepFlatNode>;
  treeFlattener: MatTreeFlattener<Step, StepFlatNode>;
  dataSource: MatTreeFlatDataSource<Step, StepFlatNode>;

  chartData;

  sidebarStyle = {};

  constructor(private database: ChartDatabase,private modalService: NgbModal,private _store: Store) {
    this.releaseView$.subscribe(
      (_releaseView: ReleasePlan[]) => {
        if (_releaseView.length > 0) {
          this.releaseView = _releaseView[0];          
        }
      });
     this.refreshChart()
   
  }
  refreshChart()
  {
    this.milestones$.subscribe((_milestones) => {
      if (_milestones.length > 0) {
        this.previousMilestones = _milestones;
        this.treeFlattener = new MatTreeFlattener(this.transformer, this._getLevel,
          this._isExpandable, this._getChildren);
        this.treeControl = new FlatTreeControl<StepFlatNode>(this._getLevel, this._isExpandable);
        this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
         let data=this.previousMilestones;
         
         
          let tree:Step= new Step();
          tree.dates = {
            start: this.releaseView.releaseDate,
           end:this.releaseView.releaseDate
         };
          tree.name=this.releaseView.name;
          tree.steps=[]; 
          tree.expanded=true;
          tree.progress=0;
         
    
         data.forEach(milestone => {
           let ctree:Step= new Step();
           ctree.dates = {
            start: milestone.startDate,
            end:milestone.endDate
          };
           ctree.name=milestone. description;
           ctree.id=milestone.milestoneId;
           ctree.releaseid=milestone.releaseId;
           ctree.milestoneid=milestone.milestoneId;
           ctree.progress=milestone.percentComplete;
           if(new Date(tree.dates.end)< new Date(milestone.endDate))
           {
                  tree.dates.end=milestone.endDate;
           }
           if(new Date(tree.dates.start)> new Date(milestone.startDate))
           {
                  tree.dates.start=milestone.startDate;
           }
           ctree.steps=[];
           ctree.progressDates=[]; 
           tree.steps.push(ctree);  
          });
          if (tree) {
            this.chartData = tree;
            this.dates=[];
            this.buildCalendar(tree);
            this.dataSource.data = [tree]; 

            this.treeControl.dataNodes.forEach(node => {
              if (node.expanded) {
                this.treeControl.expand(node);
              } else {
                this.treeControl.collapse(node);
              }
            });
          }       
      }
    });
   
    
  }

  /** utils of building tree */
  transformer = (node: Step, level: number) => {
    const flatNode = new StepFlatNode(node.id,!!node.steps.length, level, node.name, node.progress,node.releaseid,node.milestoneid, node.progressDates, node.dates, node.expanded);
    this.flatNodeMap.set(flatNode, node);
    this.nestedNodeMap.set(node, flatNode);
    return flatNode;
  }

  private _getLevel = (node: StepFlatNode) => node.level;

  private _isExpandable = (node: StepFlatNode) => node.expandable;

  private _getChildren = (node: Step): Observable<Step[]> => of(node.steps);

  addMilestone(node: StepFlatNode) {
    const createMilestoneModalRef= this.modalService.open(AddMilestoneComponent, {ariaLabelledBy: 'modal-basic-title', centered: true , backdrop: 'static', keyboard: false,size: 'lg',backdropClass: 'light-blue-backdrop'});
    createMilestoneModalRef.componentInstance.releaseInfo = this.releaseView;
    createMilestoneModalRef.componentInstance.refreshChart.subscribe(($e) => {
      this.refreshChart();
    })
  }

  deleteMilestone(node: StepFlatNode) { 
   const milestone=new MileStone();
    milestone.description=node.name;
    milestone.milestoneId=node.milestoneid;
    milestone.startDate=node.dates.start;
    milestone.endDate=node.dates.end;
    milestone.percentComplete=node.progress;
    milestone.releaseId=node.releaseid;
    const deleteMilestoneModalRef= this.modalService.open(DeleteconfirmComponent);
    deleteMilestoneModalRef.componentInstance.confirmResponse.subscribe(($e)=>{
    if($e="Yes")
    {
    this._store.dispatch(new ClearMilestones(milestone))
    this.refreshChart();
    }
    })
   /* if(confirm("Are you sure to delete ")) {
    this._store.dispatch(new  ClearMilestones(milestone));
    this.refreshChart();
  } */
        
  }
    EditMilestone(node: StepFlatNode) {
    const createMilestoneModalRef= this.modalService.open(AddMilestoneComponent, {ariaLabelledBy: 'modal-basic-title', centered: true , backdrop: 'static', keyboard: false,size: 'lg',backdropClass: 'light-blue-backdrop'});
    const milestone=new MileStone();
    milestone.description=node.name;
    milestone.milestoneId=node.milestoneid;
    milestone.startDate=node.dates.start;
    milestone.endDate=node.dates.end;
    milestone.percentComplete=node.progress;
    milestone.releaseId=node.releaseid;
    createMilestoneModalRef.componentInstance.milestoneInfo = milestone;
    createMilestoneModalRef.componentInstance.refreshChart.subscribe(($e) => {
     this.refreshChart();
     })
  }

  getParentStep(node: StepFlatNode) {
    const { treeControl } = this;
    const currentLevel = treeControl.getLevel(node);
    // if root, ignore
    if (currentLevel < 1) {
      return null;
    }
    const startIndex = treeControl.dataNodes.indexOf(node) - 1;
    // loop back to find the nearest upper node
    for (let i = startIndex; i >= 0; i--) {
      const currentNode = treeControl.dataNodes[i];
      if (treeControl.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
  }

  toggleExpanded(node: StepFlatNode) {
    const nestedNode = this.flatNodeMap.get(node);
    this.database.toggleExpaned(nestedNode);
  }


  /** resize and validate */
  validate(event: ResizeEvent): boolean {
    const MIN_DIMENSIONS_PX = 200;
    if (
      event.rectangle.width &&
      (event.rectangle.width < MIN_DIMENSIONS_PX)
    ) {
      return false;
    }
    return true;
  }

  onResizeEnd(event: ResizeEvent): void {
    this.sidebarStyle = {
      'width': `${event.rectangle.width}px`
    };
  }
  
  buildCalendar(step: Step) {
    const start = this.moment(step.dates.start);
    const end = this.moment(step.dates.end);
    const range = this.moment.range(start, end);
    let rangeBy=Array.from(range.by('days'));
     if (this.timePeriod == 2) {
      rangeBy = Array.from(range.by('weeks'));      
    } else if (this.timePeriod == 3) {
      rangeBy = Array.from(range.by('months'));     
    } else if (this.timePeriod == 4) {
      rangeBy = Array.from(range.by('years'));     
    }
    if(this.dates.length>=0)
          this.dates =  Array.from(rangeBy).map(d => d.format('YYYY-MM-DD'));
    const progress = Math.round(rangeBy.length * step.progress / 100); // estimated completed days
    const totalDates = Array.from(rangeBy).map(d => d.format('YYYY-MM-DD'));
    step.progressDates=totalDates.splice(0,progress)
    step.steps.forEach(child => {
      this.buildCalendar(child);
     });
   
  }

  changeFrequency(frequency)
  {   
     this.timePeriod=frequency;
     this.dates=[];
      this.buildCalendar(this.chartData);
      this.dataSource.data = [this.chartData];
      this.treeControl.dataNodes.forEach(node => {
        if (node.expanded) {
          this.treeControl.expand(node);
        } else {
          this.treeControl.collapse(node);
        }
      });
  }

}

