import { Component, Input, ViewChild, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TaskResultsComponent } from '../task-results/task-results.component';
import { Task, Stage } from '../../../models/release.model';
import { Store, Select } from '@ngxs/store';
import { EditTaskName } from '../../../actions/workflow.action';
import { DynamicWorkflowState } from 'src/app/release-management/state/dynamic-workflow.state';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-edit-task-name',
  templateUrl: './edit-task-name.component.html',
  styleUrls: ['./edit-task-name.component.scss']
})
export class EditTaskNameComponent implements OnInit{

  formActive = false;
  form: FormGroup;
  @Input('task') task: Task;
  @Input('stage') stage: Stage;
  @ViewChild('tasknameInput') nameInputField;
  @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
  public tasks: Task[] = [];
  public operation: string;
  public popoverTitle: string;
  public popoverData: string;

  constructor(
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private modalService: NgbModal,
    private _store: Store
  ) {
    this.operation = this.activatedRoute.snapshot.url.join('/');
    if (this.operation === 'view') {
      this.popoverTitle = 'View Task Results';
      this.popoverData = 'Click to view Task Results';
    } else {
      this.popoverTitle = 'Edit Task Name';
      this.popoverData = 'Click to Edit Task Name';
    }
  }
  
  ngOnInit(){
    this.stages$.subscribe((_stages)=> {
      _stages.forEach((_stage: Stage)=>{
        _stage.tasks.forEach((_task)=> {
          this.tasks.push(_task);
        });
      });
    });
  }

  openForm() {
    if (this.operation !== 'view') {
      this.form = this.formBuilder.group({
        name: [this.task.taskName,
          [
            Validators.required,
            Validators.maxLength(30),
            this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should start with an alphabet' }),
            this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_ ]+$'), { 'remainingpattern': 'should not contain special characters except _' }),
            this.isTaskNameUnique.bind(this) // async Validator passed as 3rd parameter
          ]
        ]
      });
      this.formActive = true;
      this.focusNameField();
    } else {
      const modalRef = this.modalService.open(TaskResultsComponent, { size: 'lg', centered: true });
      // modalRef.componentInstance.taskInput = this.task.configuration;
      // modalRef.componentInstance.taskOutput = this.task.output;
      modalRef.componentInstance.task = this.task;
    }

  }

  closeForm() {
    this.formActive = false;
  }

  get name(){
    return this.form.get('name');
  }

  focusNameField() {
    setTimeout(() => {
      this.nameInputField.nativeElement.focus();
    });
  }

  onFormSubmit() {
    if (this.form.valid) {
      let stageId = this.stage.id;
      let taskId = this.task.id;
      let taskName = this.form.getRawValue().name;
      this._store.dispatch(new EditTaskName({stageId,taskId,taskName}));
      this.formActive = false;
    }else{
      this.validateAllFormFields(this.form);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }

  isTaskNameUnique(control: FormControl) {
    let keyUnique;
    for (let previousKey of this.tasks) {
      if (previousKey.taskName.toLowerCase() === control.value.toLowerCase()) {
        keyUnique = { 'keyInUse': true }
        break;
      } else {
        keyUnique = null;
      }
    }
    return keyUnique;
  }
}
