import { Component, Input, OnInit,ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs';
import { DataStateChangeEvent, PageChangeEvent } from '@progress/kendo-angular-grid';
import { SortDescriptor } from '@progress/kendo-data-query';
import { CostEliminate } from '../../models/cost.model';

@Component({
  selector: 'matilda-cost-eliminate',
  templateUrl: './cost-eliminate.component.html',
  styleUrls: []
})
export class CostEliminateComponent implements OnInit {

  @Input()
  public costEliminateDetails;
  public sort: Array<SortDescriptor> = [];
  public pageSize = 10;
  public viewEliminate: Observable<any>;
  public skip = 0;
  constructor() { }

  public ngOnInit(): void {
    this.viewEliminate = this.costEliminateDetails;
  }
  public pageChange({ skip, take }: PageChangeEvent): void {
      this.skip = skip;
  }
  public dataStateChange({ skip, take, sort }: DataStateChangeEvent): void {
    // Save the current state of the Grid component
    this.skip = skip;
    this.pageSize = take;
    this.sort = sort;
}

}
