import { Component, OnInit, Input } from '@angular/core';
import { Observable } from 'rxjs';
import { Store, Select } from '@ngxs/store';
import { HttpClient } from '@angular/common/http';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { DiscoveryState } from 'src/app/discovery/state/discovery.state';
import { FetchDiscoveryHostRetryList, CreateDiscoveryHostRetryLists } from 'src/app/discovery/actions/discovery-result.actions';

@Component({
  selector: 'matilda-host-service-retry',
  templateUrl: './host-service-retry.component.html',
  styleUrls: ['./host-service-retry.component.scss']
})
export class HostServiceRetryComponent implements OnInit {
  public hostserviceretrylist$:any;
  public serviceDiscoveryId:any;
   public retrylist1:any;
   @Select(DiscoveryState) RetryList$: Observable<any[]>;
  // @Input('retrylist1') retrylist1:any
  constructor( private _store: Store,private _http: HttpClient ,public _activeModal: NgbActiveModal) { }

  ngOnInit() {
    this.serviceDiscoveryId = localStorage.getItem("serviceDiscoveryId");
         this._store.dispatch(new FetchDiscoveryHostRetryList({ "discoveryId": this.serviceDiscoveryId }));
     this.hostserviceretrylist$  = this._store.select(state => state.discoveryResultDetails.discoveryHostRetryList);
     this.hostserviceretrylist$.subscribe((retry) => {
               this.retrylist1 = retry;
                  });
                  }
                  OnUpdate(serviceDiscoveryId:string){    
                    // /alert(serviceDiscoveryId) 
                    localStorage.setItem("serviceDiscoveryId",serviceDiscoveryId );            

                  let postData = {
                    // serviceDsicoveryId: this.retrylist1.serviceDiscoveryId,
                    // serviceDiscoveryName: this.retrylist1.serviceDiscoveryName,
                    servicePath:this.retrylist1.servicePath
                  //   ipAddress: this.retrylist1.ipAddress,
                  // serviceType: this.retrylist1.serviceType    
                  }
             this._store.dispatch(new CreateDiscoveryHostRetryLists(postData));
            this.RetryList$.subscribe(postData => {
                if (postData) {
                        this._activeModal.close();
                                        }
            });
          }
}
