import { State, Selector, Action, StateContext, Store } from "@ngxs/store";
import { ExistingReleasePlan } from "../models/existing-release-plan.model";
import { UpdateExistingReleasePlan, ClearExistingReleasePlan } from "../actions/existing-release-plan.action";
import { ReleasePlan } from "../models/release.model";
import { Project } from "../models/project.model";
import { Platform } from "../models/platform.model";
import { ReleasePlanningDropdownsState } from "./release-planning-dropdowns.state";
import { VNFNode } from "../models/VNFNodeType.model";
import { ReleaseType } from "../models/releaseType.model";

export class ExistingReleasePlanModel {
    existingReleasePlan: ExistingReleasePlan[];
}
@State<ExistingReleasePlanModel>({
    name: 'existingReleasePlan',
    defaults: {
        existingReleasePlan: [],
    }
})

export class ExistingReleasePlanState {
    constructor(private _store: Store) { }

    @Selector()
    static existingReleasePlan(state: ExistingReleasePlanModel) {
        return state.existingReleasePlan;
    }

    @Action(UpdateExistingReleasePlan)
    UpdateExistingReleasePlan(context: StateContext<ExistingReleasePlanModel>, { payload }: UpdateExistingReleasePlan) {
        var state = context.getState();

        let releaseplan: ReleasePlan = payload;
        let mapToExistingPlan: ExistingReleasePlan = new ExistingReleasePlan(releaseplan);

        let projectsOptions: Project[] = this._store.selectSnapshot(ReleasePlanningDropdownsState.projectOptions);
        let platformOptions: Platform[] = this._store.selectSnapshot(ReleasePlanningDropdownsState.platformOptions);
        let vnfNodeOptions: VNFNode[] = this._store.selectSnapshot(ReleasePlanningDropdownsState.VNFNodeTypeOptions);
        
        let _releaseType = new ReleaseType();
        _releaseType.releaseTypeCode = releaseplan.releaseTypeCode;
        _releaseType.releaseType = releaseplan.releaseType;
        mapToExistingPlan.releaseType = _releaseType;
        
        switch (releaseplan.releaseTypeCode) {
            case 1:
                //Application Release Type
                projectsOptions.forEach((_projectOption) => {
                    if (_projectOption.id === releaseplan.projectId) {
                        mapToExistingPlan.project = _projectOption;
                        _projectOption.applications.forEach((_application) => {
                            if (_application.id === releaseplan.applicationId) {
                                mapToExistingPlan.application = _application;
                            }

                        });
                        mapToExistingPlan.platform = null;
                        mapToExistingPlan.operatingsystem = null;
                    }
                });
                state.existingReleasePlan[0] = mapToExistingPlan;
                context.patchState(state);
                break;
            case 2:
                //Infrastructure Release Type
                platformOptions.forEach((_platformOption) => {
                    if (_platformOption.id === releaseplan.platformId) {
                        mapToExistingPlan.platform = _platformOption;
                        _platformOption.operatingsystems.forEach((_operatingsystem) => {
                            if (_operatingsystem.id === releaseplan.operatingsystemId) {
                                mapToExistingPlan.operatingsystem = _operatingsystem;
                            }
                        });
                        mapToExistingPlan.project = null;
                        mapToExistingPlan.application = null;
                    }
                });
                state.existingReleasePlan[0] = mapToExistingPlan;
                context.patchState(state);
                break;
            case 3:
                //Logic for VNF Release Type
                vnfNodeOptions.forEach((_vnfNodeOption) => {
                    if (_vnfNodeOption.id === releaseplan.vnfNodeTypeId) {
                        mapToExistingPlan.vnfNodeType = _vnfNodeOption;
                        _vnfNodeOption.vnfSites.forEach((_vnfSite) => {
                            if (_vnfSite.id === releaseplan.vnfSiteId) {
                                mapToExistingPlan.vnfSite = _vnfSite;
                            }
                        });
                        mapToExistingPlan.project = null;
                        mapToExistingPlan.application = null;
                        mapToExistingPlan.platform = null;
                        mapToExistingPlan.operatingsystem = null;
                        state.existingReleasePlan[0] = mapToExistingPlan;
                    }
                });
                state.existingReleasePlan[0] = mapToExistingPlan;
                context.patchState(state);
                break;
            default:
        }
    }

    @Action(ClearExistingReleasePlan)
    clearExistingReleasePlan(context: StateContext<ExistingReleasePlanModel>) {
        var state = context.getState();
        state.existingReleasePlan = [];
        context.patchState(state);
    }

}