  @Selector()
  static discoveryIPist(state: DiscoveryStateModel) {
    return state.discoveryIPlist;
  }
  @Action(GetDiscoveryIPList)
  GetDiscoveryIPList(context: StateContext<DiscoveryStateModel>,{ payload }: any) {
    var state=context.getState();
    
     return this._http.get('../../../assets/mockdata/Discovery/IpDiscovery.json')
    
    .subscribe((data: any[]) => {
        if (data) {
          state.discoveryIPlist = data["DiscoveryIpDetails"];
          
          context.setState(state);
        }
      }
      );
     
  }
  @Selector()
  static discoverystackist(state: DiscoveryStateModel) {
    return state.discoverystacklist;
  }
  @Action(GetDiscoveryStackList)
  GetDiscoveryStackList(context: StateContext<DiscoveryStateModel>,{ payload }: any) {
    var state=context.getState();
    
     return this._http.get('../../../assets/mockdata/Discovery/stack.json')
    
    .subscribe((data: any[]) => {
        if (data) {
          state.discoverystacklist = data;
          
          context.setState(state);
        }
      }
      );
     
  }

  
