import { Component, OnInit } from '@angular/core';
import * as echarts from 'echarts';
import { ListOfServicesService } from '../../services/list-of-services.service';
@Component({
  selector: 'matilda-releasechart1',
  templateUrl: './releasechart1.component.html',
  styleUrls: ['./releasechart1.component.scss']
})
export class Releasechart1Component implements OnInit {
  public sdata:any=[];
  public sdata1:any[]=[];
  public sdata2:any[]=[];
  public sdata3:any[]=[];
  public sdata4:any[]=[];
  public sdata5:any[]=[];
  public keys1=[];
  public values1=[];
  public values2=[];
  public values3=[];
  public values4=[];
  public values5=[];
  public arr1=[];
  public arr2=[];
  public arr3=[];
  public arr4=[];
  public arr5=[];

  constructor(private servermetrics:ListOfServicesService){}

  options: any;
  ngOnInit() {
    this.servermetrics.getServerMetrics().subscribe((x) => {
    
      this. sdata= x;
      
  
      this.sdata1=this.sdata.Released_Metrics.Planned_Changes;
      this.sdata2=this.sdata.Released_Metrics.Changes_Deployed;
      this.sdata3=this.sdata.Released_Metrics.Success_Changes;
      this.sdata4=this.sdata.Released_Metrics.Changes_with_Success;
      this.sdata5=this.sdata.Released_Metrics.Changes_Rolled_Back;
  
      for(let i=0; i<this.sdata1.length;i++){
        this.keys1.push(Object.keys(this.sdata1[i]));
        this.values1.push(Object.values(this.sdata1[i]));
      }
      for(let j=0; j<this.sdata2.length;j++){
        this.values2.push(Object.values(this.sdata2[j]));
      }
      for(let k=0; k<this.sdata3.length;k++){
        this.values3.push(Object.values(this.sdata3[k]));
      }
      for(let l=0; l<this.sdata4.length;l++){
        this.values4.push(Object.values(this.sdata4[l]));
      }
      for(let m=0; m<this.sdata5.length;m++){
        this.values5.push(Object.values(this.sdata5[m]));
      }
      
      
for(let i1=0; i1<this.values1.length; i1++){
  this.arr1[i1] = this.values1[i1][0];
 }
 for(let j1=0; j1<this.values2.length; j1++){
  this.arr2[j1] = this.values2[j1][0];
 }

 for(let k1=0; k1<this.values3.length; k1++){
  this.arr3[k1] = this.values3[k1][0];
 }
 for(let l1=0; l1<this.values4.length; l1++){
  this.arr4[l1] = this.values4[l1][0];
 }
 for(let m1=0; m1<this.values5.length; m1++){
  this.arr5[m1] = this.values5[m1][0];
 }

             

      this.options = 
       {
            tooltip: {},
        calculable: true,
      xAxis: [
          {
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                rotate: 45
              },
              silent: false,
              splitLine: {
                show: false
              },
             
              type: 'category',
              
              data: this.keys1
          }
      ],
      yAxis: {
          
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          
        },
      series: [
        
          {
              name: 'Planned Changes',
              type: 'bar',
              barGap: 0,
              label: 'labelOption',
              data: this.arr1,
              itemStyle: {
                      normal: {
                        color: new echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            { offset: 0, color: '#34bfa3' },
                            { offset: 0.5, color: '#34bfa3' },
                            { offset: 1, color: '#34bfa3' }
                          ]
                        )
                      }
                    },
          },
          {
              name: 'Changes Deployed',
              type: 'bar',
              label: 'labelOption',
              data:this.arr2,
              itemStyle: {
                      normal: {
                        color: new echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            { offset: 0, color: '#ffb822' },
                            { offset: 0.5, color: '#ffb822' },
             
                            { offset: 1, color: '#ffb822' }
                          ]
                        )
                      }
                    },
          },
          {
            name: 'Successful Changes',
            type: 'bar',
            label: 'labelOption',
            data: this.arr3,
            itemStyle: {
                    normal: {
                      color: new echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          { offset: 0, color: '#f4516c' },
                          { offset: 0.5, color: '#f4516c' },
                          { offset: 1, color: '#f4516c' }
                        ]
                      )
                    }
                  },
        },
        {
            name: 'Changes with Issues',
            type: 'bar',
            label: 'labelOption',
            data: this.arr4,
            itemStyle: {
                    normal: {
                      color: new echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          { offset: 0, color: '#36a3f7' },
                          { offset: 0.5, color: '#36a3f7' },
                          { offset: 1, color: '#36a3f7' }
                        ]
                      )
                    }
                  },
        },
        {
          name: 'Changes Rolled Back',
          type: 'bar',
          label: 'labelOption',
          data: this.arr5,
          itemStyle: {
                  normal: {
                    color: new echarts.graphic.LinearGradient(
                      0, 0, 0, 1,
                      [
                        { offset: 0, color: '#87bdd8' },
                        { offset: 0.5, color: '#87bdd8' },
                        { offset: 1, color: '#87bdd8' }
                      ]
                    )
                  }
                },
      }
      ],
        
        animationEasing: 'elasticOut',
        animationDelayUpdate: function (idx) {
          return idx * 8;
        }
      }
    
  
  

    });
  }
  onChartEvent(event: any, type: string) {
  }
}
