import { Component, OnInit, Input, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { FormBase } from '../../../../shared/dynamic_forms/form-base';
import { DynamicWorkflowState } from '../../../state/dynamic-workflow.state';
import { Stage } from '../../../models/release.model';
import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { GETPluginOutputParameters } from 'src/app/release-management/actions/workflow-available-services.action';
import { WorkflowAvailableServicesState } from 'src/app/release-management/state/workflow-available-services.state';

@Component({
  selector: 'app-release-form-template',
  templateUrl: './release-form-template.component.html',
  styleUrls: ['./release-form-template.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ReleaseFormTemplateComponent implements OnInit {
  public lookupForm: FormGroup;
  isRequired: boolean = true;
  selected_dataItem: any;
  showLookupForm: boolean;
  invalidForm: boolean;
  showBuildPluginForm: boolean;
  pluginOutputParameters: any[];

  @Input() dataItem: FormBase<any>;
  @Input() form: FormGroup;
  @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
  @Select(WorkflowAvailableServicesState.pluginOutputParameters) pluginOutputParameters$: Observable<any[]>;
  @Output('selectedFromLookup') selectedFromLookup = new EventEmitter<any>(); //Send Selected Value
  @Output('parameterList') parameterList = new EventEmitter<any>();

  //Start::Dropdown settings
  singleDropdownSettings: any = {};
  taskSingleDropdownSettings: any = {};
  closeDropdownSelection = false;
  //Start::Dropdown settings

  //Start::DropDowns
  stageOptions: any[] = [];
  taskOptions: any[] = [];
  variableOptions: any[] = [
    "overall_status",
    "action",
    "output",
    "job_name",
    "job_id",
    "summary"
  ];
  showOutputFiledsWaitMessage: boolean;

  get isValid() {
    return this.form.controls[this.dataItem.key].valid
  }
  constructor(private _fb: FormBuilder,
    private _store: Store) {
  }
  ngOnInit() {
    
    this.singleDropdownSettings = {
      singleSelection: true,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      allowSearchFilter: false,
      closeDropDownOnSelection: this.closeDropdownSelection
    };

    this.taskSingleDropdownSettings = {
      singleSelection: true,
      idField: 'id',
      textField: 'taskName',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      allowSearchFilter: false,
      closeDropDownOnSelection: this.closeDropdownSelection
    };

    this.initForm();
  
    //Stages-Dropdown
    this.stages$.subscribe((stages)=>{
       stages.forEach(stage => this.stageOptions.push(stage));
    });
  }

  open(dataItem: any) {
    this.showLookupForm = true;
    this.selected_dataItem = dataItem;
    this.initForm();
  }

  onStageSelect(selectedstage: Stage, form: any) {
    this.taskOptions = [];
    form.controls.task.setValue('');
    this.stages$.subscribe(stages=>{
      stages.forEach(stage => {
        if(stage.name === selectedstage.name){
          stage.tasks.forEach(task => {
            this.taskOptions.push(task);
          });
        }
      });
    });

  }
  onStageDeSelect(selectedstage: string, form: any) {
    this.taskOptions = [];
    form.controls.task.setValue('');
  }
 
  onTaskSelect(selectedTask:any){
    let selectedStage: any = this.lookupForm.get('stage').value[0];
    this.stageOptions.forEach((_stage: Stage)=> {
      if(_stage.id === selectedStage.id){
        _stage.tasks.forEach((_task)=>{
          if(_task.id === selectedTask.id){
            this.showOutputFiledsWaitMessage = true;
            this._store.dispatch(new GETPluginOutputParameters(
              {
                "serviceId":_task.serviceId,
                "actionId":_task.actionId
              }
              )).subscribe( value => {
                 this.pluginOutputParameters$.subscribe((_pluginOutputParameters)=> {
                   this.pluginOutputParameters = _pluginOutputParameters;
                   if(_pluginOutputParameters.length > 0){
                    this.showOutputFiledsWaitMessage = false;
                   }
                 });
              });
          }
        });
      }
    });
  }

  initForm() {
    this.lookupForm = this._fb.group({
      stage: ['', Validators.required],
      task: ['', Validators.required],
      variable: ['', Validators.required]
    });
  }
  closeForm(){
    this.showLookupForm = false;
    this.showOutputFiledsWaitMessage = false;
  }

  saveForm(form: any){
    if (this.lookupForm) {
      const selectedData = {
        dataItem: this.selected_dataItem,
        stage: form.stage[0].id,
        task: form.task[0].taskName,
        variable: form.variable[0],
      }
      this.selectedFromLookup.emit(selectedData);
      
      this.showLookupForm = false;
      this.invalidForm = false;
    }else{
      this.validateAllFormFields(this.lookupForm);
      this.invalidForm = true;
    }
  }
  
  openBuildPluginForm(dataItem){
    this.showBuildPluginForm = true;
  }
 
  onCloseBuildPluginForm(event){
    this.showBuildPluginForm = false;
  }

  onUpdateParameterList(_parameterList){
     this.parameterList.emit(_parameterList);
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  get stageControl(){
    return this.lookupForm.get('stage');
  }
}
