import { Component, OnInit } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';

import { GetMigrationList } from '../../actions/migration.action';
import { MigrationState } from '../../state/migration.state';
import { Router } from '@angular/router';

@Component({
  selector: 'matilda-migration-list',
  templateUrl: './migration-list.component.html',
  styles: []
})
export class MigrationListComponent implements OnInit {

  @Select(MigrationState.migrationList) migrationList$: Observable<any>;

  constructor(private router: Router, private _store: Store) { }

  ngOnInit() {    
    this._store.dispatch(new GetMigrationList(null));
  }
  loadMigrationModule(){
    this.router.navigate(['/migration/dashboard']);
  }
  newMigration(){
    this.router.navigate(['/migration/newmigration']);
  }

}
