import { ReleaseEnvironment } from "../models/release.model";
import { State, Action, StateContext, Selector } from "@ngxs/store";
import { ChangeSelectedEnvironment } from "../actions/selected-environment.action";

export class SelectedEnvironmentModel {
    selectedEnvironment: ReleaseEnvironment[];
}

@State<SelectedEnvironmentModel>({
    name: 'selectedEnvironment',
    defaults: {
        selectedEnvironment: []
    }
})

export class SelectedEnvironmentState {
    constructor(){}
    @Selector()
    static selectedEnvironment(state: SelectedEnvironmentModel) {
        return state.selectedEnvironment;
    }

    @Action(ChangeSelectedEnvironment)
    changeSelectedEnvironment(context: StateContext<SelectedEnvironmentModel>, {payload}: ChangeSelectedEnvironment) {
        var state = context.getState();
        state.selectedEnvironment[0] = payload;
        context.patchState(state);
    }

}