import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NewNetworkDiscoveryComponent } from '../../new-discovery/new-network-discovery/new-network-discovery.component';
import { NewServiceDiscoveryComponent } from '../../new-discovery/new-service-discovery/new-service-discovery.component';
import { Store, Select } from '@ngxs/store';
import { DiscoveryState } from 'src/app/discovery/state/discovery.state';
import { Observable } from 'rxjs';
// import {  GetNetWorkList } from 'src/app/discovery/actions/discovery.action';
import { DiscoveryList } from 'src/app/discovery/models/discovery.model';
import { DiscoveryCreatingState } from 'src/app/discovery/state/discovery-creating.state';
import { FetchDiscoveryNetworkPortList, FetchDiscoveryServiceList, FetchDiscoveryServiceSummary, FetchDiscoveryDataCenterList } from 'src/app/discovery/actions/discovery-creating.actions';
@Component({
  selector: 'matilda-discovery-home',
  templateUrl: './discovery-home.component.html',
  styleUrls: ['./discovery-home.component.scss']
})
export class DiscoveryComponent implements OnInit {
  @Select(DiscoveryCreatingState.discoveryNetworkPortlist) networkPortList$: Observable<any[]>;
  // @Select(DiscoveryState.getsummary) summary$: Observable<any>;
  public summary$: Observable<DiscoveryList>;
  public summ: any;
  public type='network';
  constructor( private activatedRoute: ActivatedRoute,private router: Router,private modalService: NgbModal, private _store: Store) { }

  ngOnInit() {
    if(this.activatedRoute.snapshot.queryParams['type']){
      this.type = this.activatedRoute.snapshot.queryParams['type'];
    }
    else{
      this.type='network';
    }
    // this.type = this.activatedRoute.snapshot.queryParams['type'];
    this._store.dispatch(new FetchDiscoveryNetworkPortList());
    this._store.dispatch(new FetchDiscoveryDataCenterList());

    this._store.dispatch(new FetchDiscoveryServiceSummary());
    this._store.dispatch(new FetchDiscoveryServiceList({}));
    // this._store.dispatch(new GetNetWorkList({}));
     this.summary$= this._store.select(state=>state.discoveryCreatingDetails.discoveryNetworkListSummary);
   this.summary$.subscribe(element=>{
      this.summ = element
         })
      }
//network discovery page
  loadnewnetwork() {
    const newnetwork = this.modalService.open(NewNetworkDiscoveryComponent, ({ backdrop : 'static',
    keyboard : false,centered: true, windowClass: 'modal-extra-large'}))
    this.type='network';
  }
  // service discovery page
  loadnewservice() {
    const newservice = this.modalService.open(NewServiceDiscoveryComponent, ({ backdrop : 'static',
    keyboard : false, centered: true, windowClass: 'modal-extra-large'}))
    this.type='services';
  }
  //dash board
  loadDiscoveryModule(){
    this.router.navigate(['/discovery/dashboard']);
  }
  //discovered list
  loadDiscoverylist(){
    this.router.navigate(['/discovery/list']);
  }
}
