import { JsonProperty } from "json-typescript-mapper";


 export class Server{

    @JsonProperty('serverName')
    servername: string = '';

    @JsonProperty('listenPort')
    portnum: string = '';

    @JsonProperty('serverCluster')
    servercluster: string = '';


}
export class Cluster{

    @JsonProperty('clusterName')
    clustername: string = '';



}

export class Application{
    @JsonProperty('applicationName')
    appname: string = '';

    @JsonProperty('moduleType')
    moduletype: string = '';

    @JsonProperty('clusterName')
    clustername: string = '';

}

export class DataSource{

    @JsonProperty('datasourceName')
    datasourcename: string = '';

    @JsonProperty('targetServer')
    targetserver: string = '';

    @JsonProperty('datasourceJndiName')
    jndiname: string = '';

}


export class JMSResource{

    @JsonProperty('jmsresourceName')
    jmsname: string = '';

    @JsonProperty('targetServer')
    targetserver: string = '';

} 


export class Domain{
    @JsonProperty('domainName')
   name: string = '';

   @JsonProperty('domainVersion')
   version: string = '';
 
    @JsonProperty({clazz: JMSResource, name:'jmsresources'})
   jmsres: JMSResource[]=[];

   @JsonProperty({clazz: Server, name:'servers'})
   serverdata: Server[]=[];
   @JsonProperty({clazz: DataSource, name:'datasources'})
   datasourcedata: DataSource[]=[];

   @JsonProperty({clazz: Application, name:'applications'})
   applicationdata: Application[]=[];

   @JsonProperty({clazz: Cluster, name:'clusters'})
   clusterdata: Cluster[]=[]; 

}

export class ServiceResult {
   
    @JsonProperty('serviceDiscoveryId')
    id: string = '';

    @JsonProperty('serviceDiscoveryName')
    recordName: string = '';


    @JsonProperty('statusType')
    statustype: string = '';

    @JsonProperty('serviceType')
    servicetype: string = '';

   /*  @JsonProperty('domains')
    domain: string = ''; */

    @JsonProperty({clazz: Domain, name:'domains'})
    domains: Domain[] = [] ;
   
}


