import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'matilda-impacted-applications-grid',
  templateUrl: './impacted-applications-grid.component.html',
  styleUrls: ['./impacted-applications-grid.component.scss']
})
export class ImpactedApplicationsGridComponent implements OnInit {
   @Input('gridData') gridData:any[];
  constructor() { }

  ngOnInit() {
    console.log(this.gridData)
  }

  getStatusBadge(status: string) {
    if (status) {
      if (status.toLowerCase() === 'completed' || status.toLowerCase() === 'complete' ) {
        return 'm-badge--success';
      } else if (status.toLowerCase() === 'failed' || status.toLowerCase() === 'not started') {
        return 'm-badge--danger';
      }
      else if (status.toLowerCase() === 'in progress' || status.toLowerCase() === 'pending') {
        return 'm-badge--warning';
      } else {
        return 'm-badge--metal';
      }
    }else{
      return 'm-badge--brand';
    }
  }

}
