import { Component, OnInit, ViewChild } from '@angular/core';
import { ServiceDiscoveryList } from '../../../models/discovery.model';
import { Observable } from 'rxjs';
import { Store } from '@ngxs/store';
import { Router } from '@angular/router';
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';
import { SortDescriptor } from '@progress/kendo-data-query'
import { TooltipDirective } from '@progress/kendo-angular-tooltip';
import { FetchDiscoveryServiceList, FetchDiscoveryServiceSummary } from 'src/app/discovery/actions/discovery-creating.actions';
import { FetchDiscoveryServiceSummaryPageList, FetchDiscoveryServiceTopologyPageList, FetchDiscoveryServiceFailedReason } from 'src/app/discovery/actions/discovery-serviceresult.actions';

@Component({
  selector: 'matilda-service-discovery',
  templateUrl: './service-discovery.component.html'
})
export class ServiceDiscoveryComponent implements OnInit {
  //ToolTip
  @ViewChild(TooltipDirective) public tooltipDir: TooltipDirective;
  public showTooltip(e: MouseEvent): void {
    const element = e.target as HTMLElement;
    if ((element.nodeName === 'TD' || element.nodeName === 'TH')
      && element.offsetWidth < element.scrollWidth) {
      this.tooltipDir.toggle(element);
    } else {
      this.tooltipDir.hide();
    }
  }
  public serviceList$: Observable<ServiceDiscoveryList>;
  public services: any;
  public summaryList$: any;
  public summary: any;
  public openedrecommendationWindow: boolean = false;

  public pageSize = 10;
  public skip = 0;
  public failedReason$: any;
  public failed: any;
  public a: any;

  public closerecommendationWindow() {
    this.openedrecommendationWindow = false;
  }

  public sort: SortDescriptor[] = [{
    field: 'service_discovery_name',
    dir: 'desc'
  }];
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
  }

  public onPageChange(state: any): void {
    this.pageSize = state.take;
  }
  constructor(private router: Router, private _store: Store) { }


  ngOnInit() {
    this._store.dispatch(new FetchDiscoveryServiceSummary());
    this.summaryList$ = this._store.select(state => state.discoveryCreatingDetails.discoveryServiceListSummary);
    this.summaryList$.subscribe(element => {
      this.summary = element
    })
    this._store.dispatch(new FetchDiscoveryServiceList({}));
    this.serviceList$ = this._store.select(state => state.discoveryCreatingDetails.discoveryServiceList);
    this.serviceList$.subscribe(element => {
      this.services = element

      var occurences = this.services.reduce(function (r, row) {
        r[row.statusType] = ++r[row.statusType] || 1;
        return r;

      }, {});
      var result = Object.keys(occurences).map(function (key) {
        return { name: key, value: occurences[key] };
      });

      for (var i = 0; i++; i <= result.length) {
        if (result[i].name == "Failed") {
          this.a = result[i].value;
          // alert(this.a);
        }
      }

    })

  }


  getStatusBadge(status: string) {
    if (status) {
      if (status.toLowerCase() === 'active' || status.toLowerCase() === 'completed') {
        return 'm-badge--success';
      } else if (status.toLowerCase() === 'failed') {
        return 'm-badge--danger';
      }
      else if (status.toLowerCase() === 'inprogress' || status.toLowerCase() === 'pending') {
        return 'm-badge--warning';
      } else {
        return 'm-badge--brand';
      }
    }
  }

  getResults(statusType, serviceDiscoveryId: string, ipAddress: any) {
    if (statusType == 'Failed') {
      this.openedrecommendationWindow = true;
      localStorage.setItem("serviceDiscoveryId", serviceDiscoveryId);
      localStorage.setItem("serviceDiscoveryipAddress", ipAddress);
      this._store.dispatch(new FetchDiscoveryServiceFailedReason({ "discoveryId": serviceDiscoveryId }));
      this.failedReason$ = this._store.select(state => state.discoveryCreatingDetails.discoveryServiceListFailedReason);
      this.failedReason$.subscribe(element => {
        this.failed = element
        console.log(this.failed )
      })
    }
    if (statusType == 'Completed') {
      localStorage.setItem("serviceDiscoveryId", serviceDiscoveryId);
      localStorage.setItem("serviceDiscoveryipAddress", ipAddress);
      this._store.dispatch(new FetchDiscoveryServiceSummaryPageList({ "discoveryId": serviceDiscoveryId }));
      this._store.dispatch(new FetchDiscoveryServiceTopologyPageList({ "discoveryId": serviceDiscoveryId }));
      this.router.navigate(['/discovery/servicedetails']);
    }


  }

}
