import { Component, OnInit, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import { EditService } from 'src/app/cost-management/services/edit.service';
import { DeploymentService } from 'src/app/shared/services/deployment.service';
import { Store } from '@ngxs/store';
import { Navigate } from '@ngxs/router-plugin';
declare let mLayout: any;
@Component({
  selector: 'app-header-nav',
  templateUrl: './header-nav.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [Location, EditService, { provide: LocationStrategy, useClass: PathLocationStrategy }],
})
export class HeaderNavComponent implements OnInit, AfterViewInit {
  public selectedModule: string = 'discovery';
  public releaselist: string = 'Release List';
  public costlist: string = 'Cost List';
  public discoverylist: string = 'Discovery List';
  public migrationList: string = 'Migration List';
  public cloudlist: string = 'Cloud List';
  public assetlist: string = 'Asset List';
  public listname: string;
  public listItems: Array<string> = ['AWS | cnet-dev (xxxxxxxxxxx1)', 'AZURE | cnet-dev (xxxxxxxx1124)'];
  public environmentItems: Array<string> = ['AWS | All', 'AWS | Dev', 'AWS | Prod', 'AWS | Test'];
  public selectedValue: string = 'AWS | cnet-dev (xxxxxxxxxxx1)';
  public selectedEnvironment: string = 'AWS | All';
  message: string;


  public aid = localStorage.getItem("selectedValue");
  handleCategoryChange(value) {
    this.data.changeMessage(this.selectedValue);
    if (this.selectedValue = 'AWS | cnet-dev (xxxxxxxxxxx1)') {

    }
    else {
      this.selectedModule = 'cost-management';
      this.listname = this.costlist;
    }
  }

  constructor(
    private _location: Location,
    private router: Router,
    private _store: Store,
    private data: DeploymentService) {
    //   msg.messageSource$.subscribe(
    //     id=>{
    // this.selectedValue
    //     }
    //   );

  }


  ngOnInit() {
    //this.loadReleaseModule();
    // console.log("i");
    // console.log(this.selectedValue);
    this.data.changeMessage(this.selectedValue);
    this.data.currentMessage.subscribe(message => this.message = message)
  }


  ngAfterViewInit() {
    mLayout.initHeader();
    this.selectedModule = this._location.path().split('/')[1];
    //this.loadDashboard();
    this.UpdateHeaderList(this.selectedModule);
  }

  loadReleaseModule() {
    this.listname = this.releaselist;
    this.selectedModule = 'release-orchestration';
    //this._store.dispatch(new Navigate(['/release-orchestration']));
  }
  loadCostModule() {
    this.selectedModule = 'cost-management';
    this.listname = this.costlist;
    //this._store.dispatch(new Navigate(['/cost-management/dashboard']));
  }
  loadDiscoveryModule() {
    this.selectedModule = 'discovery';
    this.listname = this.discoverylist;
   // this._store.dispatch(new Navigate(['/discovery/dashboard']));
  }
  loadMigrationModule() {
    this.selectedModule = 'environment-migration';
    this.listname = this.migrationList;
   // this._store.dispatch(new Navigate(['/migration/dashboard']));
  }
  loadCloudManagementModule() {
    this.selectedModule = 'on-boarding';
    this.listname = this.cloudlist;
    //this._store.dispatch(new Navigate(['/on-boarding']));
  }
  loadAssetManagementModule() {
    this.selectedModule = 'asset-management';
    this.listname = this.assetlist;
    this._store.dispatch(new Navigate(['/asset-management']));
  }
  loadMonitoringModule() {
    this.selectedModule = 'monitoring';
    this._store.dispatch(new Navigate(['/monitoring']));
  }

  loadList() {
    switch (this.listname) {
      case 'Release List': {
        this._store.dispatch(new Navigate(['/release-orchestration/list']));
        break;
      }
      case 'Discovery List': {
        this._store.dispatch(new Navigate(['/discovery/list']));
        break;
      }
      case 'Migration List': {
        this._store.dispatch(new Navigate(['/environment-migration/list']));
        break;
      }

      case 'Cloud List': {
        this._store.dispatch(new Navigate(['/on-boarding']));
        break;
      }
      case 'Asset List': {
        this._store.dispatch(new Navigate(['/asset-management/list']));
        break;
      }

      default: {
        break;
      }
    }

  }
  loadSettings() {
    this._store.dispatch(new Navigate(['/contact']));
  }

  UpdateHeaderList(selectedModule: string) {
    switch (selectedModule) {
      case 'release-orchestration': {
        this.listname = this.releaselist;
        break;
      }
      case 'discovery': {
        this.listname = this.discoverylist;
        break;
      }
      case 'migration': {
        this.listname = this.migrationList;
        break;
      }
      case 'cloud-management': {
        this.listname = this.cloudlist;
        break;
      }
      case 'asset-management': {
        this.listname = this.assetlist;
        break;
      }

      default: {
        break;
      }
    }
  }

  loadNavItem(navItem: string) {
    switch (navItem) {
      case 'release-templates': {
        // this.router.navigate(['/release-management/list']);
        break;
      }
      case 'cost-cloud': {
        this._store.dispatch(new Navigate(['/cost-management/cloud']));
        break;
      }
      case 'cost-ri': {
        this._store.dispatch(new Navigate(['/cost-management/ri']));
        break;
      }
      case 'cost-analytics': {
        this._store.dispatch(new Navigate(['/cost-management/analytics']));
        break;
      }
      case 'cost-budget': {
        this._store.dispatch(new Navigate(['/cost-management/budget']));
        break;
      }
      case 'cost-saving': {
        this._store.dispatch(new Navigate(['/cost-management/saving']));
        break;
      }
      case 'configure': {
        this._store.dispatch(new Navigate(['/cost-management/configure']));
        break;
      }
      default: {
        break;
      }
    }
  }

  loadDashboard() {
    switch (this.selectedModule) {
      case 'release-orchestration': {
        this._store.dispatch(new Navigate(['/release-orchestration/dashboard']));
        break;
      }
      case 'cost-management': {
        this._store.dispatch(new Navigate(['/cost-management/dashboard']));
        break;
      }
      case 'discovery': {
        this._store.dispatch(new Navigate(['/discovery/dashboard']));
        break;
      }
      case 'migration': {
        this._store.dispatch(new Navigate(['/environment-migration/dashboard']));
        break;
      }
      case 'on-boarding': {
        this._store.dispatch(new Navigate(['/on-boarding']));
        break;
      }
      default: {
        break;
      }
    }
  }


}
