import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs';
import { DataStateChangeEvent, PageChangeEvent } from '@progress/kendo-angular-grid';
import { SortDescriptor } from '@progress/kendo-data-query';
import { CostSaving } from '../../models/cost.model';
import { Store, Select } from '@ngxs/store';
import { Savings } from '../../models/savings.model';
import { SavingsState } from '../../state/savings.state';
import { Navigate } from '@ngxs/router-plugin';
import { GetSavingsCost } from '../../actions/savings.action';


@Component({
    selector: 'matilda-cost-saving',
    templateUrl: './cost-saving.component.html',
    styleUrls: []
})
export class CostSavingComponent implements OnInit {

    public savingsList$: Observable<Savings>;
    public metadata$: Observable<any>;
    public pageSize;
    public view = true;
    itemsPerPage: number;
    totalItems: any;
    page: any;
    p: number = 1;
    previousPage: any;
    public dataSource: any;

    @Select(SavingsState.savingsCost) savingsCost$: Observable<any[]>;
    @Select(SavingsState.savingsMetaData) savingsMetaData$: Observable<any[]>;
    constructor(private _store: Store) { }

    ngOnInit() {
        this._store.dispatch((new GetSavingsCost()));
    }
    ngAfterViewInit() {
        this.pageSize = 5;
    }
}

