import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngxs/store';
import { Release } from '../../models/release.model';
import { CloneRelease } from '../../actions/release.action';
import { addYears } from '@progress/kendo-date-math';
import { serialize } from 'json-typescript-mapper';

@Component({
  selector: 'matilda-clone-release',
  templateUrl: './clone-release.component.html',
  styleUrls: ['./clone-release.component.scss']
})
export class CloneReleaseComponent implements OnInit {
  public cloneReleaseForm: FormGroup;
  @Input('sourceRelease') sourceRelease: Release;
  public min: Date = new Date();
  public max: Date = addYears(this.min,2);//new Date(2020, 11, 31); //
  constructor(private _fb: FormBuilder,
    public activeModal: NgbActiveModal,
    private _store: Store) { }

  ngOnInit() {
    this.cloneReleaseForm = this._fb.group({
      releaseName: ['', [
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(30),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_-]+$'), { 'remainingpattern': 'should not contain special characters' }),
      ]],
      releaseDate: ['', Validators.required]
    });
  }

  onSubmit(form: any) {
    if (this.cloneReleaseForm.valid) {
      let targetRelease = new Release();
      targetRelease.name = form.releaseName;
      targetRelease.releaseDate = form.releaseDate;
      this._store.dispatch(new CloneRelease({"sourceRelease": this.sourceRelease,
       "targetRelease":serialize(targetRelease)}));
    }
  }
  get releaseName() {
    return this.cloneReleaseForm.get('releaseName');
  }
  get releaseDate() {
    return this.cloneReleaseForm.get('releaseDate');
  }
  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
        if (!control.value) {
            return null;
        }
        const valid = regex.test(control.value);
        return valid ? null : error;
    };
}
}
