import { TemplateVersion } from "./available-workflow-templates.model";
import { Frequency } from "./frequency.model";

export class WorkflowSchedule {
    name: string;
    templateName: string;
    templateVersion: TemplateVersion;
    frequency: Frequency;

    constructor(name: string, templateName: string, templateVersion: TemplateVersion, frequency: Frequency){
        this.name = name;
        this.templateName = templateName;
        this.templateVersion = templateVersion;
        this.frequency = frequency;
    }
}