import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SelectableSettings } from '@progress/kendo-angular-grid';

@Component({
  selector: 'app-lookup-conditions',
  templateUrl: './lookup-conditions.component.html',
  styleUrls: ['./lookup-conditions.component.scss']
})
export class LookupConditionsComponent implements OnInit {
  public selectableSettings: SelectableSettings;
  @Input('data') data: any;
  @Output('selectedPreCondition') selectedPreCondition = new EventEmitter<any>(); //Send Selected Row
  public gridData: any[] = [];
  constructor(public activeModal: NgbActiveModal) { }

  ngOnInit() {
    this.setSelectableSettings();
    this.gridData = this.data;
  }

  public setSelectableSettings(): void {
    this.selectableSettings = {
      mode: 'single'
    };
  }

  public selectRow(event: any) {
    let i = event.index;
    if (event.selectedRows.length > 0) {
      let selectedCondition = this.gridData[i];
      this.selectedPreCondition.emit(selectedCondition);
      setTimeout(() => {
        this.activeModal.close();
      }, 1000);
    }

  }

}
