import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { GetHostServiceList, GetHostTopologyList } from 'src/app/discovery/actions/discovery.action';
import { Store } from '@ngxs/store';
import { HttpClient } from '@angular/common/http';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { NotificationsService } from 'angular2-notifications';
@Component({
  selector: 'matilda-host-service-details',
  templateUrl: './host-service-details.component.html',
  styleUrls: ['./host-service-details.component.scss']
})
export class HostServiceDetailsComponent implements OnInit {

  // public hostservicedetails$: Observable<any>;
  public hostservicedetails$: Observable<any>;
  public topology$: any;
  public errormessage: any;
  public errortopologymessage: any;
  public servicesummarydetails$: any;
  public serviceDiscoveryId: any;
  public topodata: any;
  public pageSize = 5;
  public skip = 0;
  public serdata: any;
  public errorSummaryMessage :any;
  public errorTopologyMessage:any;
  public error:any;
  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
  }
  public onPageChange(state: any): void {
    this.pageSize = state.take;
  }

  constructor(private _store: Store,private _notificationsService: NotificationsService, private _http: HttpClient, public _activeModal: NgbActiveModal, ) { }

  ngOnInit() {

    this.serviceDiscoveryId = localStorage.getItem("serviceDiscoveryId");
    this.servicesummarydetails$ = this._store.select(state => state.discoveryResultDetails.discoveryHostSummaryPageList);
    this.servicesummarydetails$.subscribe((x) => {
      if (x ) {
      
      //  console.log(x);
        this.serdata = x.summaryDetails;
        //alert(x['errorMessage'])
          this.errorSummaryMessage = x;
      }
      
    }
      
    );

    this.topology$ = this._store.select(state => state.discoveryResultDetails.discoveryHostTopologyPageList);
    this.topology$.subscribe((x) => {
      this.topodata = x;
      this.errorTopologyMessage =  x;
      // alert( this.errorTopologyMessage )
      // this.errortopologymessage=x.errorMessage
    });

  }
  //inorder plus sign for empty child records
  public showOnlyDetails(dataItem: any, index: number): boolean {
    if (dataItem.details.length > 0)
      return true;
  }
}
