import { Component, ViewChild,  Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { GridComponent, DataStateChangeEvent, GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';
import { Store, Select } from '@ngxs/store';
import { SortDescriptor } from '@progress/kendo-data-query';

@Component({
  selector: 'matilda-firstline',
  templateUrl: './firstline.component.html',
  styleUrls: ['./firstline.component.scss']
})
export class FirstlineComponent implements OnInit {

  @Input() public networkdetails;
  
  @ViewChild(GridComponent) grid: GridComponent;

  public sort: Array<SortDescriptor> = [];
  public pageSize = 10;
  public view: Observable<GridDataResult>;
  public skip = 0;
  public gridData: GridDataResult;
  public clone = true;

  constructor() { }

  public ngOnInit(): void {
      this.view = this.networkdetails["servers"];
  }

  public pageChange({ skip, take }: PageChangeEvent): void {
      this.skip = skip;
  }
  public dataStateChange({ skip, take, sort }: DataStateChangeEvent): void {
      // Save the current state of the Grid component
      this.skip = skip;
      this.pageSize = take;
      this.sort = sort;       
  }
}

