import { Component, OnInit, Input } from '@angular/core';
import { FormBase } from 'src/app/shared/dynamic_forms/form-base';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'matilda-view-release-form-template',
  templateUrl: './view-release-form-template.component.html',
  styleUrls: ['./view-release-form-template.component.scss']
})
export class ViewReleaseFormTemplateComponent implements OnInit {
   
  @Input() dataItem: FormBase<any>;
  @Input() form: FormGroup;
  get isValid() {
    return this.form.controls[this.dataItem.key].valid
  }
  constructor() {
  }

  ngOnInit() {
  }

}
