import { Component, EventEmitter, ViewChild, Output, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, ValidationErrors, ValidatorFn, AbstractControl } 
from '@angular/forms';
import { DynamicWorkflowState } from 'src/app/release-management/state/dynamic-workflow.state';
import { Select } from '@ngxs/store';
import { Observable } from 'rxjs/Observable';
import { Stage } from 'src/app/release-management/models/release.model';

@Component({
    selector: 'app-workflow-list-item',
    templateUrl: './workflow-list-item.component.html',
    styleUrls: ['./workflow-list-item.component.scss']
})
export class WorkflowListItemComponent implements OnInit{

    formActive = false;
    form: FormGroup;
    @Output() onlistAdd = new EventEmitter();
    @ViewChild('nameInput') nameInputField;
    @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
    public stages: Stage[];

    constructor(
        private formBuilder: FormBuilder
    ) {
    }

    ngOnInit(){
        this.stages$.subscribe((_stages)=> {
            this.stages = _stages;
          });
    }

    openForm() {
        this.form = this.formBuilder.group({
            name: ['', 
                [
                    Validators.required,
                    this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
                    Validators.required,
                    Validators.maxLength(30),
                    this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should start with an alphabet' }),
                    this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_ ]+$'), { 'remainingpattern': 'should not contain special characters except _' }),
                    this.isStageNameUnique.bind(this) // async Validator passed as 3rd parameter
                ]
            ]
        });
        this.formActive = true;
        this.focusNameField();
    }

    closeForm() {
        this.formActive = false;
    }

    get name() {
        return this.form.get('name');
    }

    focusNameField() {
        setTimeout(() => {
            this.nameInputField.nativeElement.focus();
        });
    }

    onFormSubmit() {
        if (this.form.valid) {
            this.onlistAdd.emit(this.form.getRawValue().name);
            this.formActive = false;
        } else {
            this.validateAllFormFields(this.form);
        }
    }


    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }

    regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } => {
            if (!control.value) {
                return null;
            }
            const valid = regex.test(control.value);
            return valid ? null : error;
        };
    }

    isStageNameUnique(control: FormControl) {
        let keyUnique;
        for (let previousKey of this.stages) {
          if (previousKey.name.toLowerCase() === control.value.toLowerCase()) {
            keyUnique = { 'keyInUse': true }
            break;
          } else {
            keyUnique = null;
          }
        }
        return keyUnique;
      }

}
