import { Component, OnInit, ElementRef, ViewChild, AfterViewInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { Store, Select } from '@ngxs/store';
import { UpdateMilestoneList, GETMilestoneTypes, GETMilestones } from 'src/app/release-management/actions/release-milestones.action';
import { ReleaseMilestonesState } from 'src/app/release-management/state/release-milestones.state';
import { Observable } from 'rxjs';
import { MilestoneType } from 'src/app/release-management/models/milestoneType.model';
import { MileStone, ReleasePlan } from 'src/app/release-management/models/release.model';
import { ReleasePlanningState } from 'src/app/release-management/state/release-planning.state';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { CreateMilestoneTypeComponent } from './create-milestone-type/create-milestone-type.component';
import { Content } from '@angular/compiler/src/render3/r3_ast';
import { AddMilestoneComponent } from './add-milestone.component';
import { dateFieldName } from '@telerik/kendo-intl';
//import  '@progress/kendo-ui';


@Component({
  selector: 'matilda-release-milestones',
  templateUrl: './release-milestones.component.html',
  styleUrls: ['./release-milestones.component.scss']
})
export class ReleaseMilestonesComponent implements OnInit {
  public releaseMilestonesForm: FormGroup;
  public defaultItem: { milestoneType: string, milestoneTypeCode: number } = { milestoneType: "Select Milestone", milestoneTypeCode: null };
  public min: any;
  closeResult: string;
  public max: any;
  public releaseView: ReleasePlan;
  public previousMilestones: MileStone[] = [];
  @Select(ReleaseMilestonesState.milestoneTypes) milestoneTypes$: Observable<MilestoneType[]>;
  @Select(ReleaseMilestonesState.milestones) milestones$: Observable<MileStone[]>;
  @Select(ReleasePlanningState.releasePlan) releaseView$: Observable<ReleasePlan[]>;
  tasksDataSource;
  constructor(private _fb: FormBuilder,
    private modalService: NgbModal,
    private _store: Store,
    private hostEl: ElementRef) {
  }

  ngOnInit() {


    this.releaseView$.subscribe(
      (_releaseView: ReleasePlan[]) => {
        if (_releaseView.length > 0) {
          this.releaseView = _releaseView[0];
          this._store.dispatch(new GETMilestoneTypes(this.releaseView.releaseTypeCode));
          this._store.dispatch(new GETMilestones(this.releaseView.releaseId));
        }
      });
    this.milestones$.subscribe((_milestones) => {
      if (_milestones.length > 0) {
        this.previousMilestones = _milestones;
      }
    })
    this.initForm();
    //this.releaseMilestonesForm.statusChanges.subscribe( val => this.onFormValidation(val));
  }

  initForm() {
    this.releaseMilestonesForm = this._fb.group({
      milestoneList: this._fb.array([
        this.initMileStone()
      ])
    });

    if (this.previousMilestones.length > 0) {
      const previousMilestoneControl = <FormArray>this.releaseMilestonesForm.controls['milestoneList'];
      previousMilestoneControl.removeAt(0);
      this.previousMilestones.forEach(_previousMilestone => {
        const milestoneLevelGroup = this._fb.group({
          // milestoneTypeCode: [_previousMilestone.milestoneTypeCode],
          startDate: [new Date(_previousMilestone.startDate)],
          endDate: [new Date(_previousMilestone.endDate)],
          description: [_previousMilestone.description],
        });
        previousMilestoneControl.push(milestoneLevelGroup);
      });
    }

  }

  initMileStone() {
    return this._fb.group({
      milestoneTypeCode: [, Validators.required],
      startDate: [, Validators.nullValidator],
      endDate: [, Validators.nullValidator],
      description: ['', Validators.nullValidator]
    });
  }
  getMilestones(form) {
    return form.controls.milestoneList.controls;
  }

   addMilestone() {
    //this.noTaskLevelConditions = false;
    const control = <FormArray>this.releaseMilestonesForm.get('milestoneList');
    
    control.push(this.initMileStone());
  }
  open(content) {
        this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', centered: true , backdrop: 'static', keyboard: false,size: 'sm',backdropClass: 'light-blue-backdrop'});
  }


  deleteMilestone(milestoneIndex: number) {
    const control = <FormArray>this.releaseMilestonesForm.get('milestoneList');
    // remove the chosen row
    control.removeAt(milestoneIndex);
    if (milestoneIndex === 0) {
      //this.noTaskLevelConditions = true;
    }
  }
  addMilestoneType() {
    const createMilestoneTypeModalRef = this.modalService.open(CreateMilestoneTypeComponent, { backdrop: 'static', keyboard: false });
    createMilestoneTypeModalRef.componentInstance.releaseInfo = this.releaseView;
  }

  get milestoneList(): FormArray { return this.releaseMilestonesForm.get('milestoneList') as FormArray; }

  onStartDateChange(index) {
    this.milestoneList.controls[index].get('endDate').patchValue(null);
  }

  onSubmit(form: any) {
    if (this.releaseMilestonesForm.valid) {
      let _milestoneList = this.releaseMilestonesForm.value.milestoneList.map(_milestone => {
        let mappedMilestone = new MileStone();
        // mappedMilestone.milestoneTypeCode = _milestone.milestoneTypeCode;
        mappedMilestone.startDate = _milestone.startDate;
        mappedMilestone.endDate = _milestone.endDate;
        mappedMilestone.description = _milestone.description;
        mappedMilestone.releaseId = this.releaseView.releaseId;
        mappedMilestone.percentComplete = 2;
        mappedMilestone.milestoneId = 200;
        return mappedMilestone;
      });

      console.log(_milestoneList);
      this._store.dispatch(new UpdateMilestoneList({
        releaseId: this.releaseView.releaseId,
        milestones: _milestoneList
      }));
    }
  }

  onFormValidation(validity: string) {
    switch (validity) {
      case "VALID":
        // do 'form valid' action
        this._store.dispatch(new UpdateMilestoneList(this.releaseMilestonesForm.value.milestoneList));
        break;
      case "INVALID":
        // do 'form invalid' action
        break;
    }
  }
  
}
