import { Component, OnInit } from '@angular/core';
import * as echarts from 'echarts';
@Component({
  selector: 'matilda-number-of-deployments',
  templateUrl: './number-of-deployments.component.html',
  styleUrls: ['./number-of-deployments.component.scss']
})
export class NumberOfDeploymentsComponent implements OnInit {

  constructor() { }
  options: any;
  ngOnInit() {
    const dataAxis = ['Cost Management Dev', 'Cost Management QA', 'Discovery Management QA', 'Discovery QA', 'Migration Test', 'Migration Dev', 'Migration Dev', 'Cloud Mangement Platform Dev', 'Cloud Mangement Platform Dev', 'Cloud Mangement Platform QA', 'Cloud Mangement Platform Dev', 'svc_qa', 'svc_demo'];
    const data = [18, 15, 13, 14, 9, 13, 25, 27, 26, 21, 24, 15, 12];
    const yMax = 30;
    const dataShadow = [];

    for (let i = 0; i < data.length; i++) {
      dataShadow.push(yMax);
    }

    this.options = {
      title: {
        text: 'Avg Deployments'
      },
      legend: {
        data: ['Time'],
        align: 'left'
      },
      tooltip: {},
      calculable: true,
    xAxis: [
        {
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            silent: false,
            splitLine: {
              show: false
            },
            type: 'category',
            data: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun']
        }
    ],
    yAxis: {
        show: true,
          name: 'Time Line (in minutes)',
          nameLocation: 'middle',
          nameGap: 50,
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
                    
          textStyle: {
            color: '#999'
          }
        }
      },
    series: [
      
        {
            name: 'Success',
            type: 'line',
            // showSymbol: false,
            smooth: true,
            stack: 'true',
            barGap: 0,
            label: 'labelOption',
            data: [8, 3, 6,5,7,4],
            itemStyle: {
                    normal: {
                      color: new echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          { offset: 0, color: '#36a3f7' },
                          { offset: 0.5, color: '#36a3f7' },
                          { offset: 1, color: '#36a3f7' }
                        ]
                      )
                    }
                  },
        },
    ],
      animationEasing: 'elasticOut',
      animationDelayUpdate: function (idx) {
        return idx * 8;
      }
    };
  }

  onChartEvent(event: any, type: string) {
    console.log('chart event:', type, event);
  }

}
