import { Component, OnInit } from '@angular/core';
import { Store } from '@ngxs/store';
import { Navigate } from '@ngxs/router-plugin';
import { ResetReleasePlan } from '../../actions/release.action';
import { ClearExistingReleasePlan } from '../../actions/existing-release-plan.action';

@Component({
  selector: 'matilda-release-dashboard',
  templateUrl: './release-dashboard.component.html',
  styleUrls: ['./release-dashboard.component.scss']
})
export class ReleaseDashboardComponent implements OnInit {
  gridData: any[] = [
    {
      "status": "Completed",
      "applications": "NMROC, VISDM",
    },
    {
      "status": "In Progress",
      "applications": "iOrder, ISR, GESC, iPrice, ESP",
    },
    {
      "status": "Delayed",
      "applications": "SFDC, iQuote",
    },
  ]

  constructor(private _store: Store) { }

  ngOnInit() {
  }

  newRelease() {
    this._store.dispatch(new ResetReleasePlan({}));
    this._store.dispatch(new ClearExistingReleasePlan());
    this._store.dispatch(new Navigate(['/release-orchestration/newrelease']));
  }

}
