import { Component, OnInit, ViewEncapsulation, Input, EventEmitter, Output } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup, FormBuilder, FormArray, Validators, FormControl } from '@angular/forms';
import { Task, Stage, PreConditions } from '../../../models/release.model';
import { Select } from '@ngxs/store';
import { DynamicWorkflowState } from '../../../state/dynamic-workflow.state';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-pre-conditions',
  templateUrl: './pre-conditions.component.html',
  styleUrls: ['./pre-conditions.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PreConditionsComponent implements OnInit {
  public preconditionsForm: FormGroup;
  //Start::Dropdown settings
  singleDropdownSettings: any = {};
  closeDropdownSelection = false;
  //Start::Dropdown settings

  //Start::DropDowns
  stageOptions: any[] = [];
  taskOptions: any[] = [];
  variableOptions: any[] = [
    "overall_status",
    "action",
    "output",
    "job_name",
    "job_id",
    "summary"
  ];
  conditionOptions: any[] = ['Equal To', 'Not Equal To'];
  noTaskLevelConditions: boolean = false;
  //End::DropDowns

  @Input('task') task: Task;
  @Select(DynamicWorkflowState.getStages) stages$: Observable<Stage[]>;
  @Output('preConditionsConfiguration') preConditionsConfiguration = new EventEmitter<PreConditions>();
  public saveMessage: boolean = false;
  public validationMessage: boolean = false;

  constructor(
    private fb: FormBuilder,
    public activeModal: NgbActiveModal) { 
    }

  ngOnInit() {
    //Intialize Form
    this.initForm();

    //Multi-select dropdown
    this.singleDropdownSettings = {
      singleSelection: true,
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      allowSearchFilter: false,
      closeDropDownOnSelection: this.closeDropdownSelection
    };

    //Stages-Dropdown
    this.stages$.subscribe((stages) => {
      stages.forEach(stage => this.stageOptions.push(stage.name));
    });

    //Variable-Dropdown
    // this.filteredVariableList();

  }

  initForm() {
    this.preconditionsForm = this.fb.group({
      taskLevel: this.fb.array([
        this.initTaskLevelDependencies(),
      ]),
      ignoreFailure: [false]
    });

    // Fill the Previous Pre-conditions
    if (this.task.preConditions) {
      if (this.task.preConditions.taskLevel.length > 0) {
        const pretasklevelControl = <FormArray>this.preconditionsForm.controls['taskLevel'];
        this.preconditionsForm.controls['ignoreFailure'].setValue(this.task.preConditions.ignoreFailure);
        pretasklevelControl.removeAt(0);
        this.task.preConditions.taskLevel.forEach(taskLevel_PreCondition => {
          const taskLevelGroup = this.fb.group({
            stage: [[taskLevel_PreCondition.stage]],
            task: [[taskLevel_PreCondition.task]],
            variable: [[taskLevel_PreCondition.variable]],
            condition: [[taskLevel_PreCondition.condition]],
            value: [taskLevel_PreCondition.value]
          });
          pretasklevelControl.push(taskLevelGroup);
        });
      }

    }
  }

  initTaskLevelDependencies() {
    return this.fb.group({
      stage: ['', Validators.required],
      task: ['', Validators.required],
      variable: ['', Validators.required],
      condition: ['', Validators.required],
      value: ['']
    });
  }

  getTasklevelDependencies(form) {
    return form.controls.taskLevel.controls;
  }

  addtasklevelDependency() {
    this.noTaskLevelConditions = false;
    const control = <FormArray>this.preconditionsForm.get('taskLevel');
    control.push(this.initTaskLevelDependencies());
  }
  deletetasklevelDependency(tasklevelDependencyIndex: number) {
    const control = <FormArray>this.preconditionsForm.get('taskLevel');
    // remove the chosen row
    control.removeAt(tasklevelDependencyIndex);
    if(tasklevelDependencyIndex === 0){
      this.noTaskLevelConditions = true;
    }
  }

  onStageSelect(selectedStage: string, index: number, form: any) {
    this.taskOptions = [];
    form.controls.taskLevel.controls[index].controls.task.setValue('');
    this.stages$.subscribe(stages => {
      stages.forEach(stage => {
        if (stage.name === selectedStage) {
          stage.tasks.forEach(task => {
            this.taskOptions.push(task.taskName);
          });
        }
      });
    });
  }

  onStageDeSelect(selectedStage: string, index: number, form: any) {
    this.taskOptions = [];
    form.controls.taskLevel.controls[index].controls.task.setValue('');
  }

  // filteredVariableList() {
  //   this._preconditionsservice.getVariableList(this.task.serviceName.toLowerCase().replace(/ /g, "_"), this.task.action.toLowerCase().replace(/ /g, "_"))
  //     .subscribe(variableList => this.variableOptions = variableList);
  // }

  public onSubmit(form: any): void {
    event.stopPropagation();

    if (this.preconditionsForm.valid) {
      for (let precondition of form.taskLevel) {
        if (Array.isArray(precondition.stage)) {
          precondition.stage = precondition.stage[0];
          precondition.task = precondition.task[0];
          precondition.condition = precondition.condition[0];
          precondition.variable = precondition.variable[0];
        } else {
          precondition.stage = precondition.stage;
          precondition.task = precondition.task;
          precondition.condition = precondition.condition;
          precondition.variable = precondition.variable;
        }
      }
      if(form.taskLevel.length > 0){
        this.preConditionsConfiguration.emit(form);
        this.saveMessage = true;
        this.validationMessage = false;
        setTimeout(() => {
          this.saveMessage = false;
        }, 3000);
      }else{
        this.saveMessage = false;
        this.validationMessage = true;
      }
     
    } else {
      this.validateAllFormFields(this.preconditionsForm);
    }

  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

}
