import { State, Store, Action, StateContext, Selector, NgxsOnInit } from '@ngxs/store';
import { GetMigrationList, PostServiceDiscovery, SaveMigrationTable, GetServiceInformation, GetCloudProviders, GetAccounts, GetRegionslist, GetProjects, GetEnvironments, GetTargetConfiguration, GetSourceConfiguration } from '../actions/migration.action';
import { HttpClient } from '@angular/common/http';
import settings from '../../settings';
import * as _ from 'lodash';

export class MigrationStateModel {
    migrationList: any;
    migrationserviceDiscoveryList: any;
    saveMigrationTable: any;
    serviceInformationList: any;
    cloudProviders: any;
    accounts: any;
    regionslist: any;
    projects: any;
    environments: any;
    targetConfigurations: any;
    sourceConfigurations: any;
}

@State<MigrationStateModel>({
    name: 'migration',

    defaults: {
        migrationList: null,
        migrationserviceDiscoveryList: null,
        saveMigrationTable: null,
        serviceInformationList: null,
        cloudProviders: null,
        accounts: null,
        regionslist: null,
        projects: null,
        environments: null,
        targetConfigurations: null,
        sourceConfigurations: null
    }
})

export class MigrationState  {
    headers: Headers;
    constructor(private _store: Store, private _http: HttpClient) {
    }

   

    @Selector()
        static migrationList(state: MigrationStateModel) {
        return state.migrationList;
    }

    @Action(GetMigrationList)
        getGetMigrationList(context: StateContext<MigrationStateModel>, { payload }: any) {
            var state = context.getState();

            return this._http.get(_.template(settings.MIGRATION_LIST_GRID)({ }))
            .subscribe(
                (data: any[]) => {
                    state.migrationList =  data;
                    context.patchState(state);
                }
            );
        }

    @Selector()
        static migrationserviceDiscoveryList(state: MigrationStateModel) {
        return state.migrationserviceDiscoveryList;
    }

    @Action(PostServiceDiscovery)
        postServiceDiscovery(context: StateContext<MigrationStateModel>, { payload }: any) {
            var state = context.getState();
            let url = payload["url"];
            return this._http.post(_.template(settings.SERVICE_LIST_URL)({ url }), payload.postData)
            .subscribe(
                (data: any[]) => {
                    state.migrationserviceDiscoveryList =  data;
                    context.patchState(state);
                }
            );
        }

    @Selector()
        static saveMigrationTable(state: MigrationStateModel) {
        return state.saveMigrationTable;
    }

    @Action(SaveMigrationTable)
        saveMigrationTable(context: StateContext<MigrationStateModel>, { payload }: any) {
            var state = context.getState();

            state.saveMigrationTable =  payload;
            context.patchState(state);
        }


    @Selector()
        static serviceInformationList(state: MigrationStateModel) {
        return state.serviceInformationList;
    }

    @Action(GetServiceInformation)
        getServiceInformation(context: StateContext<MigrationStateModel>, { payload }: any) {
            var state = context.getState();
            let ipAddress = payload;
            let os = localStorage.getItem("os");
            let name = localStorage.getItem("migrationName");
            return this._http.get(_.template(settings.SERVICE_INFO_URL)({ os, name, ipAddress }))
            .subscribe(
                (data: any[]) => {
                    state.serviceInformationList =  data;
                    context.patchState(state);
                }
            );
        }



    @Selector()
        static cloudProviders(state: MigrationStateModel) {
        return state.cloudProviders;
    }
    @Action(GetCloudProviders)
    getCloudProviders(context: StateContext<MigrationStateModel>, { payload }: any) {
        var state = context.getState();
        return this._http.get(_.template(settings.CLOUDPROVIDER_URL)({ }))
        .subscribe(
            (data: any[]) => {
                state.cloudProviders =  data;
                context.patchState(state);
            }
        );
    }

    @Selector()
        static accounts(state: MigrationStateModel) {
        return state.accounts;
    }
    @Action(GetAccounts)
    getAccounts(context: StateContext<MigrationStateModel>, { payload }: any) {
        var state = context.getState();
        return this._http.get(_.template(settings.ACCOUNT_URL)({ }))
        .subscribe(
            (data: any[]) => {
                state.accounts =  data;
                context.patchState(state);
            }
        );
    }

    @Selector()
        static regionslist(state: MigrationStateModel) {
        return state.regionslist;
    }
    @Action(GetRegionslist)
    getRegionslist(context: StateContext<MigrationStateModel>, { payload }: any) {
        var state = context.getState();
        return this._http.get(_.template(settings.REGION_URL)({ }))
        .subscribe(
            (data: any[]) => {
                state.regionslist =  data;
                context.patchState(state);
            }
        );
    }

    @Selector()
        static projects(state: MigrationStateModel) {
        return state.projects;
    }
    @Action(GetProjects)
    getProjects(context: StateContext<MigrationStateModel>, { payload }: any) {
        var state = context.getState();
        return this._http.get(_.template(settings.PROJECT_URL)({ }))
        .subscribe(
            (data: any[]) => {
                state.projects =  data;
                context.patchState(state);
            }
        );
    }

    @Selector()
        static environments(state: MigrationStateModel) {
        return state.environments;
    }
    @Action(GetEnvironments)
    getEnvironments(context: StateContext<MigrationStateModel>, { payload }: any) {
        var state = context.getState();
        return this._http.get(_.template(settings.ENVIRONMENT_URL)({ }))
        .subscribe(
            (data: any[]) => {
                state.environments =  data;
                context.patchState(state);
            }
        );
    }


    @Selector()
        static targetConfigurations(state: MigrationStateModel) {
        return state.targetConfigurations;
    }
    @Action(GetTargetConfiguration)
        getTargetConfiguration(context: StateContext<MigrationStateModel>, { payload }: any) {
            var state = context.getState();
            return this._http.get(_.template(settings.TARGET_CONFIG_URL)({ }))
            .subscribe(
                (data: any[]) => {
                    state.targetConfigurations =  data;
                    context.patchState(state);
                }
            );
    }
    @Selector()
        static sourceConfigurations(state: MigrationStateModel) {
        return state.sourceConfigurations;
    }
    @Action(GetSourceConfiguration)
        getSourceConfiguration(context: StateContext<MigrationStateModel>, { payload }: any) {
            var state = context.getState();
            return this._http.get(_.template(settings.SOURCE_CONFIG_URL)({ }))
            .subscribe(
                (data: any[]) => {
                    state.sourceConfigurations =  data;
                    context.patchState(state);
                }
            );
        }


}
