//import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';

import { AppComponent } from './app.component';
import { LayoutModule } from './theme/layouts/layout.module';
import { FormsModule } from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { AuthModule } from './auth/auth.module';
import { SimpleNotificationsModule } from 'angular2-notifications';
import { DndModule } from 'ng2-dnd';
//import { NgxPaginationModule } from 'ngx-pagination';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ScriptLoaderService } from './_services/script-loader.service';
import { ArchwizardModule } from 'angular-archwizard';
import { NgSelectModule } from '@ng-select/ng-select';
import { ConfigService } from './app-config.service';
import { ThemeComponent } from './theme/theme.component';
import { ReleaseManagementModule } from './release-management/release-management.module';
import { SharedModule } from './shared/shared.module';
import { OnBoardingModule } from './on-boarding/on-boarding.module';
import { MigrationModule } from './migration/migration.module';
import { CostManagementModule } from './cost-management/cost-management.module';
import { DiscoveryModule } from './discovery/discovery.module';
import { NgxsModule } from '@ngxs/store';
import { NgxsReduxDevtoolsPluginModule } from '@ngxs/devtools-plugin';
import { NgxsLoggerPluginModule } from '@ngxs/logger-plugin'
import { HttpClientModule } from '@angular/common/http';
import { ChartsModule } from '@progress/kendo-angular-charts';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// Imports the AutoComplete module
import { AutoCompleteModule } from '@progress/kendo-angular-dropdowns';

// Imports the ComboBox module
import { ComboBoxModule } from '@progress/kendo-angular-dropdowns';
import 'hammerjs';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { AssetManagementModule } from './asset-management/asset-management.module';
import { MonitoringModule } from './monitoring/monitoring.module';
import { NgxsRouterPluginModule } from '@ngxs/router-plugin';
import { NgxsFormPluginModule } from '@ngxs/form-plugin';
import { GaugesModule } from '@progress/kendo-angular-gauges';
import { NgxsStoragePluginModule } from '@ngxs/storage-plugin';
import { EnvironmentMigrationModule } from './environment-migration/environment-migration.module';
import { TruncateTextPipe } from './truncate-text.pipe';
import { ExcelExportModule } from '@progress/kendo-angular-excel-export';


export function configServiceFactory(config: ConfigService) {
  return () => config.load();
}
@NgModule({
  declarations: [
    ThemeComponent,
    AppComponent,
    TruncateTextPipe,
  ],
  imports: [
    LayoutModule,
    // BrowserModule,
    FormsModule,
    ArchwizardModule,
    ButtonsModule,
    AppRoutingModule,
    NgSelectModule,
    
    NgxsModule.forRoot(),
    NgxsStoragePluginModule.forRoot(),
    NgxsFormPluginModule.forRoot(),
    NgxsRouterPluginModule.forRoot(),
    NgxsReduxDevtoolsPluginModule.forRoot({
      disabled: false
    }),
    NgxsLoggerPluginModule.forRoot({
      disabled: false
    }),
    NgbModule.forRoot(),
    DndModule.forRoot(),
    AuthModule,
    HttpClientModule,
    SimpleNotificationsModule.forRoot(),
    NgMultiSelectDropDownModule.forRoot(),
    ReleaseManagementModule,
    SharedModule,
    OnBoardingModule,
    MigrationModule,
    EnvironmentMigrationModule,
    CostManagementModule,
    DiscoveryModule,
    ChartsModule,
    BrowserAnimationsModule,
    AutoCompleteModule,
    ComboBoxModule,
    InputsModule,
    GaugesModule,
    AssetManagementModule,
    MonitoringModule,
    ExcelExportModule
  ],
  providers: [ScriptLoaderService, ConfigService,
    {
      provide: APP_INITIALIZER,
      useFactory: (config: ConfigService) => () => config.load(),
      deps: [ConfigService],
      multi: true
    }],
  schemas: [
    CUSTOM_ELEMENTS_SCHEMA
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
