import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, ValidationErrors, AbstractControl, ValidatorFn } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngxs/store';
import { AddMilestoneType } from 'src/app/release-management/actions/release-milestones.action';
import { ReleasePlan } from 'src/app/release-management/models/release.model';
import { MilestoneType } from 'src/app/release-management/models/milestoneType.model';

@Component({
  selector: 'matilda-create-milestone-type',
  templateUrl: './create-milestone-type.component.html',
  styleUrls: ['./create-milestone-type.component.scss']
})
export class CreateMilestoneTypeComponent implements OnInit {
  public createMilestoneTypeForm: FormGroup;
  public saveMessage: boolean = false;
  @Input('releaseInfo') releaseInfo: ReleasePlan;
  constructor(private _fb: FormBuilder,
    public activeModal: NgbActiveModal,
    private _store: Store) { }

  ngOnInit() {
    this.initForm();
  }

  initForm(){
    this.createMilestoneTypeForm = this._fb.group({
      milestoneType: [,[
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(30),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
        this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_-]+$'), { 'remainingpattern': 'should not contain special characters' }),
      ]]
    });
  }

  onSubmit(form: any){
    if(this.createMilestoneTypeForm.valid){
      this.saveMessage = true;
      let _milestoneType = new MilestoneType();
      _milestoneType.milestoneType = form.milestoneType;
      this._store.dispatch(new AddMilestoneType({
        releaseId: this.releaseInfo.releaseId,
        releaseTypeCode: this.releaseInfo.releaseTypeCode,
        milestoneType: _milestoneType
      })).subscribe( value => { 
        this.saveMessage = false;
        this.activeModal.dismiss();
      });
    }
  }
  get milestoneType(){
    return this.createMilestoneTypeForm.get('milestoneType');
  }

  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }

}
