import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MigrationListComponent } from './components/migration-list/migration-list.component';
import { StartMigrationComponent } from './components/start-migration/start-migration.component';
import { ServiceDiscoveryComponent } from './components/start-migration/service-discovery/service-discovery.component';
import { ServiceInformationComponent } from './components/start-migration/service-information/service-information.component';
import { MigrationStrategyComponent } from './components/start-migration/migration-strategy/migration-strategy.component';
import { TargetConfigurationComponent } from './components/start-migration/target-configuration/target-configuration.component';
import { MigrationReviewComponent } from './components/start-migration/migration-review/migration-review.component';
import { MigrationDashboardComponent } from './components/migration-dashboard/migration-dashboard.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ChartsModule } from '@progress/kendo-angular-charts';
import { EnvironmentMigrationRoutingModule } from './environment-migration-routing.module';
import { LayoutModule } from '../theme/layouts/layout.module';
import { SharedModule } from '../shared/shared.module';
import { GaugesModule } from '@progress/kendo-angular-gauges';
import { ArchwizardModule } from 'angular-archwizard';
import { NgxsModule } from '@ngxs/store';
import { GridModule } from '@progress/kendo-angular-grid';
import { MigrationState } from './state/migration.state';

@NgModule({
  declarations: [MigrationListComponent, StartMigrationComponent, ServiceDiscoveryComponent, ServiceInformationComponent, MigrationStrategyComponent, TargetConfigurationComponent, MigrationReviewComponent, MigrationDashboardComponent],
  imports: [
    CommonModule,
    FormsModule,
    GridModule,
    ReactiveFormsModule,
    ChartsModule,
    EnvironmentMigrationRoutingModule,
    LayoutModule,
    SharedModule,
    GaugesModule,
    ArchwizardModule,
     NgxsModule.forFeature([
     MigrationState
    ])
  ]
})
export class EnvironmentMigrationModule { }
