
import {throwError as observableThrowError,  BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import settings from '../../settings';
import * as _ from 'lodash';

@Injectable({
    providedIn: 'root'
})
export class ListOfServicesService {
    headers: Headers;
    url =  "http://192.168.10.133:6002/api/release/";//<release_id>/<env>/wf;
    // "http://192.168.20.221:6031/matilda/wf/workflow/create";
    private dataStore: any;
    private _workflowData = new BehaviorSubject<any>(null);
    get mapworkflow(): Observable<any> {
        return this._workflowData.asObservable();
    }
    constructor(private http: Http) {
        this.headers = new Headers();
        this.headers.append('Content-Type', 'application/json');
        this.dataStore = {
            wrkflwData: null
        };
    }
    public getServerMetrics() {
        return this.http.get(_.template(settings.RELEASE_SERVER_METRICS)({}))
            .map(this.extractJSONResponse)
            .catch(this.handleError)
    }
   
    public getEnvList(): Observable<any> {
        return this.http.get(_.template(settings.CREATE_ENV_URL)({}))
            .map(this.extractJSONResponse)
            .catch(this.handleError);
    }
    public getConfigureService(service: string, action: string): Observable<any> {
        return this.http.get(_.template(settings.SERVICE_ACTION_URL)({ service, action }))
            .map(this.extractJSONResponse)
            .catch(this.handleError);
    }

    public getReleaseListSerivce() {
        return this.http.get(_.template(settings.RELEASE_LIST_URL)({}))
            .map(this.extractJSONResponse)
            .catch(this.handleError)
    }
    public getProducts() {
        return this.http.get(_.template(settings.DEPLOYMENTDEF_URL)({}))
            .map(this.extractJSONResponse)
            .catch(this.handleError)
    }

    public getWorkflowListSerivce() {
        return this.http.get(_.template(settings.WORKFLOW_LIST_URL)({}))
            .map(this.extractJSONResponse)
            .catch(this.handleError)
    }
    public sendWorkflow(id: number): void {
        this.http.get(_.template(settings.EXISTING_WORKFLOW_URL)({ id }))
            .map(this.extractJSONResponse)
            .catch(this.handleError)
            .subscribe((data) => {
                this.dataStore.wrkflwData = data || {};
                this._workflowData.next(this.dataStore.wrkflwData);
            });
    }
    public getworkflowTemplate(id: string): Observable<any> {
        return this.http.get(_.template(settings.EXISTING_WORKFLOW_URL)({ id }))
            .map(this.extractJSONResponse)
            .catch(this.handleError);
    }
    public createWorkFlow(releaseId: string, env: string, data: any) {
        let options = new RequestOptions({ headers: this.headers });
        return this.http.post(this.url+releaseId+'/'+env+'/wf', data, options)
            .map(this.extractJSONResponse)
            .catch(this.handleError);
    }

    private extractJSONResponse(res: Response) {
        const body = res.json();
        return body;
    }


    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        console.error(errMsg);
        return observableThrowError(errMsg);
    }
}
