import { ActivatedRoute } from '@angular/router';
import { Component, OnInit, Input, Output, ViewChild, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';

@Component({
  selector: 'app-edit-input',
  templateUrl: './input-edit.component.html',
  styleUrls: []
})
export class InputChangeComponent {
  formActive = false;
  form: FormGroup;
  @Input() stage;
  @Output() onNameChanged = new EventEmitter();
  @ViewChild('editInput') nameInputField;
  public operation: string; 

  constructor(
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute
  ) {
    this.operation = this.activatedRoute.snapshot.url.join('/');
  }

  openForm() {
    this.form = this.formBuilder.group({
      name: [this.stage,
        [
          Validators.required,
          this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
        ]]
    });
    this.formActive = true;
    this.focusNameField();
  }
  
  get name() {
    return this.form.get('name');
  }

  closeForm() {
    this.formActive = false;
  }

  focusNameField() {
    setTimeout(() => {
      this.nameInputField.nativeElement.focus();
    });
  }

  onFormSubmit() {
    if (this.form.valid) {
      this.stage.name = this.form.getRawValue().name;
      this.onNameChanged.next(this.stage.name);
      this.formActive = false;
    }
    else{
      this.validateAllFormFields(this.form);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }

}
