import { Component, OnInit, Input } from '@angular/core';
import { GroupDescriptor } from '@progress/kendo-data-query';

@Component({
  selector: 'matilda-impacted-systems-grid',
  templateUrl: './impacted-systems-grid.component.html',
  styleUrls: ['./impacted-systems-grid.component.scss']
})
export class ImpactedSystemsGridComponent implements OnInit {
  @Input('gridData') gridData: any[];
  public groups: GroupDescriptor[] = [{ field: 'ip' }];
//   gridData: any[] = [  
//     {  
//        "status":"Installed",
//        "os":"CentOS",
//        "platform":"Linux",
//        "ip":"192.168.10.188",
//        "applications":[  
//           {  
//              "name":"Matilda Migration",
//              "status":"Complete"
//           }
//        ]
//     }
//  ]
  constructor() { }

  ngOnInit() {
  }
  
  getStatusBadge(status: string) {
    if (status) {
      if (status.toLowerCase() === 'completed' || status.toLowerCase() === 'complete' ) {
        return 'm-badge--success';
      } else if (status.toLowerCase() === 'failed' || status.toLowerCase() === 'not started') {
        return 'm-badge--danger';
      }
      else if (status.toLowerCase() === 'in progress' || status.toLowerCase() === 'pending') {
        return 'm-badge--warning';
      } else {
        return 'm-badge--metal';
      }
    }else{
      return 'm-badge--brand';
    }
  }
}
