import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { CalendarView,  CalendarEventAction, CalendarEventTimesChangedEvent, CalendarEvent } from 'angular-calendar';
import { Subject, from } from 'rxjs';
import {ReleaseCalendar} from 'src/app/release-management/models/release.model'
import {ReleasePostService } from 'src/app/release-management/services/release-post.service'
import {
  startOfDay,
  endOfDay,
  subDays,
  addDays,
  endOfMonth,
  isSameDay,
  isSameMonth,
  addHours
} from 'date-fns';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { deserialize } from 'json-typescript-mapper';




const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
  selector: 'matilda-release-calendar',
  templateUrl: './release-calendar.component.html',
  styleUrls: ['./release-calendar.component.scss']
})
export class ReleaseCalendarComponent implements OnInit {

  @ViewChild('modalContent') modalContent: TemplateRef<any>;
  @ViewChild('modalContent1') modalContent1: TemplateRef<any>;
  view: CalendarView = CalendarView.Month;
public relcalendar:ReleaseCalendar=new ReleaseCalendar();
  CalendarView = CalendarView;
  
  viewDate: Date = new Date();

  modalData: {
    action: string;
    event: CalendarEvent;
  };

  actions: CalendarEventAction[] = [
    {
      label: '<i class="fa fa-fw fa-pencil"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.handleEvent('Edited', event);
      }
    },
    {
      label: '<i class="fa fa-fw fa-times"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        // this.events = this.events.filter(iEvent => iEvent !== event);
        this.handleEvent1('Deleted', event);
      }
    }
  ];

  refresh: Subject<any> = new Subject();
  // {
  //   start: subDays(startOfDay(new Date()), 1),
  //   end: addDays(new Date(), 1),
  //   title: 'A 3 day event',
  //   color: colors.red,
  //   actions: this.actions,
  //   allDay: true,
  //   resizable: {
  //     beforeStart: true,
  //     afterEnd: true
  //   },
  //   draggable: true
  // }

  events: CalendarEvent[] = [];
  
  
  activeDayIsOpen: boolean = true;
  closeResult: string;
  public releaseOptions=[];
  public releasePlans=[];
    
    public value2: Date = new Date();
public test:any;
    public value1: any=[];
    public value3:string;
    public value4:string;
    public value5:string;
  constructor(private modal: NgbModal, private postcalendar:ReleasePostService ) {}

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      this.viewDate = date;
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
    }
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    event.start = newStart;
    event.end = newEnd;
    this.handleEvent('Dropped or resized', event);
    this.refresh.next();
  }
   
  ngOnInit(){
    this.postcalendar.getReleasePlans().subscribe(data => {
      this.releasePlans = data.map(_releaseplan => deserialize(ReleaseCalendar, _releaseplan));
       this.releasePlans.forEach((_releasePlan: ReleaseCalendar)=> {
         let _calenderEvent: any = Object.assign({},{
           end: new Date(_releasePlan.releaseDate),
           title:_releasePlan.releaseName,
           actions: this.actions,
           allDay: true,
    resizable: {
      beforeStart: true,
      afterEnd: true
    },
    draggable: true
          
         });
         this.events.push(_calenderEvent);
       })
     

     
    });
    this.postcalendar.getReleaseType().subscribe(data => {
      this.releaseOptions = data;
    });

  }
  handleEvent(action: string, event: CalendarEvent): void {
    this.modalData = { event, action };
    this.modal.open(this.modalContent, { size: 'lg' });
  }
  handleEvent1(action: string, event: CalendarEvent): void {
    this.modalData = { event, action };
    this.modal.open(this.modalContent1, { size: 'lg' });
  }
  
  saveEvent(){
    
   let postJson={
    
      
      release_type_cd:this.value1.release_type_cd,
     
      
      release_plan_name:this.value3,
     
      
      release_plan_description:	this.value4,
      
      release_owner:this.value5,
           
      release_dt:this.value2
     
   }
   this.postcalendar.postCalendarData(postJson).subscribe(data=>{
     
   })
  }
  
  open(content) {
    this.modal.open(content, 
    {ariaLabelledBy: 'modal-basic-title', size:'lg', centered:true, backdrop:'static', keyboard:false }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      // this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  saveEdit(){
    let putJson={
    
      
      release_type_cd:this.value1.release_type_cd,
     
      
      release_plan_name:this.value3,
     
      
      release_plan_description:	this.value4,
      
      release_owner:this.value5,
           
      release_dt:this.value2
     
   }
   this.postcalendar.putCalendarData(putJson).subscribe(data=>{
     
   })
  }
  deleteEvent(){
    let deleteJson={
    
      
      release_type_cd:this.value1.release_type_cd,
     
      
      release_plan_name:this.value3,
     
      
      release_plan_description:	this.value4,
      
      release_owner:this.value5,
           
      release_dt:this.value2
     
   }
   this.postcalendar.deleteCalendarData(deleteJson).subscribe(data=>{
     
   })
    
  }
  
}

