import { Component, OnInit, EventEmitter, Output, Input, SimpleChanges, OnChanges } from '@angular/core';
import { FormGroup, Validators, FormControl, ValidatorFn, AbstractControl, ValidationErrors, FormArray } from '@angular/forms';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotificationsService } from 'angular2-notifications';

import { SerivceModal } from '../../../models/migration.model';
import { MigrationListService } from '../../../services/migration-list.service';
import { ModaldialogComponent } from '../../../../shared/modaldialog/modaldialog.component';
import { PostService } from '../../../services/postservice';

import * as _ from 'lodash';
import { PageChangeEvent, GridDataResult, DataStateChangeEvent, SelectAllCheckboxState, RowArgs } from '@progress/kendo-angular-grid';

import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';

import { PostServiceDiscovery, SaveMigrationTable } from '../../../actions/migration.action';
import { MigrationState } from '../../../state/migration.state';

@Component({
  selector: 'matilda-service-discovery',
  templateUrl: './service-discovery.component.html',
  styleUrls: ['./service-discovery.component.scss']
})
export class ServiceDiscoveryComponent implements OnInit {
  public _serviceForm: FormGroup;
  public credentials = [{
    "controlType": "TextBox",
    "key": "user",
    "label": "hostusername",
    "order": 1,
    "placeholder": "",
    "required": "false"
  },
  {
    "controlType": "TextBox",
    "key": "hostpassword",
    "label": "hostpassword",
    "order": 2,
    "placeholder": "",
    "required": "false"
  }]
  model: any = {};
  loading = false;
  returnUrl: string;
  public gridData: any[];
  public testingarray: any[] = [];
  public checkedRows: any[] = [];
  public selectedrows: any[] = [];
  public mySelection: any[] = [];

  public selectedLink: string = "login";
  public selectAllState: SelectAllCheckboxState = 'unchecked';
  @Output('selectionChange') selectionChange = new EventEmitter();
  @Output() serviceform = new EventEmitter<FormGroup>();
  @Input('data') data: SerivceModal;
  public _load: boolean= true;
  public type = true;
  public defaultOS: string = 'windows';
  public operatingSystems: any[] = ["windows", "linux"]
  public prevalidKeys: any[] = [];

  @Select(MigrationState.migrationserviceDiscoveryList) serviceDiscoveryList$: Observable<any[]>;

  constructor(
    private _fb: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private _notificationsService: NotificationsService,
    private _serviceList: MigrationListService,
    private _modalService: NgbModal,
    private postRequest: PostService,
    private _store: Store
  ) { }

  sdStatus: string = "false";
  @Output() sdEvent = new EventEmitter<string>();

  setIpAddress(e: any) {
        if (e === 'cidr') {
            this.type = true;
            this.addCidr();
            this.removeRange();

        } else {
            this.type = false;
            this.addRange();
            this.removeCidr();

        }
    }
    addRange() {
        this._serviceForm.addControl('netrange', new FormControl('', Validators.required));
        this._serviceForm.addControl('netrangeport', new FormControl('', Validators.required));
    }
    removeRange() {
        this._serviceForm.removeControl('netrange');
        this._serviceForm.removeControl('netrangeport');
    }
    addCidr() {
        this._serviceForm.addControl('host', new FormControl('', Validators.required));
    }
    removeCidr() {
        this._serviceForm.removeControl('host');
    }

  setradio(e: string): void {
    this.selectedLink = e;
    if (e == 'login') {
      this.hostusername.setValidators([Validators.required,
      Validators.minLength(4),
      Validators.maxLength(30),
      this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
      this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_ ]+$'), { 'remainingpattern': 'should not contain special characters' })]);
      this.hostpassword.setValidators([Validators.required,
      Validators.minLength(4),
      Validators.maxLength(30),]);

      // this.hostip.setValue('192.168.0.24');
      this.hostusername.updateValueAndValidity();
      this.hostpassword.updateValueAndValidity();

    }
    else if (e == 'sso' || e == 'credentialtour' || e == 'ssh') {
      this.hostusername.setValidators([Validators.nullValidator]);
      this.hostpassword.setValidators([Validators.nullValidator]);
      this.hostpassword.setValue(null);
      // this.hostip.setValue('192.168.0.24');
      this.hostusername.updateValueAndValidity();
      this.hostpassword.updateValueAndValidity();
    }
  }

  public mySelectionKey(context: RowArgs): string {
    return context.dataItem;
  }
  public onSelectedKeysChange(e) {
    this._store.dispatch(new SaveMigrationTable(e));
    const len = e.length;
    if (len === 0) {
      this.selectAllState = 'unchecked';
      this.sdStatus = "false";
      this.sdEvent.emit(this.sdStatus);
    } else if (len > 0 && len < this.gridData.length) {
      this.selectAllState = 'indeterminate';
      this.sdStatus = "true";
      this.sdEvent.emit(this.sdStatus);
    } else {
      this.selectAllState = 'checked';
      this.sdStatus = "true";
      this.sdEvent.emit(this.sdStatus);
    }
    this.gridData.forEach((e1) => {
      let i = this.gridData.indexOf(e1);
      e.forEach((e2) => {
        if (e1['IPAddress'] === e2['IPAddress']) {
          // objMap[e1]=objMap[e1]+1||1 ;
          let index = this.gridData.findIndex(function (el) {
            return el.IPAddress === e2.IPAddress;
          })
          this.selectedrows[index] = false;
        }

        else if (e1['IPAddress'] !== e2['IPAddress']) {
          let i = this.gridData.findIndex(function (ele) {
            return ele.IPAddress;
          })
          this.selectedrows[i] = true;
        }

      });
    });

    // for(let k = 0; k<this.gridData.length; k++){
    //   console.log(this.gridData[k].IPAddress)
    //   const char: string= this.gridData[k].IPAddress
    //   if(this.itemcompare(char,e, len)){
    //     this.selectedrows[k]= false;
    //   }
    // else{
    //   this.selectedrows[k]= true;
    // }
    // }
    this.selectionChange.emit({ data: e });
  }


  public itemcompare(data: string, selected: any[], len: number) {
    for (let i = 0; i < len; i++) {
      if (selected[i]['IPAddress'] == data) {
        return true;
      }
      return false;
    }
  }
  public onSelectAllChange(checkedState: SelectAllCheckboxState) {
    if (checkedState === 'checked' && this.mySelection != []) {
      this.mySelection = this.gridData.map((item) => item);
      this.selectAllState = 'checked';
    } else {
      this.mySelection = [];
      this.selectAllState = 'unchecked';
    }
  }
  ngOnInit() {
    // this.setradio('login');

    this.initForm();
    if (this.selectedLink == 'login') {
      this.hostusername.setValidators([Validators.required,
      Validators.minLength(4),
      Validators.maxLength(30),
      this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
      this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_ ]+$'), { 'remainingpattern': 'should not contain special characters' })]);
      this.hostpassword.setValidators([Validators.required,
      Validators.minLength(4),
      Validators.maxLength(30),]);
      // this.hostip.setValue('192.168.0.24');
      this.hostusername.updateValueAndValidity();
      this.hostpassword.updateValueAndValidity();

    }
    else if (this.selectedLink == 'sso' || this.selectedLink == 'credentialtour' || this.selectedLink == 'ssh') {
      this.hostusername.setValidators([Validators.nullValidator]);
      this.hostpassword.setValue(null);
      // this.hostip.setValue('192.168.0.24');
      this.hostusername.updateValueAndValidity();
      this.hostpassword.updateValueAndValidity();
    }
  }
  public initForm() {
    this._serviceForm = this._fb.group({
      // id: [this.data.id, Validators.nullValidator],
      name: ['', Validators.required],
      hostusername: ['', Validators.nullValidator],
      hostpassword: ['', Validators.nullValidator],
      host: this._fb.array([this.initTags()]),
      operating_system: ['', Validators.required]
    });
    this.serviceform.emit(this._serviceForm);
    this._serviceForm.controls['operating_system'].setValue(this.defaultOS, {onlySelf: true});
  }

  initTags() {
    return this._fb.group({
        ip: ['',
            [
                Validators.nullValidator,
                Validators.maxLength(15),
                // this.regexValidator(new RegExp('^([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})[,]([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})$'), { 'remainingpattern': 'IP Address format should be 192.168.0.0' }),
                this.isKeyUnique.bind(this) // async Validator passed as 3rd parameter
            ]]
    });


}

gethips(form) {
  return form.controls.host.controls;
}
existingKeys(existingKeys: any[]) {
  if (existingKeys.length > 1) {
      let totalKeys: any[] = existingKeys;
      this.prevalidKeys = this.remove(totalKeys, totalKeys[totalKeys.length - 1]);
  }
}

remove(array, element) {
  return array.filter(e => e !== element);
}
public addhip(event: Event) {
  const arrayControl = <FormArray>this._serviceForm.controls['host'];
  if(arrayControl.length < 5){
    arrayControl.push(this.initTags());
  }
}
delInput(index: number): void {
    const arrayControl = <FormArray>this._serviceForm.controls['host'];
    arrayControl.removeAt(index);
}
isKeyUnique(control: FormControl) {
  let keyUnique;
  for (let previousKey of this.prevalidKeys) {
      if (previousKey.ip == control.value) {
          keyUnique = { 'keyInUse': true }
          break;
      } else {
          keyUnique = null;
      }
  }
  return keyUnique;

}

  get name() {
    return this._serviceForm.get('name');
  }
  get operating_system() {
    return this._serviceForm.get('operating_system');
  }
  get hostip() {
    return this._serviceForm.get('hostip');
  }
  get hostusername() {
    return this._serviceForm.get('hostusername');
  }
  get hostpassword() {
    return this._serviceForm.get('hostpassword');
  }



  ngOnChanges(changes: SimpleChanges) {
    if (changes.serviceform && !changes.serviceform.firstChange) {
      this._serviceForm.patchValue({
        name: [this.data.name, Validators.required],
        host: [this.data.host, [
          Validators.required,
          this.regexValidator(new RegExp('^([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})$'), { 'remainingpattern': 'IP Address format should be 192.168.0.0' })],
        ],
        hostusername: [this.data.hostusername, [
          Validators.required,
          Validators.minLength(4),
          Validators.maxLength(30),
          this.regexValidator(new RegExp('^[a-zA-Z]{1}'), { 'firstLetterpattern': 'should not start with space' }),
          this.regexValidator(new RegExp('^[a-zA-Z]{1}[a-zA-Z0-9_ ]+$'), { 'remainingpattern': 'should not contain special characters' })
        ]],
        hostpassword: [this.data.hostpassword, [
          Validators.required,
          Validators.minLength(4),
          Validators.maxLength(30),
        ]]
      })
    }
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }
  regexValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }
  public onServiceSubmit(event: Event, dataform: any): void {
    let postData = {};
    event.stopPropagation();
    if (this._serviceForm.valid) {

      

      

      this._notificationsService.info("Discovering Services..");
      let apiURL, os;
      if(dataform.operating_system == "windows"){
        apiURL = "/windows";
        // os = "winhost"
       os = "windows"
      } else {
        apiURL = "";
      //  os = "hostinfo"
        os = "linux"
      }

      localStorage.setItem("os", os);
      localStorage.setItem("migrationName", dataform["name"]);

      let tempHost = [];
      for (let key in dataform["host"]) {
        tempHost.push(dataform["host"][key].ip);
      }
      delete dataform["operating_system"];
      dataform["host"] = tempHost;

      postData['request'] = dataform;

      this._store.dispatch(new PostServiceDiscovery({"postData": postData, "url": apiURL}));

      this.serviceDiscoveryList$.subscribe(res => {
            if(res){
              setTimeout(() => {
                this.gridData = res;
                if (this.gridData.length != 0) {
                          for (let i = 0; i < this.gridData.length; i++) {
                            this.selectedrows.push(true);
                          }
                        }
                        this._load = false;
              }, 4000);

            }
        });
    } else {
      this.validateAllFormFields(this._serviceForm);
    }
  }
}
