import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Select, Store } from '@ngxs/store';
import { DynamicWorkflow, ReleaseEnvironment } from '../../../models/release.model';
import { Observable } from 'rxjs';
import { ViewDynamicWorkflowState } from '../../../state/view-dynamic-workflow.state';
import { Navigate } from '@ngxs/router-plugin';
import { OpenSaveTemplateConfiguration, ViewTaskConfiguration, PollWorkflow } from 'src/app/release-management/actions/workflow.action';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ViewPreConditionsComponent } from '../pre-conditions/view-pre-conditions/view-pre-conditions.component';
import { SelectedEnvironmentState } from 'src/app/release-management/state/selected-environment.state';


@Component({
  selector: 'app-view-dynamic-workflow',
  templateUrl: './view-dynamic-workflow.component.html',
  styleUrls: ['./view-dynamic-workflow.component.scss']
})
export class ViewDynamicWorkflowComponent implements OnInit, OnDestroy {

  public dynamicworkflow: DynamicWorkflow;
  public invalidLaunch: boolean = true;
  public operation: string;
  public enableLaunchButton: boolean = false;
  public isLoading: boolean = true;
  public interval;
  public selectedEnvironmentStatusCode: number = 0;
  @Select(ViewDynamicWorkflowState.viewWorkflow) dynamicWorkflow$: Observable<DynamicWorkflow[]>;
  @Select(SelectedEnvironmentState.selectedEnvironment) selectedEnvironment$: Observable<ReleaseEnvironment[]>;

  constructor(
    private _activatedroute: ActivatedRoute,
    private modalService: NgbModal,
    private _store: Store,
  ) {

  }

  ngOnInit() {
    this.operation = this._activatedroute.snapshot.url.join('/');

    this.dynamicWorkflow$.subscribe((viewWorkflows) => {
      viewWorkflows.forEach(viewWorkflow => {
        this.dynamicworkflow = viewWorkflow;
        if (viewWorkflow) {
          this.isLoading = false;
        }
      });
    });

    this.selectedEnvironment$.subscribe((_selectedEnvironment) => {
      this.selectedEnvironmentStatusCode = _selectedEnvironment[0].statusCode;
      if (this.selectedEnvironmentStatusCode > 1) {
        this.refreshWorkflow();
      }
    });


  }

  open(task, stageId) {
    this._store.dispatch(new ViewTaskConfiguration({ task, stageId }));
  }

  taskpreconditions(task, stageId) {
    const taskconditionsRef = this.modalService.open(ViewPreConditionsComponent, { windowClass: 'modal-extra-large', centered: true, backdrop: 'static', keyboard: false });
    taskconditionsRef.componentInstance.taskPreConditions = task.preConditions;
  }
  loadReleaseModule() {
    this._store.dispatch(new Navigate(['/release-orchestration']));
  }

  getWorkflowStatusType(workflowStatusCode: number) {
    if (workflowStatusCode) {
      if (workflowStatusCode === 4) {
        return 'success';
      } else if (workflowStatusCode === 5) {
        return 'danger';
      }
      else if (workflowStatusCode === 3) {
        return 'custom-progressbar-inprogress';
      }
      else if (workflowStatusCode === 2) {
        return 'custom-progressbar-configured';
      }
      else if (workflowStatusCode === 1) {
        return 'custom-progressbar-defined';
      } else {
        return 'warning';
      }
    }
  }

  getStatusBadge(statusCode: number) {
    if (statusCode) {
      if (statusCode === 4) {
        return 'badge-success custom-badge-success';
      } else if (statusCode === 5) {
        return 'badge-danger';
      }
      else if (statusCode === 3) {
        return 'custom-badge-Inprogress';
      } else if (statusCode === 2) {
        return 'custom-badge-configured';
      } else if (statusCode === 1) {
        return 'custom-badge-defined';
      } else {
        return 'badge-warning';
      }
    }
  }


  saveAsTemplate() {
    let workflow = new DynamicWorkflow();
    workflow.createDate = this.dynamicworkflow.createDate || null;
    workflow.description = this.dynamicworkflow.description;
    workflow.environmentId = this.dynamicworkflow.environmentId;
    workflow.environmentName = this.dynamicworkflow.environmentName;
    workflow.frequencyId = this.dynamicworkflow.frequencyId;
    workflow.frequencyType = this.dynamicworkflow.frequencyType;
    workflow.name = this.dynamicworkflow.name;
    workflow.overallProgress = this.dynamicworkflow.overallProgress;
    workflow.overallStatus = this.dynamicworkflow.overallStatus;
    workflow.release = this.dynamicworkflow.release;
    workflow.releaseId = this.dynamicworkflow.releaseId;
    workflow.scheduleDate = this.dynamicworkflow.scheduleDate;
    workflow.workflowId = this.dynamicworkflow.workflowId || 0;
    workflow.stages = this.dynamicworkflow.stages;
    this._store.dispatch(new OpenSaveTemplateConfiguration(workflow));

  }

  refreshWorkflow() {

    this.interval = setInterval(() => {

      this._store.dispatch(new PollWorkflow({
        releaseId: this.dynamicworkflow.releaseId,
        environmentId: this.dynamicworkflow.environmentId,
        workflowId: this.dynamicworkflow.workflowId
      }));

    }, 1000 * 15);
  }

  ngOnDestroy() {
    clearInterval(this.interval);
  }

  loadReleaseList() {
    this._store.dispatch(new Navigate(['/release-orchestration']));
  }
  loadReleaseView() {
    this._store.dispatch(new Navigate(['/release-orchestration/viewrelease']));
  }
  loadReleaseDashboard() {
    this._store.dispatch(new Navigate(['/release-orchestration/dashboard']));
  }


}
