import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LogoutComponent } from './auth/logout/logout.component';
import { AuthGuard } from './auth/_guards';
import { ThemeComponent } from './theme/theme.component';

const routes: Routes = [

    { path: 'login', loadChildren: './auth/auth.module#AuthModule' },
    { path: 'logout', component: LogoutComponent },
    {
        path: '',
        component: ThemeComponent,
        canActivate: [AuthGuard],
        children: [
            { path: '', redirectTo: 'discovery', pathMatch: 'full' },
            {
                path: 'index',
                loadChildren: '.\/theme\/pages\/aside-left-display-disabled\/index\/index.module#IndexModule'
            },
            {
                path: '404',
                loadChildren: '.\/theme\/pages\/default\/not-found\/not-found.module#NotFoundModule'
            },
            // {
            //     path: 'release-orchestration',
            //     loadChildren: './release-management/release-management.module#ReleaseManagementModule'
            // },
            // {
            //     path: 'cost-management',
            //     loadChildren: './cost-management/cost-management.module#CostManagementModule'
            // }
            // ,
            {
                path: 'discovery',
                loadChildren: './discovery/discovery.module#DiscoveryModule'
            }
            // {
            //     path: 'migration',
            //     loadChildren: './migration/migration.module#MigrationModule'
            // },
            // {
            //     path: 'environment-migration',
            //     loadChildren: './environment-migration/environment-migration.module#EnvironmentMigrationModule'
            // },
            // {
            //     path: 'on-boarding',
            //     loadChildren: './on-boarding/on-boarding.module#OnBoardingModule'
            // },
            // {
            //     path: 'asset-management',
            //     loadChildren: './asset-management/asset-management.module#AssetManagementModule'
            // },
            // {
            //     path: 'monitoring',
            //     loadChildren: './monitoring/monitoring.module#MonitoringModule'
            // },
            // {
            //     path: 'profile',
            //     loadChildren: '.\/theme\/pages\/default\/profile\/profile.module#ProfileModule'
            // },
            // {
            //     path: 'contact',
            //     loadChildren: '.\/theme\/pages\/default\/contact-info\/contact-info.module#ContactInfoModule'
            // }
            
            // {
            //     path: '',
            //     redirectTo: 'index',
            //     pathMatch: 'full'
            // }
        ]
    },
    {
        path: '**',
        redirectTo: '404',
        pathMatch: 'full'
    }
];

@NgModule({
    //  imports: [RouterModule.forRoot(routes)],
    imports: [
        RouterModule.forRoot(routes, {
          scrollPositionRestoration: 'enabled', // Add options right here
        })
      ],
    exports: [RouterModule]
})
export class AppRoutingModule { }
