import { Component, OnInit, Input } from '@angular/core';
import { TargetConfigurationService } from '../../../services/targetconfig.service';

@Component({
  selector: 'matilda-migration-review',
  templateUrl: './migration-review.component.html',
  styles: []
})
export class MigrationReviewComponent implements OnInit {

  @Input() cloud: string;
  public sourcedata:any;
  public targetdata: any;
  constructor( private targetService:TargetConfigurationService) { }

  ngOnInit() {
    this.targetService.getSourceConfiguration(this.cloud)
    .subscribe((data) => {
      this.sourcedata = data;
    });
    this.targetService.gettargetConfiguration(this.cloud)
    .subscribe((data) => {
      this.targetdata = data;
    });
  }

}
