import { Stage, Task, WorkFlowProperties, DynamicWorkflow, ReleaseEnvironment } from './../models/release.model';
import {
    AddStage, DeleteStage, LoadTask,
    DeleteTask, SaveConfiguration, SavePreConditions,
    SavePostConditions, CloneTask, AddWorkflowProperties,
    EditStageName, EditTaskName, LoadWorkflowTemplate,
    SortingWorkflow, LaunchWorkflow, OpenTaskConfiguration,
    VerifyWorkflowLaunch, ResetCreateWorkflow, LoadWorkflow,
    SaveWorkflowAsTemplate, OpenSaveTemplateConfiguration
} from './../actions/workflow.action';
import { State, Selector, Action, StateContext, Store } from "@ngxs/store";
import * as _ from 'lodash';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotificationsService } from 'angular2-notifications';
import { deserialize, serialize } from 'json-typescript-mapper';
import { ReleaseMgmtAppSettings } from '../release-management.settings';
import { UpdateEnvironment } from '../actions/release.action';
import { ReleasePlanningState } from './release-planning.state';
import { InfraEnvironmentsState } from './infra-impactedsystems.state';
import { Navigate } from '@ngxs/router-plugin';
import { SaveWorkflowTemplateComponent } from '../components/dynamic-workflow/save-workflow-template/save-workflow-template.component';
import { ReleasePluginModalComponent } from '../shared/components/release-plugin-modal/release-plugin-modal.component';
import { PluginConfiguration, ClearPluginConfiguration } from '../actions/workflow-available-services.action';

export class WorkflowModel {
    stages: Stage[];
    workflowProperties: WorkFlowProperties[];
    validLaunch: boolean
}

@State<WorkflowModel>({
    name: 'workflow',
    defaults: {
        workflowProperties: [],
        stages: [],
        validLaunch: false
    },
})
export class DynamicWorkflowState {
    public httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json'
        })
      };

    constructor(private _http: HttpClient,
        private _notificationsService: NotificationsService,
        private _store: Store,
        private modalService: NgbModal,
        private _releaseMgmtAppSettings: ReleaseMgmtAppSettings) { }

    @Selector()
    static getStages(state: WorkflowModel) {
        return state.stages;
    }

    @Selector()
    static workFlowProperties(state: WorkflowModel) {
        return state.workflowProperties[0];
    }

    @Selector()
    static validLaunch(state: WorkflowModel) {
        return state.validLaunch;
    }

    ngxsOnInit(context: StateContext<WorkflowModel>) {
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(AddStage)
    addStage(context: StateContext<WorkflowModel>, { payload }: AddStage) {
        var state = context.getState();
        state.stages.push(payload);
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(DeleteStage)
    deleteStage(context: StateContext<WorkflowModel>, { payload }: DeleteStage) {
        var state = context.getState();
        state.stages = state.stages.filter(stage => stage !== payload)
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(EditStageName)
    editStageName(context: StateContext<WorkflowModel>, { payload }: EditStageName) {
        var state = context.getState();
        state.stages.forEach(stage => {
            if (stage.id === payload.stageId) {
                stage.name = payload.stageName;
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(EditTaskName)
    editTaskName(context: StateContext<WorkflowModel>, { payload }: EditTaskName) {
        var state = context.getState();
        state.stages.forEach(stage => {
            if (stage.id === payload.stageId) {
                stage.tasks.forEach(task => {
                    if (task.id === payload.taskId) {
                        task.taskName = payload.taskName;
                    }
                })
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(LoadTask)
    loadTask(context: StateContext<WorkflowModel>, { payload }: LoadTask) {
        var state = context.getState();
        state.stages.forEach((stage) => {
            if (stage.id === payload.stageId) {
                let task = new Task();
                task.id = (payload.serviceName + payload.action.name + this.randomString()).replace(/ /g, ""),
                    task.serviceName = payload.serviceName,
                    task.actionName = payload.action.description,
                    task.serviceId = payload.action.serviceId,
                    task.actionId = payload.action.actionId
                stage.tasks.push(task);
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(CloneTask)
    cloneTask(context: StateContext<WorkflowModel>, { payload }: CloneTask) {
        var state = context.getState();
        state.stages.forEach((stage) => {
            if (stage.id === payload.selectedStageId) {
                stage.tasks.push(payload.newTask);
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));

    }

    @Action(DeleteTask)
    deleteTask(context: StateContext<WorkflowModel>, { payload }: DeleteTask) {
        var state = context.getState();
        state.stages.forEach((stage) => {
            if (stage.id === payload.stageId) {
                stage.tasks = stage.tasks.filter(task => task !== payload.task);
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(SaveConfiguration)
    saveConfiguration(context: StateContext<WorkflowModel>, { payload }: SaveConfiguration) {
        var state = context.getState();
        state.stages.forEach(stage => {
            if (stage.id === payload.stageId) {
                stage.tasks.forEach(task => {
                    if (task.id === payload.task.id) {
                        task.configuration = Object.assign({}, payload.configuration);
                    }
                });
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(SavePreConditions)
    savePreConditions(context: StateContext<WorkflowModel>, { payload }: SavePreConditions) {
        var state = context.getState();
        state.stages.forEach(stage => {
            if (stage.id === payload.stageId) {
                stage.tasks.forEach(task => {
                    if (task.id === payload.task.id) {
                        task.preConditions = Object.assign({}, payload.preConditions);
                    }
                })
            }
        });
        context.patchState(state);
        context.dispatch(new VerifyWorkflowLaunch({}));
    }

    @Action(SavePostConditions)
    savePostConditions(context: StateContext<WorkflowModel>, { payload }: SavePostConditions) {
        var state = context.getState();
        //Logic goes here..
    }

    @Action(OpenSaveTemplateConfiguration)
    openSaveTemplateConfiguration(context: StateContext<WorkflowModel>, { payload }: OpenSaveTemplateConfiguration) {
        const saveTemplateRef = this.modalService.open(SaveWorkflowTemplateComponent);
        saveTemplateRef.componentInstance.workflow = payload;
    }

    @Action(SaveWorkflowAsTemplate)
    SaveWorkflowAsTemplate(context: StateContext<WorkflowModel>, { payload }: SaveWorkflowAsTemplate) {
        let url = this._releaseMgmtAppSettings.getURL('SAVE_WORKFLOW_TEMPLATE');
        return this._http.post(url, payload, this.httpOptions)
            .subscribe(
                (templateSaveResponse) => {
                    this._notificationsService.success('Template has been Saved!', '');
                    this.modalService.dismissAll();
                },
                (error) => {
                     if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }

    @Action(AddWorkflowProperties)
    addWorkflowProperties(context: StateContext<WorkflowModel>, { payload }: AddWorkflowProperties) {
        var state = context.getState();
        if (state.workflowProperties.length === 0) {
            state.workflowProperties.push(payload);
        } else {
            state.workflowProperties[0] = Object.assign({}, state.workflowProperties[0], payload);
        }
        context.patchState(state);
    }

    @Action(LoadWorkflowTemplate)
    loadWorkflowTemplate(context: StateContext<WorkflowModel>, { payload }: LoadWorkflowTemplate) {
        var state = context.getState();
        let templateId = payload;
        let url = this._releaseMgmtAppSettings.getURL('GET_WORKFLOW_TEMPLATE')
        return this._http.get(_.template(url)({ templateId }))
            .subscribe(
                (workflowTemplate: any) => {
                    if (workflowTemplate) {
                        state.stages = [];
                        let _templateResponse = workflowTemplate[0]['template_json'];
                        let _mappedTemplate = deserialize(DynamicWorkflow, _templateResponse);
                        // Mapping exclusively
                        let _unMappedTasks: any[] = [];
                        _templateResponse.stages.forEach((_unMappedStage) => {
                            _unMappedStage.tasks.forEach((_unMappedTask) => {
                                _unMappedTasks.push(_unMappedTask);
                            });
                        });

                        _mappedTemplate.stages.forEach((_stage) => {
                            _stage.tasks.forEach((_task) => {
                                _unMappedTasks.forEach((_unMappedTask) => {
                                    if (_task.id == _unMappedTask.task_ui_id) {
                                        _task.configuration = _unMappedTask.input ;
                                    }
                                });
                            });
                        });
                        state.stages = _mappedTemplate.stages;
                        context.patchState(state);
                        context.dispatch(new VerifyWorkflowLaunch({}));
                    }
                },
                (error) => {
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            );
    }

    @Action(OpenTaskConfiguration)
    openTaskConfiguration(context: StateContext<WorkflowModel>, { payload }: OpenTaskConfiguration) {
        var state = context.getState();
        context.dispatch(new ClearPluginConfiguration());

        let previousConfig:any = {};
        state.stages.forEach(stage => {
            if (stage.id === payload.stageId) {
                stage.tasks.forEach(_task => {
                    if (_task.id === payload.task.id) {
                        previousConfig = _task.configuration;
                    }
                })
            }
        });


        let serviceId: string = payload.task.serviceId;
        let actionId: string = payload.task.actionId;
        context.dispatch(new PluginConfiguration({ serviceId, actionId }));
        const modalRef = this.modalService.open(ReleasePluginModalComponent, { size: 'lg', centered: true , backdrop: 'static', keyboard: false  });
        modalRef.componentInstance.previousConfig = previousConfig;
        modalRef.componentInstance.task = payload.task;
        modalRef.componentInstance.stageId = payload.stageId;

    }

    @Action(LaunchWorkflow)
    launchWorkflow(context: StateContext<WorkflowModel>, { payload }: LaunchWorkflow) {
        const state = context.getState();
        let workflow = serialize(payload);
        let releaseId = payload.releaseId;
        let environmentId = parseInt(payload.environmentId);
        let workflowId = payload.workflowId;
        const _currentRelease = this._store.selectSnapshot(ReleasePlanningState.releasePlan);
        if (_currentRelease.length > 0) {
            if (_currentRelease[0].releaseTypeCode === 2) {
                const _infraEnvironments = this._store.selectSnapshot(InfraEnvironmentsState.infraEnvironments);
                _infraEnvironments.forEach((_infraEnvironment) => {
                    if (_infraEnvironment.environmentId == payload.environmentId && _infraEnvironment.environmentName == payload.environmentName) {
                        let _impactedSystems = _infraEnvironment.impactedSystems.map(_impactedSystem => serialize(_impactedSystem));
                        workflow = Object.assign({}, workflow, {
                            impactedSystems: _impactedSystems
                        });
                    }
                })

            }
        }
       
        let url = this._releaseMgmtAppSettings.getURL('CREATE_WORKFLOW');
        let workflowUpdateURL = this._releaseMgmtAppSettings.getURL('UPDATE_WORKFLOW');

        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        //Create Workflow POST call
        if (workflowId === 0) {
            this._http.post(_.template(url)({ releaseId, environmentId }), workflow, this.httpOptions)
                .subscribe(
                    (savedWorkflowResponse: any[]) => {
                        if (savedWorkflowResponse) {
                            let updatedEnvironment = deserialize(ReleaseEnvironment, savedWorkflowResponse);
                            let environmentStatus = updatedEnvironment.status;
                            let environmentStatusCode = updatedEnvironment.statusCode;
                            let environmentId = updatedEnvironment.environmentId;
                            let updatedWorkflow = updatedEnvironment.workflows[0];
                            context.dispatch(new UpdateEnvironment({ environmentId, environmentStatus, environmentStatusCode, updatedWorkflow }));
                            this._notificationsService.remove();
                            this._notificationsService.success('WorkFlow information has been Saved!', '');
                            setTimeout(() => {
                                context.dispatch(new Navigate(['/release-orchestration/viewrelease']));
                            }, 3000);
                        }
                    },
                    (error) => {
                        this._notificationsService.remove();
                        if (error.status === 0) {
                            this._notificationsService.error('Error Occurred', 'Server is not responding');
                        } else if (error.status === 400) {
                           // this._notificationsService.error('Something went wrong', error.error['message']);
                            this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                        } else {
                            this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                        }
                    }
                );
        } else { //PATCH the workflow
            this._http.put(_.template(workflowUpdateURL)({ releaseId, environmentId, workflowId }), workflow, this.httpOptions)
                .subscribe(
                    (savedWorkflowResponse: any[]) => {
                        if (savedWorkflowResponse) {
                            let updatedEnvironment = deserialize(ReleaseEnvironment, savedWorkflowResponse);
                            let environmentStatus = updatedEnvironment.status;
                            let environmentStatusCode = updatedEnvironment.statusCode;
                            let environmentId = updatedEnvironment.environmentId;
                            let updatedWorkflow = updatedEnvironment.workflows[0];
                            context.dispatch(new UpdateEnvironment({ environmentId, environmentStatus, environmentStatusCode, updatedWorkflow }));
                            this._notificationsService.remove();
                            this._notificationsService.success('WorkFlow information has been Saved!', '');
                            setTimeout(() => {
                                context.dispatch(new Navigate(['/release-orchestration/viewrelease']));
                            }, 3000);
                        }
                    },
                    (error) => {
                        this._notificationsService.remove();
                        if (error.status === 0) {
                            this._notificationsService.error('Error Occurred', 'Server is not responding');
                        } else if (error.status === 400) {
                            //this._notificationsService.error('Something went wrong', error.error['message']);
                            this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                        } else {
                            this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                        }
                    }
                );
        }
    }

    @Action(VerifyWorkflowLaunch)
    verifyWorkflowLaunch(context: StateContext<WorkflowModel>, { payload }: VerifyWorkflowLaunch) {
        var state = context.getState();
        let validLaunch: boolean = false;
        if (state.stages.length > 0) {
            for (let stage of state.stages) {
                if (stage.tasks.length > 0) {
                    for (let task of stage.tasks) {
                        // && Object.keys(task.configuration).length > 0
                        if (task.taskName != '') {
                            validLaunch = true;
                        } else {
                            validLaunch = false;
                        }
                    }
                } else {
                    validLaunch = false;
                }
            }
        } else {
            validLaunch = false;
        }
        state.validLaunch = validLaunch;
        context.patchState(state);
    }

    @Action(SortingWorkflow)
    sortingWorkflow(context: StateContext<WorkflowModel>, { payload }: SortingWorkflow) {
        var state = context.getState();
        context.patchState(state);
    }

    randomString() {
        return Math.floor((Math.random() * Math.pow(10, 5))) + Math.random().toString(36).substring(7);
    }

    @Action(ResetCreateWorkflow)
    resetCreateWorkflow(context: StateContext<WorkflowModel>, { payload }: ResetCreateWorkflow) {
        var state = context.getState();
        state.workflowProperties = [];
        state.stages = [];
        context.patchState(state);
    }

    @Action(LoadWorkflow)
    loadWorkflow(context: StateContext<WorkflowModel>, { payload }: LoadWorkflow) {
        var state = context.getState();
        let workflowId = payload.workflowId;
        let releaseId = payload.releaseId;
        let envId = payload.environmentId;
        let url = this._releaseMgmtAppSettings.getURL('GET_WORKFLOW');

        this._notificationsService.info('', 'Please wait.. while we are waiting for the server to respond', { timeOut: 0 });
        this._http.get(_.template(url)({ releaseId, envId, workflowId }))
            .subscribe(
                (workflow: any) => {
                    if (workflow) {
                        this._notificationsService.remove();
                        let requestedWorkflow = deserialize(DynamicWorkflow, workflow);
                        let mapWorkflowProperties = new WorkFlowProperties();
                        mapWorkflowProperties.createDate = requestedWorkflow.createDate;
                        mapWorkflowProperties.description = requestedWorkflow.description;
                        mapWorkflowProperties.environmentId = requestedWorkflow.environmentId;
                        mapWorkflowProperties.environmentName = requestedWorkflow.environmentName;
                        mapWorkflowProperties.frequencyId = requestedWorkflow.frequencyId;
                        mapWorkflowProperties.frequencyType = requestedWorkflow.frequencyType;
                        mapWorkflowProperties.name = requestedWorkflow.name;
                        mapWorkflowProperties.overallProgress = requestedWorkflow.overallProgress;
                        mapWorkflowProperties.overallStatus = requestedWorkflow.overallStatus;
                        mapWorkflowProperties.release = requestedWorkflow.release;
                        mapWorkflowProperties.releaseId = requestedWorkflow.releaseId;
                        mapWorkflowProperties.scheduleDate = requestedWorkflow.scheduleDate;
                        mapWorkflowProperties.template = requestedWorkflow.template;
                        mapWorkflowProperties.workflowId = requestedWorkflow.workflowId;

                        context.dispatch(new AddWorkflowProperties(mapWorkflowProperties));
                        // Mapping exclusively
                        let _unMappedTasks: any[] = [];
                        workflow.stages.forEach((_unMappedStage) => {
                            _unMappedStage.tasks.forEach((_unMappedTask) => {
                                _unMappedTasks.push(_unMappedTask);
                            });
                        });
                      
                        requestedWorkflow.stages.forEach((_stage) => {
                            _stage.tasks.forEach((_task) => {
                                _unMappedTasks.forEach((_unMappedTask) => {
                                    if (_task.id == _unMappedTask.task_ui_id) {
                                        _task.configuration = _unMappedTask.input;
                                    }
                                });
                            });
                        });
                        state.stages = requestedWorkflow.stages;
                        context.patchState(state);
                        context.dispatch(new VerifyWorkflowLaunch({}));
                    }
                },
                (error) => {
                    this._notificationsService.remove();
                    if (error.status === 0) {
                        this._notificationsService.error('Error Occurred', 'Server is not responding');
                    } else if (error.status === 400) {
                        this._notificationsService.error('Something went wrong', error.error['message']);
                    } else {
                        this._notificationsService.error('Error Occurred', 'Something went wrong. Please retry again');
                    }
                }
            )
    }
}
