import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';
import { AsideLeftDisplayDisabledComponent } from '../theme/pages/aside-left-display-disabled/aside-left-display-disabled.component';
import { AssetDashboardComponent } from './components/asset-dashboard/asset-dashboard.component';
import { AssetListComponent } from './components/asset-list/asset-list.component';

const routes: Routes = [
    {
        path: '',
        'component': AsideLeftDisplayDisabledComponent,
  'children': [
    {
      path: 'dashboard',
      component: AssetDashboardComponent
   },
   {
       path: 'list',
       component: AssetListComponent
   }
]
    }
];

@NgModule({
    imports: [ 
        SharedModule,
        RouterModule.forChild(routes,)],

    exports: [RouterModule]
})

export class AssetManagementRoutingModule {}
