import { State, StateContext, Selector, Action } from '@ngxs/store';
import { Savings } from '../models/savings.model';
import { CostManagementSettings } from '../cost-management-settings';
import * as _ from 'lodash';
import { deserialize } from 'json-typescript-mapper';
import { GetSavingsCost } from '../actions/savings.action';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NotificationsService } from 'angular2-notifications';

export class SavingsStateModel {
    savingsCost: Savings[];
    savingsMetaData: any[];
}

@State<SavingsStateModel>({
    name: 'savings',
    defaults: {
        savingsCost: [],
        savingsMetaData: []
    }
})

export class SavingsState {
    public metaData: any[] = [];
    public httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
        })
    };
    constructor(private _http: HttpClient,
        private _notificationsService: NotificationsService,
        private _modalService: NgbModal,
        private _costMgmtAppSettings: CostManagementSettings) { }


    calculate(val) {
        if (_.size(val) < 5) {
            return 10
        }
        else if (_.size(val) > 5 && _.size(val) < 12) {
            return 40
        }
        else return 20
    }
    @Selector()
    static savingsCost(state: SavingsStateModel) {
        return state.savingsCost;
    }

    @Selector()
    static savingsMetaData(state: SavingsStateModel) {
        return state.savingsMetaData;
    }


    @Action(GetSavingsCost)
    getSavingsCost(context: StateContext<SavingsStateModel>, payload: GetSavingsCost) {
        const state = context.getState();
        let url = this._costMgmtAppSettings.getLocalURL('SAVINGS_GRID_URL');
        return this._http.get(_.template(url)({  }))
            .subscribe((data: any[]) => {
                if (data) {
                    state.savingsMetaData = [
                        {
                            "field": "application",
                            "title": "Application",
                            "locked": false,
                            "filterable": true,
                            "width": 10
                        },
                        {
                            "field": "resourceID",
                            "title": "Resource ID",
                            "locked": false,
                            "filterable": true,
                            "width": 11
                        },
                        {
                            "field": "resourceName",
                            "title": "Resource Name",
                            "locked": false,
                            "filterable": true,
                            "width": 11
                        },
                        {
                            "field": "environment",
                            "title": "Environment",
                            "locked": false,
                            "filterable": true,
                            "width": 10
                        },
                        {
                            "field": "project_name",
                            "title": "Project Name",
                            "locked": false,
                            "filterable": true,
                            "width": 11
                        },
                        {
                            "field": "cost_spent",
                            "title": "Cost Spent",
                            "locked": false,
                            "filterable": false,
                            "width": 11
                        },
                        {
                            "field": "projected_savings_monthly",
                            "title": "Projected Savings Monthly",
                            "locked": false,
                            "filterable": true,
                            "width": 14
                        },
                        {
                            "field": "projected_savings_yearly",
                            "title": "Projected Savings Yearly",
                            "locked": false,
                            "filterable": false,
                            "width": 11
                        },
                        {
                            "field": "recommendations",
                            "title": "Recommendations",
                            "locked": false,
                            "filterable": false,
                            "width": 11
                        }
                    ];
                    state.savingsCost = [];
                    data.forEach((_Savings) => {
                        const _mappedSavings = deserialize(Savings, _Savings);
                        state.savingsCost.push(_mappedSavings);
                    });
                    context.patchState(state);
                }
            });

    }

}
