import { Component, ViewChild, OnInit, Input } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';

import {
    GridComponent,
    GridDataResult,
    DataStateChangeEvent
} from '@progress/kendo-angular-grid';

import { SortDescriptor } from '@progress/kendo-data-query';

@Component({
  selector: 'migration-list-grid',
  templateUrl: './migration-list-grid.component.html',
  styles: []
})
export class MigrationListGridComponent implements OnInit {

    @Input() public migrationlist;
    @ViewChild(GridComponent) grid: GridComponent;

    public sort: Array<SortDescriptor> = [];
    public pageSize = 10;
    public skip = 0;
    public gridData: GridDataResult;
    public clone = true;

    constructor() { }

    ngOnInit() {     
    }   

    public dataStateChange({ skip, take, sort }: DataStateChangeEvent): void {
        // Save the current state of the Grid component
        this.skip = skip;
        this.pageSize = take;
        this.sort = sort;       
    }

}
