import { Component, OnInit } from '@angular/core';
import { Store, Actions } from '@ngxs/store';
import { Router } from '@angular/router';

@Component({
  selector: 'matilda-discovery-avgdiscoveries',
  templateUrl: './discovery-avgdiscoveries.component.html',
  styleUrls: ['./discovery-avgdiscoveries.component.scss']
})
export class DiscoveryAvgdiscoveriesComponent implements OnInit {
  public discoveryIP$:any;
  public IPdata:any;
  public options:any=[];
  public IPtable:any;

  constructor(private _store: Store, private router: Router, private actions$: Actions) { }

  ngOnInit() {
    this.discoveryIP$ = this._store.select(state => state.discoverylist.discoveryIPlist);
    this.discoveryIP$.subscribe((x) => {
      this.IPdata= x;
     

      this.options= {
        title: {
          text:'Discovered IP', 
          x:'center'
           },
        tooltip: {
        trigger:'item',
        // formatter:"{b} : {c} ({d}%)"
        formatter:"{b} : {c} ({d}%)"
         },
        legend: {
        type:'scroll',
        bottom:40,
          
        },
        
        series: [
         {
          color:['#34bfa3','#ffb822', '#f4516c', '#36a3f7','#87bdd8', '#91c7ae','#749f83', '#ca8622'],
        label: {
        normal: {
        show:true,
        position:'inside',
        
        formatter:'{b} ({c})',
        avoidLabelOverlap:true,
        // align: 'center',
        fontSize:8,
         }, 
        
        },
        
        type:'pie',
        radius: '60%',
        center: ['50%', '40%'],
        data:this.IPdata, 
        itemStyle: {
        emphasis: {
        shadowBlur:10,
        shadowOffsetX:0,
        // shadowColor: 'rgba(0, 0, 0, 0.5)'
         }
         }
         }
         ]
         };
      
                     });
  }
  public onChartEvent($event){
    $event.name;
     this.IPdata.forEach(item => {
     if(item.name== $event.name){
            this.IPtable= item.details
         }
   });
   }


}
