import { Component, OnInit, Input, Output, AfterViewInit } from '@angular/core';
import { ServiceInformationService } from '../../../services/service-information.service';
import { ActivatedRoute, Router } from '@angular/router';
import {MigrationListService} from '../../../services/migration-list.service'

import { Store, Select } from '@ngxs/store';
import { Observable } from 'rxjs';
import * as _ from 'lodash';
import { GetServiceInformation } from '../../../actions/migration.action';
import { MigrationState } from '../../../state/migration.state';

@Component({
  selector: 'matilda-service-information',
  templateUrl: './service-information.component.html',
  styles: []
})
export class ServiceInformationComponent implements OnInit, AfterViewInit {
  // @Input() servicelist: any[] = [];
  objectKeys = Object.keys;
  public selectedIp: any;
  public data;
  public gridData: any[] = [];
  public metaData: any[] = [];
  public storageData: any[] =[]
  public storageMetadata: any[] =[]
  public networkData: any[] =[]
  public networkMetadata: any[] =[]
  public portconfigurationsData: any[] =[]
  public portconfigurationsMetadata: any[]= []
  public serviceData: any[] =[]
  public serviceMetaData: any[] =[]
  public value: number = 10;
  public serviceList: any[];
  public selectedService;
  public count:number = 0;
  public tree1:boolean=false;
  public tree2:boolean=false;
  public y1: any ;
  
  public y2: any ;
  public table: boolean = true;
  public osn=localStorage.getItem("osName");
  @Select(MigrationState.serviceInformationList) serviceInformationList$: Observable<any[]>;
constructor(
  private activatedroute: ActivatedRoute,
  private serivceinfo: ServiceInformationService,
  private router: Router,
  private migrationservice:MigrationListService,
  private _store: Store
) { }

tabled(e) {
  if (e.target.checked) {
    this.table = false;
  }
  else {
    this.table = true;
  }
}
ngOnInit(): void {
  //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
  //Add 'implements OnInit' to the class.
  // this.loadinfra(this.servicelist[0])


  this.serviceList = this._store.snapshot().migration.saveMigrationTable;
  this.loadinfra(this.serviceList[0]["IPAddress"]);

 
  this.migrationservice.getWindowsIISService().subscribe((x) => {
      this.tree2=true;
      this. y2= x
     }
    );
  this.migrationservice.getLinuxWeblogicService().subscribe((x) => {
  this.tree1=true;
  this.y1 = x
 
 });
  
 
}
ngAfterViewInit(): void {
  //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
  //Add 'implements AfterViewInit' to the class.
  this.selectedIp = this.activatedroute.snapshot.queryParams['ip'];
}

setActive(event, newValue) {
  console.log(newValue);
  this.selectedService = newValue;
  this.count = -1;
}

loadinfra(service: any) {
  if (service != null) {

    this._store.dispatch(new GetServiceInformation(service));

    this.serviceInformationList$.subscribe(res => {
          if(res){
            this.data = res;
            this.storageMetadata = this.tableMeta(this.data.Storage_Information);
            this.storageData = this.data.Storage_Information;
            this.networkMetadata = this.tableMeta(this.data.Interfaces);
            this.networkData = this.data.Interfaces;
            this.portconfigurationsMetadata = this.tableMeta(this.data.Ports);
            this.portconfigurationsData = this.data.Ports;
            this.serviceMetaData = this.tableMeta(this.data.services);
            this.serviceData = this.data.services;
            // this.router.navigate(['/migration/new-migration'], { queryParams: { ip: service } })
          }
      });


  }
}
/**
 * tableMeta
 */
public tableMeta(data) {
  let resultsArray: any[] = [], metaData: any[] = [];
            _.each(data, function(service, keytitle) {
                if(service.length){
                    let value = _.keys(service[0]);
                    _.each(value, (key)=>{
                        let obj  = {
                            field: _.trim(key),
                            title: _.startCase(key),
                            locked: false,
                            filterable: true
                        }
                        if(obj.field !== ""){
                            metaData.push(obj)
                        }
                        _.uniqBy(metaData, 'field')
                    });
                    resultsArray.push({
                        "data": service,
                        "metadata": metaData,
                        "title": keytitle
                    });
                    metaData = [];
                }
            });
  return metaData;
}
}
