import { Component, OnInit } from '@angular/core';
import { Store, Select, Actions, ofActionSuccessful } from '@ngxs/store';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';
import { Router } from '@angular/router';
import { ChartComponent } from '@progress/kendo-angular-charts';
import { Workbook } from '@progress/kendo-angular-excel-export';
import { State, process } from '@progress/kendo-data-query';
import { mapKeys } from 'lodash';
import { GridComponent } from '@progress/kendo-angular-grid';
import { exportPDF, Group } from '@progress/kendo-drawing';
import { saveAs } from '@progress/kendo-file-saver';

@Component({
  selector: 'matilda-service-details',
  templateUrl: './service-details.component.html',
  styleUrls: ['./service-details.component.scss']
})
export class ServiceDetailsComponent implements OnInit {
  private chart: ChartComponent;
  public pageSize = 10;
  public topology$: any;
   public topodata: any;
  public seriesColors = [
    '#3B8AD9', '#7BC0F7', '#F18226', '#FFDB69', '#61737B', '#DF7970',
    '#E24B26', '#3B5C9F', '#433B9F', '#633B9F', '#8D3B9F', '#9F3B9C',
    '#9F3B7F', '#9F3B62', '#9F3B47']
  public serviceipAddress: any;
  public servicesummarypiedetails$: any;
  public skip = 0;
  public serviceDiscoveryId;
  public options: any;
  public piedata: any;
  public servicesummarydetails$: any;
  public summarypiedata: any;
  public summarytable: any;
  public summary: any;
  public image: any;
  public gridState: State = {
    sort: [],
    skip: 0,
    take: 10,
  };
  public allData(): ExcelExportData {
    const result: ExcelExportData = {
      data: process(this.summarypiedata, { filter: this.gridState.filter }).data,
    };
    return result;
  }
  // @Select(DiscoveryState.serviceDiscoveryResults) serviceResult$: Observable<ServiceResult>;
  //  @Select(DiscoveryState.servicetopologylist) topology$: Observable<any>;
  constructor(private _store: Store, private router: Router, private actions$: Actions) {
    this.allData = this.allData.bind(this);
  }

  ngOnInit() {
    this.serviceDiscoveryId = localStorage.getItem("serviceDiscoveryId");
    this.serviceipAddress = localStorage.getItem("serviceDiscoveryipAddress");

    // this._store.dispatch(new GetServiceSummaryList({ "discoveryId": this.serviceDiscoveryId }));
    this.servicesummarydetails$ = this._store.select(state => state.discoveryServiceResultDetails.discoveryServiceSummaryPageList);
    this.servicesummarydetails$.subscribe((x) => {
      if (x != null && x != undefined) {
        this.summarypiedata = x.summaryDetails;
        this.summary = x;
        // alert(this.summary.serviceType)
        switch (this.summary.serviceType) {
          
          case 'Weblogic':
            this.image = 'col-md-4 weblogic'
            break;
          case 'Tomcat': this.image = 'col-md-4  tomcat'
            break;
          case 'Jboss': this.image = 'col-md-4 jboss'
            break;
          case 'IIS': this.image = 'col-md-4 iis'
            break
        }
      }
      // alert(this.summary.serviceType);
      this.options = {
        // title: {
        //   text:'Policies Overview', 
        //   x:'center'
        //    },
        tooltip: {
          trigger: 'item',
          // formatter:"{b} : {c} ({d}%)"
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          type: 'scroll',
          bottom: 40,
        },
        series: [
          {
            color: ['#34bfa3', '#ffb822', '#f4516c', '#36a3f7', '#87bdd8', '#91c7ae', '#749f83', '#ca8622'],
            label: {
              normal: {
                show: true,
                position: 'inside',
                formatter: ' {c}',
                avoidLabelOverlap: true,
                // align: 'center',
                fontSize: 8,
              },
            },
            type: 'pie',
            radius: '60%',
            center: ['50%', '40%'],
            data: this.summarypiedata,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                // shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };

    });


    //  this._store.dispatch(new GetServiceTopologyList({ "discoveryId": this.serviceDiscoveryId }));
    this.topology$ = this._store.select(state => state.discoveryServiceResultDetails.discoveryServiceTopologyPageList);
    this.topology$.subscribe((x) => {
      this.topodata = x;
    });
  }

  public onChartEvent($event) {
    $event.name;
    this.summarypiedata.forEach(item => {
      if (item.name == $event.name) {
        this.summarytable = item.details
      }
    });
  }



  public sliderChange(pageIndex: number): void {
    this.skip = (pageIndex - 1) * this.pageSize;
  }

  public onPageChange(state: any): void {
    this.pageSize = state.take;
  }
  //redirecting to dash board
  loadDiscoveryModule() {
    this.router.navigate(['/discovery/dashboard']);
  }
  //Exporting To Exel
  public onExcelExport(args: any): void {
    // Prevent automatically saving the file. We will save it manually after we fetch and add the details
    args.preventDefault();
    const observables = [];
    const workbook = args.workbook;
    const rows = workbook.sheets[0].rows;
    const headerOptions = rows[0].cells[0];
    const data = this.summarypiedata;
    let rowsModified = [];
    rows.forEach((row, index) => {
      if (!index) { // table header rows
        rowsModified.push(row)
      }
      if (index) {
        rowsModified.push(row); // Insert parent row

        const details = data[index - 1].details; // since 0 is on header
        if (details && details.length) { // condn to make sure details is available and data is there
          const keys = Object.keys(details[0]);
          let temp = [{ background: "", colSpan: 1, color: "", rowSpan: 1, value: "" }]
          keys.forEach((key) => {

            temp.push({ background: "#7a7a7a", colSpan: 1, color: "#fff", rowSpan: 1, value: key });
          });
          rowsModified.push({ type: "header", cells: temp })
          details.forEach((detail) => {

            let dataTemp = [{ value: "" }]
            keys.forEach((key) => {
              dataTemp.push({ value: detail[key] });
            });
            rowsModified.push({ type: "data", cells: dataTemp })
          })

        }
      }
    })
    workbook.sheets[0].rows = rowsModified;
    console.log("rowsModified", rowsModified);
    new Workbook(workbook).toDataURL().then((dataUrl: string) => {
      saveAs(dataUrl, 'Services.xlsx');
    });

  }

}
