  //     data: process(this.port, { filter: this.gridState.filter }).data,
  //   };
  //   return result;
  // }


  //Getting details from state--service
  public hostsData$: Observable<any>;
  public memoryData$: Observable<any>;
  public diskData$: Observable<any>;
  public operating$: Observable<any>;
  public totalListeningports$: Observable<any>;
  public totalInterfaces$: Observable<any>;
  public totalServices$: Observable<any>;
  public vcpucount$:  Observable<any>;
  public vcpucores$:  Observable<any>;
  public memory$: Observable<any>;
  public Instance_Type$: Observable<any>;
  public Service_Provider$: Observable<any>;
  public system_date$: Observable<any>;
  public cpucount$: Observable<any>;
  public cpucores$: Observable<any>;
  public cpuname$: Observable<any>;
  public memoryTotal$: Observable<any>;
  public memoryUsed$: Observable<any>;
  public memoryBalance$: Observable<any>;
  public diskTotal$: Observable<any>;
  public diskUsed$: Observable<any>;
  public diskBalance$: Observable<any>;
  public successList: any[];
  public failureList: any[];
  public cpuData$: any;
  public cpuPercent: any;
  public cpuCapacity$ :any;
  public totalServices:any;
  public cpuAvailable$:any;
  public cpuCapacity:any;
  public cpuAvailable:any;
  public cpudata: any;
  public memorydata:any;
  public diskdata:any;
  public hostVersion$ :any;
  public hostRelVersion$ :any;
  public disktotaldata:any;
  public diskuseddata:any
public diskbalancedata:any;
public memorytotaldata:any;
public memoryuseddata:any;
public memorybalancedata:any;
public hostName$:any;
public hostbios$:any;
// green='blue';
  constructor(private _store: Store, private router: Router) {
    // this.allData = this.allData.bind(this);
  }
  ngOnInit() {
    // this._store.dispatch(new GetHostsData({}));
    //Getting id  from state and ip from state to present on top
    this.ID = localStorage.getItem("networkDiscoveryIdHost");
    this.IP = localStorage.getItem("networkDiscoveryHostIP");
    //  this._store.dispatch(new GetHostDetails({ "discoveryIdHost": this.ID}));
     this.portinfo$= this._store.select(state=>state.discoveryResultDetails.hostPortData);
     this.portinfo$.subscribe(element=>{
       this.port = element
         });
   //Hardware details
   this.hostName$ = this._store.select(state => state.discoveryResultDetails.hostName);
    this.hostbios$ = this._store.select(state => state.discoveryResultDetails.hostBios);
    this.operating$ = this._store.select(state => state.discoveryResultDetails.hostOperating);
    this.memory$ = this._store.select(state => state.discoveryResultDetails.hostMemory);
    this.vcpucount$ = this._store.select(state => state.discoveryResultDetails.hostvCpuCount);

    this.hostVersion$ = this._store.select(state => state.discoveryResultDetails.hostVersion);
    this.hostRelVersion$ = this._store.select(state => state.discoveryResultDetails.hostRelVersion);


   
    this.Instance_Type$ = this._store.select(state => state.discoveryResultDetails.hostInstanceType);
    this.Service_Provider$ = this._store.select(state => state.discoveryResultDetails.hostServiceProvider);
    this.system_date$=this._store.select(state => state.discoveryResultDetails.hostSystemDate);
    this.totalListeningports$ = this._store.select(state => state.discoveryResultDetails.hostTotalListeningports);
    this.totalInterfaces$ = this._store.select(state => state.discoveryResultDetails.hostTotalInterfaces);
    this.totalServices$ = this._store.select(state => state.discoveryResultDetails.hostTotalServices);
    // this.totalServices$ .subscribe(TotalServicedata=>{
    //   this.totalServices=TotalServicedata;
    //      })
        //  console.log(this.totalServices$)
        //  console.log(this.totalInterfaces$)
    this.hostsData$ = this._store.select(state => state.discoveryResultDetails.hostsData);
//Cpu Information
    this.cpuData$ = this._store.select(state => state.discoveryResultDetails.hostDetailsData);
    this.cpuData$.subscribe(cpusdata=>{
      this.cpudata=cpusdata;
     
     
    })
    this.cpucount$ = this._store.select(state => state.discoveryResultDetails.hostCpuCount);
    this.cpucores$ = this._store.select(state => state.discoveryResultDetails.hostCpuCores);
    this.cpuname$ = this._store.select(state => state.discoveryResultDetails.hostCpuName);
    this.cpuCapacity$ = this._store.select(state =>  state. discoveryResultDetails.hostCapacityData);
    this.cpuCapacity$ .subscribe(element=>{
      this.cpuCapacity=element;
       //alert(this.diskdata);
    })
    this.cpuAvailable$ = this._store.select(state=> state.discoveryResultDetails.hostAvailableData );
    this.cpuAvailable$ .subscribe(element=>{
      this.cpuAvailable=element;
       //alert(this.diskdata);
    })
    this.diskData$ = this._store.select(state => state.discoveryResultDetails.hostDiskData);
    this.diskData$ .subscribe(disksdata=>{
      this.diskdata=disksdata;
       //alert(this.diskdata);
    })
    //memory information
    this.memoryData$ = this._store.select(state => state.discoveryResultDetails.hostMemoryData);
    this.memoryData$.subscribe(memorysdata=>{
      //alert(memorysdata)
      this.memorydata=memorysdata;
      // alert(JSON.stringify(  this.memorydata))
     
    })
    this.memoryTotal$ = this._store.select(state => state.discoveryResultDetails.hostMemoryTotal);
    this.memoryTotal$.subscribe(mtdata=>{
      this.memorytotaldata=mtdata;
    })
    this.memoryUsed$ = this._store.select(state => state.discoveryResultDetails.hostMemoryUsed);
    this.memoryUsed$ .subscribe(mudata=>{
      this.memoryuseddata=mudata;
    })
    this.memoryBalance$ = this._store.select(state => state.discoveryResultDetails.hostMemoryBalance);
    this.memoryBalance$.subscribe(mbdata=>{
      this.memorybalancedata=mbdata;
    })
    //disk information
    
    this.diskTotal$ = this._store.select(state => state.discoveryResultDetails.hostDiskTotalSpace);
    this.diskTotal$.subscribe(dtdata=>{
      this.disktotaldata=dtdata;
    })
    this.diskUsed$ = this._store.select(state => state.discoveryResultDetails.hostDiskUsedSpace);
    this.diskUsed$ .subscribe(dudata=>{
      this.diskuseddata=dudata;
    })
    this.diskBalance$ = this._store.select(state => state.discoveryResultDetails.hostDiskBalance);
    this.diskBalance$ .subscribe(dbdata=>{
      this.diskbalancedata=dbdata;
    })
  
  }
  
  //red to discovery details component--host detail ip
  loadDiscoveryList(){
    this.router.navigate(['/discovery/discovery-details']);
  }
  //red to discovery dash board
  loadDiscoveryModule(){
    this.router.navigate(['/discovery/dashboard']);
  }
  //red to network discovery list
