colors(data){
  if(data <= '25%' ){
        return '#0CC27E'
      }
     if(data <= '50%' && data > '25%' ){
     return   '#1772b8'
    }
    if (data <= '75%' && data> '50%'){
       return '#FF8D60'
    
    }
    if(data > '75%' ){
      return '#FF586B'
    }
}
