import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AsideLeftDisplayDisabledComponent } from '../theme/pages/aside-left-display-disabled/aside-left-display-disabled.component';
import { DiscoveryComponent } from './components/discovery-list/discovery-home/discovery-home.component';
import { DiscoveryDashboardComponent } from './components/discovery-dashboard/discovery-dashboard.component';
//import { NetworkDiscoveryResultsComponent } from './components/discovery-results/network-discovery-results/network-discovery-results.component';
//import { ServiceDiscoveryResultsComponent } from './components/discovery-results/service-discovery-results/service-discovery-results.component';
import { HostDetailsComponent } from './components/host-details/host-details.component';
import { ServiceDetailsComponent } from './components/service-details/service-details.component';
import { DiscoveryDetailsComponent } from './components/discovery-list/discovery-details/discovery-details.component';
//  import { ServiceDiscoveryComponent } from '../migration/components/new-migration/service-discovery/service-discovery.component';
//  import { DiscoveryDetailsComponent } from './components/discovery-list/discovery-details.component';
const routes: Routes = [
{
  path: '',
  'component': AsideLeftDisplayDisabledComponent, 
  'children': [
    {
      path: '', redirectTo: 'dashboard', pathMatch: 'full'
    },
    {
      path: 'dashboard',
      component: DiscoveryDashboardComponent
    },
    {
      path: 'list',
      component: DiscoveryComponent
    },
    // {
    //   path: 'network-discovery-results',
    //   component: NetworkDiscoveryResultsComponent
    // },
    {
      path: 'discovery-details',
      component: DiscoveryDetailsComponent
    },
    // {
    //   path: 'service-discovery-results',
    //   component: ServiceDiscoveryResultsComponent
    // },
    // {
    //   path: 'service-discovery',
    //   component: ServiceDiscoveryComponent
    // },
    {
      path: 'hostdetails',
      component: HostDetailsComponent
    }
    ,
    {
      path: 'servicedetails',
      component: ServiceDetailsComponent
    }
  ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  // imports: [
  //   RouterModule.forRoot(routes, {
  //     scrollPositionRestoration: 'enabled', // Add options right here
  //   })
  // ],
  exports: [RouterModule]
})
export class DiscoveryRoutingModule { }
