import { MetricsService } from "../../services/metrics.service";
import { Component, OnInit, ViewEncapsulation, Input } from "@angular/core";
import * as _ from "lodash";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { CostSaving,CostEliminate } from "../../models/cost.model";
import { deserialize } from "json-typescript-mapper";
import { CostSavingMain,CostEliminateMain } from "../../models/cost-saving.model";
import { PanelBarItemModel } from "@progress/kendo-angular-layout";

@Component({
  selector: 'matilda-cost-forecast',
  templateUrl: './cost-forecast.component.html',
  styleUrls: [],
  encapsulation: ViewEncapsulation.None
})
export class CostForecastComponent implements OnInit {
  public checked: boolean = true;
  public Math = Math;
  @Input("data")
  public data: any;
  public categories: any[] = [];

  public optimizedata;
  public eliminatedata;
  public costSavingMap = new Map();
  public costSavingArray: any[] = [];
  public costSavingMainArray: any[] = [];

  public costEliminateMap = new Map();
  public costEliminateArray: any[] = [];
  public costEliminateMainArray: any[] = [];

  @Input()
  costSavingMain: CostSavingMain = null;
  
  @Input()
  costEliminateMain: CostEliminateMain = null;

  private items: Array<PanelBarItemModel> = [
    <PanelBarItemModel> {title: "First item", content: "First item content", expanded: true },
   
];

  constructor(
    private metrics: MetricsService) {
  }

  ngOnInit() {

    this.metrics.getOptimize().subscribe(data => {
      this.optimizedata = data.response;
      for(let _dataItem of Object.keys(data.response)){ 
        
        this.costSavingMain = new CostSavingMain();
       // CustomerModel : CostSavingMain = new CostSavingMain();
       this.costSavingMain.cheader = _dataItem;
       
        let costSavingarr: CostSaving[] = [];
        for(let val of data.response[_dataItem]){
          costSavingarr.push(deserialize(CostSaving, val));
        }
      
        this.costSavingMain.costSaving = costSavingarr;
        this.costSavingMainArray.push(this.costSavingMain);

        this.costSavingMap.set(_dataItem, costSavingarr);
      }
    });

    this.metrics.getEliminate().subscribe(data => {
      this.eliminatedata = data.response;

      for(let _dataItem of Object.keys(data.response)){ 
        
      this.costEliminateMain = new CostEliminateMain();
       this.costEliminateMain.cheader = _dataItem;

        let costElminateArray: CostEliminate[] = [];
        for(let val of data.response[_dataItem]){
          costElminateArray.push(deserialize(CostEliminate, val));
        }
        this.costEliminateMain.costEliminate = costElminateArray;
        this.costEliminateMainArray.push(this.costEliminateMain);

        this.costEliminateMap.set(_dataItem, costElminateArray);
      }
    });

    this.initForm();
  }

  public labelContent(e: any): string {
    return `${e.key}: \n ${e.value}$`;
  }

  initForm() { }
}
