export class DashboardData {
  budgetname: string;
  timeunit: string;
  type: string;
  isStack: boolean;
  budgetLimit: BudgetValue[];
  calculatedSpend: CalculatedValue;
  constructor(budgetname, timeunit, type, isStack, budgetLimit, calculatedSpend) {
    this.budgetname = budgetname;
    this.timeunit = timeunit;
    this.type = type;
    this.isStack = isStack;
    this.budgetLimit = budgetLimit;
    this.calculatedSpend = calculatedSpend;
  }
}

export class BudgetValue {
  key: any;
  value: any;
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
}

export class CalculatedValue {
  key: any[];
  value: ActualSpend[];
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
}
export class ActualSpend {
  key: any;
  value: any[] = [];
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
}
