import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';
import { AsideLeftDisplayDisabledComponent } from '../theme/pages/aside-left-display-disabled/aside-left-display-disabled.component';


import { MonitoringDashboardComponent } from './components/monitoring-dashboard/monitoring-dashboard.component';

const routes: Routes = [
    {
        path: '',
        'component': AsideLeftDisplayDisabledComponent,
        'children': [
            {
              path: 'dashboard',
              component: MonitoringDashboardComponent
           }
        ]
    }
];

@NgModule({
    imports: [ 
        SharedModule,
        RouterModule.forChild(routes)],

    exports: [RouterModule]
})

export class MonitoringRoutingModule {}
