import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AsideLeftDisplayDisabledComponent } from '../theme/pages/aside-left-display-disabled/aside-left-display-disabled.component';
import { CreateDynamicWorkflowComponent } from './components/dynamic-workflow/create-dynamic-workflow/create-dynamic-workflow.component';
import { ViewDynamicWorkflowComponent } from './components/dynamic-workflow/view-dynamic-workflow/view-dynamic-workflow.component';
import { ReleasePlanningComponent } from './components/release-planning/release-planning.component';
import { ReleaseViewComponent } from './components/release-view/release-view.component';
import { ReleaseListComponent } from './components/release-list/release-list.component';
import { ReleaseDashboardComponent } from './components/release-dashboard/release-dashboard.component';
import { InfraReleaseViewComponent } from './components/release-view/infra-release-view/infra-release-view.component';

const routes: Routes = [
  {
    path: '',
    component: AsideLeftDisplayDisabledComponent,
    'children': [
      {
        path: '',
        redirectTo: 'list', pathMatch: 'full'
        //component: ReleaseListComponent
      },
      {
        path: 'dashboard',
        component: ReleaseDashboardComponent
      },
      {
        path: 'list',
        component: ReleaseListComponent
      },
      {
        path: 'newrelease',
        component: ReleasePlanningComponent
      },
      {
        path: 'viewrelease',
        component: ReleaseViewComponent
      },
      {
        path: 'infrarelease',
        component: InfraReleaseViewComponent
      },
      {
        path: 'create',
        component: CreateDynamicWorkflowComponent
      },
      {
        path: 'clone',
        component: CreateDynamicWorkflowComponent
      },
      {
        path: 'view',
        component: ViewDynamicWorkflowComponent
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReleaseManagementRoutingModule { }
