import { DiscoveryInfoList, DiscoverySummaryDetails } from "../models/discovery-list.model";
import { State, Store, Selector, Action, StateContext } from "@ngxs/store";
import { HttpClient } from "@angular/common/http";
import { NotificationsService } from "angular2-notifications";
import * as _ from 'lodash';
import { FetchDiscoveryDetailList, FetchDiscoveryServiceDetails } from "../actions/discovery-list.actions";
import { deserialize } from "json-typescript-mapper";
import { DiscoverySettings } from "../discovery-settings";
import { Navigate } from "@ngxs/router-plugin";
export class DiscoverListModel {
    discoveryListDetails: DiscoveryInfoList[];
    discoverySummaryDetails: any[];
    discoveryServicesDetails: any[];
}
@State<DiscoverListModel>({
    name: 'discoveryListDetails',
    defaults: {
        discoveryListDetails: [],
        discoverySummaryDetails: [],
        discoveryServicesDetails: []
    }
})

export class DiscoveryDetailsState {
    constructor(private _store: Store, 
        private _http: HttpClient, 
        private _notificationsService: NotificationsService, 
        private _discoverySettings: DiscoverySettings) {
    }
  
    @Selector()
    static discoveryDetailList(state: DiscoverListModel) {
      return state.discoveryListDetails;
    }
    @Selector()
    static discoverySummaryDetails(state: DiscoverListModel) {
      return state.discoverySummaryDetails;
    }
    @Selector()
    static discoveryServicesDetails(state: DiscoverListModel) {
      return state.discoveryServicesDetails;
    }

    @Action(FetchDiscoveryDetailList)
    fetchDiscoveryDetailList(context: StateContext<DiscoverListModel>,{ payload }: FetchDiscoveryDetailList) {
    
    var state=context.getState();
    let task_id = payload.discoveryID;
    
    let url=this._discoverySettings.getNetworkURL('NETWORK_DISCOVERY_LIST_URL'); 
    return this._http.get(_.template(url)({task_id})).subscribe((data: any[]) => {
        if (data) {
          
            let list:any[] = data["response"];
            let mappedList = list.map(_list => deserialize(DiscoveryInfoList,_list));
            state.discoveryListDetails = mappedList;
            
            state.discoverySummaryDetails = [];
            state.discoverySummaryDetails.push(data["summary"]);

            context.patchState(state);
            context.dispatch(new Navigate(['/discovery/discovery-details']));
            context.dispatch(new FetchDiscoveryServiceDetails(payload));
        }
      }
      );
     
  }

  @Action(FetchDiscoveryServiceDetails)
    fetchDiscoveryServiceDetails(context: StateContext<DiscoverListModel>,{ payload }: FetchDiscoveryServiceDetails) {
        
        var state=context.getState();
        let task_id = payload.discoveryID;
        
        let url=this._discoverySettings.getNetworkURL('NETWORK_DISCOVERYLIST_SERVICE_LIST_URL'); 
        return this._http.get(_.template(url)({task_id}))
        .subscribe((data: any[]) => {
          if (data) {
            state.discoveryServicesDetails = data["Services"];
            context.patchState(state);
            
          }
        }
          );
         
     
  }
}