import { CreateWorkflow, StartRelease, StartEnvironmentRelease, OpenCloneEnvironmentConfiguration, RehydrateEnvironment } from './../../actions/release-planning.action';
import { Component, OnInit, DoCheck } from "@angular/core";
import {
  ActivatedRoute
} from "../../../../../node_modules/@angular/router";
import { Store, Select } from "@ngxs/store";
import { Observable } from "rxjs";
import { ReleasePlan, ReleaseEnvironment } from "../../models/release.model";
import { ReleasePlanningState } from "../../state/release-planning.state";
import { OpenEnvironmentConfiguration } from "../../actions/release-planning.action";
import { ViewWorkflow } from '../../actions/workflow.action';
import { InfraEnvironments } from '../../models/impactedSystems.model';
import { InfraEnvironmentsState } from '../../state/infra-impactedsystems.state';
import { Navigate } from '@ngxs/router-plugin';
import { GETEnvironmenTypes } from '../../actions/release-planning-dropdowns.action';
import { ChangeSelectedEnvironment } from '../../actions/selected-environment.action';
import { process, State } from '@progress/kendo-data-query';
import { DataStateChangeEvent } from '@progress/kendo-angular-grid';

@Component({
  selector: "app-release-view",
  templateUrl: "./release-view.component.html",
  styleUrls: ["./release-view.component.scss"]
})
export class ReleaseViewComponent implements OnInit, DoCheck {

  @Select(ReleasePlanningState.releasePlan) releaseView$: Observable<ReleasePlan[]>;
  @Select(ReleasePlanningState.environmentList) environmentList$: Observable<ReleaseEnvironment[]>;
  @Select(InfraEnvironmentsState.infraEnvironments) infraEnvironments$: Observable<InfraEnvironments[]>;

  public releaseView: ReleasePlan;
  public operation: string;
  public selected: number = 0;
  public gridData: any;
  public environmentTabLabel: string = 'ENVIRONMENTS';

  public state: State = {
    skip: 0,
    take: 5,
    filter: {
      logic: 'and',
      filters: [{ field: 'environmentName', operator: 'contains', value: '' }],
    },
    sort: [{
      field: '',
      dir: 'asc'
    }]
  };

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _store: Store) {
    this.releaseView = new ReleasePlan();
    this._store.dispatch(new GETEnvironmenTypes());
  }

  ngOnInit() {
    this.operation = this._activatedRoute.snapshot.url.join("/");
    this.releaseView$.subscribe(
      (_releaseView: ReleasePlan[]) => {
        if (_releaseView.length > 0) {
          this.releaseView = _releaseView[0];
          this.gridData = process(this.releaseView.environmentList, this.state);
          if(this.releaseView.releaseTypeCode === 3){
            this.environmentTabLabel = 'SITES';

          }else{
            this.environmentTabLabel = 'ENVIRONMENTS';
          }
        }
      });
  }

  ngDoCheck() {
    this.gridData = process(this.releaseView.environmentList, this.state);
  }
  createEnvironment(releaseId: string) {
    this._store.dispatch(new OpenEnvironmentConfiguration(releaseId));
  }

  createEnvironmentWorkFlow(selectedEnvironment) {
    this._store.dispatch(new ChangeSelectedEnvironment(selectedEnvironment));
    this._store.dispatch(new CreateWorkflow(selectedEnvironment));
  }

  viewEnvironmentWorkflow(selectedEnvironment: ReleaseEnvironment) {

    let releaseId = selectedEnvironment.releaseId;
    let environmentId = selectedEnvironment.environmentId;
    let workflowId = selectedEnvironment.workflows[0].workflowId;
    this._store.dispatch(new ChangeSelectedEnvironment(selectedEnvironment));
    this._store.dispatch(new ViewWorkflow({
      releaseId: releaseId,
      environmentId: environmentId,
      workflowId: workflowId
    }));
  }
  cloneEnvironmentWorkflow(selectedEnvironment) {
    this._store.dispatch(new OpenCloneEnvironmentConfiguration(selectedEnvironment));
  }

  startRelease() {
    this._store.dispatch(new StartRelease({}));
  }

  startEnvironmentRelease(selectedEnvironment) {
    // const reHydrateEnvironmentModalRef = this.modalService.open(ReHydrateComponent, { size: 'lg', centered: true });
    // reHydrateEnvironmentModalRef.componentInstance.selectedEnvironment = selectedEnvironment;
    
    this._store.dispatch(new StartEnvironmentRelease(selectedEnvironment));
  }

  RehydrateEnvironment(selectedEnvironment){
     //VZ Re-hydration
     this._store.dispatch(new RehydrateEnvironment(selectedEnvironment));
  }

  getStatusBadge(status: string) {
    if (status) {
      if (status.toLowerCase() === 'success') {
        return 'm-badge--success';
      } else if (status.toLowerCase() === 'failed' || status.toLowerCase() === 'not started') {
        return 'm-badge--danger';
      }
      else if (status.toLowerCase() === 'in progress' || status.toLowerCase() === 'pending') {
        return 'm-badge--warning';
      } else if (status.toLowerCase() === 'configured') {
        return 'm-badge--info';
      }
      else {
        return 'm-badge--metal';
      }
    } else {
      return 'm-badge--brand';
    }
  }

  getConsolidatedReleaseStatus() {
    let allEnvironmentsStatus: string[] = [];
    let overallReleaseStatus: boolean = false;
    if (this.releaseView.environmentList.length > 0) {
      for (let _environment of this.releaseView.environmentList) {
        allEnvironmentsStatus.push(_environment.status);
      }
      overallReleaseStatus = allEnvironmentsStatus.every(_status => _status === 'Configured');
    } else {
      overallReleaseStatus = false;
    }
    return overallReleaseStatus;
  }

  public dataStateChange(state: DataStateChangeEvent): void {
    this.state = state;
    this.gridData = process(this.releaseView.environmentList, this.state);
  }
  
  loadReleaseList() {
    this._store.dispatch(new Navigate(['/release-orchestration']));
  }
 
  loadReleaseDashboard() {
    this._store.dispatch(new Navigate(['/release-orchestration/dashboard']));
  }
}
