
import { State, Selector, Action, StateContext } from "@ngxs/store";
import { GetImpactedSystems, ResetImpactedSystems } from "../actions/infra-impactedsystems.action";
import {ReleaseMgmtAppSettings} from "../release-management.settings";
import { HttpClient } from "@angular/common/http";
import * as _ from 'lodash';
import { deserialize } from "json-typescript-mapper";
import { InfraEnvironments } from "../models/impactedSystems.model";
import { GetEnvironments } from "../actions/release-planning.action";

export class InfraEnvironmentsModel {
    infraEnvironments: InfraEnvironments[]
}

@State<InfraEnvironmentsModel>({
    name: 'infraEnvironments',
})
export class InfraEnvironmentsState  {
    constructor(private _http: HttpClient,private _releaseMgmtAppSettings:ReleaseMgmtAppSettings){}
    

    @Selector()
    static infraEnvironments(state: InfraEnvironmentsModel) {
        return state.infraEnvironments;
    }

    @Action(GetImpactedSystems)
    getImpactedSystems(context: StateContext<InfraEnvironmentsModel>, { payload }: GetImpactedSystems) {
        let url = this._releaseMgmtAppSettings.getURL('GET_IMPACTED_SYSTEMS');
        let releaseTypeCode: number = payload.releaseTypeCode;
        let releaseId = payload.releaseId;
        const state = context.getState();
        return this._http.get(_.template(url)({releaseId,releaseTypeCode}))
                .subscribe((data: any[])=> {
                    if(data.length> 0){
                        context.dispatch(new GetEnvironments(releaseId));
                        let infraEnvironments = data.map(_infraEnvironment => deserialize(InfraEnvironments, _infraEnvironment));
                        state.infraEnvironments = infraEnvironments;
                        context.patchState(state); 
                    }
                });
    }

    @Action(ResetImpactedSystems)
    resetImpactedSystems(context: StateContext<InfraEnvironmentsModel>, { payload }: ResetImpactedSystems) {
        const state = context.getState();
        state.infraEnvironments = [];
        context.patchState(state);
    }

}
